import { throwErrorAsync } from '@pdd/core-util';
import {
  JS_API_LIST,
  OPEN_TAG_LIST,
  WX_MENU_ITEMS,
  DEFAULT_HIDE_ITEM_LIST,
  SHARE_TYPE,
  SHARE_TYPE_MAP_MENU_ITEM,
  DEFAULT_SHARE_TYPE_ENABLE
} from './constant';
import './weixinJSBridge';
import { sendMetrics } from '@pdd/metrics';

const PACKAGE_NAME = 'com.xunmeng.pinduoduo';
const WX_CHECK_PDD_KEY = 'WxCheckPdd';

// 
function getShareMenuItems(shareTypeEnable, onlyWeChat) {
  const { WECHAT_SESSION, WECHAT_TIMELINE } = SHARE_TYPE;
  // merge
  shareTypeEnable = { ...DEFAULT_SHARE_TYPE_ENABLE, ...shareTypeEnable };
  return Object.keys(shareTypeEnable).reduce(
    (result, shareType) => {
      const menuName = SHARE_TYPE_MAP_MENU_ITEM[shareType];
      // onlyWeChatqqqq
      if(onlyWeChat && [WECHAT_SESSION, WECHAT_TIMELINE].indexOf(+shareType) < 0) {
        menuName && result.menuShareItemsToHide.push(menuName);
      } else {
        const buff = shareTypeEnable[shareType] ? result.menuShareItemsToShow : result.menuShareItemsToHide;
        menuName && buff.push(menuName);
      }
      return result;
    },
    { menuShareItemsToShow: [], menuShareItemsToHide: [] }
  );
}

/**
 * @typedef {Object} WxMenuConfig 
 *
 * @property {boolean} [onlyWeChat=false] qqqq
 * @property {boolean} [disableShare=false] 
 * @property {boolean} [disableCopyUrl=false] 
 * @property {boolean} [enableBrowserOpen=false] 
 */

/**
 * @external {AxiosInstance} https://github.com/axios/axios#instance-methods
 */

/**
 * client
 *
 * @param {{wechatAppID: string, timestamp: number, noceStr: string, signature: ?string, shareTypeEnable: ?Object<string, number>}} wxConfig 
 * @param {WxMenuConfig} wxMenuConfig 
 * @return {Promise<void>}
 */
export function initWxConfig({ wechatAppID, timestamp, nonceStr, signature, shareTypeEnable = {} }, wxMenuConfig) {
  return new Promise((resolve) => {
    const wx = top.wx || window.wx;

    wx.config({
      timestamp,
      signature,
      nonceStr,
      debug: false,
      beta: true,
      appId: wechatAppID,
      jsApiList: JS_API_LIST,
      openTagList: OPEN_TAG_LIST
    });

    wx.ready(() => {
      const { menuShareItemsToShow, menuShareItemsToHide } = getShareMenuItems(
        shareTypeEnable,
        wxMenuConfig.onlyWeChat
      );
      let menuItemsToShow = [];
      let menuItemsToHide = DEFAULT_HIDE_ITEM_LIST.concat(menuShareItemsToHide);

      // disable share
      if(wxMenuConfig.disableShare) {
        menuItemsToHide = menuItemsToHide.concat(menuShareItemsToShow);
      } else {
        menuItemsToShow = menuItemsToShow.concat(menuShareItemsToShow);
      }

      // disable copy url
      if(wxMenuConfig.disableCopyUrl) {
        menuItemsToHide.push(WX_MENU_ITEMS.COPY_URL);
      }

      if(wxMenuConfig.enableBrowserOpen) {
        menuItemsToHide.splice(0, 1);
      }

      wx.hideMenuItems({ menuList: menuItemsToHide });
      wx.showMenuItems({ menuList: menuItemsToShow });

      if(!window.wxReady) {
        Object.defineProperty(window, 'wxReady', {
          get() {
            console.warn('window.wxReady ');
            return true;
          }
        });
      }

      // wx.configready
      resolve();
    });

    wx.error((res) => {
      throwErrorAsync(res, {
        error_type: 'wx_config_error',
        error_message: JSON.stringify(res),
        payload: {
          signature
        }
      });
    });

    initAppInstallState();
  });
}

/**
 * app
 */
function initAppInstallState() {
  window.wx.ready(async () => {
    const { wxCheckPdd } = await getAppInstallState();
    const preWxCheckPdd = +localStorage.getItem(WX_CHECK_PDD_KEY);
    // ,
    if(preWxCheckPdd !== wxCheckPdd) {
      sendMetrics({
        op: 'event',
        // eslint-disable-next-line
        sub_op: 'app_check',
        uninstall_status: wxCheckPdd
      });
    }
    localStorage.setItem(WX_CHECK_PDD_KEY, '' + wxCheckPdd);
  });
}

/**
 * app
 *
 * @ignore
 * @return {Promise<{wxCheckPdd: number, version: string}>}
 */
function getAppInstallState() {
  if(!(window.WeixinJSBridge && window.WeixinJSBridge.invoke)) {
    return Promise.resolve({
      version: '',
      wxCheckPdd: 2 // - 1;- 0; 2
    });
  }

  return new Promise((resolve) => {
    window.WeixinJSBridge.invoke(
      'getInstallState',
      { packageName: PACKAGE_NAME, packageUrl: 'pinduoduo://' },
      // eslint-disable-next-line
      ({ err_msg = '', versionName = '' }) => {
        // eslint-disable-next-line
        const installed = /get_install_state:yes/.test(err_msg);
        const uninstalled = /get_install_state:no/.test(err_msg);
        resolve({
          version: versionName,
          wxCheckPdd: installed ? 1 : uninstalled ? 0 : 2
        });
      }
    );
  });
}
