import { nanoid } from './nanoid';
import { getCurrentPlatform, SYSTEM } from '@pdd/platform-util';
import { setTitle } from '@pdd/native-util';

const LAUNCH_WAIT_TIME = 2000; // 2s
const LAUNCH_TOTAL_TIME_THRESHOLD = 2500; // 2.5sjs
const LAUNCH_FAIL = 'App';

let launchTimer;
/**
 * 
 * @private
 * @typedef {Object} LAUNCH_TYPE
 * @property {number} openDirectly 
 * @property {number} openByTag A
 * @property {number} openByIframe iframe
 */
const LAUNCH_TYPE = {
  openDirectly: 1,
  openByTag: 2,
  openByIframe: 3
};

/**
 *
 * 
 * @param {string} url url
 * @param {LAUNCH_TYPE} launchType 
 * @return {Promise} Promise
 */
export function launchApp(url, launchType) {
  // 
  let hidden, visibilityChange;
  if (typeof document.hidden !== 'undefined') {
    // Opera 12.10 and Firefox 18 and later support
    hidden = 'hidden';
    visibilityChange = 'visibilitychange';
  } else if (typeof document.webkitHidden !== 'undefined') {
    hidden = 'webkitHidden';
    visibilityChange = 'webkitvisibilitychange';
  }
  const launchBegin = Date.now();
  const getLaunchResultPromise = () => {
    return new Promise((resolve, reject) => {
      // 
      document.addEventListener(visibilityChange, handleVisibilityChange, false);

      function handleVisibilityChange(e) {
        document.removeEventListener(visibilityChange, handleVisibilityChange);
        clearTimeout(launchTimer);
        if (document[hidden] || e.hidden || document.visibilityState === 'hidden') {
          return resolve();
        }
        reject(new Error(LAUNCH_FAIL));
      }

      launchTimer = setTimeout(() => {
        document.removeEventListener(visibilityChange, handleVisibilityChange);
        // 
        if (document[hidden] || document.visibilityState === 'hidden') {
          return resolve();
        }

        if (Date.now() - launchBegin > LAUNCH_TOTAL_TIME_THRESHOLD) {
          return resolve();
        }

        reject(new Error(LAUNCH_FAIL));
      }, LAUNCH_WAIT_TIME);
    });
  };
  // launchTypelaunchType
  if (launchType) {
    switch (launchType) {
      case LAUNCH_TYPE.openByIframe:
        openByIframe(url);
        break;
      case LAUNCH_TYPE.openByTag:
        openByTag(url);
        break;
      case LAUNCH_TYPE.openDirectly:
        openDirectly(url);
        break;
    }
    return getLaunchResultPromise();
  }
  const ua = navigator.userAgent.toLowerCase();
  const env = {
    isIOS: getCurrentPlatform().system === SYSTEM.IOS,
    isAndroid: /android|adr/.test(ua) && !/windows phone/.test(ua),
    isChrome: /(?:chrome|crios)\/([\d\.]+)/.test(ua), // eslint-disable-line
    isQQ: /qq\/|mqqbrowser\/[\d\.]+/.test(ua) // eslint-disable-line
  };
  env.isOriginalChrome = env.isAndroid && env.isChrome;
  if (env.isIOS || env.isQQ) {
    openByTag(url);
  } else if (env.isOriginalChrome) {
    openDirectly(url);
  } else {
    openByIframe(url);
  }
  return getLaunchResultPromise();
}

/**
 * app
 * @param {string} url url
 */
function openDirectly(url) {
  window.location.href = url;
}

/**
 * iframe
 *  chromeiframe,safariiframe
 * @param {string} url
 */
function openByIframe(url) {
  const iframe = document.createElement('iframe');
  iframe.src = url;
  iframe.style.display = 'none';
  document.body.appendChild(iframe);
  setTimeout(() => {
    document.body.removeChild(iframe);
  }, 200);
}

const DEFAULT_TAG_ID = `pdd-launch-node-${nanoid()}`;

/**
 * a
 * @param url
 */
function openByTag(url) {
  let node = document.getElementById(DEFAULT_TAG_ID);
  if (!node) {
    node = document.createElement('a');
    node.id = DEFAULT_TAG_ID;
    node.style.display = 'none';
    document.body.appendChild(node);
  }

  node.href = url;
  node.click();
}

/**
 * 
 *
 * @param {string} title 
 */
export function changeTitle(title) {
  if (!process.env.BROWSER) {
    return;
  }

  if (getCurrentPlatform().isNativePlatform) {
    setTitle({ title });
  } else {
    document.title = title;
  }
}
