import React from 'react';
import PropTypes from 'prop-types';
import { useChunk } from './context';
import ErrorBoundary from './ErrorBoundary';

const ServerChunkRender = ({ chunkDataKey, Component, ComponentProps, children }) => {
  const chunk = useChunk(chunkDataKey);
  const chunkId = 'chunk-' + Math.random().toString(36).slice(2);
  chunk && chunk.add(chunkId, Component, ComponentProps);
  return (
    <div data-chunk-id={chunkId}>{children}</div>
  );
};

const ClientChunkRender = ({ chunkDataKey, Component, ComponentProps, FallbackComponent }) => {
  const chunk = useChunk(chunkDataKey);

  return chunk.isReady ? <div>
    <ErrorBoundary chunkData={chunk.data} {...ComponentProps} FallbackComponent={FallbackComponent} >
      <Component chunkData={chunk.data} {...ComponentProps} />
    </ErrorBoundary>
  </div> : <div suppressHydrationWarning dangerouslySetInnerHTML={{ __html: '' }} />;
};

export const ChunkRender = process.env.BROWSER ? ClientChunkRender : ServerChunkRender;

ChunkRender.propTypes = {
  chunkDataKey: PropTypes.string.isRequired,
  Component: PropTypes.elementType.isRequired,
  ComponentProps: PropTypes.object,
  children: PropTypes.node,
  FallbackComponent: PropTypes.elementType
};
