/**
 * @file ShareController/qq.js
 * @author daben
 */
import ShareController from './base';
import ShareType from '../ShareType';
import ShareEventType from '../ShareEventType';
import ShareResultType from '../ShareResultType';
import QQShareEntity from '../ShareEntity/QQShareEntity';
import ShareError from '../ShareError';
const { ShareErrorCode } = ShareError;
const SUPPORTED_SHARE_TYPE = [
  ShareType.QQ_CHAT_SESSION,
  ShareType.QQ_ZONE,
  ShareType.WECHAT_SESSION,
  ShareType.WECHAT_TIMELINE,
  ShareType.COPY_LINK,
  ShareType.COPY_CIPHER
];

const shareTypeMapType = {
  [ShareType.QQ_CHAT_SESSION]: 0,
  [ShareType.QQ_ZONE]: 1,
  [ShareType.WECHAT_SESSION]: 2,
  [ShareType.WECHAT_TIMELINE]: 3
};

const SHARE_TYPE_MAP_TAG = {
  [ShareType.QQ_CHAT_SESSION]: 'menuItem:share:qq',
  [ShareType.QQ_ZONE]: 'menuItem:share:QZone',
  [ShareType.WECHAT_SESSION]: 'menuItem:share:appMessage',
  [ShareType.WECHAT_TIMELINE]: 'menuItem:share:timeline'
};

/**
 * @access private
 * @extends {ShareController}
 */
class QQShareController extends ShareController {
  constructor(opts) {
    super(opts);
    this.ShareEntityClass = QQShareEntity;
    this._enableShareTypes = SUPPORTED_SHARE_TYPE.concat();
  }

  /**
   * @override
   * @param {ShareType} shareType 
   */
  share(shareType) {
    if (this._enableShareTypes.indexOf(shareType) < 0) {
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }
    if (ShareType.COPY_CIPHER === shareType) {
      return this.copyCipher();
    }
    if (
      typeof mqq === 'undefined' ||
      typeof window.mqq.ui === 'undefined'
    ) {
      return ShareErrorCode.SHARE_NOT_SUPPORTED;
    }
    const shareEntity = this.getShareEntityByShareType(shareType);
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    shareEntity.normalize(shareType).then((shareParams = {}) => {
      // qq share_type is special
      shareParams.share_type = shareTypeMapType[shareType];
      window.mqq.ui.shareMessage(
        shareParams,
        this.createShareCallback(shareType)
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * @override
   */
  _adjustMenu() {
    // diff this._enableShareTypes && SUPPORTED_SHARE_TYPE
    // and hide disabled type
    const tags = SUPPORTED_SHARE_TYPE.filter((type) => {
      return this._enableShareTypes.indexOf(type) < 0;
    }).map((type) => {
      return SHARE_TYPE_MAP_TAG[type];
    });
    window.mqq && window.mqq.hideMenuItems({
      menuList: tags
    });
  }

  /**
   * @ignore
   */
  createShareCallback(shareType) {
    return (res = {}) => {
      const code = parseInt(res.retCode, 10);
      switch (code) {
        case 0:
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.SUCCESS
          });
          break;
        case 1:
        case -2:
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.CANCEL
          });
          break;
        default:
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.FAIL
          });
      }
    };
  }

  /**
   * @override
   */
  _setupShareTypes() {
    const currentLength = this._enableShareTypes.length;
    this._enableShareTypes = SUPPORTED_SHARE_TYPE.filter((shareType) => {
      return (this.disableShareTypes || []).indexOf(shareType) < 0;
    });
    return currentLength === this._enableShareTypes.length;
  }

  /**
   * @override
   */
  _setupPlatformShareSDK() {
    if (
      typeof mqq === 'undefined' ||
      typeof window.mqq.ui === 'undefined'
    ) {
      this.emit(ShareEventType.SDK_INIT_FAILED);
      return Promise.resolve();
    }
    window.mqq.ui.setOnShareHandler((type) => {
      const shareType = SUPPORTED_SHARE_TYPE[parseInt(type, 10)];
      const shareEntity = this.getShareEntityByShareType(shareType);
      // if no share params, do not setup
      if (!shareEntity || !SHARE_TYPE_MAP_TAG[shareType]) {
        return;
      }
      this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
      shareEntity.normalize(shareType).then((shareParams = {}) => {
        // qq share_type is special
        shareParams.share_type = type;
        window.mqq.ui.shareMessage(
          shareParams,
          this.createShareCallback(shareType)
        );
      }, this.getNormalizeErrorHandler(shareType));
    });
    return Promise.resolve();
  }

  /**
   * @override
   * @param {ShareType} shareType 
   */
  checkShareTypeEnable(shareType) {
    if (this._enableShareTypes.indexOf(shareType) < 0) {
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }
    return 0;
  }
}

QQShareController.SUPPORTED_SHARE_TYPE = SUPPORTED_SHARE_TYPE;

export default QQShareController;
