/**
 * A web
 * package_activity/fruits/apple/apple?
 * target_page=encode(
 *     /pages/web/web?specialUrl=1&src=encode(h5 === shareURL)
 * )
 *
 * B 
 * package_activity/fruits/apple/apple?
 * target_page=encode(
 *     pages/launch/launch?
 *         jump_url={
 *             (/pages/web/web?specialUrl=1&src=encode(h5 === shareURL))
 *         }&launch_url={h5 === shareURL}
 * )
 *
 * C 
 * package_activity/fruits/apple/apple?
 * target_page=encode(
 *     /package_activity/garden/pages/index/index?xxx=123
 * )
 */
import { buildQuery } from '@pdd/core-util';
import buildURL from './buildURL';
import URLParser from './URLParser';
import getUserID from './getUserID';
import { app as applicationConfig } from '@pdd/configs';
let config = {};
if (typeof window !== 'undefined') {
  config = applicationConfig.get(location.host) || {};
}

export function pickUpRefer(shareEntity = {}) {
  const query = new URLParser(shareEntity.shareURL).query || {};
  return {
    refer_share_id: query.refer_share_id,
    // userID 
    // refershare_url url
    // refer_share_uidshare_urlrefer_share_uid
    // undefined, undefined
    refer_share_uid: query.refer_share_uid || getUserID(),
    refer_share_channel: query.refer_share_channel,
    refer_share_uin: query.refer_share_uin
  };
}

export function toHttps(url = '') {
  return url.replace(/^(?:https?:?)?\/\//, 'https://');
}

export default function (shareEntity = {}, opts = {}) {
  const miniObjectPath = shareEntity.miniObjectPath;
  const statusPartA = new URLParser(miniObjectPath);
  const statusPartB = new URLParser(statusPartA.query.target_page || '');
  const partBQuery = statusPartB.query || {};
  const appendQuery = opts.appendQuery || {};
  const {
    // refer_share_uidkey
    refer_share_uid: referShareUid,
    ...referInfo
  } = { ...pickUpRefer(shareEntity), ...appendQuery };
  const domain = shareEntity.shareDomain || config.shareDomain;
  // web
  // srcshareURL
  if (partBQuery.specialUrl || partBQuery.src) {
    if (partBQuery.src) {
      partBQuery.src = buildURL(referInfo, partBQuery.src, domain);
    } else {
      partBQuery.src = toHttps(shareEntity.shareURL);
    }
    return `${statusPartA.pathname}?${buildQuery(Object.assign({}, statusPartA.query, {
      target_page: `${statusPartB.pathname}?${buildQuery(partBQuery)}`
    }))}`;
  }
  // 
  // jump_urllaunchURLshareURL
  if (partBQuery.jump_url || partBQuery.launch_url) {
    if (partBQuery.jump_url) {
      const jumpURLStatus = new URLParser(partBQuery.jump_url);
      // 
      if (!jumpURLStatus.hostname) {
        const jumpURLQuery = jumpURLStatus.query;
        if (jumpURLQuery.specialUrl) {
          //  jumpURLQuery.src  shareURL
          //  jumpURLQuery.src  
          if (jumpURLQuery.src) {
            jumpURLQuery.src = buildURL(referInfo, jumpURLQuery.src, domain);
          } else {
            jumpURLQuery.src = toHttps(shareEntity.shareURL);
          }
          partBQuery.jump_url = `${jumpURLStatus.pathname}?${buildQuery(jumpURLQuery)}`;
        }
      }
    }
    // launch_url  
    // launch_url  
    if (partBQuery.launch_url) {
      partBQuery.launch_url = buildURL(referInfo, partBQuery.launch_url);
    }
    return `${statusPartA.pathname}?${buildQuery(Object.assign({}, statusPartA.query, {
      target_page: `${statusPartB.pathname}?${buildQuery(partBQuery)}`
    }))}`;
  }
  // 
  // targetPage
  // target_page
  if (!statusPartA.hostname && !statusPartB.hostname && statusPartA.query.target_page) {
    return `${statusPartA.pathname}?${buildQuery(Object.assign({}, statusPartA.query, {
      target_page: `${statusPartB.pathname}?${buildQuery(
        Object.assign(partBQuery, referInfo)
      )}`
    }))}`;
  }
  // 
  return `${statusPartA.pathname}?${buildQuery(Object.assign({}, statusPartA.query, referInfo))}`;
}
