/**
 * @file client
 * @author direnjie(direnjie@pinduoduo.com)
 */
import { callLogin } from './login.js';
import {
  setUserCookie,
  setUserLocalstorage,
  fetchAccessTokenAndUid,
  clearUserLocalStorage,
  clearUserCookie,
  getUidFromLocal,
  getUinFromLocal,
  getAccessTokenFromLocal,
  isNativeSupportSilentLogin
} from './util';

/**
 * 
 */
let authInited = false;

/**
 * usergetAppLoginType
 * tokenuiduintypeapp
 */
let userInfo = {};

function setUserVariableStorage(accessToken, uid, uin, type) {
  userInfo = { accessToken, uid, uin, type };
}

function clearUserVariableStorage() {
  userInfo = {};
}

/**
 * cookie
 *
 * @param {UserInfo} userInfo
 * @return {UserInfo}
 */
function onLoginSuccess({ accessToken, uid, uin = '', type }) {
  uid = uid && String(uid);
  setUserCookie(accessToken, uid, uin);
  setUserVariableStorage(accessToken, uid, uin, type);
  setUserLocalstorage(accessToken, uid, uin);
  return { accessToken, uid, uin, type };
}

/**
 * 
 */
export function logout() {
  clearUserCookie();
  clearUserVariableStorage();
  clearUserLocalStorage();
}

/**
 * @param {AxiosInstance} axiosHttp @pdd/isomorphic-request
 *
 * )
 * cookiecodenative apicookielocalStorage
 *
 * @return {Promise<{UserInfo}>}
 * Error
 * @throws {Error} code
 * @throws {UnLoginError} errorCode40001
 * @throws {RedirectedError} errorCode302
 */
export function init(axiosHttp) {
  authInited = true;

  return fetchAccessTokenAndUid(axiosHttp).then(onLoginSuccess, (e) => {
    logout();
    throw e;
  });
}

/**
 * accessToken -> [] -> cookie
 *
 * @return {Promise<string>}
 */
export async function getAccessToken() {
  if(!authInited) {
    console.warn('await init() first to get correct accesstoken');
  }
  return userInfo.accessToken || getAccessTokenFromLocal();
}

/**
 * uid -> [] -> cookie
 *
 * @return {Promise<string>}
 */
export async function getUid() {
  if(!authInited) {
    console.warn('await init() first to get correct uid');
  }
  return userInfo.uid || getUidFromLocal();
}

/**
 * uin -> [] -> cookie
 *
 * @return {Promise<string>}
 */
export async function getUin() {
  if(!authInited) {
    console.warn('await init() first to get correct uin');
  }
  return userInfo.uin || getUinFromLocal();
}

/**
 * userInfo
 *
 * @return {Promise<UserInfo>}
 */
export async function getUserInfo() {
  if(!authInited) {
    console.warn('await init() first to get correct userInfo');
  }

  return { ...userInfo };
}

/**
 * userInfo
 * @notice !!!
 * @return {UserInfo}
 */
export function getUserInfoSync() {
  if(!authInited) {
    throw new Error('getUserInfoSync');
  }

  return { ...userInfo };
}

/**
 * 
 *
 * @param {LoginParams} [loginParams={}] 
 *
 * @param {Object} [loginParams.nativeParams] native
 * @param {number} [loginParams.nativeParams.login_type=0] ; 0: (), 1: (), 2: (, );
 *  https://docs.corp.yiran.com/pinbridge/route/JSNavigation.html
 * @param {number} [loginParams.nativeParams.login_page=0] ; 0: /qq 1:
 * @param {number} [loginParams.nativeParams.login_style=0] ;
 * login_page=0/qq0:, 1:, 2:, 4:7, 5:logo
 * @param {number} [loginParams.nativeParams.login_channel=-1] ;
 * -1: qqqq;
 * 1: (qq);
 * 2: qq/wxbutton;wx qq [4.78 4.79, ]
 * 3: 
 *
 * @param {string} [loginParams.from=location.href] url
 * @param {boolean} [loginParams.silent=true] apptrue,falsetrue,app5.24.0
 * @param {string} [loginParams.scopeType=ScopeType.Base] oauth https://open.weixin.qq.com/cgi-bin/showdocument?action=dir_list&t=resource/res_list&verify=1&id=open1419316505&token=&lang=zh_CN
 *
 * @return {Promise<UserInfo>} appresolve, userInfoaccessTokenError
 * @throws {RedirectedError} errorCode302
 * @throws {Error} apperrorCode60006
 *
 */
export function login(loginParams = {}) {
  // silenttrue,@pdd/isomorphic-request0.14.9,package.jsondependencies
  if(typeof loginParams.silent === 'undefined') {
    loginParams.silent = true;
  }
  const isNativeSilentLogin = loginParams.silent && isNativeSupportSilentLogin();
  if(!isNativeSilentLogin) {
    logout();
  }
  return callLogin(loginParams).then(onLoginSuccess);
}
