// 
export const bizErrorCheck = () => {
  return {
    response: response => {
      const { data, config, request } = response;

      if (data && data.error_code && data.success !== true) {
        const message = data.error_msg || 'unknown biz error';
        const error = new Error(message);
        error.errorType = 'api_biz_error';
        error.errorCode = data.error_code;
        error.config = config;
        error.request = request;
        error.response = response;

        throw error;
      }

      return response;
    },

    responseError: error => {
      if (!error.response) {
        error.errorType = 'api_http_error';
        throw error;
      }

      const { data } = error.response;

      if (data && data.error_code) {
        error.message = data.error_msg || error.message;
        error.errorCode = data.error_code;
        error.errorType = 'api_biz_error';
      }

      throw error;
    }
  };
};

// 
export const retrieveData = () => {
  return {
    response: response => response.config.useRawResponse ? response : response.data
  };
};

// 
export const tracker = () => {
  /* eslint-disable no-console */
  return {
    requestError: error => {
      const { baseURL, url, data, params, method, headers } = error.config;
      console.group('Axios Request requestError:' + url);
      console.log('request:', { baseURL, url, data, params, method, headers });
      console.error('error:', error);
      console.groupEnd();
      throw error;
    },

    response: response => {
      const { baseURL, url, data, params, method, headers } = response.config;
      console.group('Axios Request responseSuccess:' + url);
      console.log('request:', { baseURL, url, data, params, method, headers });
      console.log('response:', response);
      console.groupEnd();
      return response;
    },

    responseError: error => {
      const { baseURL, url, data, params, method, headers } = error.config;
      console.group('Axios Request responseError:' + url);
      console.log('request:', { baseURL, url, data, params, method, headers });
      console.log('response:', error.response);
      console.error('error:', error);
      console.groupEnd();
      throw error;
    }
  };
  /* eslint-enable no-console */
};
