/**
 *  URL  ID
 *
 * ID  21 
 *
 * @param {number} [size=21]  ID 
 *
 * @returns {string} ID
 *
 * @example
 * nanoid() //=> "vux6ytrz7wm9z2x80dmzj"
 */
export function nanoid(size = 21) {
  let id = '';
  while (id.length < size) {
    id += Math.random()
      .toString(36)
      .slice(2);
  }
  return id.slice(0, size);
}
