import { PLATFORM } from '@pdd/platform-util';
import { parseQuery } from '@pdd/core-util';
import { memo } from '../common/utils';

const HTJ_HOST = 'https://pmmtk.hutaojie.com';
const HOST = 'https://pmmtk-a.pinduoduo.com';

const LOG_PATH = '/api/pmm/front_log';
const ERROR_PATH = '/api/pmm/front_err';

const defaultBizSide = 'consumer-platform-fe';

// 
export const CATEGORY = {
  ERROR: 5, //  5
  LOG: 6 //  6  type: 600 
};

export const ERROR_TYPE_CONFIG = {
  API_ERROR: 500,
  RESOURCE_ERROR: 501,
  CUSTOM_ERROR: 502,
  NORMAL_LOG: 600
};

const isTestEnv =
  typeof window !== 'undefined'
    ? window.location.href.includes('hutaojie') || window.location.hostname.includes('panduoduo.yangkeduo.com')
    : true;

// 
export const getReporterUrl = (type, appInfo = {}) => {
  const { pmmHost } = appInfo;
  const currentHost = pmmHost || HOST;
  if(type === ERROR_TYPE_CONFIG.NORMAL_LOG) {
    return isTestEnv ? `${HTJ_HOST}${LOG_PATH}` : `${currentHost}${LOG_PATH}`;
  }
  return isTestEnv ? `${HTJ_HOST}${ERROR_PATH}` : `${currentHost}${ERROR_PATH}`;
};

export const getAppInfo = ({ bizSide, appId, testAppId }) => {
  if(typeof window !== 'undefined') {
    const appInfo = {
      bizSide: defaultBizSide
    };

    if(bizSide) {
      appInfo.bizSide = bizSide;
    }

    if(isTestEnv && !testAppId) {
      console.error(' testAppId  id');
      return appInfo;
    }

    if(!appId) {
      console.error(' appId  id');
      return appInfo;
    }

    appInfo.appId = isTestEnv ? testAppId : appId;
    return appInfo;
  }
  return {};
};

export const getRunningAppId = (platform) => {
  const RunningAppConfig = isTestEnv
    ? {
        [PLATFORM.PddAndroid]: '1',
        [PLATFORM.PddIOS]: '3',
        [PLATFORM.PDDTinyAndorid]: '0'
      }
    : {
        [PLATFORM.PddAndroid]: '3',
        [PLATFORM.PddIOS]: '5',
        [PLATFORM.PDDTinyAndorid]: '59'
      };
  return RunningAppConfig[platform] || '-1';
};

export const getRealPagePath = memo((universalPaths = [], universalQueryKeys = [], reportPath) => {
  if(reportPath) {
    return reportPath;
  }

  const currentPathName = window.location.pathname;

  if(universalPaths.indexOf(currentPathName) < 0 || universalQueryKeys.length <= 0) {
    return currentPathName;
  }

  const search = window.location.search;
  const universalQueryKey = universalQueryKeys.find((key) => {
    return search.indexOf(key) > -1;
  });

  let realPathName;
  if(universalQueryKey) {
    const queryObject = parseQuery(search);
    realPathName = queryObject[universalQueryKey];
  }

  if(!realPathName) {
    return currentPathName;
  }

  if(realPathName[0] === '/') {
    return `${realPathName}.html`;
  }

  return `/${realPathName}.html`;
});

export function isObject(target) {
  return Object.prototype.toString.call(target) === '[object Object]';
}

export function combineURLs(baseURL, relativeURL) {
  return relativeURL ? baseURL.replace(/\/+$/, '') + '/' + relativeURL.replace(/^\/+/, '') : baseURL;
}

let _pmmAppInfo = {};
let _pageInfo = {};
let _defaultExtraInfo = {}; // 

export function setPmmAppInfo(appInfo = {}) {
  _pmmAppInfo = appInfo;
}

export function setPageInfo(pageInfo = {}) {
  _pageInfo = pageInfo;
}

export function setDefaultExtraInfo(defaultExtraInfo = {}) {
  _defaultExtraInfo = defaultExtraInfo;
}

export function getPmmAppInfo() {
  return _pmmAppInfo;
}

export function getPageInfo() {
  return _pageInfo;
}

export function getDefaultExtraInfo() {
  return _defaultExtraInfo;
}
