/**
 * @file VenderAppIDCache/index.js
 * @author daben
 */
import { getCurrentPlatform } from '@pdd/platform-util';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import getAppChannel, { AppChannel } from './getAppChannel';
// import ShareChannel from '../ShareChannel';

export const getVenderAppIds = (appChannels) => {
  const platform = getCurrentPlatform();
  if (!platform.statisfy('4.49.0', '4.49.0', 'greaterThan')) {
    return Promise.reject({
      message: 'not support'
    });
  }
  if (!Array.isArray(appChannels)) {
    return Promise.reject({
      message: 'invalid params'
    });
  }
  return invokeBridgeCallNativeFun('JSShare', 'venderAppIds', {
    channels: appChannels
  });
};

class VenderAppIDCache {
  constructor() {
    this._venderAppIDs = {};
    this._isInited = false;
  }

  build({
    wechatAppID
  }) {
    if (!this._isInited) {
      this._isInited = true;
    } else {
      return Promise.resolve();
    }
    if (getCurrentPlatform().isNativePlatform) {
      return this._buildNativeCache();
    } else if (getCurrentPlatform().isWeChatPlatform) {
      return this._buildWeChatCache(wechatAppID);
    }
    return Promise.resolve();
  }

  isInited() {
    return this._isInited;
  }

  _buildNativeCache() {
    return getVenderAppIds([
      // wechat
      AppChannel.WECHAT
    ]).then((data = {}) => {
      this._venderAppIDs = Object.assign(this._venderAppIDs, data);
    }).catch(() => { return Promise.resolve(); });
  }

  _buildWeChatCache(wechatAppID) {
    this._venderAppIDs[AppChannel.WECHAT] = wechatAppID;
    return Promise.resolve();
  }

  getAppIDSync(shareChannel) {
    const appChannel = getAppChannel(shareChannel);
    return this._venderAppIDs[appChannel];
  }
}

let instance = null;
VenderAppIDCache.getInstance = function (opts) {
  if (!instance) {
    instance = new VenderAppIDCache(opts);
  }
  return instance;
};

export default VenderAppIDCache;
