import { getCurrentPlatform } from '@pdd/platform-util';
import ShareEventType from '../ShareEventType';
import ShareTracker from '../ShareTracker';
import ShareResultType from '../ShareResultType';
import shareContentCheck from './shareContentCheck';
import performShareLog from './performShareLog';
import MenuWatcher from '../ShareLimiter/MenuWatcher';
import ErrorCode from '../ShareLimiter/LimitErrorCode';
import trackError from '../ShareTracker/trackError';
import { refreshUid } from '../tools/getUserID';
import {
  shareEntityToParams,
  shareEntityToMethod
} from './translation';
import platformTag from './platformTag';
import ShareType from '../ShareType';
const isClientEnv = typeof window !== 'undefined';

async function getControllerHybrid() {
  return (await import(/* webpackChunkName: "ms_native_c" */'../ShareController/native')).default;
}

async function getControllerRaw() {
  const platform = getCurrentPlatform();
  if (platform.isWeChatMiniProgram) {
    return (await import(/* webpackChunkName: "ms_wx_app_c" */'../ShareController/wxapp')).default;
  } else if (platform.isWeChatPlatform) {
    return (await import(/* webpackChunkName: "ms_wx_c" */'../ShareController/wechat')).default;
  } else if (platform.isQQPlatform) {
    return (await import(/* webpackChunkName: "ms_qq_c" */'../ShareController/qq')).default;
  } else if (
    platform.isNativePlatform ||
    platform.isTinyNativePlatform
  ) {
    return (await import(/* webpackChunkName: "ms_native_c" */'../ShareController/native')).default;
  } else {
    return (await import(/* webpackChunkName: "ms_web_c" */'../ShareController/web')).default;
  }
}

const getController = process.env.BROWSER ? (process.env.HYBRID ? getControllerHybrid : getControllerRaw) : (_ => null);

class ShareManager {
  constructor(opts = {}) {
    this.shareController = null;
    this.shareTracker = null;
    this.menuWatcher = null;
    this.platform = platformTag();
    //   
    this._disableContentCheck = true;
    //   
    this._disablePerformShareLog = false;
    this.controllerOptions = opts.controllerOptions || {};
    // 
    // this.disableTracker = opts.disableTracker || false;
    this.noDefaultShare = opts.noDefaultShare || false;
    this.disableShare = opts.disableShare || false;
    // this.useNativeShareButton = opts.useNativeShareButton || false;
    this._initControllerPromise = null;
    this.loadErrorRetry = opts.loadErrorRetry || 0;
    this.enableInfiniteRetry = opts.enableInfiniteRetry || false;
    this._loadControllerFailed = false;
  }

  /**
   * @ignore
   */
  async setup() {
    if (!isClientEnv || this.shareController) {
      return Promise.resolve();
    }
    if (this._loadControllerFailed && !this.enableInfiniteRetry) {
      return Promise.reject(new Error('load controller failed'));
    }
    this._setupShareTracker();
    this._setupMenuWatcher();
    this._initControllerPromise = this._initControllerPromise || this._setupController();
    let retry = false;
    try {
      await this._initControllerPromise;
    } catch (e) {
      this._initControllerPromise = null;
      if (this.loadErrorRetry > 0) {
        trackError({
          errorCode: 0,
          message: `, ${this.loadErrorRetry}`,
          options: {
            message: e && e.message
          }
        });
        this.loadErrorRetry--;
        retry = true;
      } else {
        this._loadControllerFailed = true;
        // 
        trackError({
          errorCode: 0,
          message: '',
          options: {
            message: e && e.message
          }
        });
        throw e;
      }
    }
    if (retry) {
      return this.setup();
    }
  }

  /**
   * @ignore
   */
  async _setupController() {
    if (this.shareController) {
      return Promise.resolve();
    }
    const options = this.controllerOptions;
    const platform = getCurrentPlatform();
    const Controller = await getController();
    this.shareController = new Controller(options);
    if (platform.isWeChatPlatform || platform.isQQPlatform) {
      if (!this.noDefaultShare) {
        this._prepareShareController();
      }
    } else if (
      platform.isNativePlatform ||
      platform.isTinyNativePlatform
    ) {
      // 
      this.shareController.on(ShareEventType.ON_SHARE_COMPLETED, ({
        shareType,
        shareResultType,
        nativeShareErrorCode
      }) => {
        if (
          shareResultType === ShareResultType.FAIL &&
          [
            ErrorCode.REACH_HOUR_LIMIT,
            ErrorCode.REACH_DAY_LIMIT
          ].indexOf(nativeShareErrorCode) >= 0
        ) {
          this.shareController.emit(ShareEventType.SHARE_REACH_LIMIT, {
            shareType,
            errorCode: nativeShareErrorCode
          });
          trackError({
            errorCode: nativeShareErrorCode,
            message: ''
          });
        }
      });
    }

    this.shareTracker.listen(this.shareController);
    this.menuWatcher.listen(this.shareController);
    this._setupPerformShareLog();
  }

  /**
   * @ignore
   */
  _prepareShareController() {
    if (this.shareController && !this.shareController.isInited()) {
      this.shareController.initShare();
    }
  }

  /**
   * @ignore
   */
  _setupMenuWatcher() {
    if (this.menuWatcher) {
      return;
    }
    this.menuWatcher = new MenuWatcher();
  }

  /**
   * @ignore
   */
  _setupShareTracker() {
    if (this.disableTracker || this.shareTracker) {
      return;
    }
    this.shareTracker = new ShareTracker({
      transparentTransmission: this.trackerTrans
    });
  }

  /**
   * @ignore
   */
  _setupPerformShareLog() {
    if (!this.shareController || getCurrentPlatform().isNativePlatform) {
      return;
    }
    this.shareController.on(ShareEventType.ON_SHARE_COMPLETED, (payload = {}) => {
      if (
        this._disablePerformShareLog ||
        // wechat && qq will return success
        payload.shareResultType !== ShareResultType.SUCCESS ||
        // perform
        payload.shareType === ShareType.COPY_CIPHER
      ) {
        return;
      }
      const shareType = payload.shareType;
      const shareEntity = this.getShareEntity(shareType);
      performShareLog('post', {
        platform: this.platform,
        shareType: shareType,
        shareParams: shareEntityToParams(shareEntity),
        shareMethod: shareEntityToMethod(shareEntity, shareType),
        extra: shareEntity.payloadMixin && shareEntity.payloadMixin.extra
      }).catch((e) => {
        this.shareController.emit(ShareEventType.PERFORM_LOG_FAILED, {
          shareType: shareType,
          error: e
        });
      });
    });
  }

  /**
   * 
   * @param {ShareType} shareType 
   * @returns {promise} 
   */
  async share(shareType) {
    if (!this.shareController || this.disableShare) {
      const error = new Error('no controller exists');
      error.shareType = shareType;
      return Promise.reject(error);
    }
    await refreshUid();
    this._prepareShareController();
    const invokeShare = () => new Promise((res, rej) => {
      const me = this;
      const onResult = (payload = {}) => {
        const shareResultType = payload.shareResultType;
        removeListeners();
        if (ShareResultType.SUCCESS === shareResultType) {
          res(payload);
        } else {
          const err = new Error('');
          Object.assign(err, payload);
          rej(err);
        }
      };
      function removeListeners() {
        me.shareController.removeListener(ShareEventType.ON_SHARE_COMPLETED, onResult);
      }
      this.shareController.on(ShareEventType.ON_SHARE_COMPLETED, onResult);
      const shareErrorType = this.shareController.share(shareType);
      if (shareErrorType) {
        removeListeners();
        this.shareController.emit(ShareEventType.ON_SHARE_INTERRUPT, {
          shareType,
          shareErrorType
        });
        trackError({
          errorCode: shareErrorType,
          message: '',
          options: {
            shareType
          }
        });
        const err = new Error('');
        Object.assign(err, {
          shareType,
          shareErrorType
        });
        rej(err);
      }
    });
    if (getCurrentPlatform().isNativePlatform) {
      return this.shareController.waitShareLimiter().then(_ => {
        return invokeShare();
      }, _ => {
        return invokeShare();
      });
    } else {
      return invokeShare();
    }
  }

  /**
   * 
   * @param {ShareEntity} shareEntity 
   */
  checkShareContent(shareEntity = {}) {
    return shareContentCheck('post', {
      platform: this.platform,
      shareParams: shareEntityToParams(shareEntity)
    });
  }

  /**
   * 
   * @param {Boolean} isEnable
   */
  setContentCheck(isEnable) {
    this._disableContentCheck = !isEnable;
  }

  /**
   * 
   * @param {Boolean} isEnable
   */
  setPerformShareLog(isEnable) {
    this._disablePerformShareLog = !isEnable;
  }

  /**
   * ShareManager.prepareShare 
   * 
   *  
   * @param {object} shareInfoPack 
   * @param {ShareType} shareType 
   * @param {object} opts 
   * @returns {promise} 
   */
  async prepareShare(shareInfoPack = {}, shareType, opts = {}) {
    if (!this.shareController) {
      return Promise.reject(new Error('no controller exists'));
    }
    await refreshUid();
    this._prepareShareController();
    const {
      entityConfig = {}
    } = shareInfoPack;
    const shareEntity = this.shareController.createShareEntity(entityConfig, shareType);
    if (
      opts.skipShareContentCheck ||
      this._disableContentCheck ||
      getCurrentPlatform().isNativePlatform
    ) {
      return this.shareController.refreshShare(shareEntity, shareType);
    }
    // do refresh at last
    return this.checkShareContent(shareEntity).then(() => {
      return this.shareController.refreshShare(shareEntity, shareType);
    });
  }

  /**
   * 
   * @returns {ShareController} 
   */
  getShareController() {
    return this.shareController;
  }

  /**
   * 
   * @returns {ShareController} 
   */
  async getShareControllerAsync() {
    if (this.shareController) {
      return this.shareController;
    }
    if (this._initControllerPromise) {
      return this._initControllerPromise.then(_ => {
        return this.shareController;
      }).catch(_ => null);
    }
    // null
    return this.setup().then(_ => {
      return this.shareController;
    }).catch(_ => null);
  }

  /**
   * 
   * @returns {ShareTracker} 
   */
  getShareTracker() {
    return this.shareTracker;
  }

  /**
   * 
   * @param {ShareType} shareType 
   * @param {boolean} noGuarantee 
   */
  getShareEntity(shareType, noGuarantee) {
    if (!this.shareController) {
      return null;
    }
    return this.shareController.getShareEntityByShareType(shareType, noGuarantee);
  }

  /**
   * @ignore
   */
  setupNativeShareButton(buttonConfig, onClickListener) {
    if (!this.shareController) {
      return;
    }
    this.shareController.removeAllListeners(ShareEventType.ON_SHARE_BUTTON_CLICKED);
    if (typeof onClickListener === 'function') {
      this.shareController.on(ShareEventType.ON_SHARE_BUTTON_CLICKED, onClickListener);
    }
    this.shareController.setupNativeShareButton(buttonConfig);
  }
}

export default ShareManager;
