import { EventEmitter } from 'events';

import ShareError from '../ShareError';
import ShareEntity from '../ShareEntity';
import ShareType from '../ShareType';
import ShareEventType from '../ShareEventType';
import ShareResultType from '../ShareResultType';
const { ShareErrorCode } = ShareError;

/**
 * 
 * @access private
 * @extends {EventEmitter}
 */
class ShareController extends EventEmitter {
  constructor(opts = {}) {
    super(opts);
    this.ShareEntityClass = ShareEntity;
    this._hasInited = false;
    // this default share info config
    this.shareEntity = opts.shareEntity;
    // if you want set different share info for different share type
    // use this shareEntityMap
    // key = shareType && value = shareEntity
    this.shareEntityMap = opts.shareEntityMap || {};
    this.disableShareTypes = opts.disableShareTypes || [];
  }

  /**
   * @ignore
   */
  initShare() {
    if (this._hasInited) {
      return;
    }
    this._hasInited = true;
    this.setMaxListeners(0);
    // setup enabled share types
    this._setupShareTypes();
    // setup platform sdk
    this._setupPlatformShareSDK();
  }

  /**
   * @ignore
   */
  isInited() {
    return this._hasInited;
  }

  /**
   * @abstract
   * @param {ShareType} shareType 
   */
  share(shareType) {
    return ShareErrorCode.SHARE_ACTION_DISABLE;
  }

  /**
   * @abstract
   * @param {boolean} isEnable flag
   */
  setShareCheck(isEnable) {
    // TODO
  }

  /**
   * @abstract
   * @param {ShareType} shareType 
   */
  checkShareTypeEnable(shareType) {
    return 0;
  }

  /**
   * @abstract
   * @return {boolean} is enabled shareTypes changed
   */
  _setupShareTypes() {
    return true;
  }

  /**
   * prepare third party SDK
   * @abstract
   */
  _setupPlatformShareSDK() {
    return Promise.resolve();
  }

  /**
   * shareType
   * @param {ShareType} shareType 
   * @param {boolean} noGuarantee 
   * @returns {ShareEntity}
   */
  getShareEntityByShareType(shareType = 0, noGuarantee) {
    if (!shareType) {
      return this.shareEntity;
    }
    return this.shareEntityMap[shareType] || (noGuarantee ? null : this.shareEntity);
  }

  /**
   * reset share info
   * if not pass param shareType, the default shareEntity will be refreshed
   * @param  {ShareEntity} shareEntity 
   * @param  {ShareType} shareType 
   * @returns {promise} 
   */
  refreshShare(shareEntity, shareType) {
    if (!shareEntity) {
      return Promise.resolve();
    }
    if (shareType) {
      this.shareEntityMap[shareType] = shareEntity;
    } else {
      this.shareEntity = shareEntity;
    }
    return this._setupPlatformShareSDK();
  }

  /**
   * icon
   * @param {Array<ShareType>|ShareType} shareTypes 
   */
  disableShare(shareTypes) {
    if (shareTypes && typeof shareTypes === 'number') {
      if (this.disableShareTypes.indexOf(shareTypes) < 0) {
        this.disableShareTypes.push(shareTypes);
      }
    } else if (Object.prototype.toString.call(shareTypes) === '[object Array]') {
      this.disableShareTypes = this.disableShareTypes.concat(
        shareTypes.filter((shareType) => {
          return (this.disableShareTypes || []).indexOf(shareType) < 0;
        }
        ));
    } else {
      return;
    }
    const notChanged = this._setupShareTypes();
    !notChanged && this._adjustMenu();
    // _setupPlatformShareSDK is duplicate when disable share
    // !notChanged && this._setupPlatformShareSDK();
  }

  /**
   * 
   * @abstract
   */
  _adjustMenu() {
    // TODO
  }

  /**
   * 
   * @param {object} params 
   * @param {ShareType} shareType 
   * @param {boolean} noGuarantee 
   */
  createShareEntity(params = {}, shareType, noGuarantee) {
    const ShareEntityClass = this.ShareEntityClass || ShareEntity;
    if (noGuarantee) {
      return new ShareEntityClass(params);
    }
    let initParams = {};
    if (shareType) {
      const currentEntity = this.shareEntityMap[shareType];
      if (currentEntity) {
        initParams = Object.assign({}, currentEntity, params);
      } else {
        initParams = Object.assign({}, this.shareEntity, params);
      }
    } else {
      initParams = Object.assign({}, this.shareEntity, params);
    }
    return new ShareEntityClass(initParams);
  }

  /**
   * 
   * @abstract
   * @param {object} buttonConfig 
   */
  setupNativeShareButton(buttonConfig) {
    return ShareErrorCode.SHARE_NOT_SUPPORTED;
  }

  // 
  // removeAllListeners(event) {
  //     throw new Error('method removeAllListeners is not allowed');
  // }

  copyCipher() {
    const shareType = ShareType.COPY_CIPHER;
    const shareEntity = this.getShareEntityByShareType(shareType);
    const delayTrigger = shareEntity.cipherScene && typeof shareEntity.cipher === 'string';
    !delayTrigger && this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    if (!shareEntity) {
      return ShareErrorCode.SHARE_PARAMS_NOT_VALID;
    }
    shareEntity.normalize(shareType).then((norParams = {}) => {
      if (delayTrigger) {
        this.emit(
          ShareEventType.ON_SHARE_TRIGGER,
          { shareType, shareID: norParams.shareID }
        );
        delete norParams.shareID;
      }

      this.emit(ShareEventType.ON_SHARE_COMPLETED, {
        shareType,
        shareResultType: ShareResultType.SUCCESS,
        content: norParams.title,
        rawResponse: norParams.rawResponse
      }, this.getNormalizeErrorHandler(shareType));
    });
    return 0;
  }

  getNormalizeErrorHandler(shareType) {
    return (...args) => {
      this.emit(ShareEventType.ON_SHARE_COMPLETED, {
        shareType,
        shareResultType: ShareResultType.FAIL,
        errorPayload: args
      });
    };
  }

  /**
   * @abstract
   */
  resetLimiter() {
    // todo
  }
}

export default ShareController;
