import UnknownPlatform from './unknown';
import { getCache, getNativeVersion, PLATFORM } from './common';

/**
 * @ignore
 */
class TintAndroidPlatform extends UnknownPlatform {
  /**
   * android
   * @type {boolean}
   */
  get isTinyNativePlatform() {
    return this.platform === PLATFORM.PDDTinyAndorid;
  }
  /**
   * 
   * @type {string}
   */
  get version() {
    return getCache(this.cache, 'version', getNativeVersion(this.ua, PLATFORM.PddAndroid));
  }
}
export default TintAndroidPlatform;
