import { LAZYLOAD_CONFIG } from './constants';
import { getFallbackUrl, getRetryLimit, isHttp, checkImageProtocol } from '@pdd/cui-image';
import { throwErrorAsync } from '@pdd/core-util';

if (process.env.BROWSER) {
  require('intersection-observer');
}

function getRetryCount(el) {
  return parseInt(el.dataset.retryCount, 10) || 0;
}

function getErrorMsg(el) {
  const retryCount = getRetryCount(el);
  return retryCount ? `lazy_image_error_retry_${retryCount}` : 'lazy_image_error';
}

const HOSTNAME_REG = /\/\/([\w+.-]+)\//;
function getHost(url) {
  return (url.match(HOSTNAME_REG) || [])[1] || '';
}

function recordLoadError(el, useImageSdk) {
  const src = el.src;
  const errorMsg = getErrorMsg(el);
  const host = getHost(src);
  const error = new Error(errorMsg);
  throwErrorAsync(error, { payload: { src, host, useImageSdk }, error_msg: errorMsg });
}

const PROCESSED_ATTR = 'data-was-processed';

// lazyloadsrc
// vanilla-lazyloadsrc
// 
//    https://github.com/verlok/lazyload/issues/324#issuecomment-472195078
//    https://github.com/verlok/lazyload/issues/361
function resetLazyloadStatus(el) {
  el.classList.remove('error');
  el.removeAttribute(PROCESSED_ATTR);
}

// [issue 11](https://gitlab.hutaojie.com/pdd-fe-common/lazy-image/issues/11)
// copydestoryhttps://github.com/verlok/lazyload/blob/12.0.0/src/lazyload.js#L35
// _settings_elementsloadhandler
function destoryLazyloadInstance(lazyload) {
  if (lazyload._observer) {
    lazyload._elements.forEach(element => {
      lazyload._observer.unobserve(element);
    });
    lazyload._observer = null;
  }
  // lazyload._elements = null; 
  // lazyload._settings = null; 
}

/**
 * LazyLoader
 * @description
 * vanilla-lazyload
 * 1. 
 * 2. 
 */
export default class LazyLoader {
  /**
   * @param {object} [options] - 
   * @param {HTMLElement} options.container - dom
   * @param {string} options.thresholds = '100px' - [vanilla-lazyloadthresholds](https://github.com/verlok/lazyload#options)
   */
  constructor(options = {}) {
    if (process.env.BROWSER) {
      const LazyLoad = require('vanilla-lazyload');
      const targetOptions = {
        ...LAZYLOAD_CONFIG,
        thresholds: options.thresholds || LAZYLOAD_CONFIG.thresholds,
        // eslint-disable-next-line camelcase
        callback_error: async el => {
          const useImageSdk = await checkImageProtocol();
          recordLoadError(el, useImageSdk);
          if (!useImageSdk) {
            this._retry(el);
          }
        }
      };

      if (options.container) {
        targetOptions.container = options.container;
      }
      this._lazyLoad = new LazyLoad(
        targetOptions,
        [] // Lazyloadupdate
      );
    }
  }

  start({ el, src }) {
    this._start({ el, src, retryCount: 0 });
  }

  destroy() {
    this._lazyLoad && destoryLazyloadInstance(this._lazyLoad);
  }

  _start({ el, src, retryCount }) {
    resetLazyloadStatus(el);
    el.dataset.src = src;
    el.dataset.retryCount = retryCount;
    this._lazyLoad && this._lazyLoad.update([el]);
  }

  /**
   * 
   * @description
   * 1. 3
   * 2. 0ms400ms
   * emage: https://gitlab.hutaojie.com/pdd-fe-common/enhance-image/blob/master/src/index.ts#L298
   */
  _retry(el) {
    if (!isHttp(el.src)) {
      return;
    }
    const retryCount = getRetryCount(el);
    if (retryCount >= getRetryLimit()) {
      return;
    }

    setTimeout(() => {
      this._start({
        el,
        src: getFallbackUrl(el.src, retryCount),
        retryCount: retryCount + 1
      });
    }, retryCount * 400);
  }
}
