/**
 * @file ShareDomainModel.js
 * @author daben
 */
import { getPageID, getPageSN } from '../tools/getPageContext';
import { createDefaultAxios } from '@pdd/isomorphic-request';
import { pickUpRefer } from './processMiniObjectPath';
import URLParser from './URLParser';
import { buildQuery } from '@pdd/core-util';

export const ShareDomainType = {
  WEIXIN_TINY_DOMAIN: 'weixin',
  SHORT_LINK: 'link'
};

export const ShareDomainPlatform = {
  WECHAT: 'h5',
  IOS: 'ios',
  ANDROID: 'android'
};

const EXTRA_QEURIES_API = `api/flow/chron/share/miniprogram`;

/**
 * share_url
 * @param {string} method 
 * @param {object} opts 
 */
export function getExtraQueriesFromServer(method = '', opts = {}) {
  const httpClient = createDefaultAxios({
    pageInfo: {
      pageId: getPageID()
    }
  }, {
    defaultErrorHandler: _ => {}
  });
  let promise = null;
  let params = {};
  params.share_refer = pickUpRefer({ shareURL: opts.shareURL });
  if (opts.payloadMixin && typeof opts.payloadMixin === 'object') {
    // payloadMixin.extra maybe exists. if it is used, here should assign extra
    params = Object.assign(params, opts.payloadMixin);
  }
  const apiPath = opts.shortURLApi || EXTRA_QEURIES_API;

  if (method.toLowerCase() === 'post') {
    promise = httpClient.post(apiPath, params);
  } else {
    promise = httpClient.get(apiPath, { params });
  }

  return promise.then((data = {}) => {
    return data;
  });
}

export default function (method = '', opts = {}) {
  const httpClient = createDefaultAxios({
    pageInfo: {
      pageId: getPageID()
    }
  }, {
    defaultErrorHandler: _ => {}
  });
  let promise = null;
  let params = {};
  params.url = opts.shareURL || '';
  params.activity = opts.marketActivityID || 0;
  params.platform = opts.platform || '';
  params.share_refer = pickUpRefer({ shareURL: opts.shareURL });
  params.page_sn = getPageSN();
  if (opts.payloadMixin && typeof opts.payloadMixin === 'object') {
    // payloadMixin.extra maybe exists. if it is used, here should assign extra
    params = Object.assign(params, opts.payloadMixin);
  }

  // coderefer_xx
  const urlAlias = opts.urlAlias || 'url_code';
  const longUrl = params[urlAlias];
  if (longUrl && typeof longUrl === 'string') {
    const urlCodeQuery = new URLParser(longUrl).query || {};
    Object.assign(urlCodeQuery, params.share_refer);
    params[urlAlias] = `${longUrl.split('?')[0] || ''}?${buildQuery(urlCodeQuery)}`;
  }

  let apiPath = 'api/flow/audience/activity/domain/weixin';
  if (opts.shareDomainType === ShareDomainType.SHORT_LINK) {
    if (opts.shortURLApi) {
      apiPath = opts.shortURLApi;
    } else if (opts.shortCommandApi) {
      apiPath = opts.shortCommandApi;
    } else {
      apiPath = 'api/flow/audience/activity/domain/short_url';
    }
  } else {
    if (opts.domainURLApi) {
      apiPath = opts.domainURLApi;
    }
  }

  if (method.toLowerCase() === 'post') {
    promise = httpClient.post(apiPath, params);
  } else {
    promise = httpClient.get(apiPath, { params });
  }

  return promise.then((data = {}) => {
    // if (opts.shortCommandApi) {
    //   return {
    //     shareText: data.share_text || ''
    //   };
    // }
    return {
      // weixin: link:
      // type: data.type,
      domain: data.domain,
      // type=weixin
      openAppID: data.open_app_id || '',
      // type=link 
      shortURL: data.short_url || '',
      // shareText
      shareText: data.share_text || '',
      raw: data
    };
  });
}
