import React from 'react';
import PropTypes from 'prop-types';
import LazyImage from './LazyImage';
import { generateCDNImageUrl } from '@pdd/core-util';
import { Image } from '@pdd/cui-image';

const DEFAULT_PARAM = {
  resizeWidth: 1300,
  quality: 80,
  strip: true
};

export default function SimpleEmage({ lazy, src, processParam, ...otherProps }) {
  const params = {
    ...DEFAULT_PARAM,
    ...processParam
  };

  const Cmp = lazy ? LazyImage : Image;
  return <Cmp {...otherProps} src={generateCDNImageUrl(src, params)} />;
}

SimpleEmage.propTypes = {
  /**
   * 
   */
  src: PropTypes.string.isRequired,
  /**
   * lazyfalse
   */
  lazy: PropTypes.bool,
  /**
   * CDN[@pdd/core-utilgenerateCDNImageUrl](http://fweb1st.prod.yiran.com:5000/docs/core-util/function/index.html#static-function-generateCDNImageUrl)
   */
  processParam: PropTypes.object
};

SimpleEmage.defaultProps = {
  lazy: true
};
