/**
 * filter shareInfo config
 * if a new key is passed by shareInfo, it is better to mark it into constants
 * otherwise the new key will be ignored
 *
 * @file ShareInfoFilter/index.js
 * @author daben
 */
import {
  ShareInfoKeyType,
  SI_ENTITY_KEYS,
  SI_CONFIG_FLAGS,
  SI_OTHERS
} from './config';
const KT = ShareInfoKeyType;

class ShareInfoFilter {
  constructor(opts = {}) {
    this.cacheRaw = opts.cacheRaw || false;
    this.deleteAfterCollect = opts.deleteAfterCollect || false;
  }

  filter(shareInfo = {}) {
    const entityConfig = this._filterShareEntity(shareInfo);
    const flowControllConfig = this._filterShareConfigFlag(shareInfo);
    const otherConfig = this._filterShareOther(shareInfo);
    const compatibleShareConfig = {};
    return {
      _rawShareInfo: this.cacheRaw ? shareInfo : null,
      entityConfig,
      flowControllConfig,
      otherConfig,
      compatibleShareConfig
    };
  }

  _filterByItems(shareInfo = {}, items = {}) {
    const keys = Object.keys(items) || [];
    const filteredItem = {};
    keys.forEach((key, index) => {
      const type = items[key];
      if (type === KT.FORBIDDEN) {
        // console.error(`share params [${key}] is forbidden`);
        delete shareInfo[key];
      } else if (type === KT.ABANDONED) {
        // console.warn(`share params [${key}] is abandoned`);
        delete shareInfo[key];
      } else {
        if (typeof shareInfo[key] !== 'undefined') {
          filteredItem[key] = shareInfo[key];
          if (this.deleteAfterCollect) {
            delete shareInfo[key];
          }
        }
      }
    });
    return filteredItem;
  }

  _filterShareEntity(shareInfo) {
    return this._filterByItems(shareInfo, SI_ENTITY_KEYS);
  }

  _filterShareConfigFlag(shareInfo) {
    return this._filterByItems(shareInfo, SI_CONFIG_FLAGS);
  }

  _filterShareOther(shareInfo) {
    return this._filterByItems(shareInfo, SI_OTHERS);
  }
}

export default ShareInfoFilter;
