
import shareDomainModel, {
  ShareDomainType,
  ShareDomainPlatform,
  getExtraQueriesFromServer
} from './shareDomainModel';
import { getCurrentPlatform, PLATFORM } from '@pdd/platform-util';
import { isShareToQQ } from '../qqShareTypes';
import MagicTraceManager from './MagicTraceManager';
import calculateURLHash from './calculateURLHash';
import { getInstance as getApiCacheManager } from './ApiCacheManager';
import URLWrapType, {
  isServerControlType,
  isTinyDomainType
} from './URLWrapType';
import URLParser from './URLParser';
import makeUpProtocol from './makeUpProtocol';
import buildURL from './buildURL';

// import trackError from '../ShareTracker/trackError';

const SHARE_DOMAIN_MAIN = 'mobile.yangkeduo.com';

const QQ_ENABLE_DOMAIN = [
  'mobile.yangkeduo.com',
  'm.hutaojie.com',
  'panduoduo.yangkeduo.com'
];

export const getWebWrappedShareURLInfo = (shareEntity = {}) => {
  const shareURL = shareEntity.shareURL || '';
  const urlWrapType = shareEntity.urlWrapType;
  if (
    urlWrapType === URLWrapType.DEFAULT ||
    urlWrapType === URLWrapType.RAW
  ) {
    return Promise.resolve({
      missed: true,
      realShareURL: shareURL,
      targetShareURL: shareURL
    });
  }
  if (
    shareEntity.marketActivityID &&
    isServerControlType(urlWrapType)
  ) {
    const shareDomainType = isTinyDomainType(urlWrapType)
      ? ShareDomainType.WEIXIN_TINY_DOMAIN
      : ShareDomainType.SHORT_LINK;
    const requestShareURL = shareURL.replace(/^(\w+:)?\/\//, '');
    const shareURLHash = calculateURLHash(shareEntity);
    const apiCacheManager = getApiCacheManager();
    // 
    const remoteRequest = () => {
      const req = new Promise((res, rej) => {
        const shareDomainRP = shareDomainModel(
          shareEntity.requestUsePost ? 'post' : 'get',
          {
            shareURL: requestShareURL,
            marketActivityID: shareEntity.marketActivityID,
            platform: getShareDomainPlatform(),
            shareDomainType: shareDomainType,
            shortURLApi: shareEntity.shortURLApi,
            shortCommandApi: shareEntity.shortCommandApi,
            domainURLApi: shareEntity.domainURLApi,
            payloadMixin: shareEntity.payloadMixin
          }
        );
        shareDomainRP.then((data) => {
          if (data) {
            const {
              domain = '',
              shortURL,
              shareText,
              raw
            } = data;
            if (shareDomainType === ShareDomainType.WEIXIN_TINY_DOMAIN) {
              if (urlWrapType === URLWrapType.DOMAIN_REPLACE) {
                res(getDomainReplacedURL(shareURL, domain));
              }
            } else if (
              shareDomainType === ShareDomainType.SHORT_LINK
            ) {
              res({
                useShortURL: true,
                realShareURL: shortURL || shareURL,
                targetShareURL: shareURL,
                shareText: shareText,
                rawResponse: raw
              });
              return;
            }
          }
          res({
            realShareURL: shareURL,
            targetShareURL: shareURL
          });
        }).catch(e => {
          rej(e);
        });
      });
      // 
      shareDomainType === ShareDomainType.SHORT_LINK && req.then(response => {
        // 
        if (response && response.useShortURL) {
          apiCacheManager.cacheResponseByHash(shareURLHash, response);
          apiCacheManager.setShareID(shareURLHash, (new URLParser(shareURL).query || {}).refer_share_id);
        }
      });
      return req;
    };

    //  
    if (urlWrapType === URLWrapType.SERVER_SHORT_URL_WITH_CACHE) {
      // 
      let cachedResponse = apiCacheManager.getResponseByHash(shareURLHash);
      if (cachedResponse) {
        apiCacheManager.increaseCount(shareURLHash);
        // 
        return Promise.resolve(cachedResponse);
      }
      return remoteRequest();
    }
    return remoteRequest();
  }
  if (urlWrapType === URLWrapType.MAGIC_TRACE) {
    const url = MagicTraceManager.getInstance().transform(shareURL);
    return Promise.resolve({
      realShareURL: url,
      targetShareURL: url
    });
  }
  if (urlWrapType === URLWrapType.MAGIC_TRACE_DYNAMIC) {
    return MagicTraceManager.getInstance().transformByShareEntity(shareEntity).then((url) => {
      return {
        realShareURL: url,
        targetShareURL: url
      };
    }, () => {
      return {
        realShareURL: shareURL,
        targetShareURL: shareURL
      };
    });
  }
  if (urlWrapType === URLWrapType.SERVER_QUERY_APPEND) {
    return getExtraQueriesFromServer(
      shareEntity.requestUsePost ? 'post' : 'get',
      {
        shareURL,
        payloadMixin: shareEntity.payloadMixin,
        shortURLApi: shareEntity.shortURLApi
      }
    ).then((queries = {}) => {
      const useCipher = queries && queries.code_method;
      if (useCipher) {
        return {
          useCipher,
          realShareURL: shareURL,
          targetShareURL: shareURL
        };
      } else {
        const { share_title: title, share_img: thumbnailURL, dev_infos: tpInfos, ...restQueries } = queries;
        const url = buildURL(restQueries, shareURL);
        return {
          appendQuery: restQueries,
          // 
          categoryShareInfo: { title, thumbnailURL },
          realShareURL: url,
          targetShareURL: url,
          tpInfos
        };
      }
    });
  }

  return Promise.resolve({
    missed: true,
    realShareURL: shareURL,
    targetShareURL: shareURL
  });
};

export const getDomainReplacedURL = (shareURL, domain) => {
  const targetURL = buildURL({}, shareURL, makeUpProtocol(domain));
  return {
    targetShareURL: targetURL,
    realShareURL: targetURL
  };
};

export const getShareDomainPlatform = () => {
  const current = getCurrentPlatform();
  if (current.platform === PLATFORM.PddAndroid) {
    return ShareDomainPlatform.ANDROID;
  } else if (current.platform === PLATFORM.PddIOS) {
    return ShareDomainPlatform.IOS;
  }
  return ShareDomainPlatform.WECHAT;
};

export const getQQShareURL = (shareType, shareURL) => {
  // QQ
  if (!isShareToQQ(shareType)) {
    return shareURL;
  }
  const urlStatus = new URLParser(shareURL);
  const hostname = urlStatus.hostname;
  if (QQ_ENABLE_DOMAIN.indexOf(hostname) < 0) {
    shareURL = shareURL.replace(`//${hostname}`, `//${SHARE_DOMAIN_MAIN}`);
  }
  return shareURL;
};
