import buildURL from 'axios/lib/helpers/buildURL';
import buildFullPath from 'axios/lib/core/buildFullPath';
import { parseQuery, getCookie, buildQuery } from '@pdd/core-util';
import { compareVersion, PLATFORM, isWebBundle, getCurrentPlatform } from '@pdd/platform-util';
import { throwErrorAsync } from '@pdd/core-util';
import { errors, getMsgFromCode } from '@pdd/error-status';
import { login, getAccessToken } from '@pdd/auth';
import navigation from '@pdd/navigation';
import { info } from '@pdd/cui-toast';
import { getUserInfo } from '@pdd/native-util';
import cmt from '../util/cmt';
import { pmmTracking } from '../util/pmm';

const RISK_CONTROL_CODE = 54001;

const UID_KEY = 'pdd_user_id';

const handleVerifyAuthInNative = (() => {
  if(!process.env.BROWSER) {
    return false;
  }
  // APP
  const { platform, version } = getCurrentPlatform();
  const handleInIOS = platform === PLATFORM.PddIOS && compareVersion(version, '4.45.0') >= 0;
  const handleInAndroid = platform === PLATFORM.PddAndroid && compareVersion(version, '4.40.0') >= 0;
  return handleInIOS || handleInAndroid;
})();

export * from './common';

// h5proxyoptions
export const domainProxy = useGlbDomain => {
  const PROXY_BASE = location.protocol + '//' + location.host + '/proxy/api/';
  const { isNativePlatform, isTinyNativePlatform } = getCurrentPlatform();

  useGlbDomain = useGlbDomain || isNativePlatform || isTinyNativePlatform;

  return {
    request: config => {
      config.baseURL = useGlbDomain ? config.baseURL : PROXY_BASE;
      return config;
    }
  };
};

// todo: 
export const extendQuery = () => {
  return {
    request: config => {
      const uidP = isWebBundle() ? getUserInfo().then(({ user_id }) => user_id).catch(() => 0) : Promise.resolve(0);
      return uidP.then(uid => {
        config.params = {
          pdduid: uid || parseInt(getCookie(UID_KEY), 10) || 0,
          // eslint-disable-next-line
          is_back: parseQuery(window.location.search).is_back === '1' ? '1' : null,
          ...config.params
        };

        return config;
      });
    }
  };
};

// token
export const appendHeader = () => {
  return {
    request: async (config) => {
      const headers = config.headers;

      if(config.method === 'post') {
        headers['Content-Type'] = 'application/json;charset=UTF-8';
      }

      const accessToken = await getAccessToken();

      if(accessToken) {
        headers.AccessToken = accessToken;
      }

      // APP APP headers  VerifyAuthToken
      const verifyAuthToken = handleVerifyAuthInNative ? null : localStorage.getItem('VerifyAuthToken');

      if(verifyAuthToken) {
        headers.VerifyAuthToken = verifyAuthToken;
      }

      return config;
    }
  };
};

export const requestLogger = pageInfo => {
  const isNative = getCurrentPlatform().isNativePlatform;

  const request = config => {
    config.requestTime = Date.now();
    return config;
  };

  const response = response => {
    const { data, config } = response;
    config.resTimeConsume = Date.now() - response.config.requestTime;
    const logParams = {
      requestTime: config.requestTime,
      resTimeConsume: config.resTimeConsume,
      reqData: { params: config.params, data: config.data }, // 
      resData: data,
      apiUrl: buildURL(buildFullPath(config.baseURL, config.url), config.params, config.paramsSerializer),
      statusCode: 200,
      pageId: pageInfo.pageId
    };

    cmt.tracking(logParams);

    pmmTracking({
      ...logParams,
      method: config.method,
    });

    return response;
  };

  const responseError = error => {
    const response = error.response;
    const config = error.config;

    if(process.env.NODE_ENV === 'development') {
      // eslint-disable-next-line
      console.error(error);
    }

    const errorInfo = {
      sub_op: 'api_error',
      url: config.method + ':' + config.url,
      error_code: error.errorCode,
      error_message: error.message,
      error_type: error.errorType,
      params: JSON.stringify({ params: config.params, data: config.data })
    };

    if(!response) {
      // todo: cmt
      // todo: error_code0error_code 2. 
      errorInfo.errorCode = errorInfo.errorCode || 0;
      throwErrorAsync(error, errorInfo);
      throw error;
    }

    const { status, statusText } = response;
    const data = response.data || { error_code: 0 };
    error.errorCode = error.errorCode || data.error_code;
    errorInfo.http_code = status;
    errorInfo.statusText = statusText;
    errorInfo.resData = response.data;
    throwErrorAsync(error, errorInfo);

    // cmt
    if(!isNative) {
      // responsecmt
      const logParams = {
        requestTime: config.requestTime,
        resTimeConsume: Date.now() - config.requestTime,
        reqData: { params: config.params, data: config.data }, // 
        resData: response.data,
        apiUrl: buildURL(buildFullPath(config.baseURL, config.url), config.params, config.paramsSerializer),
        statusCode: parseInt(data.error_code, 10) || status,
        errorCode: data.error_code,
        pageId: pageInfo.pageId
      };
      cmt.tracking(logParams);

      pmmTracking({
        ...logParams,
        method: config.method,
      });
    }

    throw error;
  };

  return isNative ? { responseError } : { request, response, responseError };
};

let isDoingLogin = false; // 
export const responseErrorHandler = () => {
  return {
    responseError: error => {
      const errorCode = parseInt(error.errorCode, 10);
      const isNativePlatform = getCurrentPlatform().isNativePlatform;
      switch (errorCode) {
        // 40001
        case errors.AUTHENTICATION_FAILED:
          if(!isDoingLogin) {
            isDoingLogin = true;
            login({ silent: false }).then(() => {
              isNativePlatform && navigation.reload();
            }).catch(e => {
              isDoingLogin = false;
              isNativePlatform && navigation.back();
              throw e;
            });
          }
          break;

        case RISK_CONTROL_CODE:
          handleRiskControlError(error.response.data || {});
          break;

        default: {
          if(error.config && error.config.defaultErrorHandler) {
            error.config.defaultErrorHandler(error);
          } else {
            info(getMsgFromCode(errorCode));
          }
        }
      }

      throw error;
    }
  };
};

function handleRiskControlError(data) {
  // APP
  if(handleVerifyAuthInNative) {
    return;
  }

  // 54001
  const { verify_auth_token } = data;

  // 
  localStorage.setItem('VerifyAuthToken', verify_auth_token);
  navigation.uniformForward(`/psnl_verification.html?${buildQuery({
    VerifyAuthToken: verify_auth_token,
    from: location.href
  })}`);
}
