import { PLATFORM } from './common/index';
import IPlatform from './IPlatform';

/**
 * @ignore
 */
class QQPlatform extends IPlatform {
  /**
   * QQ
   * @type {boolean}
   */
  get isQQPlatform() {
    return this.platform === PLATFORM.QQ || this.platform === PLATFORM.QQApp;
  }

  /**
   * QQ
   * @type {boolean}
   */
  get isPureQQPlatform() {
    return this.platform === PLATFORM.QQ;
  }

  /**
   * QQ
   * @type {boolean}
   */
  get isQQMiniProgram() {
    return this.platform === PLATFORM.QQApp;
  }

  /**
   * App WebView
   * @type {boolean}
   */
  get isEmbeddedBrowser() {
    return this.isQQPlatform;
  }

  /**
   * qq
   * @type {boolean}
   */
  get isQQPayAvailable() {
    return this.isQQPlatform;
  }

  /**
   * 
   * @type {boolean}
   */
  get isWeChatPayAvailable() {
    return this.isQQPlatform;
  }
}
export default QQPlatform;
