import xhr from 'axios/lib/adapters/xhr';

/**
 * client
 *
 * @param {AxiosRequestConfig} config
 * @return {Promise<any>}
 */
export default function clientAdapter(config) {
  const { clientTimeout, timeout } = config;
  config.timeout = typeof clientTimeout === 'number' ? clientTimeout : timeout;

  return xhr(config).catch(error => {
    error.config = error.config || config;
    throw error;
  });
}
