import React, { Component } from 'react';
import { findDOMNode } from 'react-dom';
import Controller from './ImprController';
import PropTypes from 'prop-types';

class Impr extends Component {
  static defaultProps = {
    doImpr: true,
    id: '',
    once: true,
    trackingInfo: {},
    immediate: false
  };

  static propTypes = {
    doImpr: PropTypes.bool,
    id: PropTypes.string,
    once: PropTypes.bool,
    trackingInfo: PropTypes.object.isRequired,
    immediate: PropTypes.bool
  }

  componentDidMount = () => {
    const { id, doImpr } = this.props;
    this.el = findDOMNode(this) || document.getElementById(id);
    if(doImpr) {
      this.registerImpr();
    }
  }

  componentDidUpdate = (prevProps) => {
    if(this.lock) return;
    if(!prevProps.doImpr && this.props.doImpr) {
      this.registerImpr();
      this.lock = true;
    }
  }

  componentWillUnmount = () => {
    //  
    if(this.el && this.isRegistered) {
      const id = parseInt(this.el.dataset.uniqid, 10) || 0;
      Controller.unRegister(this.el, id);
    }
  }

  registerImpr = () => {
    const { trackingInfo, once, handleCustomTracking, immediate } = this.props;
    Controller.register({
      el: this.el,
      info: trackingInfo,
      once,
      handleCustomTracking,
      immediate
    });
    this.isRegistered = true;
  }

  render() {
    return React.Children.only(this.props.children);
  }
}

export default Impr;
