/**
 * @file index.js, Container
 * @author tenghu
 */
import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { withStyles } from '@pdd/cui-util';
import styles from './index.iso.scss';

/**
 * @visibleName Spinner.Container
 */
function Container({ className, style, bigger, hideBg, ...otherProps }) {
  const computedStyle = hideBg ? { backgroundColor: 'transparent', ...style } : style;
  return <div {...otherProps} style={computedStyle} className={cx(styles.root, className, bigger && styles.bigger)} />;
}

Container.propTypes = {
  /**
   * 
   */
  hideBg: PropTypes.bool,
  /**
   * size
   */
  bigger: PropTypes.bool
};

export default withStyles(styles)(Container);
