/**
 * @file url.js, urlnode
 * @author zongheng
 */
import { memoize, throwErrorAsync } from './util';

/**
 * url
 *
 * @param {string} url url
 * @return {boolean}
 */
export function isAbsoluteUrl(url) {
  return /^[a-z][a-z\d+.-]*:/.test(url);
}

/**
 *  '?'
 *
 * @param {Object} query 
 * @return {string} 
 */
export function buildQuery(query) {
  return Object.entries(query)
    .reduce((result, [key, val]) => {
      return val == null ? result : result + '&' + encodeURIComponent(key) + '=' + encodeURIComponent(String(val));
    }, '')
    .slice(1);
}

/**
 * @ignore
 * @private
 * 
 * @param string split
 * @param separator split
 * @return {*[]} split
 */
function splitOnFirst(string, separator) {
  if (separator === '') {
    return [string];
  }

  const separatorIndex = string.indexOf(separator);

  if (separatorIndex === -1) {
    return [string];
  }

  return [string.slice(0, separatorIndex), string.slice(separatorIndex + separator.length)];
}

const memoizedParseQuery = memoize(search => {
  // 
  search = search[0] === '?' ? search.slice(1) : search;
  return search.split('&').reduce((result, item) => {
    if (item) {
      const [key, value = ''] = splitOnFirst(item, '=');
      try {
        result[decodeURIComponent(key)] = decodeURIComponent(value);
      } catch (e) {
        throwErrorAsync(e);
      }
    }

    return result;
  }, {});
});

/**
 * 
 *
 * @param {string} search 
 * @return {Object} 
 */
export function parseQuery(search) {
  return { ...memoizedParseQuery(search) };
}

/**
 * url
 *
 * @example
 *
 * //  /a.html?t=1#h=1
 * const url = getPathnameWithSearchAndHash('http://yangkeduo.com/a.html?t=1#h=1');
 *
 * //  /a.html?t=1#h=1
 * const url = getPathnameWithSearchAndHash('a.html?t=1#h=1');
 *
 * @param {string} url url
 * @return {string} url
 */
export function getPathnameWithSearchAndHash(url) {
  // URL object  amcomponent 
  if (url.indexOf('amcomponent://') === 0) {
    const index = url.indexOf('/', 'amcomponent://'.length);
    return index === -1 ? '/' : url.slice(index);
  }

  if (isAbsoluteUrl(url)) {
    const { pathname, search, hash } = getURLObject(url);
    return pathname + search + hash;
  }

  return url.charAt(0) === '/' ? url : '/' + url;
}

function getURLObject(url) {
  if (typeof URL === 'function' && !process.env.BROWSER) {
    return new URL(url);
  }

  const a = document.createElement('a');
  a.href = url;

  return a;
}

/**
 * searchsqueryString Appendurlurlquerystring
 * @example
 * // '/personal.html?a=2&b=3'
 * const url = appendQuery({ a: 2, b: 3 }, '/personal.html?a=1')
 *
 * @param {object} searchs urlsearch
 * @param {string} [url=''] url url
 * @return {string}
 */
export function appendQuery(searchs, url) {
  url = url || '';
  const urlHash = url.split('#')[1] || '';
  const urlQuerys = parseUrlQuery(url);
  const newSearchs = Object.assign({}, urlQuerys, searchs);
  const queryString = buildQuery(newSearchs);
  const rawUrl = url.split('?')[0];
  let resultUrl = queryString ? [rawUrl, queryString].join('?') : rawUrl;
  resultUrl = urlHash ? [resultUrl, urlHash].join('#') : resultUrl;
  return resultUrl;
}

/**
 * parseUrlQuery url
 * @param {string} url queryurl
 * @return {object} query
 */
export function parseUrlQuery(url) {
  const splitHash = url.split('#');
  const noHashTag = splitHash[0] || '';
  const parts = noHashTag.split('?');
  const query = parts[1] || '';
  return parseQuery(query);
}

/**
 * clienturl,severurl
 * @param {string} url url
 * @param {object} queries urlquery
 * @return {string} url
 */
export function resolveFullUrl(url, queries) {
  const getFullUrl = url => {
    if (!process.env.BROWSER) {
      console.warn('url');
      return url;
    }
    // 
    if (window.location.href.indexOf('amcomponent://') === 0) {
      return getPathnameWithSearchAndHash(url);
    }
    const a = document.createElement('a');
    a.href = url;
    return a.href;
  };
  const fullUrl = getFullUrl(url);
  return queries ? appendQuery(queries, fullUrl) : fullUrl;
}

/**
 * https
 * @param {string} url
 * @returns {string} httpsurl
 * @example
 *
 * http => https
 * forceHttps('http://t04img.yangkeduo.com/images/2018-04-14/bf681b1161280ea98532bc4aff6a1af5.png');
 * // => https://t04img.yangkeduo.com/images/2018-04-14/bf681b1161280ea98532bc4aff6a1af5.png
 *
 *  => https
 * forceHttps('//t04img.yangkeduo.com/images/2018-04-14/bf681b1161280ea98532bc4aff6a1af5.png');
 * // => https//t04img.yangkeduo.com/images/2018-04-14/bf681b1161280ea98532bc4aff6a1af5.png
 */
export function forceHttps(url) {
  return url.replace(/^(http:\/\/|\/\/)/, 'https://');
}

/**
 * @deprecated forceHttps
 * @param {string} url
 * @returns {string} url
 */
export function httpsSafe(url) {
  if (process.env.NODE_ENV !== 'production') {
    console.warn('httpsSafeforceHttps');
  }
  return forceHttps(url);
}
