import React from 'react';
import { renderClient } from '@pdd/cui-util';
import Toast from '../Toast';

let root;
function getRoot() {
  if (root) {
    return root;
  }
  root = document.createElement('div');
  document.body.appendChild(root);
  return root;
}

function getRandomKey() {
  return `${Math.ceil(Math.random() * Date.now())}`;
}

let content = null;

function renderContent() {
  renderClient(content, getRoot());
}

function handleTimeout(cb) {
  content = null;
  renderContent();
  cb && cb();
}

/**
 * renderToast
 * @param {Object} options - toastToastToast
 * @param {number} options.duration - 2
 * @param {function} options.onTimeout - 
 */
export default function renderToast({ onTimeout, ...otherProps }) {
  if (!process.env.BROWSER) {
    return;
  }
  content = (
    <Toast {...otherProps} key={getRandomKey()}
      onTimeout={() => handleTimeout(onTimeout)}
      ControllerProps={{ PortalComponent: null }} />
  );
  renderContent();
}
