/**
 * pinbridgedecodeFunctions
 *
 * @param {Object} params 
 */
export function invokeBridgeDecodeFuncs(params) {
  if (process.env.BROWSER) {
    pinbridge.decodeFunctions(params);
  } else {
    if (process.env.NODE_ENV !== 'production') {
      console.warn(errorMessage.env('pinbridge', 'decodeFunctions'));
    }
  }
}

/**
 * pinbridgecallNative
 *
 * @param {string} moduleName 
 * @param {string} funcName 
 * @param {Object} [params] 
 * @param {string} [minAndroidVer] android
 * @param {string} [minIOSVer] ios
 * @param {Boolean} [reportWhenTimeout=true] 1s
 * @return {Promise} Promise
 */
export function invokeBridgeCallNativeFun(
  moduleName,
  funcName,
  params = null,
  minAndroidVer,
  minIOSVer,
  reportWhenTimeout = true
) {
  if (isIOS || isAndroid) {
    const minVersion = isAndroid && minAndroidVer ? minAndroidVer : isIOS && minIOSVer ? minIOSVer : '';
    if (minVersion && compareVersion(appVersion, minVersion) < 0) {
      const message = `App${moduleName} ${funcName}${minVersion}`;
      const error = new Error(message);
      error.errorType = 'version_error';
      if (process.env.NODE_ENV !== 'production') {
        console.warn(message);
      }
      return Promise.reject(error);
    }
    const timeout = time => {
      const ignoredApi = [
        'JSNetwork:request',
        'JSShare:queryShareTypes',
        'JSShare:performShare',
        'JSNotification:register',
        'JSNavigation:forward',
        'AMAnalytics:send',
        'PDDHighLayerManager:getPageInitData',
        'WebScene:getPageShownType',
        'JSPhoto:getV2',
        'AMAlert:showAlert',
        'JSAlert:showAlert',
        'JSPay:pay'
      ];
      const ignoreModule = ['JSReporter'];
      if (!ignoredApi.includes(`${moduleName}:${funcName}`) && !ignoreModule.includes(moduleName)) {
        return setTimeout(() => {
          const longTimeCallError = new Error(`call native ${moduleName}:${funcName} cost over ${time}ms`);
          throw longTimeCallError;
        }, time);
      }
    };
    const timeoutId = reportWhenTimeout && timeout(1000);
    return new Promise((resolve, reject) => {
      pinbridge.callNative(
        moduleName,
        funcName,
        params,
        data => {
          timeoutId && clearTimeout(timeoutId);
          resolve(data);
        },
        (data, errorCode) => {
          timeoutId && clearTimeout(timeoutId);
          const error = new Error(`callNative ${moduleName} ${funcName} error: ${errorCode}`);
          error.errorCode = errorCode;
          error.params = params;
          error.data = data;
          error.toJSON = function () {
            return {
              errorCode: this.errorCode,
              params: this.params,
              data: this.data
            };
          };
          reject(error);
        }
      );
    });
  } else {
    const message = errorMessage.env(moduleName, funcName);
    if (process.env.NODE_ENV !== 'production') {
      console.warn(message);
    }
    return Promise.reject(new Error(message));
  }
}

