/**
 * app, valueJSON.stringify
 *
 * @param {string} key key
 * @param {string | Object} value 
 * @return {Promise} Promise
 */
export function setStorage(key, value) {
  try {
    value = JSON.stringify(value);
  } catch (e) {
    const message = 'setNativeStoragevalue';
    return Promise.reject(new Error(message));
  }
  return invokeBridgeCallNativeFun(STORAGE_MODULE, 'set', { key, value });
}

/**
 * appkey
 *
 * @param {string} key key
 * @return {Promise<{value: string | Object}>} Promise
 */
export function getStorage(key) {
  return invokeBridgeCallNativeFun(STORAGE_MODULE, 'get', { key }).then((data = {}) => {
    if (typeof data.value !== 'undefined') {
      try {
        return JSON.parse(data.value);
      } catch (e) {
        const message = 'getNativeStoragevalue';
        throw new Error(message);
      }
    }
    return data;
  });
}

