/**
 * page context
 *
 * @param {Object} params page_sn
 * @return {Promise} Promise
 */
export function setPageContext(params) {
  const _setPageContextPromise = invokeBridgeCallNativeFun(navigationModule, 'setPageContext', params);
  if (!deferedPromise.fullfilled) {
    deferedPromise.fullfilled = true;
    deferedPromise.resolve(_setPageContextPromise);
    return setPageContextPromise;
  }
  setPageContextPromise = _setPageContextPromise;
  return setPageContextPromise;
}

