/**
 * AccessToken
 *
 * @return {Promise<{value: string}>} Promise
 */
export function getAccessToken() {
  const isLoginVersion = isAndroid
    ? compareVersion(appVersion, '1.2.2')
    : isIOS
      ? compareVersion(appVersion, '2.0.8')
      : '';
  if (isLoginVersion >= 0) {
    return invokeBridgeCallNativeFun(USER_MODULE, 'info').then(data => ({ value: data && data.access_token }));
  }
  if (isLoginVersion < 0) {
    return invokeBridgeCallNativeFun(STORAGE_MODULE, 'getSecure', { key: '__ACCESS_TOKEN__' });
  }
  const message = errorMessage.env(USER_MODULE, 'info');
  return Promise.reject(new Error(message));
}

