import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getCurrentPlatform } from '@pdd/platform-util';

export function getContent() {
  if (getCurrentPlatform().isNativePlatform) {
    return new Promise((res, rej) => {
      // AMStorageJSStorage
      invokeBridgeCallNativeFun('AMStorage', 'getPasteboard', null).then((data = {}) => {
        res(data.text);
      });
    });
  }
  return Promise.reject();
}

export function setContent(content) {
  if (typeof content !== 'string') {
    return Promise.reject();
  }
  if (getCurrentPlatform().isNativePlatform) {
    // AMStorageJSStorage
    return invokeBridgeCallNativeFun('AMStorage', 'setPasteboard', {
      text: content
    });
  }
  return Promise.reject();
}
