/**
 * ShareTracker
 * (modify is discouraged, as default tracking should be steady)
 *
 * (warning)
 * if you just want listen to share events, but want to do something
 * which is not related to tracking, get ShareController instance and
 * addEventListener directly
 * @file ShareTracker/index.js
 * @author daben
 */
import { sendMetrics } from '@pdd/metrics';
import { getCurrentPlatform, PLATFORM } from '@pdd/platform-util';
import ShareType from '../ShareType';
import ShareMethod from '../ShareMethod';
import ShareEventType from '../ShareEventType';
import ShareResult from '../ShareResult';
import ShareResultType from '../ShareResultType';
import ShareError from '../ShareError';
import ShareForm from '../ShareForm';
import ShareController from '../ShareController/base';
import TrackingEntity from './TrackingEntity';
import createShareID from '../tools/createShareID';
import getShareChannelByType from '../tools/getShareChannelByType';
import URLParser from '../tools/URLParser';
import getUserID, { getCachedUin } from '../tools/getUserID';
import VenderAppIDCache from '../tools/VenderAppIDCache';
import { getInstance as getApiCacheManager } from '../tools/ApiCacheManager';
import URLWrapType from '../tools/URLWrapType';
import calculateURLHash from '../tools/calculateURLHash';
import { getPageID, getPageName, getPageSN } from '../tools/getPageContext';
const { ShareErrorCode } = ShareError;
const { TRACKING_SUB_OP } = TrackingEntity;

function pickUpShareAppID(data) {
  if (!data || typeof data !== 'object') {
    return null;
  }
  if (typeof data['wx_app_id'] !== 'string') {
    return null;
  }
  return data['wx_app_id'];
}

/**
 * 
 */
class ShareTracker {
  constructor(opts = {}) {
    this.transparentTransmission = opts.transparentTransmission || {};
    this.trackingEntity = null;
    this.onTriggerListener = null;
    this.onCompletedListener = null;
    this.shareController = null;
    this._cachePrepareInfo = {};
  }

  /**
   * 
   * @param {ShareController} shareController 
   */
  listen(shareController) {
    if (!shareController || !(shareController instanceof ShareController)) {
      return;
    }
    this.disconnect();
    this.shareController = shareController;
    this._setupDefaultTrackListener(shareController);
  }

  /**
   * 
   */
  disconnect() {
    this.shareController && this._removeDefaultTrackListener(this.shareController);
  }

  /**
   * 
   * @param {object} params 
   */
  updateTrackerTrans(params) {
    this.transparentTransmission = Object.assign({}, this.transparentTransmission, params);
  }

  /**
   * @ignore
   */
  _setupDefaultTrackListener(shareController) {
    this._removeDefaultTrackListener(shareController);
    this.onPrepareListener = this._onDefaultSharePrepare.bind(this);
    this.onTriggerListener = this._onDefaultShareTrigger.bind(this);
    this.onCompletedListener = this._onDefaultShareCompleted.bind(this);
    shareController.on(
      ShareEventType.ON_SHARE_PREPARE,
      this.onPrepareListener
    );
    shareController.on(
      ShareEventType.ON_SHARE_TRIGGER,
      this.onTriggerListener
    );
    shareController.on(
      ShareEventType.ON_SHARE_COMPLETED,
      this.onCompletedListener
    );
  }

  /**
   * @ignore
   */
  _removeDefaultTrackListener(shareController) {
    this.onPrepareListener && shareController.removeListener(
      ShareEventType.ON_SHARE_PREPARE,
      this.onPrepareListener
    );
    this.onTriggerListener && shareController.removeListener(
      ShareEventType.ON_SHARE_TRIGGER,
      this.onTriggerListener
    );
    this.onCompletedListener && shareController.removeListener(
      ShareEventType.ON_SHARE_COMPLETED,
      this.onCompletedListener
    );
  }

  /**
   * @ignore
   */
  _sendLog(params = {}) {
    const data = Object.assign(params, this.transparentTransmission);
    sendMetrics({
      page_id: getPageID(),
      page_name: getPageName(),
      page_sn: getPageSN(),
      ...data
    });
  }

  /**
   * 
   * @param {object} param 
   * @param {ShareType} param.shareType 
   */
  _onDefaultSharePrepare({ shareType }) {
    // 
    if (this._cachePrepareInfo[shareType]) {
      // reset
      const shareEntity = this.shareController.getShareEntityByShareType(shareType) || {};
      const lastCacheInfo = this._cachePrepareInfo[shareType];
      lastCacheInfo.userID = getUserID();
      lastCacheInfo.uin = getCachedUin();
      shareEntity.setShareURLQueries(TrackingEntity.getTrackingQueries(lastCacheInfo));
      return;
    }
    const shareEntity = this.shareController.getShareEntityByShareType(shareType);
    // prepare
    const cacheInfo = {
      userID: getUserID(),
      uin: getCachedUin(),
      // share_idshare_id
      shareID: this._getCachedShareID(shareEntity) || createShareID(),
      shareChannel: getShareChannelByType(shareType)
    };
    if (shareEntity) {
      shareEntity.setShareURLQueries(TrackingEntity.getTrackingQueries(cacheInfo));
    }
    this._cachePrepareInfo[shareType] = cacheInfo;
  }

  /**
   * 
   * @ignore
   * @param {object} param 
   * @param {ShareType} param.shareType 
   * @param {string} param.shareID id
   */
  _onDefaultShareTrigger({ shareType, shareID }) {
    //  / ()
    if (
      shareType === ShareType.PIN_TIMELINE ||
      (
        getCurrentPlatform().isWeChatMiniProgram &&
        shareType === ShareType.WECHAT_SESSION
      )
    ) {
      return 0;
    }
    const shareEntity = this.shareController.getShareEntityByShareType(shareType) || {};
    const preparedInfo = this._cachePrepareInfo[shareType];
    this._cachePrepareInfo[shareType] = null;
    // shareURL and shareDomain should wait for share entity update shareURL
    const shareChannel = getShareChannelByType(shareType);
    const venderAppIDCache = VenderAppIDCache.getInstance();
    this.trackingEntity = new TrackingEntity(Object.assign(
      {
        userID: getUserID(),
        uin: getCachedUin(),
        shareID: this._getCachedShareID(shareEntity) || createShareID(),
        subOP: TRACKING_SUB_OP.SHARE,
        shareChannel: shareChannel,
        timelineDomain: shareEntity.timelineDomain,
        cipherScene: shareEntity.cipherScene,
        shareForm: shareEntity.getShareForm && shareEntity.getShareForm(shareType),
        shareMethod: shareEntity.getShareMethod && shareEntity.getShareMethod(shareType),
        shareAppID: venderAppIDCache.getAppIDSync(shareChannel)
      },
      preparedInfo,
      // shareID shareID
      // appshareID
      shareID ? { shareID } : null
    ));
    // modify shareURL with tracking params
    !preparedInfo && shareEntity.setShareURLQueries(TrackingEntity.getTrackingQueries(this.trackingEntity));
    if (shareEntity.getShareForm(shareType) !== ShareForm.COMMAND) {
      const shareURL = shareEntity.targetShareURL || shareEntity.shareURL;
      // set tracking url
      this.trackingEntity.shareURL = shareURL;
      this.trackingEntity.shareDomain = (new URLParser(shareURL)).origin;
    }
    this._sendLog(this.trackingEntity.normalize(shareType));
  }

  /**
   * 
   * @ignore
   * @param {object} param 
   * @param {ShareType} param.shareType 
   * @param {ShareResultType} param.shareResultType 
   * @param {boolean} param.useComplete 
   * @param {object|null} param.responseData 
   * @param {object|null} param.tpInfo 
   */
  _onDefaultShareCompleted({
    shareType,
    shareResultType,
    useComplete,
    responseData = {},
    tpInfo
  }) {
    const currentPlatform = getCurrentPlatform();
    //  / ()
    if (
      shareType === ShareType.PIN_TIMELINE ||
      (
        currentPlatform.isWeChatMiniProgram &&
        shareType === ShareType.WECHAT_SESSION
      )
    ) {
      return 0;
    }
    if (!this.trackingEntity) {
      return ShareErrorCode.SHARE_STATUS_ILLEGAL;
    }
    const shareEntity = this.shareController.getShareEntityByShareType(shareType) || {};
    let shareResult = this.getShareResultByType(shareResultType);
    // 
    // 
    // share_result = complete
    if (useComplete) {
      shareResult = ShareResult.COMPLETE;
    }

    if (shareEntity.getShareForm(shareType) !== ShareForm.COMMAND) {
      const shareURL = shareEntity.realShareURL || shareEntity.shareURL;
      // set tracking url
      this.trackingEntity.shareURL = shareURL;
      this.trackingEntity.shareDomain = (new URLParser(shareURL)).origin;
    } else {
      // pcode
      this.trackingEntity.pcode = shareEntity.pcode;
    }
    const wxAppID = pickUpShareAppID(responseData);
    if (wxAppID) {
      this.trackingEntity.shareAppID = wxAppID;
    }
    if (responseData.hasOwnProperty('wx_share_count')) {
      this.trackingEntity.wxShareCount = responseData['wx_share_count'];
      // 
      delete responseData.wx_share_count;
    }
    this.trackingEntity.subOP = TRACKING_SUB_OP.SHARE_RESULT;
    this.trackingEntity.shareResult = shareResult;
    if (tpInfo) {
      this.trackingEntity.walrusID = tpInfo.group_id;
      this.trackingEntity.walrusType = tpInfo.dev_type;
    }
    // ios  
    if (
      shareEntity.forceDowngrade ||
      (
        currentPlatform.platform === PLATFORM.PddIOS &&
        shareType === ShareType.WECHAT_SESSION_TEXT_WITH_NO_SDK
      ) ||
      (
        // 
        // share_result
        ShareType.COPY_LINK === shareType &&
        !(currentPlatform.isNativePlatform || currentPlatform.isTinyNativePlatform)
      ) ||
      ShareType.COPY_CIPHER === shareType
    ) {
      // TODO
    } else {
      this._sendLog(this.trackingEntity.normalize(shareType));
      this.trackingEntity = null;
    }
  }

  /**
   * IOS  
   * 
   * @param {ShareType} shareType 
   * @param {object} payload  
   * @param {function} handler  
   * @returns {boolean} 
   */
  trackProlongation(shareType, payload = {}, handler) {
    if (!this.trackingEntity) {
      return false;
    }
    this.trackingEntity.shareMethod = ShareMethod.MANUAL;
    this.trackingEntity.shareResult = ShareResult.COMPLETE;
    const params = Object.assign(
      this.trackingEntity.normalize(shareType),
      payload
    );
    this.trackingEntity = null;
    if (typeof handler === 'function') {
      handler(params);
    } else {
      this._sendLog(params);
    }
    return true;
  }

  /**
   * @ignore
   * @param {ShareResultType} shareResultType 
   * @returns {ShareResult} 
   */
  getShareResultByType(shareResultType = 0) {
    switch (shareResultType) {
      case ShareResultType.COMPLETE:
        return ShareResult.COMPLETE;
      case ShareResultType.SUCCESS:
        return ShareResult.SUCCESS;
      case ShareResultType.FAIL:
        return ShareResult.FAIL;
      case ShareResultType.CANCEL:
        return ShareResult.CANCEL;
      default: return ShareResult.ERROR;
    }
  }

  /**
   * @ignore
   */
  _getCachedShareID(shareEntity) {
    if (shareEntity.urlWrapType === URLWrapType.SERVER_SHORT_URL_WITH_CACHE) {
      const manager = getApiCacheManager();
      const hash = calculateURLHash(shareEntity);
      // 
      return manager.getShareID(hash);
    }
  }
}

export default ShareTracker;
