import getConfig from './getConfig';
import FallbackUrlCalculator from './FallbackUrlCalculator';

let calculator;
export default function getFallbackUrl(url, retriedTimes) {
  if (!calculator) {
    calculator = new FallbackUrlCalculator(getConfig());
  }
  return calculator.getFallbackUrl(url, retriedTimes);
}
