import constant from './constant';
import mpConfig from './mpConfig';

/**
 * appconfig
 *
 * @param {?string} hostNmae, host name
 *
 * @return {Object}
 */
export default {
    get: function get(hostName, userAgent) {
        if (!userAgent) {
            userAgent = typeof window !== 'undefined' ? window.navigator.userAgent || window.navigator.vendor || window.opera || '' : '';
        }
        const config = mpConfig.get({
            isModuleVersion: userAgent ? !!(userAgent.match(/phh_ios_version/i) || userAgent.match(/phh_android_version/i)) : false,
            hostName
        });

        return config;
    },
    constant: constant
};
