import { compareVersion, getCurrentPlatform } from '@pdd/platform-util';
import { getPathnameWithSearchAndHash } from './url';
import { launchApp } from './app';

const PACKAGE_NAME = 'com.xunmeng.pinduoduo';
const SCHEMA = 'pinduoduo:';
const WECHAT_CAN_LUANCH_MIN_VERSION_ANDROID = '3.13.0'; // androidapp
const APP_ID = 'wx77d53b84434b9d9a'; // APPID
const JSSDK_NOT_READY = 'sdk';
const LAUNCH_FAIL = 'App';

const currentPlatform = process.env.BROWSER ? getCurrentPlatform() : {};

/**
 * app https://wiki2.corp.yiran.com/pages/viewpage.action?pageId=7310006 
 * jssdk
 * @param {string} url url
 * @param {number} [launchType]  1: 2:A 3:iframe
 * @return {Promise} Promise
 */
export function launchPDDApp(url, launchType) {
  // node,,native
  if (!process.env.BROWSER || currentPlatform.isNativePlatform || currentPlatform.isWeChatMiniProgram) {
    const msg = 'Native,node,';
    return Promise.reject(new Error(msg));
  }

  const prefix = `${SCHEMA}//${PACKAGE_NAME}`;
  url = getPathnameWithSearchAndHash(url); // 

  if (!currentPlatform.isWeChatPlatform) {
    return launchApp(`${prefix}${url}`, launchType);
  }

  if (!window.wx) {
    return Promise.reject(new Error(JSSDK_NOT_READY));
  }
  return new Promise(resolve => {
    window.wx.ready(() => {
      resolve(
        (() => {
          if (currentPlatform.isIOSWeChatPlatform) {
            return wechatLuanchApplication(`${prefix}${url}`);
          }

          // Android url/
          return wechatGetInstallState().then(({ installed, version }) => {
            if (installed && compareVersion(version, WECHAT_CAN_LUANCH_MIN_VERSION_ANDROID) > 0) {
              return wechatLuanchApplication(url.slice(1));
            }

            throw new Error(`${LAUNCH_FAIL}${JSON.stringify({ installed, version })}`);
          });
        })()
      );
    });
  });
}

/**
 * jssdkappandroid,
 * @return {Promise<{installed: boolean, version: string}>} appPromise
 */
export function wechatGetInstallState() {
  // node
  if (!process.env.BROWSER) {
    const msg = '';
    return Promise.reject(new Error(msg));
  }
  if (!currentPlatform.isAndroidWeChatPlatform) {
    return Promise.reject(new Error('iOS'));
  }
  if (!window.wx) {
    return Promise.reject(JSSDK_NOT_READY);
  }
  return new Promise((resolve, reject) => {
    window.wx.ready(() => {
      if (!window.WeixinJSBridge || !window.WeixinJSBridge.invoke) {
        return reject(new Error(JSSDK_NOT_READY));
      }
      window.WeixinJSBridge.invoke(
        'getInstallState',
        { packageName: PACKAGE_NAME, packageUrl: 'pinduoduo://' },
        // eslint-disable-next-line
        ({ err_msg = '', versionName = '' }) => {
          if (/get_install_state:yes/.test(err_msg)) {
            return resolve({ installed: true, version: versionName });
          }
          if (/get_install_state:no/.test(err_msg)) {
            return resolve({ installed: false, version: versionName });
          }
          reject(new Error(err_msg));
        }
      );
    });
  });
}

/**
 * jssdkapp
 * @param url url
 * @return {Promise<any>} resolved promise rejection
 */
function wechatLuanchApplication(url) {
  const extInfo = 'from=wx_webview&forward_url=' + encodeURIComponent(url);
  if (!window.wx || !window.wx.invoke) {
    return Promise.reject(JSSDK_NOT_READY);
  }
  return new Promise((resolve, reject) => {
    window.wx.invoke('launchApplication', { appID: APP_ID, extInfo, messageExt: url }, res =>
      res.err_msg === 'launchApplication:ok' ? resolve() : reject(new Error(`${LAUNCH_FAIL}:${res.err_msg}`))
    );
  });
}
