import { isObject } from '@pdd/cui-util';
import recordError from './recordError';

const GLOBAL_CONFIG_KEY = '__CDN_IMG__';

export default function getConfigFromWindow() {
  if (!process.env.BROWSER) {
    return;
  }

  const globalConfig = window[GLOBAL_CONFIG_KEY];
  if (window.hasOwnProperty(GLOBAL_CONFIG_KEY) && !isObject(globalConfig)) {
    const errorMsg = `cui-image: window.${GLOBAL_CONFIG_KEY}`;
    recordError({
      errorMsg,
      payload: { value: globalConfig },
      error: new Error(errorMsg)
    });
  }

  return globalConfig;
}
