/**
 * @file WeChatShareEntity.js
 * @author daben
 */
import ShareMethod from '../ShareMethod';
import ShareForm from '../ShareForm';
import ShareEntity from './index';
import ShareType from '../ShareType';
import { isShareToQQ } from '../qqShareTypes';
import { getQQShareURL, getWebWrappedShareURLInfo } from '../tools/urlWrapper';

class WeChatShareEntity extends ShareEntity {
  normalize(shareType) {
    const shareEntity = Object.assign({}, this);
    if (shareType === ShareType.COPY_CIPHER) {
      return this.processCipher();
    }
    return new Promise((res, rej) => {
      const shareURL = shareEntity.shareURL;
      this.targetShareURL = this.shareURL;
      this.realShareURL = this.shareURL;
      this.useShortURL = false;
      if (
        shareType === ShareType.WECHAT_TIMELINE ||
        shareType === ShareType.WECHAT_SESSION
      ) {
        getWebWrappedShareURLInfo(shareEntity).then(({
          realShareURL,
          targetShareURL,
          useShortURL
        }) => {
          this.useShortURL = useShortURL;
          this.targetShareURL = targetShareURL;
          this.realShareURL = realShareURL;
          res({
            title: shareEntity.title,
            desc: shareEntity.message,
            imgUrl: shareEntity.thumbnailURL,
            link: realShareURL
          });
        }, () => {});
      } else if (isShareToQQ(shareType)) {
        this.targetShareURL = getQQShareURL(shareType, shareURL);
        this.realShareURL = this.targetShareURL;
        res({
          title: shareEntity.title,
          desc: shareEntity.message,
          imgUrl: shareEntity.thumbnailURL,
          link: this.realShareURL
        });
      } else {
        res({
          title: shareEntity.title,
          desc: shareEntity.message,
          imgUrl: shareEntity.thumbnailURL,
          link: shareURL
        });
      }
    });
  }

  getShareForm(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return ShareForm.COMMAND;
    }
    return ShareForm.CARD;
  }

  getShareMethod(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return ShareMethod.MANUAL;
    }
    return ShareMethod.SDK;
  }
}

export default WeChatShareEntity;
