import { compareVersion, PLATFORM, getCache } from './common';
import IPlatform from './IPlatform';

const regWeChatVersion = /micromessenger\/([^\s]+)\s*/i;

const WECHATPLATFORM = {
  WeChatAndroid: 'wechat_android',
  WeChatIOS: 'wechat_ios',
  WeChatUnknown: 'wechat_unknown'
};

/**
 * ua
 * @ignore
 * @param ua
 * @returns {*}
 */
export function getWechatVersion(ua = '') {
  const matchVersions = ua.match(regWeChatVersion);
  if (matchVersions) {
    return matchVersions[1] || '';
  }
  return '';
}

/**
 * AndroidIos
 * @ignore
 * @param ua
 * @returns {string}
 */
export function getWechatPlatform(ua = '') {
  ua = ua.toUpperCase();
  if (ua.indexOf('ANDROID') !== -1) {
    return WECHATPLATFORM.WeChatAndroid;
  }
  if (ua.indexOf('IPHONE') !== -1 || ua.indexOf('IPAD') !== -1 || ua.indexOf('ITOUCH') !== -1) {
    return WECHATPLATFORM.WeChatIOS;
  }
  return WECHATPLATFORM.WeChatUnknown;
}

/**
 * @ignore
 */
class WechatPlatForm extends IPlatform {
  /**
   * 
   * @type {string}
   */
  get wechatVersion() {
    return getCache(this.cache, 'wechatVersion', getWechatVersion(this.ua));
  }

  /**
   * wechat_android,wechat_ios,wechat_unknown
   * @type {string}
   */
  get wechatPlatform() {
    return getCache(this.cache, 'wechatPlatform', getWechatPlatform(this.ua));
  }

  /**
   * historyAPI
   * @type {boolean}
   */
  get isSupportHistoryAPI() {
    return getCache(this.cache, 'isSupportHistoryAPI', compareVersion(this.wechatVersion, '6.5.1') > 0);
  }
  /**
   * 
   * @type {boolean}
   */
  get isPureWeChatPlatform() {
    return this.platform === PLATFORM.WeChat;
  }

  /**
   * 
   * @type {boolean}
   */
  get isWeChatMiniProgram() {
    return this.platform === PLATFORM.WxApp;
  }

  /**
   * 
   * @type {boolean}
   */
  get isWeChatPlatform() {
    return this.platform === PLATFORM.WeChat || this.platform === PLATFORM.WxApp;
  }

  /**
   * android
   * @type {boolean}
   */
  get isAndroidWeChatPlatform() {
    return this.isWeChatPlatform && this.wechatPlatform === WECHATPLATFORM.WeChatAndroid;
  }

  /**
   * IOS
   * @type {boolean}
   */
  get isIOSWeChatPlatform() {
    return this.isWeChatPlatform && this.wechatPlatform === WECHATPLATFORM.WeChatIOS;
  }

  /**
   * Appwebview
   * @type {boolean}
   */
  get isEmbeddedBrowser() {
    return this.isWeChatPlatform;
  }

  /**
   * 
   * @type {boolean}
   */
  get isWeChatPayAvailable() {
    return this.isWeChatPlatform;
  }

  /**
   * webview
   * @type {boolean}
   */
  get isSupportWechatFloatingWindow() {
    return getCache(this.cache, 'isSupportWechatFloatingWindow', compareVersion(this.wechatVersion, '6.6.7') > 0);
  }

  /**
   * 
   * @type {boolean}
   */
  get isShowSMAlertPlatform() {
    return getCache(
      this.cache,
      'isShowSMAlertPlatform',
      (() => {
        if (this.isIOSWeChatPlatform) {
          return true;
        }
        if (this.isAndroidWeChatPlatform && compareVersion(this.wechatVersion, '6.5.6') >= 0) {
          return true;
        }
        return false;
      })()
    );
  }
}
export default WechatPlatForm;
