import ShareMethod from '../ShareMethod';
import ShareForm from '../ShareForm';
import ShareEntity from './index';
import ShareType from '../ShareType';
import processMiniObjectPath, { toHttps } from '../tools/processMiniObjectPath';
// import navigation from '@pdd/navigation';

class WxAppShareEntity extends ShareEntity {
  constructor(opts) {
    super(opts);
    this.wxAppRequestPayload = opts.wxAppRequestPayload;
    this.miniObjectPath = opts.miniObjectPath;
    this.shortCommandApi = opts.shortCommandApi;
    this.wxAppNavigatePayload = opts.wxAppNavigatePayload;
  }

  async normalize(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return this.processCipher();
    }
    const shareEntity = Object.assign({}, this);

    let miniObjectPath = shareEntity.miniObjectPath;
    //   refer_
    // referrefer_share_id)
    //  
    if (miniObjectPath && typeof miniObjectPath === 'string') {
      miniObjectPath = processMiniObjectPath(shareEntity);
    } else if (typeof miniObjectPath === 'function') {
      try {
        miniObjectPath = miniObjectPath(shareEntity.shareURL);
        if (typeof miniObjectPath !== 'string') {
          miniObjectPath = null;
        }
      } catch (e) {
        // todo
      }
    }
    // const path = await navigation.getMiniProgramUrl(shareEntity.shareURL);
    return Promise.resolve(Object.assign({}, shareEntity.wxAppNavigatePayload, {
      title: shareEntity.title,
      thumbnail: shareEntity.thumbnailURL,
      // 
      // @ , share_url ,
      share_url: miniObjectPath || toHttps(shareEntity.shareURL)
    }));
  }

  getShareForm(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return ShareForm.COMMAND;
    }
    return ShareForm.WXAPP_CARD;
  }

  getShareMethod(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return ShareMethod.MANUAL;
    }
    return ShareMethod.SDK;
  }
}

export default WxAppShareEntity;
