import { isMobilePlatform, getCache } from './common/index';
import IPlatform from './IPlatform';

/**
 * @ignore
 */
class UnknownPlatform extends IPlatform {
  /**
   * 
   * @type {boolean}
   */
  get isWeChatPayAvailable() {
    return getCache(this.cache, 'isWeChatPayAvailable', isMobilePlatform(this.ua));
  }

  /**
   * unknown
   * @type {boolean}
   */
  get isAliPayAvailable() {
    return true;
  }
}
export default UnknownPlatform;
