/**
 * 
 * @typedef {object} ShareErrorCode
 * @property {number} DEFAULT 
 * @property {number} SHARE_PARAMS_NOT_VALID 
 * @property {number} SHARE_TYPE_DISABLED 
 * @property {number} SHARE_TYPE_NOT_FOUND 
 * @property {number} SHARE_ACTION_DISABLE 
 * @property {number} SHARE_STATUS_ILLEGAL 
 * @property {number} SHARE_NOT_SUPPORTED 
 * @property {number} SHARE_LIMITER_NOT_INIT 
 */
export const ShareErrorCode = {
  // none
  DEFAULT: 0,
  SHARE_PARAMS_NOT_VALID: 1,
  SHARE_TYPE_DISABLED: 2,
  SHARE_TYPE_NOT_FOUND: 3,
  SHARE_ACTION_DISABLE: 4,
  SHARE_STATUS_ILLEGAL: 5,
  SHARE_NOT_SUPPORTED: 6,
  SHARE_LIMITER_NOT_INIT: 7
};

class ShareError {
  constructor(opts = {}) {
    this.errorCode = opts.errorCode || ShareErrorCode.DEFAULT;
    this.message = opts.message || '';
  }
}

ShareError.create = (errorCode) => {
  return new ShareError({
    errorCode
  });
};

ShareError.ShareErrorCode = ShareErrorCode;

export default ShareError;
