import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Portal } from '@pdd/cui-util';

/**
 * ControllerPortal
 *
 * @visibleName Toast.Controller
 */
export default class Controller extends PureComponent {
  componentDidMount() {
    // 
    this.timeoutId = setTimeout(() => {
      this.props.onTimeout && this.props.onTimeout();
      this.timeoutId = 0;
    }, this.props.duration * 1000);
  }
  componentWillUnmount() {
    // 
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
    }
  }
  render() {
    const { children, PortalComponent, PortalComponentProps } = this.props;

    return PortalComponent ? <PortalComponent {...PortalComponentProps}>{children}</PortalComponent> : children;
  }
}

Controller.defaultProps = {
  duration: 2,
  PortalComponent: Portal
};

Controller.propTypes = {
  /**  */
  onTimeout: PropTypes.func,
  /** 2 */
  duration: PropTypes.number,
  /** Portal@pdd/cui-utilPortal */
  PortalComponent: PropTypes.elementType,
  /** Portalprops */
  PortalComponentProps: PropTypes.object
};
