import getDomainByBucketId from '../utils/getDomainByBucketId';
/**
 * 
 * @param {import('../Config/constants').BackupDomainConfig} config - 
 * @returns {string} -  or ''
 */
export default function getRandomDomain(config) {
  const bucketId = Math.floor(Math.random() * Number.MAX_SAFE_INTEGER);
  return getDomainByBucketId(config, bucketId);
}
