/**
 *  error message
 *
 * @param {number|string} errorCode, server
 * @param {string} [defaultMsg=''], error message
 * @return {string} error message
 */
export const getMsgFromCode = (errorCode, defaultMsg = '') => {
  return errors[`${errorCode}_MSG`] || defaultMsg;
};
