import { getCurrentPlatform } from '@pdd/platform-util';
import { conflictWarning } from './conflictWarning';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { buildQuery } from '@pdd/core-util';

const isDev = process.env.NODE_ENV === 'development';

export const getDefaultReporter = () => {
  const { isNativePlatform, isTinyNativePlatform } = getCurrentPlatform();
  return isNativePlatform || isTinyNativePlatform ? nativeReporter : browserReporter;
};

export const browserReporter = (url, params) => {
  isDev && conflictWarning(params, 'support_beacon');

  try {
    // beaconbeaconajax
    if(typeof window.navigator.sendBeacon === 'function') {
      params.support_beacon = 1;
      if(window.navigator.sendBeacon(url, buildQuery(params))) {
        return;
      }
    }

    const http = new XMLHttpRequest();
    http.open('POST', url, true);
    http.setRequestHeader('Content-Type', 'text/plain;charset=UTF-8');
    http.withCredentials = true;
    http.send(buildQuery(params));
  } catch (e) {
    handleReporterError(e);
  }
};

export const nativeReporter = (url, params) => {
  isDev && conflictWarning(params, 'use_AMAnalytics');

  const platform = getCurrentPlatform();
  const useAMAnalytics = platform.isTinyNativePlatform || platform.statisfy('4.45.0', '4.33.0', 'greaterThanEqual');

  if(useAMAnalytics) {
    params = flatObject(params);
    params.use_AMAnalytics = 1;

    return invokeBridgeCallNativeFun('AMAnalytics', 'send', { url, value: params }).catch(handleReporterError);
  }

  return invokeBridgeCallNativeFun('JSNetwork', 'request', {
    method: 'POST',
    url,
    headers: { 'Content-Type': 'text/plain;charset=UTF-8' },
    data: buildQuery(params)
  }).catch(handleReporterError);
};

function handleReporterError(e) {
  console.error('@pdd/metrics#onReporterError', e);
}

// AMAnalyticsobject
function flatObject(params) {
  return Object.entries(params).reduce((result, [key, value]) => {
    if(value !== null) {
      result[key] = typeof value === 'object' ? JSON.stringify(value) : value;
    }
    return result;
  }, {});
}
