/**
 * replaceImgDomain
 * @param {string} imgUrl - url
 * @returns {string} - url
 * @example
 *
 * replaceImgDomain('https://t00img.yangkeduo.com/goods/images/2020-05-27/c428a469-9192-4fce-9521-a43da13fb9f9.jpeg') // => https://img.pddpic.com/goods/images/2020-05-27/c428a469-9192-4fce-9521-a43da13fb9f9.jpeg
 *
 */
export function replaceImgDomain(imgUrl = '') {
  if (imgUrl.indexOf('t00img.yangkeduo.com') > -1) {
    return imgUrl.replace('t00img.yangkeduo.com', 'img.pddpic.com');
  }
  if (imgUrl.indexOf('t16img.yangkeduo.com') > -1) {
    return imgUrl.replace('t16img.yangkeduo.com', 'commimg.pddpic.com');
  }
  return imgUrl;
}
