/**
 * 
 */
const pageContextCache = {};

export function setPageContext(context = {}) {
  Object.assign(pageContextCache, context);
}

export function getPageID() {
  return pageContextCache.pageId || '';
}

export function getPageSN() {
  return pageContextCache.pageSN || '';
}

export function getPageName() {
  return pageContextCache.pageName || '';
}
