/**
 * @file isSupportWebp
 * @auhtor tenghu
 * @description
 * webp
 * isSupportWebpcookie
 */
import { isSupportWebp as testIsSupportWebp, setCookie } from '@pdd/core-util';

const WEBP_KEY = 'webp';
const WEBP_VALUE_TRUE = '1';
const ATTRS = { maxAge: 30 * 24 * 3600 };

/**
 * webpcookie
 * return {Promise}
 */
export function initCookies() {
  return testIsSupportWebp().then(v => v && setCookie(WEBP_KEY, WEBP_VALUE_TRUE, ATTRS));
}

/**
 * webp
 * @param {Object} cookies - requestContext.cookies
 * @returns {boolean} webp
 */
export function isSupportWebpFromCookie(cookies) {
  return WEBP_VALUE_TRUE === cookies[WEBP_KEY];
}
