import TraceKit from 'tracekit';
import { appendQuery, getNetWorkType, isAbsoluteUrl } from '@pdd/core-util';
import { getCurrentPlatform } from '@pdd/platform-util';
import { getUid } from '@pdd/auth';
import { getDefaultReporter } from './common/reporter';
import { dealError, replaceAccessToken, jsonStringifyReplacer } from './common/utils';
import { getOrganizedData, baseSendPmmError } from './pmm/base';
import { setPmmAppInfo, setPageInfo, combineURLs, ERROR_TYPE_CONFIG, setDefaultExtraInfo, isObject } from './pmm/utils';

export { sendApiError, sendCustomError, sendResourceError, sendError, sendNormalLog } from './pmm';
export { setDefaultExtraInfo };

/**
 * 
 * @params {{pageSN:string, pageName:string, pageId:string, referPageId?:string, referPageSN?:string, referPageName?:string}} pageInfo 
 * @params {{bizSide: string, appId: string, testAppId: string, universalPaths?:string[], universalQueryKeys: string[] }} appInfo pmm 
 * @return {Promise<void>}
 */
export async function init(pageInfo, appInfo) {
  setPageInfo(pageInfo);
  setPmmAppInfo(appInfo);
  const { bizSide, appId, testAppId } = appInfo || {};
  if(!bizSide || !appId || !testAppId) {
    console.error('@pdd/error-logger init  bizSide, appId, testAppId');
  }
  TraceKit.remoteFetching = false;
  TraceKit.extendToAsynchronousCallbacks();
  // reset inline error logger
  window.__RESET_ERROR_LISTENER__ && window.__RESET_ERROR_LISTENER__();
  const reporter = getDefaultReporter();
  // traceKitwindow.onerror
  TraceKit.report.subscribe(async (stackInfo, isWindowError, error) => {
    let errorStr = '';
    const { name, message, stack } = stackInfo || {};

    if(stackInfo) {
      errorStr = name + ':' + message;
      if(stack && stack.length) {
        for(let i = 0; i < stack.length; i++) {
          errorStr += `@ ${stack[i].func}(${stack[i].url}:${stack[i].line}:${stack[i].column})`;
        }
      }
    }

    const params = await resolveErrorParams(
      {
        error_message: String(message),
        ...error?.extraInfo,
        page: stackInfo ? name + ' ' + message + ' ' + JSON.stringify(stack) : 'no info',
        debug_url: appendQuery({ JS_ERROR: errorStr }, location.href)
      },
      pageInfo
    );
    const { sub_op: subOp } = error?.extraInfo || {};
    const isApiError = subOp === 'api_error';

    const { method, baseURL, url } = error?.config || {};
    let fullUrl;
    if(baseURL && url) {
      if(isAbsoluteUrl(url)) {
        fullUrl = url;
      } else {
        fullUrl = combineURLs(baseURL, url);
      }
    }
    reporter(params);
    try {
      baseSendPmmError(
        getOrganizedData(
          {
            ...params,
            ...(method ? { httpMethod: method } : {}),
            ...(fullUrl ? { url: fullUrl } : {}),
            // api error 
            // case unhandledrejection  api error 
            ...(isApiError ? { type: ERROR_TYPE_CONFIG.API_ERROR } : {})
          },
          appInfo
        ),
        appInfo
      );
      // eslint-disable-next-line no-empty
    } catch (e) {}
  });

  // unhandledrejection
  window.addEventListener('unhandledrejection', async (event) => {
    const error = dealError(event.reason);
    const { error_msg } = error || {};
    const { errorCode, params: bridgeParams, data } = event.reason || {};
    const extraInfo = (event.reason || {}).extraInfo || {};
    const { payload } = extraInfo || {};
    // errorCode bridgeParams data  native-util -> invokeBridgeCallNativeFun 
    //  error  throwErrorAsync  error 
    // 
    if(errorCode) {
      extraInfo.errorCode = errorCode;
    }
    const pinBridgeErrorPayload = {};

    if(bridgeParams) {
      pinBridgeErrorPayload.apiParams = isObject(bridgeParams) ? JSON.stringify(bridgeParams) : String(bridgeParams);
    }

    if(data) {
      pinBridgeErrorPayload.data = isObject(data) ? JSON.stringify(data) : String(data);
    }

    extraInfo.payload = {
      ...(isObject(payload) ? payload : {}),
      ...pinBridgeErrorPayload
    };
    const params = await resolveErrorParams(
      {
        ...extraInfo,
        page: 'unhandledrejection reason:' + replaceAccessToken(JSON.stringify(error, jsonStringifyReplacer())),
        error_msg
      },
      pageInfo
    );

    reporter(params);
    try {
      baseSendPmmError(getOrganizedData(params, appInfo), appInfo);
      // eslint-disable-next-line no-empty
    } catch (e) {}
  });
}

/**
 * 
 * @private
 * @param {Object} params 
 * @param {{pageSN:string, pageName:string, pageId:string, referPageId?:string, referPageSN?:string, referPageName?:string}} pageInfo 
 * @return {Object} 
 */
async function resolveErrorParams(params, pageInfo) {
  /* eslint-disable camelcase */
  const {
    pageSN: page_sn,
    pageId: page_id,
    pageName: page_name,
    referPageId: refer_page_id,
    referPageName: refer_page_name,
    referPageSN: refer_page_sn
  } = pageInfo;

  const { platform, version } = getCurrentPlatform();
  const network = await getNetWorkType().catch(() => 0);
  const userId = await getUid();
  const processedParams = {
    log_version: '1.0.0',
    page_url: location.href,
    app_version: version,
    user_agent: navigator.userAgent,
    user_id: userId,
    platform,
    page_id,
    page_name,
    page_sn,
    refer_page_id,
    refer_page_name,
    refer_page_sn,
    network,
    ...params
  };

  processedParams.payload = isObject(processedParams.payload) ? processedParams.payload : {};

  const payloadKeys = ['status_code', 'error_message', 'params', 'sub_op', 'debug_url'];
  payloadKeys.forEach((property) => {
    if(typeof processedParams[property] !== 'undefined') {
      processedParams.payload[property] = processedParams[property];
      processedParams[property] = undefined;
    }
  });
  processedParams.payload.unique_tag = `${Date.now()}_${userId}`;
  processedParams.payload.error_logger = 1;

  return processedParams;
}
