import React, { useEffect } from 'react';
import { getCurrentPlatform } from '@pdd/platform-util';
import { showRetryPage } from '@pdd/native-util';
import { Spinner } from '@pdd/cui-spinner';

export default function TimeoutSpinner() {
  useEffect(() => {
    if(getCurrentPlatform().isNativePlatform) {
      const params = {
        errorType: 'networkError',
        params: { title: '', message: '', iconType: 'e779' }
      };
      const timer = setTimeout(() => showRetryPage(params).catch(() => showRetryPage(params)), 10000);

      return () => clearTimeout(timer);
    }
  }, []);

  return <Spinner hideBg={true} />;
}
