/**
 * @file Config
 * @description
 * 
 */
import recordError from '../utils/recordError';
import isNumber from '../utils/isNumber';
import { isObject } from '@pdd/cui-util';
import { DEFAULT_BACKUP_DOMAIN_CONFIG_MAP, DEFAULT_RETRY_LIMIT, DEFAULT_USE_IP_FALLBACK, BGP_IPS } from './constants';

const isBoolean = value => Object.prototype.toString.call(value) === '[object Boolean]';

function recordFormatError(value, msg) {
  value !== undefined && recordError({ errorMsg: msg, payload: { value: JSON.stringify(value) } });
}

function formatBackupDomainConfigMap(backupDomainConfigMap) {
  if (!isObject(backupDomainConfigMap)) {
    recordFormatError(backupDomainConfigMap, 'cui-image: backupDomainConfigMapobject');
    return DEFAULT_BACKUP_DOMAIN_CONFIG_MAP;
  }

  const result = {};
  for (let host in backupDomainConfigMap) {
    if (!isObject(backupDomainConfigMap[host])) {
      recordFormatError(
        backupDomainConfigMap,
        `cui-image: backupDomainConfigMap${host}object`
      );
      result[host] = {};
    } else {
      result[host] = backupDomainConfigMap[host];
    }
  }
  return result;
}

function formatRetryLimit(retryLimit) {
  if (!isNumber(retryLimit) || retryLimit < 0) {
    recordFormatError(retryLimit, 'cui-image: retryLimit0');
    return DEFAULT_RETRY_LIMIT;
  }
  return retryLimit;
}

function formatUseIpFallback(useIpFallback) {
  if (!isBoolean(useIpFallback)) {
    recordFormatError(useIpFallback, 'cui-image: useIpFallbackboolean');
    return DEFAULT_USE_IP_FALLBACK;
  }
  return useIpFallback;
}

function formatCustomConfig(rawConfig) {
  if (!isObject(rawConfig)) {
    recordFormatError(rawConfig, 'cui-image: object');
    return {
      backupDomainConfigMap: DEFAULT_BACKUP_DOMAIN_CONFIG_MAP,
      useIpFallback: DEFAULT_USE_IP_FALLBACK,
      retryLimit: DEFAULT_RETRY_LIMIT
    };
  }

  return {
    backupDomainConfigMap: formatBackupDomainConfigMap(rawConfig.backupDomainConfigMap),
    useIpFallback: formatUseIpFallback(rawConfig.useIpFallback),
    retryLimit: formatRetryLimit(rawConfig.retryLimit)
  };
}

export default class Config {
  constructor(rawConfig) {
    const customConfig = formatCustomConfig(rawConfig);

    this._backupDomainConfigMap = customConfig.backupDomainConfigMap;
    this._useIpFallback = customConfig.useIpFallback;
    this._retryLimit = customConfig.retryLimit;
  }

  getBackupDomainConfigMap() {
    return this._backupDomainConfigMap;
  }
  useIpFallback() {
    return this._useIpFallback;
  }
  getRetryLimit() {
    return this._retryLimit;
  }
  getBgpIps() {
    return BGP_IPS;
  }

  /**
     * @param {string} host -
     * @returns {import('../constants').BackupDomainConfig}
     */
  getBackupDomainConfig(host) {
    const backupDomainConfigMap = this._backupDomainConfigMap;
    const domainConfig = backupDomainConfigMap[host];
    return isObject(domainConfig) ? domainConfig : {};
  }
  /**
     * backupDomain''
     * @param {string} backupDomain -
     * @returns {string} originHost''
     */
  getOriginHost(backupDomain) {
    const backupDomainConfigMap = this._backupDomainConfigMap;
    for (let originHost in backupDomainConfigMap) {
      const backupDomainConfig = backupDomainConfigMap[originHost];

      if (backupDomain in backupDomainConfig) {
        return originHost;
      }
    }
    return '';
  }
}
