/**
 * @file index.js isSupportWebp
 * @author tenghu
 * @description
 * @pdd/image
 */
const WEBP_TEST_URL =
  'data:image/webp;base64,UklGRlAAAABXRUJQVlA4WAoAAAAQAAAADwAADwAAQUxQSBIAAAABBxAR/Q8ABOH/3EFE/1MDAABWUDggGAAAADABAJ0BKhAAEAACACYlpAADcAD+/PQAAA==';

/**
 * webp
 * @returns {Promise.<boolean>} - webp
 */
function testIsSupportWebp() {
  return new Promise(resolve => {
    const img = document.createElement('img');
    img.onload = img.onerror = () => resolve(!!(img.height && img.width));
    img.src = WEBP_TEST_URL;
  });
}

let isSupport;
const init = () => (isSupport = process.env.BROWSER ? testIsSupportWebp() : Promise.resolve(false));

/**
 * webp
 * false
 * returns {Promise.<boolean>} webp
 */
export function isSupportWebp() {
  return isSupport || init();
}
