import ShareType from './ShareType';

/**
 * @ignore
 */
const QQ_SHARE_TYPES = [
  ShareType.QQ_CHAT_SESSION,
  ShareType.QQ_ZONE,
  ShareType.QQ_CHAT_SESSION_IMAGE,
  ShareType.QQ_ZONE_IMAGE
];

/**
 * QQ
 * @param {ShareType} shareType 
 */
const isShareToQQ = (shareType) => {
  return QQ_SHARE_TYPES.indexOf(shareType) >= 0;
};

export {
  isShareToQQ,
  QQ_SHARE_TYPES
};
