import { PLATFORM } from './common/index';
import IPlatform from './IPlatform';

/**
 * @ignore
 */
class WeiboPlatform extends IPlatform {
  /**
   * 
   * @type {boolean}
   */
  get isWeiboPlatform() {
    return this.platform === PLATFORM.Weibo;
  }

  /**
   * App Webview
   * @type {boolean}
   */
  get isEmbeddedBrowser() {
    return this.isWeiboPlatform;
  }

  /**
   * weibo
   * @type {boolean}
   */
  get isAliPayAvailable() {
    return true;
  }
}
export default WeiboPlatform;
