/**
 * @file loginUrlUtil.js url
 * @author zongheng
 */
import { app as appConfig } from '@pdd/configs';
import { buildQuery, appendQuery } from '@pdd/core-util';
import { ScopeType, WechatAuthType } from './constants';

/**
 * @ignore
 */
export function resolveQQLoginUrl(host, redirectUrl) {
  const config = appConfig.get(host);
  const redirectUri = config.RedirectURI.QQConnect + '?' + buildQuery({ refer: redirectUrl });
  const query = buildQuery({
    response_type: 'code',
    scope: 'get_user_info',
    client_id: config.ApiKey.QQPublic,
    redirect_uri: redirectUri
  });

  return {
    oAuthUrl: `https://graph.qq.com/oauth2.0/authorize?${query}#qq_redirect`,
    redirectUri
  };
}

/**
 * @ignore
 */
export function resolveWechatLoginUrl(host, redirectUrl, scopeType = ScopeType.Base) {
  const config = appConfig.get(host);

  if(config.hasThirdLevelDomain) {
    // 
    let domain = config.domain || config.shareDomain;
    domain = domain.endsWith('/') ? domain : domain + '/';
    return appendQuery({ third_level_from_url: redirectUrl }, `${domain}${config.thirdLevelLoginPath}`);
  }

  const query = buildQuery({
    response_type: 'code',
    scope: scopeType,
    appid: config.ApiKey.WeChat,
    //  hash
    // sdk confighash hash config by jiangliuer
    redirect_uri: redirectUrl.split('#')[0],
    state: scopeType === ScopeType.UserInfo ? WechatAuthType.UserInfo : WechatAuthType.Base
  });

  return `https://open.weixin.qq.com/connect/oauth2/authorize?${query}#wechat_redirect`;
}

/**
 * @ignore
 */
export function resolveH5LoginUrl(redirectUrl) {
  // todo: login.html query
  return `/login.html?from=${encodeURIComponent(redirectUrl)}`;
}

/**
 * @ignore
 */
export function resolveAppLoginUrl(redirectUrl) {
  return `/app_login.html?redirect_uri=${encodeURIComponent(redirectUrl)}&force_login=1`;
}

const FORBIDDEN_CODE = [
  54004, // 
  54008, // 
  43020, // 
  43046, // 
  43054 // 
];

/**
 * @ignore
 * @see https://note.pdd.net/doc/275276071928864768?root=257898683292676096
 *
 * 
 */
export function isForbiddenErrorCode(errorCode) {
  return FORBIDDEN_CODE.includes(errorCode) || (errorCode >= 54100 && errorCode <= 54200);
}
