/**
 * @file bootClient.js, render-server + isomorphic-mobx-framework 
 * @author zongheng(zongheng@pinduoduo.com)
 */
import { configure } from 'mobx';
import { getCurrentPlatform, SYSTEM } from '@pdd/platform-util';
import page from '@pdd/isomorphic-mobx-framework/lib/initPage';
import { initCookies } from '@pdd/isomorphic-mobx-framework/lib/cookie';
import { waitRawDataReady } from '../core/util';

const DEFAULTS = {
  forceWKWebviewBackRefresh: true,
  useStrict: false
};

/**
 * 
 *
 * @param {React.Component} App 
 * @param {Object} [options={}] @pdd/page
 * @param {boolean} [options.useStrict=false] mobx
 * @param {boolean} [options.disablePullReload=false] 
 * @param {function} [options.initNativeBehavior] 
 * @param {boolean} [options.forceWKWebviewBackRefresh=true] WKWebview
 */
export function bootClient(
  App,
  {
    useStrict = DEFAULTS.useStrict,
    forceWKWebviewBackRefresh = DEFAULTS.forceWKWebviewBackRefresh,
    ...pageOptions
  } = {}
) {
  if(useStrict) {
    configure({ enforceActions: true });
  }

  return waitRawDataReady().then(() => new Promise(resolve => {
    page.prepare(
      {
        requireLogin: App.appConfig.requireLogin,
        shareInfo: { disableShare: true },
        ...pageOptions
      },
      pageInfo => resolve(runApp(App, pageInfo.pageId))
    );

    initCookies();

    // 
    if(window.__wxjs_is_wkwebview && forceWKWebviewBackRefresh) {
      window.addEventListener('pageshow', ({ persisted }) => {
        persisted && location.reload();
      });
    }

    if(getCurrentPlatform().system === SYSTEM.IOS) {
      // 1. iosactive
      // 2. ios
      document.addEventListener('touchstart', () => {});
    }
  }));
}

function runApp(App, pageId) {
  const stores = App.createStores();
  const pageInfo = { pageId };
  Object.keys(stores).forEach(key => stores[key].fromJS((window.rawData?.stores || {})[key], pageInfo));
  if(window.rawData) {
    window.__INITIAL_PROPS__ = { stores, pageInfo };
  }
  App.stores = stores;
  App.pageInfo = pageInfo;
  if(process.env.NODE_ENV === 'development') {
    window.stores = stores;
  }
}
