import { getInstance as getShareLimit } from '../ShareLimiter/index';
import translateTPApp, { translateTPAppCard } from './translateTPApp';
import { getCurrentPlatform } from '@pdd/platform-util';
import { getWebWrappedShareURLInfo, getShareDomainPlatform, getDomainReplacedURL } from './urlWrapper';
import calculateURLHash from './calculateURLHash';
import { getInstance as getApiCacheManager } from './ApiCacheManager';
import URLWrapType, { isServerControlType, isTinyDomainType } from './URLWrapType';
import URLParser from './URLParser';
import popLocalCipher from '../tools/popLocalCipher';
import shareDomainModel, { ShareDomainType } from './shareDomainModel';

export function getWrappedShareURLInfo(...args) {
  // 
  if (!getCurrentPlatform().isNativePlatform) {
    return getWebWrappedShareURLInfo(...args);
  }
  return getNativeOnlyWrappedShareURLInfo(...args).then(payload => {
    const { missed } = payload;
    if (missed) {
      return getWebWrappedShareURLInfo(...args);
    }
    return payload;
  });
}

/**
 * 
 * @param {*} shareEntity 
 */
export const getNativeOnlyWrappedShareURLInfo = (shareEntity = {}) => {
  const shareURL = shareEntity.shareURL || '';
  const urlWrapType = shareEntity.urlWrapType;
  // /cardmatchedPkgId
  if (shareEntity.marketActivityID && shareEntity.activitySceneSn && urlWrapType === URLWrapType.CATEGORY_PROCEDURE) {
    const matchedPkgId = getShareLimit().matchedPkgId;
    return translateTPAppCard(
      Object.assign(
        {
          rom: getShareLimit().rom,
          matchedPkgId
        },
        shareEntity
      ),
      shareEntity.tpSelectCategory
    ).then(({ info, tpInfos, tpDowngrade, raw }) => {
      const response = {
        raw,
        tpDowngrade,
        tpInfo: info,
        tpInfos,
        // 
        realShareURL: (info || tpInfos[0] || {}).link_url || shareURL,
        targetShareURL: shareURL
      };
      const error = new Error('category procedure');
      error.data = response;
      return Promise.reject(error);
    });
  }
  if (
    shareEntity.marketActivityID &&
    urlWrapType === URLWrapType.CATEGORY_PROCEDURE_CUSTOM
  ) {
    const shareDomainType = isTinyDomainType(urlWrapType)
      ? ShareDomainType.WEIXIN_TINY_DOMAIN
      : ShareDomainType.SHORT_LINK;
    const requestShareURL = shareURL.replace(/^(\w+:)?\/\//, '');
    const shareDomainRP = shareDomainModel(shareEntity.requestUsePost ? 'post' : 'get', {
      shareURL: requestShareURL,
      marketActivityID: shareEntity.marketActivityID,
      platform: getShareDomainPlatform(),
      shareDomainType: shareDomainType,
      shortURLApi: shareEntity.shortURLApi,
      shortCommandApi: shareEntity.shortCommandApi,
      domainURLApi: shareEntity.domainURLApi,
      payloadMixin: shareEntity.payloadMixin
    });
    return shareDomainRP.then(payload => {
      const error = new Error('category procedure custom');
      error.data = payload;
      return Promise.reject(error);
    });
  }
  // 
  if (shareEntity.marketActivityID && shareEntity.activitySceneSn && urlWrapType === URLWrapType.SERVER_TP_APP) {
    const matchedPkgId = getShareLimit().matchedPkgId;
    // if (matchedPkgId === FORBIDDEN_MATCHED_PKG_ID) {
    //   return Promise.reject(new Error('no matched pkg id'));
    // }
    return translateTPApp(
      Object.assign(
        {
          rom: getShareLimit().rom,
          matchedPkgId: matchedPkgId
        },
        shareEntity
      ),
      shareEntity.tpSelectCategory
    ).then(
      ({ info, tpInfos, tpDowngrade }) => {
        return {
          tpInfo: info,
          // info 
          tpDowngrade,
          tpInfos,
          // 
          realShareURL: (info || tpInfos[0] || {}).link_url || shareURL,
          targetShareURL: shareURL
        };
      },
      _ => {
        return Promise.resolve({
          // 
          tpDowngrade: true,
          realShareURL: shareURL,
          targetShareURL: shareURL
        });
      }
    );
  }
  if (shareEntity.marketActivityID && isServerControlType(urlWrapType)) {
    const shareDomainType = isTinyDomainType(urlWrapType)
      ? ShareDomainType.WEIXIN_TINY_DOMAIN
      : ShareDomainType.SHORT_LINK;
    const requestShareURL = shareURL.replace(/^(\w+:)?\/\//, '');
    const shareURLHash = calculateURLHash(shareEntity);
    const cipherScene = shareEntity.cipherScene;
    const consumeMode = shareEntity.consumeMode;
    const apiCacheManager = getApiCacheManager();
    // 
    const remoteRequest = () => {
      const req = new Promise((res, rej) => {
        const shareDomainRP = shareDomainModel(shareEntity.requestUsePost ? 'post' : 'get', {
          shareURL: requestShareURL,
          marketActivityID: shareEntity.marketActivityID,
          platform: getShareDomainPlatform(),
          shareDomainType: shareDomainType,
          shortURLApi: shareEntity.shortURLApi,
          shortCommandApi: shareEntity.shortCommandApi,
          domainURLApi: shareEntity.domainURLApi,
          payloadMixin: shareEntity.payloadMixin
        });
        shareDomainRP
          .then(data => {
            if (data) {
              const { domain = '', shortURL, shareText, raw } = data;
              if (shareDomainType === ShareDomainType.WEIXIN_TINY_DOMAIN) {
                if (urlWrapType === URLWrapType.DOMAIN_REPLACE) {
                  res(getDomainReplacedURL(shareURL, domain));
                }
              } else if (shareDomainType === ShareDomainType.SHORT_LINK) {
                if (!shortURL && !shareText) {
                  const error = new Error('empty short_url with empty share_text');
                  error.data = raw;
                  error.extraInfo = raw;
                  rej(error);
                  return;
                }
                res({
                  useShortURL: true,
                  realShareURL: shortURL || shareURL,
                  targetShareURL: shareURL,
                  shareText: shareText,
                  rawResponse: raw
                });
                return;
              }
            }
            res({
              realShareURL: shareURL,
              targetShareURL: shareURL
            });
          })
          .catch(e => {
            rej(e);
          });
      });
      // 
      shareDomainType === ShareDomainType.SHORT_LINK &&
        req.then(response => {
          // 
          if (response && response.useShortURL) {
            apiCacheManager.cacheResponseByHash(shareURLHash, response);
            apiCacheManager.setShareID(shareURLHash, (new URLParser(shareURL).query || {}).refer_share_id);
          }
        });
      return req;
    };

    //  
    if (urlWrapType === URLWrapType.SERVER_SHORT_URL_WITH_CACHE) {
      let cachedResponse = apiCacheManager.getResponseByHash(cipherScene);
      // ,
      if (cachedResponse) {
        apiCacheManager.increaseCount(cipherScene);
        // 
        return Promise.resolve(cachedResponse);
      }
      // 
      cachedResponse = apiCacheManager.getResponseByHash(shareURLHash);
      if (cachedResponse) {
        apiCacheManager.increaseCount(shareURLHash);
        // 
        return Promise.resolve(cachedResponse);
      }
      // app
      if (cipherScene && getShareLimit().isCipherDowngrade) {
        return popLocalCipher({
          scene: cipherScene,
          consumeMode
        }).then(
          (data = {}) => {
            if (data && data.share_id) {
              const precacheData = {
                cipher: data.cipher,
                shareText: data.text,
                shareID: data.share_id
              };
              // 
              apiCacheManager.cacheResponseByHash(cipherScene, precacheData);
              apiCacheManager.setShareID(cipherScene, data.shareID);
              return precacheData;
            }
            // 
            return remoteRequest();
          },
          _ => remoteRequest()
        );
      }
      return remoteRequest();
    }
    return remoteRequest();
  }
  // 
  if (shareEntity.activitySceneSn && urlWrapType === URLWrapType.SERVER_TP_APP_CARD) {
    const matchedPkgId = getShareLimit().matchedPkgId;
    // if (matchedPkgId === FORBIDDEN_MATCHED_PKG_ID) {
    //   return Promise.reject(new Error('no matched pkg id'));
    // }
    return translateTPAppCard(
      Object.assign(
        {
          rom: getShareLimit().rom,
          matchedPkgId
        },
        shareEntity
      ),
      shareEntity.tpSelectCategory
    ).then(
      ({ info, tpInfos, tpDowngrade, raw }) => {
        return {
          tpInfo: info,
          // info 
          tpDowngrade,
          tpInfos,
          // 
          realShareURL: (info || tpInfos[0] || {}).link_url || shareURL,
          categoryShareInfo: { thumbnailURL: (info || tpInfos[0] || {}).thumb_img_url },
          targetShareURL: shareURL,
          rawResponse: raw
        };
      },
      _ => {
        return Promise.resolve({
          // 
          tpDowngrade: true,
          realShareURL: shareURL,
          targetShareURL: shareURL
        });
      }
    );
  }
  return Promise.resolve({
    missed: true,
    realShareURL: shareURL,
    targetShareURL: shareURL
  });
};
