/**
 * @file ShareController/wxapp.js
 * @author  daben
 */
import ShareController from './base';
import ShareType from '../ShareType';
import ShareEventType from '../ShareEventType';
import ShareResultType from '../ShareResultType';
import WxAppShareEntity from '../ShareEntity/WxAppShareEntity';
import ShareError from '../ShareError';
import { getPageSN, getPageName, getPageID } from '../tools/getPageContext';
import { buildQuery } from '@pdd/core-util';
const { ShareErrorCode } = ShareError;

const SUPPORTED_SHARE_TYPE = [
  ShareType.WECHAT_SESSION,
  ShareType.COPY_CIPHER
];

const DEFALT_WEBSHARE_RELAY_PAGE = '/pages/web_share/web_share?';

class WxAppShareController extends ShareController {
  constructor(opts) {
    super(opts);
    this.ShareEntityClass = WxAppShareEntity;
    this._enableShareTypes = SUPPORTED_SHARE_TYPE.concat();
  }

  /**
   * @override
   * @param {ShareType} shareType 
   */
  share(shareType) {
    if (this._enableShareTypes.indexOf(shareType) < 0) {
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }
    if (ShareType.COPY_CIPHER === shareType) {
      return this.copyCipher();
    }
    const shareEntity = this.getShareEntityByShareType(shareType);
    //  
    if (!shareEntity) {
      return ShareErrorCode.SHARE_PARAMS_NOT_VALID;
    }
    return this._shareByNavigate();
  }

  /**
   * @ignore
   */
  _shareByNavigate() {
    if (
      !window.wx ||
      !window.wx.miniProgram ||
      typeof window.wx.miniProgram.navigateTo !== 'function'
    ) {
      return ShareErrorCode.SHARE_NOT_SUPPORTED;
    }
    const shareType = ShareType.WECHAT_SESSION;
    const shareEntity = this.getShareEntityByShareType(shareType);
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    shareEntity.normalize(shareType).then((norParams) => {
      window.wx.miniProgram.navigateTo({
        url: `${DEFALT_WEBSHARE_RELAY_PAGE}${buildQuery(Object.assign({
          page_sn: getPageSN() + '',
          page_name: getPageName(),
          page_id: getPageID()
        }, norParams))}`
      });
      this.emit(ShareEventType.ON_SHARE_COMPLETED, {
        shareType,
        shareResultType: ShareResultType.SUCCESS
      });
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * @deprecated
   * @param {ShareType} shareType 
   */
  refreshWxAppChannelShare(shareType) {
    return this._setupPlatformShareSDK();
  }

  /**
   * @deprecated
   * @param {ShareType} shareType 
   * @param {ShareResultType} shareResultType 
   */
  notifyWxAppShareComplete(shareType, shareResultType = ShareResultType.SUCCESS) {
    this.emit(ShareEventType.ON_SHARE_COMPLETED, {
      shareType,
      shareResultType
    });
    this.refreshWxAppChannelShare(shareType);
  }

  /**
   * @override
   */
  _setupPlatformShareSDK() {
    if (
      typeof wx !== 'object' ||
      typeof window.wx.miniProgram !== 'object' ||
      typeof window.wx.miniProgram.postMessage !== 'function'
    ) {
      this.emit(ShareEventType.SDK_INIT_FAILED);
      return Promise.resolve();
    }
    return Promise.all([ShareType.WECHAT_SESSION].map((shareType) => {
      const shareEntity = this.getShareEntityByShareType(shareType);
      if (!shareEntity) {
        return Promise.resolve();
      }
      this.emit(ShareEventType.ON_SHARE_PREPARE, { shareType });
      return shareEntity.normalize(shareType).then((norParams) => {
        try {
          window.wx.miniProgram.postMessage({
            data: {
              message_type: 'share',
              share_info: norParams,
              page_info: {
                page_sn: getPageSN() + '',
                page_name: getPageName(),
                page_id: getPageID()
              }
            }
          });
        } catch (e) {
          // todo
        }
      }, this.getNormalizeErrorHandler(shareType));
    }));
  }
}

export default WxAppShareController;
