/**
 * 
 *
 * @param {string} [message='UnLoginError']
 * @ignore
 */
export function UnLoginError(message = 'UnLoginError') {
  this.name = 'UnLoginError';
  this.message = message;
  // 40001''
  this.errorCode = 40001;
  this.stack = new Error().stack;
}

UnLoginError.prototype = Object.create(Error.prototype);
UnLoginError.prototype.constructor = UnLoginError;

/**
 * 
 *
 * @param {string} [message='RedirectedError']
 * @ignore
 */
export function RedirectedError(message = 'RedirectedError') {
  this.name = 'RedirectedError';
  this.message = message;
  this.errorCode = 302;
  this.stack = new Error().stack;
}

RedirectedError.prototype = Object.create(Error.prototype);
RedirectedError.prototype.constructor = RedirectedError;
