/**
 * @file ShareController/native.js
 * @author  daben
 */
import { getCurrentPlatform, SYSTEM } from '@pdd/platform-util';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import ShareController from './base';
import ShareType from '../ShareType';
import {
  checkShareTypeSupport,
  getShareModuleName,
  NATIVE_SAHRE_ERRORS,
  checkSupportShareCipher,
  checkSupportSilentWakeUp,
  isEnableShareCheck,
  DEFAULT_SHARE_ICON,
  DEFAULT_SHARE_ICON_SIZE
} from '../tools/NativeShareEnv';
import { ShareErrorCode } from '../ShareError';
import ShareEventType from '../ShareEventType';
import ShareResultType from '../ShareResultType';
import NativeShareEntity from '../ShareEntity/NativeShareEntity';
import { getPageName, getPageSN } from '../tools/getPageContext';
import { getInstance as getShareLimit } from '../ShareLimiter';
import ThirdPartyOpenType from '../ThirdPartyOpenType';
import { shareEntityToMethod } from '../ShareManager/translation';
import { setContent } from '../tools/clipboard';
import { CipherPasteType } from '../CipherPasteType';

// const IOS_WECHAT_SHARE_CHOICE = 'com.tencent.xin.sharetimeline';

const SUPPORTED_SHARE_TYPE = Object.keys(ShareType)
  .map(key => ShareType[key])
  .filter(shareType => {
    // remove disabled share types
    return [ShareType.WECHAT_BIG_IMAGE].indexOf(shareType) <= 0;
  });

/**
 * 
 * @access private
 * @extends {ShareController}
 */
class NativeShareController extends ShareController {
  constructor(opts) {
    super(opts);
    this.ShareEntityClass = NativeShareEntity;
    this._enableShareTypes = SUPPORTED_SHARE_TYPE.concat();
    this.shareLimiter = getShareLimit();
    this._isEnableShareCheck = false;
    this.setShareCheck(true);
  }

  /**
   * 
   * @param {boolean} isEnable 
   */
  setShareCheck(isEnable) {
    if (getCurrentPlatform().isTinyNativePlatform) {
      // tiny env only support `performShare`
      this._isEnableShareCheck = true;
      return;
    }
    this._isEnableShareCheck = isEnable ? isEnableShareCheck() : false;
  }

  /**
   * 
   * @param {ShareType} shareType 
   */
  checkShareTypeEnable(shareType) {
    const shareEntity = this.getShareEntityByShareType(shareType);
    if (!this.validateShareParams(shareType, shareEntity)) {
      return ShareErrorCode.SHARE_PARAMS_NOT_VALID;
    }

    // if app not support or this shareType is disabled
    if (!checkShareTypeSupport(shareType) || this._enableShareTypes.indexOf(shareType) < 0) {
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }

    if (
      this._isEnableShareCheck &&
      // 
      [ShareType.COPY_LINK, ShareType.COPY_CIPHER].indexOf(shareType) < 0
    ) {
      const checkResult = this.shareLimiter.checkSync(shareType, shareEntity);
      if (checkResult) {
        return checkResult;
      }
    }
    return 0;
  }

  /**
   * 
   * @override
   * @param {ShareType} shareType 
   * @returns {ShareErrorCode} 0
   */
  share(shareType) {
    const checkResult = this.checkShareTypeEnable(shareType);
    if (checkResult) {
      return checkResult;
    }

    if (shareType === ShareType.PIN_TIMELINE) {
      return this._sharePinTimeline(shareType);
    }

    // special
    if (shareType === ShareType.WECHAT_BIG_IMAGE) {
      return this._nativeShareBigImage(shareType);
    }

    // card like
    if (
      [
        ShareType.WECHAT_SESSION,
        ShareType.WECHAT_TIMELINE,
        ShareType.WEIBO,
        ShareType.QQ_CHAT_SESSION,
        ShareType.QQ_ZONE,
        ShareType.TENCENT_WEIBO,
        ShareType.WECHAT_SESSION_APP,
        ShareType.FRIENDS,
        ShareType.WECHAT_SESSION_MUSIC,
        ShareType.WECHAT_SESSION_VIDEO
      ].indexOf(shareType) >= 0
    ) {
      return this._nativeShareCard(shareType);
    }

    if (
      [
        ShareType.IOS_SYSTEM_SHARE_CARD,
        ShareType.WECHAT_SESSION_CARD_WITH_NO_SDK,
        ShareType.WECHAT_TIMELINE_CARD_WITH_NO_SDK
      ].indexOf(shareType) >= 0
    ) {
      return this._nativeShareCardSpecial(shareType);
    }

    // share image
    if (
      [
        ShareType.WECHAT_TIMELINE_IMAGE,
        ShareType.WECHAT_TIMELINE_IMAGE_WITH_NO_SDK,
        ShareType.WECHAT_SESSION_IMAGE,
        ShareType.QQ_CHAT_SESSION_IMAGE,
        ShareType.QQ_ZONE_IMAGE,
        ShareType.WEIBO_IMAGE,
        ShareType.WECHAT_SESSION_IMAGE_WITH_NO_SDK,
        ShareType.WECHAT_SESSION_IMAGES_WITH_NO_SDK,
        ShareType.SHARE_IMAGE_PASSIVELY
      ].indexOf(shareType) >= 0
    ) {
      return this._nativeShareImage(shareType);
    }

    // text share
    if (
      [
        ShareType.WECHAT_SESSION_TEXT_WITH_NO_SDK,
        ShareType.WECHAT_SESSION_TEXT,
        ShareType.WECHAT_TIMELINE_TEXT
      ].indexOf(shareType) >= 0
    ) {
      return this._nativeShareText(shareType);
    }

    if (ShareType.COPY_LINK === shareType) {
      return this._nativeShareClipboard(shareType);
    }

    if (ShareType.COPY_CIPHER === shareType) {
      return this.copyCipher();
    }

    return ShareErrorCode.SHARE_TYPE_NOT_FOUND;
  }

  /**
   * 
   * @param {ShareType} shareType 
   * @param {ShareEntity} shareEntity 
   */
  validateShareParams(shareType, shareEntity = {}) {
    // if (shareType === ShareType.WEIBO) {
    //     if (!shareEntity.redirectURI) {
    //         return false;
    //     }
    // }
    return true;
  }

  /**
   * @ignore
   */
  _setupShareTypes() {
    // OVERRIDE
  }

  resetLimiter() {
    this.shareLimiter.reset();
    this._queryShareTypePromise = null;
    return this.waitShareLimiter();
  }

  /**
   * @ignore
   */
  refreshShare(shareEntity, shareType) {
    const result = super.refreshShare(shareEntity, shareType);
    this.emit(ShareEventType.ON_SHARE_PREPARE, { shareType });
    return result;
  }

  /**
   * @ignore
   */
  _setupPlatformShareSDK() {
    // OVERRIDE
    return this.waitShareLimiter();
  }

  /**
   * 
   * @returns {promise} 
   */
  waitShareLimiter() {
    if (this._isEnableShareCheck) {
      if (!this._queryShareTypePromise) {
        this._queryShareTypePromise = this.shareLimiter.build().catch(_ => {
          this.emit(ShareEventType.SHARE_LIMIT_LOAD_FAIL);
          return Promise.resolve();
        });
      }
      return this._queryShareTypePromise;
    } else {
      return Promise.resolve();
    }
  }

  // -----------------------------------------------------------------------------------
  // share call function indeed
  // -----------------------------------------------------------------------------------

  // title + thumbnailImage + desc
  // card link
  /**
   * @ignore
   */
  _nativeShareCard(shareType) {
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    const shareEntity = this.getShareEntityByShareType(shareType);
    shareEntity.normalize(shareType).then((norParams = {}) => {
      const { tpInfo, tpInfos, tpDowngrade, wechat_mask_config: wechatMaskConfig, rawResponse = {}, ...restParams } = norParams;
      // tp failed
      if (tpDowngrade) {
        this.emit(ShareEventType.ON_SHARE_COMPLETED, {
          shareType,
          shareResultType: ShareResultType.FAIL,
          tpDowngrade: true,
          tpInfo
        });
        return;
      }
      const { sdk_type: sdkType } = rawResponse;
      const shareParams = Object.assign({
        type: shareType,
        redirect_uri: shareEntity.redirectURI || '',
        share_params: restParams,
        share_method: shareEntityToMethod(shareEntity, shareType),
        sdk_type: sdkType
      });
      if (shareEntity.needSharedAmount) {
        shareParams.show_check_detail = 1;
      }
      if (wechatMaskConfig) {
        shareParams.wechat_mask_config = wechatMaskConfig;
      }
      // tp 
      if (tpInfo && tpInfo.dev_type) {
        shareParams.dev_app = tpInfo.dev_app;
        shareParams.dev_type = tpInfo.dev_type;
      }
      if (tpInfos) {
        shareParams.dev_infos = tpInfos;
      }
      this._invokeNativeShare(
        shareParams,
        params => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.SUCCESS,
            responseData: params,
            tpInfo
          });
        },
        (e = {}) => {
          const errorCode = parseInt(e.errorCode, 10) || 0;
          const shareResultType =
            errorCode === NATIVE_SAHRE_ERRORS.USER_CANCEL ? ShareResultType.CANCEL : ShareResultType.FAIL;
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType,
            nativeShareErrorCode: errorCode,
            responseData: e.data,
            tpInfo
          });
        }
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  _nativeShareCardSpecial(shareType) {
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    const shareEntity = this.getShareEntityByShareType(shareType);
    shareEntity.normalize(shareType).then(norParams => {
      const shareParams = Object.assign({
        // use special share type
        type: ShareType.IOS_SYSTEM_SHARE_CARD,
        share_params: norParams,
        share_method: shareEntityToMethod(shareEntity, shareType)
      });
      this._invokeNativeShare(
        shareParams,
        params => {
          // eslint-disable-next-line camelcase
          // const { share_type, success } = params;
          // // share_type IOSSystemShareChoice
          // const IOS_WECHAT_SHARE_CHOICE = 'com.tencent.xin.sharetimeline';
          // // eslint-disable-next-line camelcase
          // const isSuccess = share_type === IOS_WECHAT_SHARE_CHOICE && success;
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.SUCCESS,
            responseData: params
          });
        },
        (e = {}) => {
          const errorCode = parseInt(e.errorCode, 10) || 0;
          const shareResultType =
            errorCode === NATIVE_SAHRE_ERRORS.USER_CANCEL ? ShareResultType.CANCEL : ShareResultType.FAIL;
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType,
            nativeShareErrorCode: errorCode,
            responseData: e.data
          });
        }
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * @ignore
   */
  _nativeShareImage(shareType) {
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    const shareEntity = this.getShareEntityByShareType(shareType);
    let callNativeShareType = shareType;
    // 
    // shareType WITH_NO_SDK
    // shareType
    // if (platform.Current === platform.Android &&
    //     shareType === ShareType.WECHAT_TIMELINE_IMAGE) {
    //     callNativeShareType = ShareType.WECHAT_TIMELINE_IMAGE_WITH_NO_SDK;
    // }
    shareEntity.normalize(shareType).then((norParams = {}) => {
      const {
        share_code: shareCode,
        system_share_mode: systemShareMode,
        wechat_mask_config: wechatMaskConfig,
        ...restParams
      } = norParams;
      const shareParams = {
        type: callNativeShareType,
        share_params: restParams,
        share_method: shareEntityToMethod(shareEntity, shareType),
        share_code: shareCode,
        system_share_mode: systemShareMode
      };
      if (wechatMaskConfig) {
        shareParams.wechat_mask_config = wechatMaskConfig;
      }
      let useCompleteCallback = false;
      // this type is only for android
      // can only get callback from complete
      // if (callNativeShareType === ShareType.WECHAT_TIMELINE_IMAGE_WITH_NO_SDK) {
      //   shareParams.complete = () => {
      //     // 
      //     // 
      //     // , ShareTracker
      //     this.emit(ShareEventType.ON_SHARE_COMPLETED, {
      //       shareType,
      //       useComplete: true,
      //       shareResultType: ShareResultType.SUCCESS
      //     });
      //   };
      //   useCompleteCallback = true;
      // }
      this._invokeNativeShare(
        shareParams,
        params => {
          !useCompleteCallback &&
            this.emit(ShareEventType.ON_SHARE_COMPLETED, {
              shareType,
              shareResultType: ShareResultType.SUCCESS,
              responseData: params
            });
        },
        (e = {}) => {
          const errorCode = parseInt(e.errorCode, 10);
          const shareResultType =
            errorCode === NATIVE_SAHRE_ERRORS.USER_CANCEL ? ShareResultType.CANCEL : ShareResultType.FAIL;
          !useCompleteCallback &&
            this.emit(ShareEventType.ON_SHARE_COMPLETED, {
              shareType,
              shareResultType,
              nativeShareErrorCode: errorCode,
              responseData: e.data
            });
        }
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * @ignore
   */
  _nativeShareText(shareType) {
    const shareEntity = this.getShareEntityByShareType(shareType);
    const delayTrigger = shareEntity.cipherScene && typeof shareEntity.cipher === 'string';
    !delayTrigger && this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    let callNativeShareType = shareType;
    // if (platform.Current === platform.Android &&
    //     shareType === ShareType.WECHAT_SESSION_TEXT) {
    //     callNativeShareType = ShareType.WECHAT_SESSION_TEXT_WITH_NO_SDK;
    // }
    shareEntity.normalize(shareType).then(norParams => {
      const { tpInfo, tpInfos, tpDowngrade, wechat_mask_config: wechatMaskConfig, ...restParams } = norParams;
      if (delayTrigger) {
        this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType, shareID: restParams.shareID });
        delete restParams.shareID;
      }
      if (tpDowngrade) {
        this.emit(ShareEventType.ON_SHARE_COMPLETED, {
          shareType,
          shareResultType: ShareResultType.FAIL,
          tpDowngrade: true,
          tpInfo
        });
        return;
      }
      const shareParams = Object.assign({
        type: callNativeShareType,
        share_params: restParams,
        share_method: shareEntityToMethod(shareEntity, shareType)
      });
      // tp 
      if (tpInfo && tpInfo.dev_type) {
        shareParams.dev_app = tpInfo.dev_app;
        shareParams.dev_type = tpInfo.dev_type;
      }
      if (tpInfos) {
        shareParams.dev_infos = tpInfos;
      }
      if (wechatMaskConfig) {
        shareParams.wechat_mask_config = wechatMaskConfig;
      }
      let useCompleteCallback = false;
      // 
      if (
        shareEntity.forceDowngrade ||
        (getCurrentPlatform().system === SYSTEM.IOS &&
          callNativeShareType === ShareType.WECHAT_SESSION_TEXT_WITH_NO_SDK)
      ) {
        this.emit(ShareEventType.ON_SHARE_COMPLETED, {
          shareType,
          shareResultType: ShareResultType.FAIL,
          // forceDowngradeshareParams
          // forceDowngrade
          shareParams: norParams
        });
        return 0;
      }
      // this type is only for android
      // can only get callback from complete
      // if (
      //   getCurrentPlatform().system === SYSTEM.Android &&
      //   callNativeShareType === ShareType.WECHAT_SESSION_TEXT_WITH_NO_SDK
      // ) {
      //   shareParams.complete = () => {
      //     // 
      //     // 
      //     // , ShareTracker
      //     this.emit(ShareEventType.ON_SHARE_COMPLETED, {
      //       shareType,
      //       useComplete: true,
      //       shareResultType: ShareResultType.SUCCESS
      //     });
      //   };
      //   useCompleteCallback = true;
      // }
      this._invokeNativeShare(
        shareParams,
        params => {
          !useCompleteCallback &&
            this.emit(ShareEventType.ON_SHARE_COMPLETED, {
              shareType,
              shareResultType: ShareResultType.SUCCESS,
              responseData: params
            });
        },
        (e = {}) => {
          const errorCode = parseInt(e.errorCode, 10) || 0;
          const shareResultType =
            errorCode === NATIVE_SAHRE_ERRORS.USER_CANCEL ? ShareResultType.CANCEL : ShareResultType.FAIL;
          !useCompleteCallback &&
            this.emit(ShareEventType.ON_SHARE_COMPLETED, {
              shareType,
              shareResultType,
              nativeShareErrorCode: errorCode,
              responseData: e.data
            });
        }
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * @ignore
   */
  _nativeShareBigImage(shareType) {
    return ShareErrorCode.SHARE_TYPE_DISABLED;
  }

  /**
   * @ignore
   */
  _nativeShareClipboard(shareType) {
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    const shareEntity = this.getShareEntityByShareType(shareType);
    shareEntity.normalize(shareType).then((norParams = {}) => {
      const shareURL = norParams.share_url || shareEntity.shareURL;
      setContent(shareURL).then(
        () => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.SUCCESS,
            content: shareURL,
            originContent: ''
          });
        },
        () => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.FAIL,
            content: shareURL,
            originContent: ''
          });
        }
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * @ignore
   */
  _sharePinTimeline(shareType) {
    this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
    const shareEntity = this.getShareEntityByShareType(shareType);
    shareEntity.normalize(shareType).then((norParams = {}) => {
      const shareParams = Object.assign({
        type: shareType,
        share_params: norParams
      });
      this._invokeNativeShare(
        shareParams,
        params => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.SUCCESS,
            responseData: params
          });
        },
        (e = {}) => {
          const errorCode = parseInt(e.errorCode, 10) || 0;
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.FAIL,
            nativeShareErrorCode: errorCode,
            responseData: e.data
          });
        }
      );
    }, this.getNormalizeErrorHandler(shareType));
    return 0;
  }

  /**
   * 
   * @param {object} buttonConfig 
   * @returns {ShareErrorCode} 
   */
  setupNativeShareButton(buttonConfig = {}) {
    const { shareIcon = DEFAULT_SHARE_ICON, shareIconSize = DEFAULT_SHARE_ICON_SIZE } = buttonConfig;
    if (typeof shareIcon !== 'string' || typeof shareIconSize !== 'number') {
      return ShareErrorCode.SHARE_PARAMS_NOT_VALID;
    }
    invokeBridgeCallNativeFun('JSUIControl', 'setRightBarButtons', {
      buttons: [
        {
          icons: {
            normal: shareIcon
          },
          callback: () => {
            this.emit(ShareEventType.ON_SHARE_BUTTON_CLICKED);
          },
          size: shareIconSize
        }
      ]
    }).catch(_ => {});
    return 0;
  }

  /**
   * @ignore
   */
  _invokeNativeShare(params = {}, onSuccess, onFail) {
    const shareModuleName = getShareModuleName();
    let method = 'share';
    const pageSn = getPageSN() + '';
    if (this._isEnableShareCheck) {
      params = Object.assign(
        {
          origin: 'h5',
          page_sn: pageSn,
          page_info: {
            page_name: getPageName(),
            page_url: location && location.href
          }
        },
        params
      );
      if (this.shareLimiter.getAppOptional()) {
        params.app_optional = this.shareLimiter.getAppOptional();
      }
      method = 'performShare';
    } else {
      params.page_sn = pageSn;
    }
    invokeBridgeCallNativeFun(shareModuleName, method, params).then(onSuccess, onFail);
  }

  /**
   * 
   * @param {string} cipher 
   * @param {ThirdPartyOpenType} openType 
   * @param {CipherPasteType} pasteType 
   * @param {string} shareCode 
   * @param {object} restOptions 
   * @returns {promise} 
   */
  shareCipherManually(
    cipher,
    openType = ThirdPartyOpenType.OPEN_WECHAT,
    pasteType = CipherPasteType.DEFAULT,
    shareCode,
    restOptions = {}
  ) {
    if (!cipher || typeof cipher !== 'string') {
      return Promise.reject(new Error('invalid cipher'));
    }
    if (
      // 
      !checkSupportShareCipher()
    ) {
      return Promise.reject(new Error('not support'));
    }
    if (
      openType === ThirdPartyOpenType.OPEN_WECHAT ||
      openType === ThirdPartyOpenType.OPEN_WECHAT_IOS_SILENT ||
      openType === ThirdPartyOpenType.OPEN_WECHAT_ANDROID_SILENT
    ) {
      if (checkSupportSilentWakeUp() && this.shareLimiter.isSilentMode) {
        openType =
          getCurrentPlatform().system === SYSTEM.IOS
            ? ThirdPartyOpenType.OPEN_WECHAT_IOS_SILENT
            : ThirdPartyOpenType.OPEN_WECHAT_ANDROID_SILENT;
      } else {
        openType = ThirdPartyOpenType.OPEN_WECHAT;
      }
    }
    return invokeBridgeCallNativeFun(getShareModuleName(), 'shareCipher', {
      cipher,
      open_type: openType,
      paste_type: pasteType,
      share_code: shareCode,
      ...restOptions
    });
  }
}

export default NativeShareController;
