/**
 * @file logger.js
 * @author edit by aotuman
 */
import { app } from '@pdd/configs';
import { getCurrentPlatform, PLATFORM, compareVersion } from '@pdd/platform-util';
import { buildQuery, parseQuery, nanoid, getCookie, crc32 } from '@pdd/core-util';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import TrackingKeys from './keys';
import PageManagerObj from '@pdd/page-manager';
import isPlainObject from 'lodash/isPlainObject';
import isFunc from 'lodash/isFunction';
import isArray from 'lodash/isArray';
import assign from 'lodash/assign';
import merge from 'lodash/merge';

const PageManager = PageManagerObj.getInstance();
const constant = app.constant;

let isAdTrackingTester = false;
let result = {};

if (process.env.BROWSER) {
    const clientPlatform = getCurrentPlatform();
    const config = app.get(location.host);
    const contextInfo = {
        isCached: false,
        hasReferQuery: false,
        refer: {
            refer_page_id: null,
            refer_page_name: null,
            refer_page_section: null,
            refer_page_element: null
        }
    };
    // 
    const queue = [];
    // 
    const middlewareArr = [];

    // sourcemap
    if (location.search) {
        const querys = parseQuery(location.search);
        if (querys.JS_ERROR) {
            console.error(querys.JS_ERROR.replace(/\)@/g, '\n)@')); // eslint-disable-line
        }
    }
    /**
     * 
     * @type {Object}
     */
    let defaultOpts = {
        // 
        screen_width: window.screen.width,
        // 
        screen_height: window.screen.height,
        // device pixel ratio
        dpr: window.devicePixelRatio,
        // 
        app_version: '',
        // : weixin/qq/weibo/android/ios/unknow
        platform: ''
    };

    function getLogID() {
        const logId = [
            Date.now(),
            nanoid(16)
        ].join('');
        return logId;
    }

    /**
     * 
     */
    const rkTagCntMap = {};

    function getDcf(timestamp, vds, op) {
        const dsk = vds.substr(0, 28);
        const dst = vds.substr(28);
        if (!rkTagCntMap[op]) {
            rkTagCntMap[op] = 1;
        }
        const ckv = crc32(`${timestamp}${op}${dst}${rkTagCntMap[op]}`);
        const dcf = `${dsk}.${rkTagCntMap[op]}.${ckv}`;
        rkTagCntMap[op]++;
        return dcf;
    }

    //  https://jira2.corp.yiran.com/browse/PDD-28952?filter=-3
    let riskCookies = null;
    const getRiskControlParams = (time, op = '') => {
        if (!riskCookies) {
            const { pdd_vds = '', _nano_fp } = getCookie();
            riskCookies = { pdd_vds, _nano_fp };
        }

        const params = {
            cookie_fp: riskCookies._nano_fp,
            storage_fp: localStorage.getItem('_nano_fp') || ''
        };

        const { isNativePlatform, isTinyNativePlatform } = getCurrentPlatform();
        if (!isNativePlatform && !isTinyNativePlatform) {
            params.dcf = getDcf(time, riskCookies.pdd_vds, op);
        }

        return params;
    };

    const getImprID = function (goodsID) {
        return getLogID();
    };

    const isImprTester = true;
    const imprTime = 1000;

    function getAppID() {
        if (clientPlatform.platform === PLATFORM.WeChat) {
            return config.AppID.WeChat;
        } else if (clientPlatform.platform === PLATFORM.Weibo) {
            return config.AppID.Weibo;
        } else if (clientPlatform.platform === PLATFORM.QQ) {
            if (clientPlatform.isNativePlatform) {
                return config.AppID.QQ;
            } else {
                return config.AppID.QQConnect;
            }
        }
        return '';
    }

    function cacheContextInfo() {
        if (contextInfo.isCached) {
            return;
        }
        updateCacheContextInfo();
    }

    function updateCacheContextInfo() {
        const referPageQuery = PageManager.getReferPageQuery();
        contextInfo.refer = referPageQuery || {};
        contextInfo.hasReferQuery = !!referPageQuery && JSON.stringify(referPageQuery) !== '{}';
        contextInfo.isCached = contextInfo.hasReferQuery;
    }

    function userID() {
        const uidBase36 = localStorage.getItem(constant.LocalStorageKey.UID) || '';
        if (uidBase36.toString().match(/[0-9]{10}/)) {
            return uidBase36;
        }
        const uid = uidBase36 ? parseInt(uidBase36, 36)
            : getCookie(constant.CookieKey.UID);
        return uid || 0;
    }

    function getUin() {
        const uin = localStorage.getItem(constant.LocalStorageKey.UIN) || getCookie(constant.CookieKey.UIN);
        return uin;
    }

    // 
    function addMiddleware(middlewares) {
        if (isFunc(middlewares)) {
            middlewareArr.push(middlewares);
        } else if (isArray(middlewares)) {
            middlewares.forEach((middleware) => {
                middlewareArr.push(middleware);
            });
        }
    }

    function log(type, message) {
        const page_name = PageManager.getPageName() || location.pathname;
        if (clientPlatform.statisfy && clientPlatform.statisfy('4.23.0', '4.22.0', 'greaterThanEqual')) {
            invokeBridgeCallNativeFun('JSLogger', 'log', {
                module: page_name,
                message: message,
                level: type
            });
        }
    }

    function info(message) {
        log(2, message);
    }

    function warn(message) {
        log(3, message);
    }

    function error(message) {
        log(4, message);
    }

    /**
     * 
     *
     * @param  {Object} opts 
     * @param {Function} callback 
     */
    function send(opts = {}, callback) {
        // app5.41
        const errorReportByNative = clientPlatform.isNativePlatform && compareVersion(clientPlatform.version, '5.41.0') >= 0;
        if (!errorReportByNative && window.__ERROR_SAMPLE_RATE__ && ['chat_error', 'error', 'real_error'].indexOf(opts.op) !== -1) {
            // 
            if (Math.random() > window.__ERROR_SAMPLE_RATE__) {
                if (typeof callback === 'function') {
                    callback();
                }
                return;
            }
        }
        const querys = parseQuery(window.location.search);
        opts = assign({}, opts, PageManager.getKeyParams());
        cacheContextInfo();
        // 
        opts.log_id = opts.log_id || getLogID();
        opts.user_id = opts.user_id || userID();
        opts.uin = opts.uin || getUin();
        opts.app_id = opts.app_id || getAppID() || '';
        opts.time = opts.time || opts.log_id.substring(0, 13);
        opts.page_name = (opts.useExistsParams && opts.page_name)
            ? opts.page_name
            : PageManager.getPageName();
        opts.page_id = PageManager.getPageID();
        opts.page_sn = PageManager.getPageSN();
        // wx_check_pdd
        // 
        const wxCheckPdd = localStorage.getItem('WxCheckPdd');
        if (wxCheckPdd !== null) {
            opts.wx_check_pdd = parseInt(wxCheckPdd, 10) || 0;
        }
        merge(opts, contextInfo.refer);

        // _x_xcx_trace_id 
        const transferParameters = PageManager.getExtendTransferParameters();
        merge(opts, transferParameters);

        // add channel refers
        // refer_channel_*
        if (['pv', 'click', 'impr'].indexOf(opts.op) >= 0) {
            TrackingKeys.TrackingChannels.forEach(function (key) {
                const referChannelKey = 'refer_channel_' + key;
                opts[referChannelKey] = opts[referChannelKey] || querys[key];
            });

            // add refer_share_* for share link pv
            TrackingKeys.ReferShareKeys.forEach(function (key) {
                const value = opts[key] || querys[key] || querys[key.slice(6)];
                if (value) {
                    opts[key] = value;
                }
            });
            // map short key to long key
            const keyMap = TrackingKeys.ReferShareMapShort;
            for (const key in keyMap) {
                const value = opts[key] || querys[key];
                if (value) {
                    opts[keyMap[key]] = value;
                }
            }
        }
        if (opts.op === 'pv') {
            for (const key in querys) {
                // eslint-disable-next-line no-prototype-builtins
                if (querys.hasOwnProperty(key)) {
                    if (key && key.indexOf('_p_') === 0) {
                        opts[key] = querys[key];
                    }
                }
            }

            // pveventpage_show, pvopevent
            opts.event = 'page_show';
            // add page url
            opts.page_url = location.href;
        }

        // add is_back
        if (querys['is_back'] === '1') {
            opts.is_back = '1';
        }

        // add msgid for basic
        opts.refer_channel_msgid = opts.refer_channel_msgid || querys['msgid'];

        delete opts.useExistsParams;

        if (middlewareArr && middlewareArr.length > 0) {
            opts = middlewareArr.reduce((preOpts, currentMiddleware, index, arr) => {
                return currentMiddleware(preOpts, index, arr);
            }, opts);
        }

        // 
        queue.push(opts);
        // 
        consume(callback);
    }

    /**
     * 
     *
     * @param {Function} callback 
     */
    function consume(callback) {
        if (queue.length === 0) {
            if (callback) {
                callback();
            }
            return;
        }

        /**
         *
         * @param params
         */
        function exec(params) {
            let LOG_URL = config.loggingURL;
            let LOG_ROUTES = config.logRoutes || {};
            if (window.location && window.location.protocol === 'https:') {
                LOG_ROUTES = config.httpsLogRoutes;
                LOG_URL = config.httpsLoggingURL;
            }
            if (['chat_error', 'error', 'real_error'].indexOf(params.op) >= 0) {
                params.platform = clientPlatform.platform; // platformplatform
            }
            if (params.impr_id != null) {
                return;
            }
            const riskParams = getRiskControlParams(params.time, params.op || '');
            params = assign({}, defaultOpts, params || {}, riskParams);
            //  https://jira2.corp.yiran.com/browse/PDD-23372  ad 
            // op  impr  click
            //  params.aditemsrouteKeyad
            // 
            let logRouteKey = '';
            if ((params.ad || params.routeKey === 'ad') && ['click', 'impr'].indexOf(params.op) >= 0) {
                logRouteKey = 'ad';
            } else {
                logRouteKey = params.op;
            }
            const url = LOG_ROUTES[logRouteKey] || LOG_URL;
            delete params.routeKey;
            // beacon
            if (window.navigator.sendBeacon && !clientPlatform.isNativePlatform) {
                params.support_beacon = 1;
            }
            const postData = buildFinalPostData(params);
            let { q } = postData;
            const { entries } = postData;
            // 
            const content_type = 'text/plain;charset=UTF-8';
            /* op=real_error */
            if (params.op == 'real_error') {
                // content_type="application/json;charset=UTF-8";
                // q
                if (params.status_code) {
                    params.payload ? params.payload.status_code = params.status_code : params.payload = { status_code: params.status_code };
                    delete params.status_code;
                }
                if (params.error_message) {
                    params.error_msg = params.error_message;
                    delete params.error_message;
                }
                if (params.params) {
                    params.payload ? params.payload.params = params.params : params.payload = { params: params.params };
                    delete params.params;
                }
                if (params.sub_op) {
                    params.payload ? params.payload.sub_op = params.sub_op : params.payload = { sub_op: params.sub_op };
                    delete params.sub_op;
                }
                if (params.debug_url) {
                    params.payload ? params.payload.debug_url = params.debug_url : params.payload = { debug_url: params.debug_url };
                    delete params.debug_url;
                }
                params.log_version = '1.0.0';
                q = JSON.stringify(params);
            }

            if (clientPlatform.isNativePlatform || clientPlatform.isTinyNativePlatform) {
                const useAMAnalytics = clientPlatform.statisfy && clientPlatform.statisfy('4.45.0', '4.33.0', 'greaterThanEqual') || clientPlatform.isTinyNativePlatform;
                // 
                if (useAMAnalytics &&
                    entries.length > 0) {
                    entries.forEach((entry) => {
                        invokeBridgeCallNativeFun('AMAnalytics', 'send', {
                            url: url,
                            value: entry
                        });
                    });
                    return;
                }

                if (['chat_error', 'error', 'real_error'].indexOf(params.op) < 0) {
                    if (useAMAnalytics && postData.oriParams) {
                        postData.oriParams.use_AMAnalytics = 1;
                        invokeBridgeCallNativeFun('AMAnalytics', 'send', {
                            url: url,
                            value: postData.oriParams
                        });
                        return;
                    }
                } else if (compareVersion(clientPlatform.version, '5.41.0') >= 0) {
                    // AMAnalytics.sendparamsflattenobj,JSReporter.addMarmotLogparams
                    return invokeBridgeCallNativeFun('JSReporter', 'addMarmotLog', params);
                }

                invokeBridgeCallNativeFun('JSNetwork', 'request', {
                    method: 'POST',
                    url: url,
                    headers: {
                        'Content-Type': content_type
                    },
                    data: q
                });
            } else {
                if (window.navigator.sendBeacon && ['pv', 'click', 'impr'].indexOf(params.op) >= 0 && window.navigator.sendBeacon(url, q)) {
                    return;
                }
                try {
                    let xmlhttp;
                    if (window.XMLHttpRequest) {
                        xmlhttp = new XMLHttpRequest();
                    } else {
                        return;
                    }
                    xmlhttp.open('POST', url, true);
                    xmlhttp.setRequestHeader('Content-Type', content_type);
                    xmlhttp.withCredentials = true;
                    xmlhttp.send(q);
                } catch (e) {
                    // alert('xmlhttp error!');
                }
            }
        }

        while (queue.length > 0) {
            const params = queue.pop();
            // setTimeOut
            if (params.op === 'epv') {
                exec(params);
            } else {
                setTimeout(function () {
                    exec(params);
                }, 0);
            }
        }

        if (callback) {
            if (clientPlatform.isNativePlatform || clientPlatform.isTinyNativePlatform) {
                Promise.resolve().then(() => {
                    callback();
                });
            } else {
                setTimeout(callback, 100);
            }
        }
    }

    function buildFinalPostData(params) {
        params = params || {};
        let items = params.items;
        if (typeof items === 'string') {
            try {
                items = JSON.parse(items);
            } catch (e) {
                console.log(e) // eslint-disable-line
            }
        }
        if (isArray(params.items) &&
            params.items.length > 0) {
            items = params.items;
            delete params.items;

            // generate array
            const entries = items.map((item, index) => {
                if (item && !item.log_id) {
                    item.log_id = getLogID();
                }
                const entry = assign({}, params, item);
                ['ad', 'p_rec'].forEach((key, index) => {
                    if (entry[key]) {
                        entry[key] = processString(entry[key]);
                    }
                });
                return entry;
            });

            // generate string
            let q = entries.reduce((last, next) => {
                const entryString = buildQuery(next);
                return `${last}${entryString}$`;
            }, '');
            q = q.slice(0, q.length - 1);

            return {
                q: q,
                entries: entries
            };
        } else {
            params.items = processString(params.items);
            return {
                q: buildQuery(params),
                oriParams: processObject(params),
                entries: []
            };
        }
    }

    function processObject(value) {
        if (isPlainObject(value)) {
            const result = {};
            for (const key in value) {
                if (typeof value[key] === 'object') {
                    result[key] = JSON.stringify(value[key]);
                } else {
                    result[key] = value[key];
                }
            }
            return result;
        }
        return null;
    }

    function processString(value) {
        if (!value ||
            typeof value === 'string') {
            return value;
        }
        return JSON.stringify(value);
    }

    /**
     * pv
     *
     * @param  {Object} opts 
     * @param {boolean} appendPageId idpv
     */
    function sendPv(opts, appendPageId) {
        opts = assign({}, opts, {
            'op': 'pv'
        });
        send(opts);
    }

    /**
     * jquery siblings
     *
     * @param {HTMLDom} el
     *
     * @return {Array}
     */
    function siblings(el) {
        return Array.prototype.filter.call(el.parentNode.children, function (child) {
            return child !== el;
        });
    }

    /**
     * 
     *
     * @param  {HTMLElement} ele 
     * @return {Object|undefined}  
     */
    function parseLogData(ele) {
        let log = ele.dataset.log;
        // no need log
        if (log === '') {
            return;
        }
        const params = {};

        if (typeof log === 'string') {
            try {
                log = JSON.parse(log);
            } catch (e) {
                log = {};
            }
        }

        if (typeof log === 'object') {
            let value;
            for (const key in log) {
                // eslint-disable-next-line no-prototype-builtins
                if (!log.hasOwnProperty(key) || key === 'click_callback') {
                    continue;
                }
                value = log[key];
                // eslint-disable-next-line no-prototype-builtins
                if (log.hasOwnProperty(key)) {
                    if (typeof value === 'object') {
                        value = JSON.stringify(value);
                    }
                }
                if (value) {
                    params[key] = value.toString();
                }
            }
            params.op = params.op || 'click';
            return params;
        }
    }

    /**
     * logger
     */
    function init() {
        // PV, pageNamepv
        let enter_time = Date.now();
        window.addEventListener('load', function () {
            try {
                // iframe
                if (window.parent !== window) {
                    const parentUrl = document.referrer;
                    send({
                        'op': 'real_error',
                        'page': 'page been iframed by ' + parentUrl,
                        'error_message': 'iframe_injected',
                        'page_url': location.href
                    });
                }
            } catch (ex) {
                console.log(ex); // eslint-disable-line
            }
        });

        // native
        if (!clientPlatform.isNativePlatform) {
            // visibilitychangeonbeforeunload
            if (typeof document.hidden !== 'undefined') {
                //   document.addEventListener window.addEventListener Safari <14.0
                document.addEventListener('visibilitychange', function () {
                    if (document.hidden) {
                        send({
                            op: 'epv',
                            sub_op: 'leave',
                            enter_time: enter_time,
                            time: Date.now()
                        });
                    } else {
                        enter_time = Date.now();
                    }
                });
            }
            // unload
            // 
            window.addEventListener('unload', function () {
                send({
                    op: 'epv',
                    sub_op: 'leave',
                    enter_time: enter_time,
                    close_time: Date.now()
                });
            });
        }
    }

    /**
     * 
     *
     * @param {Object} params 
     */
    function addDefaultParams(params) {
        defaultOpts = assign({}, defaultOpts, params);
    }

    /**
     * 
     */
    const _init = function () {
        defaultOpts.app_version = clientPlatform.version;
        defaultOpts.platform = platformAdapter();
    };

    /**
     * platform
     * android
     * unknown
     * @return {*}
     */
    function platformAdapter() {
        if (clientPlatform.isTinyNativePlatform) {
            return PLATFORM.PddAndroid;
        }
        if (clientPlatform.platform === PLATFORM.MaJiaBaoAndroid) {
            return PLATFORM.Unknown;
        }
        return clientPlatform.platform;
    }

    /**
     * Tracking
     *
     * @param {Object} params, params
     *
     */
    function trackingRecord(params, callback) {
        if (config.loggingURL == null || config.loggingURL.length <= 0) {
            return;
        }
        /*
        if (platform.isWeChatPlatform() && !params.domain) {
            params.domain = config.shareDomain;
        }
        */
        send(params, callback);
    }

    /**
     * logger
     */
    function initLogger() {
        init();
    }

    _init();
    result = {
        init: init,
        send: send,
        info: info,
        warn: warn,
        error: error,
        sendPv: sendPv,
        addDefaultParams: addDefaultParams,
        getLogID: getLogID,
        isImprTester: function () {
            return isImprTester;
        },
        getImprID: getImprID,
        getImprTime: function () {
            return imprTime;
        },
        initLogger: initLogger,
        trackingRecord: trackingRecord,
        hasReferQuery: function () {
            if (!contextInfo.isCached) {
                cacheContextInfo();
            }
            return contextInfo.hasReferQuery;
        },
        updateCacheContextInfo: updateCacheContextInfo,
        isAdTrackingTester: function (val) {
            isAdTrackingTester = val;
        },
        addMiddleware: addMiddleware
    };
}

let exportObj = {};
if (process.env.BROWSER) {
    exportObj = result;
}
export default exportObj;
