import buildURL from '../tools/buildURL';
import URLWrapType from '../tools/URLWrapType';
import ShareForm from '../ShareForm';
import ShareMethod from '../ShareMethod';
import { getWebWrappedShareURLInfo } from '../tools/urlWrapper';

export function format(formatString) {
  const args = [];
  for (let i = 1; i < arguments.length; ++i) {
    args.push(arguments[i]);
  }
  return formatString.replace(/{(\d+)}/g, function (match, index) {
    if (args[index] === 0) { return 0; }
    return args[index] || '';
  });
}

/**
 * 
 */
class ShareEntity {
  // opt thisopt
  constructor(opts = {}) {
    // 
    this.title = opts.title || '';
    // 
    this.message = opts.message || '';
    //  ()
    this.shareURL = opts.shareURL || '';
    //  (, url)
    this.targetShareURL = '';
    //  ()
    this.realShareURL = '';
    // 
    this.shareDomain = opts.shareDomain || '';
    // this.shareImageURL = opts.shareImageURL || '';
    // 
    this.thumbnailURL = opts.thumbnailURL || '';
    // bak
    this.timelineDomain = opts.timelineDomain || '';
    this.timelineAppId = opts.timelineAppId || '';
    this.marketActivityID = opts.marketActivityID || 0;
    this.urlWrapType = opts.urlWrapType || URLWrapType.DEFAULT;
    // short url generate api
    this.shortURLApi = opts.shortURLApi || '';
    this.domainURLApi = opts.domainURLApi || '';
    this.payloadMixin = opts.payloadMixin || null;
    this.requestUsePost = opts.requestUsePost || false;
    this.shortCommandApi = opts.shortCommandApi || '';
    // 
    this.cipherProcessor = opts.cipherProcessor;
  }

  /**
   * , SDKAPP
   * @abstract
   * @param {ShareType} shareType 
   */
  async normalize(shareType) {
    return Promise.resolve({});
  }

  /**
   * @ignore
   */
  setShareURLQueries(queries = {}) {
    if (this.urlWrapType === URLWrapType.RAW) {
      return;
    }
    this.shareID = queries.refer_share_id;
    this.shareURL = buildURL(queries, this.shareURL);
  }

  /**
   * @abstract
   * @param {ShareType} shareType 
   */
  getShareForm(shareType) {
    return ShareForm.DEFAULT;
  }

  /**
   * @abstract
   * @param {ShareType} shareType 
   */
  getShareMethod(shareType) {
    return ShareMethod.DEFAULT;
  }

  /**
   * 
   */
  processCipher() {
    const shareEntity = Object.assign({}, this);
    const params = {
      title: shareEntity.title
    };
    let shareTextFinal = params.title || '';
    return getWebWrappedShareURLInfo(shareEntity).then(({
      targetShareURL,
      useShortURL,
      shareText,
      cipher
    }) => {
      if (cipher) {
        params.cipher = cipher;
      }
      this.targetShareURL = targetShareURL;
      const withoutDefault = (useShortURL || cipher) ? shareText : '';
      const withDefault = withoutDefault || targetShareURL;
      shareTextFinal = format(shareTextFinal, withDefault, targetShareURL, withoutDefault);
      params.title = shareTextFinal;
      this.pcode = shareTextFinal;
      return params;
    });
  }
}

export default ShareEntity;
