import React, { useRef } from 'react';

import { useScale } from '../common/useScale';
import { ahexToRgba } from '../common/utils';

import styles from './index.module.scss';

/**
 * @typedef { import("./interface").ITipOnImageProps } ITipOnImageProps
 */

const TipOnImageImpl = React.memo((/** @type {ITipOnImageProps}*/ { data, style = null }) => {
    const outerContainerRef = useRef();
    const textRef = useRef();

    const { visible, scale, containerWidth } = useScale({
        outerContainerRef,
        contentRef: textRef,
    }, [data.img_text]);

    return (
        <div
            className={styles.wrapper}
            style={{
                color: ahexToRgba(data.text_color),
                background: ahexToRgba(data.bg_color),
                ...style
            }}
        >
            <div
                className={styles.container}
                ref={outerContainerRef}
            >
                <div
                    style={{
                        WebkitTransformOrigin: 'left center',
                        transformOrigin: 'left center',
                        WebkitTransform: `scale(${scale})`,
                        transform: `scale(${scale})`,
                        width: containerWidth / scale,
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                    }}
                >
                    <span ref={textRef} style={{ visibility: visible ? 'visible' : 'hidden' }}>{data.img_text || null}</span>
                </div>
            </div>
        </div>
    );
});

export const TipOnImage = React.memo((/** @type {ITipOnImageProps}*/ { data }) => {
    if (data && data.img_text) {
        return <TipOnImageImpl data={data} />;
    }
    return null;
});
