/**
 * @file makeUpProtocol.js
 * @author daben
 */

import { getCurrentPlatform } from '@pdd/platform-util';
const isClientEnv = typeof window !== 'undefined';
export default function makeUpProtocol(url = '') {
  if (url.match(/^http:?/)) {
    return url;
  }
  if (getCurrentPlatform().isNativePlatform || !isClientEnv) {
    return `http://${url}`;
  } else if (location.protocol) {
    return `${location.protocol}//${url}`;
  }
}
