import React from 'react';
import PropTypes from 'prop-types';
import { getInitialUrl } from '@pdd/cui-image';
import { getUid } from '@pdd/auth';
import { throwErrorAsync } from '@pdd/core-util';
import { CLASS_NAMES } from './constants';
import Context from './Context';
import styles from './index.scss';

async function replaceUrlDomain(src) {
  let newUrl = src;
  let uid;
  try {
    uid = await getUid();
    newUrl = getInitialUrl(src, uid);
  } catch (e) {
    const errorMsg = 'lazy_image_error: ';
    const replaceDomainError = new Error(errorMsg);
    throwErrorAsync(replaceDomainError, { payload: { src, uid }, error_msg: errorMsg });
  }
  return newUrl;
}

export default class LazyImage extends React.Component {
  static contextType = Context;
  myRef = React.createRef();

  componentDidMount() {
    this._startLazyLoad();
  }

  componentDidUpdate() {
    this._startLazyLoad();
  }

  async _startLazyLoad() {
    const src = this.props.src;
    if (this._lastSrc === src) {
      return;
    }

    if (this.context && this.context.lazyLoader) {
      this._lastSrc = src;
      const newUrl = await replaceUrlDomain(src);
      const el = this.myRef.current;
      if (!el) {
        // img
        return;
      }
      this.context.lazyLoader.start({ el, src: newUrl });
    }
  }

  render() {
    const { src, className = '', ...otherProps } = this.props;
    return <img ref={this.myRef} {...otherProps} className={`${CLASS_NAMES.IMAGE} ${className}`} />;
  }
}

LazyImage.propTypes = {
  /**
   * @type {string}
   * @description 
   */
  src: PropTypes.string.isRequired,
  /**
   * @type {string}
   * @description 
   */
  className: PropTypes.string
};

if (process.env.NODE_ENV !== 'production' && process.env.BROWSER) {
  if (styles && Object.keys(styles).length > 0) {
    // CSS Modulesstylescss
    console.error('lazy-image: index.scssCSS Modules');
  }
}
