import ShareChannel from '../ShareChannel';

export const AppChannel = {
  DEFAULT: 0,
  WECHAT: 1,
  QQ: 2,
  WEIBO: 3
};

const SHARE_CHANNEL_MAP_APP_CHANNEL = {
  [ShareChannel.UNKNOWN]: 0,
  [ShareChannel.APP_MESSAGE]: 1,
  [ShareChannel.TIMELINE]: 1,
  [ShareChannel.QQ]: 2,
  [ShareChannel.QQ_ZONE]: 2,
  [ShareChannel.QQ_WEIBO]: 2,
  [ShareChannel.SINA_WEIBO]: 3
};

function getAppChannel(shareChannel) {
  return SHARE_CHANNEL_MAP_APP_CHANNEL[shareChannel] || AppChannel.DEFAULT;
}

export default getAppChannel;
