import navigation from '@pdd/navigation';
import { init as initAuth, login } from '@pdd/auth';
import { initGlobalMetrics, setDefaultParams, sendPageViewMetrics } from '@pdd/metrics';
import { init as initErrorLogger } from '@pdd/error-logger';
import { parseQuery, createPageId, getReferPageInfo, throwErrorAsync } from '@pdd/core-util';
import PageManagerObj from '@pdd/page-manager';
import DisableClickComponent from '@pdd/ui-disable-click';
import { getCurrentPlatform } from '@pdd/platform-util';
import { createDefaultAxios } from '@pdd/isomorphic-request';
import { fetchWxConfig, getHybridPackVersion } from './service';
import {
  setupPullReload,
  setTitle,
  getPageShownType,
  onWebMounted
} from '@pdd/native-util';

const PageManager = PageManagerObj.getInstance();

const {
  isNativePlatform,
  isWeChatPlatform,
  isQQPlatform,
  isTinyNativePlatform
} = process.env.BROWSER ? getCurrentPlatform() : {};

export default {
  async prepare(options = {}, render = () => {}) {
    const {
      hybridPackName,
      pageProperty: { pageSN, pageName = getDefaultPageName(), extraPvParams, noReportPv },
      initNativeBehavior = initDefaultNativeBehavior,
      pmmAppInfo = {}
    } = options;

    //  query page_id
    const pageId = options.pageId || parseQuery(location.search).page_id || createPageId(pageSN);
    const pageInfo = { pageSN, pageId, pageName };

    initMetricsAndLogger(pageInfo, hybridPackName, pmmAppInfo).then(() => initPageContext({ pageInfo, extraPvParams, noReportPv }));

    navigation.setNextPageQuery({
        refer_page_name: pageName,
        refer_page_id: pageId,
        refer_page_sn: pageSN
    });

    const http = createDefaultAxios({ pageInfo: { pageId } });

    await initLogin(http, options);

    // pddSharerenderpddSharerender
    // 
    const initShareP = timeout(initShareAndMenu(http, pageInfo, options), 'initShareAndMenu ').catch(throwErrorAsync);

    if(isNativePlatform) {
      initNativeBehavior(options.disablePullReload);
    }

    if(isWeChatPlatform) {
      await initShareP;
    }

    render(pageInfo);

    DisableClickComponent.initGlobal();
  }
};

function timeout(promise, timeoutMsg) {
  const timeLimit = new Promise((resolve, reject) => {
    setTimeout(() => reject(new Error(timeoutMsg)), 1000);
  });

  return Promise.race([promise, timeLimit]);
}

// metricserror-logger
function initMetricsAndLogger(pageInfo, hybridPackName, pmmAppInfo) {
  const initMetricsP = getHybridPackVersion(hybridPackName).then((version) => {
    // eslint-disable-next-line camelcase
    const { pageSN: page_sn, pageId: page_id } = pageInfo;

    // metrics
    const params = version ? { page_sn, page_id, component_version: version } : { page_sn, page_id };
    setDefaultParams(params);
    initGlobalMetrics();
  });

  // 
  getReferPageInfo().then(({ refer_page_sn, refer_page_id, refer_page_name }) => initErrorLogger({
    ...pageInfo,
    referPageSN: refer_page_sn,
    referPageId: refer_page_id,
    referPageName: refer_page_name
  }, pmmAppInfo));

  return initMetricsP;
}

function initPageContext({ pageInfo, extraPvParams, noReportPv }) {
  PageManager.init(null, {
    pageProperty: pageInfo,
    async callback() {
      navigation.replaceQuery({ page_id: pageInfo.pageId });

      if (noReportPv) {
        return;
      }
      let isEmbedPage = false;
      if(isNativePlatform) {
        isEmbedPage = await getPageShownType().then(({ shown_type: shownType }) => +shownType === 1).catch(() => false);
      }

      !isEmbedPage && sendPageViewMetrics(extraPvParams); // pv
    }
  });
}

function initDefaultNativeBehavior(disablePullReload) {
  onWebMounted();
  setupPullReload({ enable: !disablePullReload });
  setTitle({ title: document.title });
}

async function initLogin(http, { requireLogin, skipLogin, loginParams = {} }) {
  try {
    // @pdd/authcookielocalstorage
    return await initAuth(http);
  } catch (e) {
    requireLogin = requireLogin || isWeChatPlatform || isQQPlatform;

    // 1. 302 code auth
    // 2. qq
    // 3. 
    if(e.errorCode === 302 || !requireLogin || skipLogin) {
      return;
    }

    return login(loginParams).catch((e) => {
      navigation.back();
      throw e;
    });
  }
}

/**
 * - market-share
 *    1. signaturemarket-shareisSDKPrepareprepareShare
 *    2. appqqisSDKPrepareprepareShare
 *
 *  - market-share
 *    1. signature
 */
async function initShareAndMenu(http, pageInfo, options) {
  const { shareInfo = {}, noDefaultShare } = options;
  const { onlyWeChat, disableCopyUrl, disableShare, enableBrowserOpen } = shareInfo;
  const setupParams = { shareInfo, noDefaultShare, pageInfo };

  const SharePromise = shareInfo.disableShare ? null : import('@pdd/market-share');

  if(isNativePlatform || isTinyNativePlatform) {
    return setupShare(SharePromise, setupParams);
  }

  if(isWeChatPlatform) {
    const [wxConfig, { initWxConfig }] = await Promise.all([
      fetchWxConfig(http, pageInfo.pageSN),
      import(/* webpackChunkName: "pdd-mod/wechat-service" */ '@pdd/wechat-service')
    ]);

    const mergedShareInfo = { ...shareInfo, ...wxConfig };
    await initWxConfig(mergedShareInfo, { onlyWeChat, disableCopyUrl, disableShare, enableBrowserOpen });

    return setupShare(SharePromise, setupParams);
  }

  if(isQQPlatform) {
    const { initQQConfig } = await import(/* webpackChunkName: "pdd-mod/qq-service" */ '@pdd/qq-service');
    initQQConfig(http).then(() => setupShare(SharePromise, setupParams));
  }
}

async function setupShare(PDDSharePromise, { shareInfo = {}, noDefaultShare, pageInfo }) {
  if(shareInfo.disableShare) {
    return;
  }

  const { default: PDDShare, setPageContext } = await PDDSharePromise;
  setPageContext(pageInfo);
  shareInfo.isSDKPrepare = true;
  const pddShare = PDDShare.getInstance({
    shareManagerConfig: {
      noDefaultShare
    }
  });

  pddShare.setup();
  pddShare.prepareShare(shareInfo);
}

//  pathname
function getDefaultPageName() {
  return location.pathname.split('/').pop().split('.')[0] || 'index';
}
