import { pageStartStr, firstPaintStr, firstScreenStr, pageEndStr } from './statisticsStr';

const pageStart = function () {
    (function (window) {
        const plt = (window._plt = window._plt || []);
        plt.push(['tm', 'ps', +new Date()]);
        const requestAnimationFrame =
            window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.setTimeout;
        requestAnimationFrame(function () {
            plt.push(['tm', 'fraf', +new Date()]);
        });
    })(window);
};

const firstPaint = function () {
    (function (window) {
        const plt = (window._plt = window._plt || []);
        plt.push(['tm', 'fp', +new Date()]);

        document.addEventListener('DOMContentLoaded', function () {
            plt.push(['tm', 'dr', +new Date()]);
        });

        window.addEventListener('load', function () {
            plt.push(['tm', 'ld', +new Date()]);
        });
    })(window);
};

const firstScreen = function () {
    (function (window, document) {
        const plt = (window._plt = window._plt || []);
        plt.fs = plt.fs || true; // fsssr
        const key = 'fsn';
        const type = 'tm';
        const loadEvent = 'load';
        const errorEvent = 'error';

        let latestTime = +new Date();
        plt.push([type, key, latestTime]);

        const imgs = document.getElementsByTagName('img');
        const height = document.documentElement.clientHeight;
        const fsItems = [];
        const isLoad = document.readyState === 'complete';

        const onLoad = function () {
            for (let i = 0; i < fsItems.length; i++) {
                const item = fsItems[i];
                const top = item.img.getBoundingClientRect().top || 0;
                if (top + item.img.height > 0 && height > top) {
                    latestTime = Math.max(item.time, latestTime);
                }
            }
            plt.push([type, 'fs', latestTime]);
        };

        function check() {
            let ready = true;

            for (let i = 0; i < fsItems.length; i++) {
                ready = ready && fsItems[i].time;
                if (!ready) {
                    return;
                }
            }

            onLoad();
        }

        for (let i = 0; i < imgs.length; i++) {
            const img = imgs[i];

            // android bugimg.srcimg.completefalsetrue,src
            if (!img.complete && img.src) {
                const item = {
                    img,
                };

                const load = function () {
                    this.removeEventListener(loadEvent, load, false);
                    this.removeEventListener(errorEvent, load, false);
                    item.time = +new Date();
                    isLoad && check();
                };

                img.addEventListener(loadEvent, load, false);
                img.addEventListener(errorEvent, load, false);
                fsItems.push(item);
            }
        }

        if (isLoad) {
            check();
        } else {
            window.addEventListener(loadEvent, onLoad);
        }

        window.__fsImgItems = fsItems;
        window.__fsImgSrcs = fsItems.map((i) => i.img.src);
    })(window, document);
};

const pageEnd = function () {
    // eslint-disable-next-line no-unused-vars
    (function (window) {
        // const plt = window._plt = window._plt || [];
        // plt.push(['tm', 'dr', +new Date()]);
    })(window);
};

export { pageStartStr, firstPaintStr, firstScreenStr, pageEndStr, pageStart, firstPaint, firstScreen, pageEnd };
