/**
 * @file PageStore.js storessrcsr
 * @author zongheng(zongheng@pinduoduo.com)
 */
import { action, observable, toJS, runInAction } from 'mobx';
import { getMsgFromCode } from '@pdd/error-status';
import { createClientContext } from './createClientContext';
import { createDefaultAxios } from '@pdd/isomorphic-request';

/**
 * @external {AxiosInstance} https://github.com/axios/axios#instance-methods
 */

/**
 * 
 *
 * ssr 
 *
 *   server:
 *     constructor -> initServerData -> initDataCallback -> initServices ->
 *     getDatasource -> loadDatasource -> finishServerRender -> finishInitLoading -> toJSON
 *
 *   client:
 *     constructor -> fromJS -> initServices -> prepareClientData -> finishInitLoading
 *
 * csr 
 *   constructor -> initClientData -> initDataCallback -> initServices ->
 *   getDatasource -> loadDatasource -> prepareClientData -> finishInitLoading
 *
 */
export class PageStore {
  /**
   *  axios http 
   *
   * @readonly
   * @type {AxiosInstance}
   * @see https://github.com/axios/axios#instance-methods
   */
  $axiosHttp = null;

  /**
   * 
   *
   * @protected
   * @type {string}
   */
  errorRedirectUrl = '/';

  /**
   * client
   *
   * @private
   * @type {boolean}
   */
  @observable isServerRendered = false;

  /**
   * 
   *
   * @private
   * @type {boolean}
   */
  @observable isFinishInitLoading = false;

  /**
   * 
   *
   * @protected
   */
  @action finishServerRender() {
    this.finishInitLoading();
    this.isServerRendered = true;
  }

  /**
   * 
   */
  @action finishInitLoading() {
    this.isFinishInitLoading = true;
  }

  /**
   * 
   */
  @action
  prepareClientData() {}

  /**
   * 
   *
   * @todo 
   *
   * @private
   * @param {*} e 
   * @return {Promise<{handled: boolean, errorCode: number, error: *, alertText: string, redirectUrl: string}>}
   *  rejected promise
   */
  dealError(e) {
    // todo: dealError
    if(e.handled) {
      console.warn('dealError');
      return Promise.reject(e);
    }

    e.errorCode = e.errorCode || 'unknown';
    e.redirectUrl = this.errorRedirectUrl;
    e.alertText = getMsgFromCode(e.errorCode);
    e.handled = true;

    return Promise.reject(e);
  }

  /**
   * 
   *
   * @protected
   * @param requestContext
   * @return {Promise<*>}
   */
  @action
  initDataCallback(requestContext) {
    this.initServices(this.$axiosHttp);
    const datasource = this.getDatasource();
    return this.loadDatasource(datasource, requestContext);
  }

  /**
   * 
   *
   * @param {{pageId: string}} pageInfo 
   * @throws {*}
   * @return {Promise<*>}
   */
  @action
  async initClientData(pageInfo) {
    try {
      this.initAxiosHttp({ pageInfo });
      await this.initDataCallback(createClientContext());
      this.prepareClientData();
    } catch (err) {
      return this.dealError(err);
    }
  }

  /**
   * 
   *
   * @param {RequestContext} requestContext 
   * @param {string} [token] token
   * @throws {*}
   * @return {Promise<*>}
   */
  @action
  async initServerData(requestContext, token) {
    try {
      this.initAxiosHttp({
        req: requestContext.__req,
        res: requestContext.__res,
        accessToken: token
      });
      await this.initDataCallback(requestContext);
      this.finishServerRender();
    } catch (err) {
      return this.dealError(err);
    }
  }

  /**
   * js
   *
   * @param {Object} [rawData] 
   * @param {{pageId: string}} pageInfo axios http
   */
  @action
  fromJS(rawData, pageInfo) {
    if(rawData && rawData.isServerRendered) {
      Object.assign(this, rawData);
      this.initAxiosHttp({ pageInfo });

      this.initServices(this.$axiosHttp);
    }
  }

  /**
   * 
   *
   * @protected
   * @param {Array<Function|Object>} datasource 
   * @param {{__req: ?Object, __res: ?Object}} requestContext 
   * @return {Promise<*>}
   */
  @action
  async loadDatasource(datasource, requestContext) {
    // 
    if(Array.isArray(datasource)) {
      return datasource.reduce(
        (result, config) => result.then(() => loadDataParallel(config, requestContext, this)),
        Promise.resolve()
      );
    }

    return loadDataParallel(datasource, requestContext, this);
  }

  /**
   * @protected
   *
   * , 
   *
   * 1. PageStore
   *       - requestContext: Provider.buildContext()
   *       - store:  store  Model  Service 
   *
   * ```
   *  //  store.key1 = value1store.key2 = value2
   *   async function loader(requestContext, authorize, store) {
   *        return {
   *            key1: value1,
   *            key2: value2
   *        };
   *   }
   * ```
   * 2. store
   *     2.1 1store
   *     2.2 loaderdumploader1dumpbool
   *     loaderstoretruestore
   *
   * ```
   * {
   *    key1(requestContext, authorize, store) {}, // 1 store.key1 = 
   *    key2(requestContext, authorize, store) {}, // 1 store.key2 = 
   *    key3(requestContext, authorize, store) {},
   *    key4: {
   *        loader(requestContext, authorize, store) {},
   *        dump: true // loaderstore
   *    }
   * }
   * ```
   *
   * 3. 12
   *
   * ```
   * [
   *    async function loader(requestContext, authorize, store) {}, // 
   *    {
   *        key1(requestContext, authorize, store) {}, // 1 store.key1 = 
   *        key2(requestContext, authorize, store) {}, // 1 store.key2 = 
   *        key3: {
   *            loader(requestContext, authorize, store) {},
   *            dump: true loaderstore
   *       }
   *    }
   * ]
   * ```
   *
   * @return {{} | Array<{}>}
   */
  getDatasource() {
    return [];
  }

  /**
   * services
   *
   * @protected
   * @param {AxiosInstance} axiosHttp axios http
   */
  initServices(axiosHttp) {}

  /**
   *  $axiosHttp
   *
   * @protected
   * @param {Object} axiosHttpParams 
   * @param {ExpressRequest} [axiosHttpParams.req] express  server
   * @param {ExpressResponse} [axiosHttpParams.res] express server
   * @param {string} [axiosHttpParams.accessToken] token
   * @param {{pageId: string}} [axiosHttpParams.pageInfo]  client
   */
  initAxiosHttp({ req, res, accessToken, pageInfo }) {
    const params = process.env.BROWSER ? { pageInfo } : { req, res, accessToken };
    this.$axiosHttp = createDefaultAxios(params);
  }

  /**
   * json
   *
   * @override
   */
  toJSON() {
    return toJS(this);
  }
}

/**
 * 
 */
async function loadDataParallel(datasource, requestContext, store) {
  if(typeof datasource === 'function') {
    const data = await datasource(requestContext, store);
    runInAction(() => Object.assign(store, data));
    return;
  }

  return Promise.all(Object.entries(datasource).map(
    async ([key, config]) => {
      if(typeof config === 'function') {
        config = { dump: false, loader: config };
      }

      const loader = config.loader;

      const data = await Promise.resolve(loader(requestContext, store));
      runInAction(() => {
        if(config.dump) {
          Object.assign(store, data);
        } else {
          store[key] = data;
        }
      });
    }
  ));
}
