/**
 * hex to rgba
 *
 * @param {string} hex '#'
 * @param {number} transparent 
 * @return {string} rgba
 */
export const hexToRGB = (hex, transparent = 1) => {
    if (!hex) {
        return 'transparent';
    }
    const sColorChange = [];

    for (let i = 1; i < 7; i += 2) {
        sColorChange.push(parseInt('0x' + hex.substring(i, i + 2), 16));
    }
    return `rgba(${sColorChange.join(',')}, ${transparent})`;
};

/**
 * 
 *
 * @param {string} str 
 * @param {number} subLen 15
 * @return {object} 
 */
function caculCharLen (str, subLen) {
    str = str || '';
    subLen = subLen || 0;
    let preSubStr = '';
    let len = 0;
    for (let i = 0; i < str.length; i++) {
        if (len < subLen) {
            preSubStr = str.substr(0, i);
        }
        if (str.charCodeAt(i) > 127 || str.charCodeAt(i) == 94) {
            len += 2;
        } else {
            len++;
        }
    }

    return {
        len,
        preSubStr
    };
}

/**
 * 
 *
 * @param {string} mallName 
 * @param {number} subLen 15
 * @param {number} limitLen 20
 * @return {string} 
 */
export function mallNameAdapted (mallName, subLen = 15, limitLen = 20) {
    if (!mallName) return '';
    const mallNameString = caculCharLen(mallName, subLen);
    if (mallNameString.len > limitLen) {
        mallName = mallNameString.preSubStr + '...' + mallName.substr(-3, 3);
    }
    return mallName;
}

const hexRegex = /^#[a-fA-F0-9]{6}$/;
const hexArgbRegex = /^#[a-fA-F0-9]{8}$/;

/**
 *  hexArgb to rgba
 * @param {string} color '#FFFFFF'  '#FFFFFFFF'
 * @returns {string}  css  color
 */
export function ahexToRgba(color) {
    if (typeof color !== 'string') {
        return '';
    }

    if (color.match(hexRegex)) {
        return color;
    }
    if (color.match(hexArgbRegex)) {
        const alpha = parseFloat(
            (
                parseInt(`${color[1]}${color[2]}`, 16) / 255
            ).toFixed(2),
        );
        const red = parseInt(`${color[3]}${color[4]}`, 16);
        const green = parseInt(`${color[5]}${color[6]}`, 16);
        const blue = parseInt(`${color[7]}${color[8]}`, 16);
        return `rgba(${red}, ${green}, ${blue}, ${alpha})`;
    }

    return color;
}
