/**
 * @file index.js, SpinnerH5
 * @author tenghu
 */
import React from 'react';
import PropTypes from 'prop-types';
import SpinnerImg from './SpinnerImg';
import Container from './Container';

/**
 * SpinnerH5
 * @visibleName SpinnerH5
 */
function SpinnerH5(props) {
  const { style, className, children, hideBg, ContainerProps, SpinnerImgProps } = props;
  const hasContent = !!children;
  return (
    <Container bigger={hasContent} hideBg={hideBg} style={style} className={className} {...ContainerProps}>
      <SpinnerImg bigger={hasContent} {...SpinnerImgProps} />
      {hideBg ? '' : children}
    </Container>
  );
}

SpinnerH5.propTypes = {
  /**
   * 
   */
  hideBg: PropTypes.bool,
  /**
   * Containerprops
   */
  ContainerProps: PropTypes.object,
  /**
   * SpinnerImgprops
   */
  SpinnerImgProps: PropTypes.object,
  /**
   * Container
   */
  className: PropTypes.string,
  /**
   * Container
   */
  style: PropTypes.object
};

export default SpinnerH5;
