// withStylesisomorphic-style-loader/withStyles
let withStyles = (styles) => {
  const result = Component => Component;
  // client, CSS Modules
  if (process.env.NODE_ENV !== 'production' && process.env.BROWSER) {
    const userAgent = ((typeof navigator !== 'undefined') && navigator.userAgent) || '';
    const isJSDom = userAgent.includes('jsdom');
    if (isJSDom) { // 
      return result;
    }

    const isUsingCSSModules = Object.keys(styles).length > 0; // CSS ModulesclassName
    if (!isUsingCSSModules) {
      console.error('iso.scssCSS Modules https://fweb.pdd.net/docs/core-ui/');
    }
  }
  return result;
};

// eslint-disable-next-line no-undef
if (typeof USE_ISO_STYLE_LOADER !== 'undefined' && USE_ISO_STYLE_LOADER === true) {
  withStyles = require('isomorphic-style-loader/withStyles');
}

export default withStyles;
