import { getFormatterFunc } from '../iso/formatter';
import { getCookie } from '@pdd/core-util';
import { logError } from '../../internal/assert';
//
// CSR
// 1. leowindow.leo { csrFallback: Boolean, fallbackData: Object, data: Object }
// 2. csrFallback csr
// 3. fallbackData csrleo
// 4. data formatter
//

/**
 * CSR
 *  csr => leo => 
 *   => 
 *
 * @param {Array} leoKeys
 */
export default function clientInit (leoKeys) {
  leoKeys = leoKeys || [];
  window.leo = window.leo || {};
  window.leo.data = window.leo.data || {};
  if (window.leo.csrFallback && window.leo.fallbackData && leoKeys.length) {
    const uid = getCookie('pdd_user_id'); // TODOcookieasync
    leoKeys.map(item => {
      const config = window.leo.fallbackData[item.leoKey];
      if (config) {
        const formatterFunc = getFormatterFunc(item);
        try {
          window.leo.data[item.leoKey] = formatterFunc(config, {
            identity: {
              uid: parseInt(uid) || 0
            }
          });
        } catch (ex) {
          logError(null, 'csrFormatLeoFailed', ex);
        }
      } else {
        logError(null, 'csrLeoFallbackDataNotFound');
      }
    });
  }
}
