/**
 * @file ShareLimiter/index.js
 * @author daben
 */
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getCurrentPlatform } from '@pdd/platform-util';
import ShareType from '../ShareType';
import ShareError from '../ShareError';
import ShareForm from '../ShareForm';
import LimitMethod from './LimitMethod';
import { getPageSN, getPageName } from '../tools/getPageContext';
import loadRuleModel from './loadRuleModel';
import { getShareModuleName } from '../tools/NativeShareEnv';
import MagicTraceManager from '../tools/MagicTraceManager';
import trackError from '../ShareTracker/trackError';
import URLWrapType, { isTinyDomainType, isShortLinkType } from '../tools/URLWrapType';
const { ShareErrorCode } = ShareError;

// pkg id
export const FORBIDDEN_MATCHED_PKG_ID = -1;

class ShareLimiter {
  constructor() {
    this._inited = false;
    this._queryShareTypePromise = null;
    this._rules = null;
    this._enableShareTypes = [];
    this.appOptional = null;
    this._rom = 0;
    this._matchedPkgId = undefined;
    this._isCipherDowngrade = false;
    this._enableSilentMode = false;
  }

  build() {
    if (this._inited) {
      return Promise.resolve();
    }
    if (!this._queryShareTypePromise) {
      this._queryShareTypePromise = this.loadRules();
      this._queryShareTypePromise
        .then(data => {
          this._inited = true;
          this._processRules(data);
          return MagicTraceManager.getInstance().build({
            magicTrace: data.magic_trace
          });
        })
        .catch(e => {
          if (process.env.NODE_ENV === 'development') {
            // eslint-disable-next-line no-console
            console.warn('');
          }
          trackError({
            errorCode: 0,
            message: '',
            options: {
              message: e && e.message,
              errorCode: e && e.errorCode
            }
          });
          this._inited = true;
        });
    }
    return this._queryShareTypePromise;
  }

  reset() {
    this._inited = false;
    this._queryShareTypePromise = null;
    this._rules = null;
    this._enableShareTypes = [];
    this.appOptional = null;
    this._rom = 0;
    this._matchedPkgId = undefined;
    this._isCipherDowngrade = false;
    this._enableSilentMode = false;
  }

  getAppOptional() {
    return this.appOptional;
  }

  getRules() {
    return this.build().then(
      () => {
        return this._rules;
      },
      () => {
        return Promise.resolve(this._rules);
      }
    );
  }

  loadRules() {
    if (process.env.NODE_ENV === 'development' && !getPageSN()) {
      // eslint-disable-next-line no-console
      console.warn('pageSn');
    }
    if (getCurrentPlatform().isNativePlatform) {
      return invokeBridgeCallNativeFun(getShareModuleName(), 'queryShareTypes', {
        page_sn: getPageSN() + '',
        page_info: {
          page_name: getPageName(),
          page_url: location && location.href
        }
      });
    } else {
      return loadRuleModel('post', {
        pageSn: getPageSN() + '',
        pageInfo: {
          page_name: getPageName(),
          page_url: location && location.href
        }
      });
    }
  }

  _processRules(data = {}) {
    const shareTypes = data.share_types || [];
    this.appOptional = data.app_optional;
    this._rom = data.rom;
    this._matchedPkgId = data.matched_pkg_id;
    this._enableSilentMode = data.is_weird;
    // 
    if (data.degrade) {
      this.setCipherDowngrade(true);
    }
    const enableShareTypes = [];
    this._rules = shareTypes.reduce((accu, next) => {
      const shareType = next.share_type;
      if (shareType) {
        accu[shareType] = next;
        enableShareTypes.push(parseInt(shareType, 10));
      }
      return accu;
    }, {});
    // 
    this._innerShareTypes = data.inner_share_types || [];
    this._innerShareTypes.forEach((item = {}) => {
      enableShareTypes.push(parseInt(item.share_type, 10));
    });
    this._enableShareTypes = enableShareTypes;
    if (process.env.NODE_ENV === 'development' && this._enableShareTypes.length <= 0) {
      // eslint-disable-next-line no-console
      console.warn('');
    }
    return this._rules;
  }

  getRuleByType(type) {
    return (this._rules || {})[type];
  }

  checkSync(shareType, shareEntity) {
    if (!this._rules) {
      return ShareErrorCode.SHARE_LIMITER_NOT_INIT;
    }
    if (this._enableShareTypes.indexOf(shareType) < 0) {
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }
    const shareForm = shareEntity.getShareForm(shareType);
    const rule = this.getRuleByType(shareType) || {};
    const methods = rule.share_methods || [];
    // 
    if ([ShareForm.COMMAND, ShareForm.TEXT].indexOf(shareForm) >= 0) {
      const isServerRely = isShortLinkType(shareEntity.urlWrapType) && shareEntity.marketActivityID;
      // 
      if (
        isServerRely &&
        // 
        ((shareEntity.shortURLApi && methods.indexOf(LimitMethod.LINK) >= 0) ||
          // 
          (shareEntity.shortCommandApi && methods.indexOf(LimitMethod.CIPHER) >= 0) ||
          // urlWrapType
          shareEntity.urlWrapType === URLWrapType.CATEGORY_PROCEDURE ||
          shareEntity.urlWrapType === URLWrapType.CATEGORY_PROCEDURE_CUSTOM)
      ) {
        return 0;
      }
      if (methods.indexOf(LimitMethod.NORMAL) >= 0) {
        return 0;
      }
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    } else if ([ShareType.WECHAT_SESSION, ShareType.WECHAT_TIMELINE].indexOf(shareType) >= 0) {
      const isServerRely = isTinyDomainType(shareEntity.urlWrapType) && shareEntity.marketActivityID;
      if (
        isServerRely &&
        // 
        methods.indexOf(LimitMethod.DOMAIN) < 0
      ) {
        return ShareErrorCode.SHARE_TYPE_DISABLED;
      }
      if (methods.indexOf(LimitMethod.NORMAL) >= 0) {
        return 0;
      }
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }
    return 0;
  }

  get isCipherDowngrade() {
    return this._isCipherDowngrade;
  }

  get rom() {
    return this._rom;
  }

  get matchedPkgId() {
    return this._matchedPkgId;
  }

  get isSilentMode() {
    return this._enableSilentMode;
  }

  setCipherDowngrade(flag) {
    this._isCipherDowngrade = flag;
  }
}

let instance = null;
export function getInstance() {
  if (!instance) {
    instance = new ShareLimiter();
  }
  return instance;
}

export default ShareLimiter;
