/*!
 * JavaScript Cookie v2.2.0
 * https://github.com/js-cookie/js-cookie
 *
 * Copyright 2006, 2015 Klaus Hartl & Fagner Brack
 * Released under the MIT license
 */
function decode(s) {
  return s.replace(/(%[0-9A-Z]{2})+/g, decodeURIComponent);
}

/**
 * cookiecookie
 *
 * @param {string} [key]  
 * @returns {string|Object} cookie 
 *
 * @example
 *
 * getCookie('uid') // => "1001"
 * getCookie('non-exist') // => ""
 * getCookie() // { uid: "1001" }
 */
export function getCookie(key) {
  const cookies = document.cookie ? document.cookie.split('; ') : [];
  const jar = {};

  cookies.some(v => {
    const parts = v.split('=');
    let cookie = parts.slice(1).join('=');
    const name = decode(parts[0]);
    cookie = decode(cookie);
    jar[name] = cookie;
    return key === name;
  });

  return key ? jar[key] || '' : jar;
}

/**
 * @typedef {Object} attributes
 * @property {string} [path="/"] cookie
 * @property {string} [domain] cookiedomain
 * @property {number} [maxAge] cookie expiresIE8
 * @property {string} [expires] cookieGMT
 * @property {boolean} [secure] cookiehttps
 */

/**
 *  cookie
 * @param {string} key 
 * @param {string} value cookie 
 * @param {attributes} [attributes]  path/expires 
 * @returns {string} document.cookie
 * @example
 *
 * setCookie('uid', '1001')
 */
export function setCookie(key, value, attributes) {
  // If no value offered, do nothing.
  if (value === undefined) {
    return '';
  }

  attributes = Object.assign({ path: '/' }, attributes);

  if (typeof attributes.maxAge === 'number') {
    attributes['max-age'] = String(attributes.maxAge);
    attributes.maxAge = false;
  }

  value = encodeURIComponent(value);
  key = encodeURIComponent(key);

  const stringifiedAttributes = Object.entries(attributes).reduce((res, [attrName, attrValue]) => {
    if (!attrValue) {
      return res;
    }
    res += '; ' + attrName;
    // The only valid case is: {secure: true}
    if (attrValue === true) {
      return res;
    }
    // Considers RFC 6265 section 5.2:
    // ...
    // 3.  If the remaining unparsed-attributes contains a %x3B (";")
    //     character:
    // Consume the characters of the unparsed-attributes up to,
    // not including, the first %x3B (";") character.
    // ...
    res += '=' + attrValue.split(';')[0];
    return res;
  }, '');

  return (document.cookie = key + '=' + value + stringifiedAttributes);
}

/**
 *  cookie
 * @param {string} key 
 */
export function removeCookie(key) {
  setCookie(key, '', {
    maxAge: -1
  });
}
