import settle from 'axios/lib/core/settle';
import buildURL from 'axios/lib/helpers/buildURL';
import buildFullPath from 'axios/lib/core/buildFullPath';
import pinbridge from '@pdd/pinbridge';
import { getPlatform, compareVersion } from '@pdd/platform-util';

const appVersion = process.env.BROWSER ? getPlatform(window.navigator.userAgent).version : '0';
const supportNativeAntiToken = compareVersion(appVersion, '4.69.0') >= 0;
const supportNativeTimeout = compareVersion(appVersion, '5.24.0') >= 0;
const isDev = process.env.NODE_ENV === 'development';
/**
 * 
 *
 * @param {AxiosRequestConfig} config
 * @return {Promise<any>}
 */
export default function nativeRequest(config) {
  // todo: abort
  return new Promise((resolve, reject) => {
    const data = config.method === 'post' && config.data ? config.data : '';
    const fullPath = buildFullPath(config.baseURL, config.url);
    const nativeParams = {
      method: config.method.toUpperCase(),
      url: buildURL(fullPath, config.params, config.paramsSerializer),
      headers: {
        ...config.headers,
        Referer: location.href,
        'p-mode': '1'
      },
      data
    };

    if (supportNativeAntiToken) {
      nativeParams.force_anti_token = !!config.useNativeAntiToken;
    }
    const timeout = config.clientTimeout;
    if (supportNativeTimeout && timeout) {
      nativeParams.timeout = timeout;
    }
    if (isDev && timeout && timeout % 1000) {
      console.warn(`iOS timeouts${timeout}msiOS${Math.floor(timeout / 1000)}s${config.url}`)
    }

    //  encode responseencodeResp
    nativeParams.encode_resp = !!config.encodeNativeResponse;

    const request = { nativeParams, config };

    pinbridge.callNative('JSNetwork', 'request', nativeParams, onSuccess, onError);

    function onSuccess(nativeResponse) {
      const {
        status,
        response,
        response_encoded: responseEncoded = true
      } = nativeResponse;
      const axiosResponse = {
        data: null,
        status,
        statusText: '' + status,
        config,
        headers: {},
        request
      };

      try {
        axiosResponse.data = JSON.parse( responseEncoded ? decodeURIComponent(response) : response);
      } catch (e) {
        /* eslint-disable */
        if (process.env.NODE_ENV === 'development') {
          console.error(e);
        }
        axiosResponse.data = { error_msg: e.message, error_code: 0 };
        /* eslint-enable */
      }

      settle(resolve, reject, axiosResponse);
    }

    // onErrorAccessToken
    function deleteAccessToken(data) {
      if (data.headers && data.headers.AccessToken) {
        data.headers.AccessToken = data.headers.AccessToken.length;
      }
      return data;
    }

    // datanativeErrorCode 60000
    function onError(data, nativeErrorCode) {
      const error = new Error(`native request error: ${nativeErrorCode}`);
      error.errorCode = nativeErrorCode;
      error.nativeErrorCode = nativeErrorCode;
      error.request = request;
      error.errorType = 'api_error_app';
      error.config = config;
      error.toJSON = function () {
        return {
          message: this.message,
          nativeParams: deleteAccessToken(this.request.nativeParams),
          config: deleteAccessToken(this.config),
          errorCode: this.errorCode,
          nativeErrorCode: this.nativeErrorCode
        };
      };

      if (compareVersion(appVersion, '4.0.0') > 0) {
        pinbridge.callNative('JSLogger', 'log', {
          module: 'h5-isomorphic-request',
          message: config.url + ' failed, ' + JSON.stringify(deleteAccessToken(nativeParams)),
          level: 4
        });
      }

      reject(error);
    }
  })
}
