/**
 * @file pmm
 */

/* eslint-disable no-return-assign */
import { crc32, getNetWorkType } from '@pdd/core-util';
import { RunningAppConfig, DEFAULT_APP_ID, DEFAULT_BIZ_SIDE, IS_TEST_ENV } from './constants';
import { getCurrentPlatform } from '@pdd/platform-util';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getUid } from '@pdd/auth';

/**
 * numberapp'5.88.0' => '58800'
 * @param {String} version
 * @returns {Number}
 */
function convertVersion(version) {
  if (!version) {
    return 0;
  }

  // eslint-disable-next-line prefer-const
  return +version.split('.').map(bit => bit.padStart(2, '0')).join('');
}

/**
 * 
 * @param {String} url
 * @param {Object} data
 */
function sendRequest(url, data) {
  if (getCurrentPlatform().isNativePlatform) {
    invokeBridgeCallNativeFun('JSNetwork', 'request', {
      url,
      method: 'POST',
      data: JSON.stringify(data),
      headers: { 'Content-Type': 'application/json;charset=UTF-8' }
    });
  } else {
    const xhr = new XMLHttpRequest();
    xhr.open('POST', url, true);
    // xhr.setRequestHeader('Content-Type', 'application/plain;charset=UTF-8');
    xhr.withCredentials = true;
    xhr.send(JSON.stringify(data));
  }
}

/**
 * 
 * @returns {String}
 */
function getRunningPlatform() {
  if (getCurrentPlatform().isLiteNativePlatform) {
    return RunningAppConfig.LITE;
  }
  return RunningAppConfig[getCurrentPlatform().platform] || '-1';
}

/**
 * 
 * @param {Array} data
 * @param {Object} groupConfig
 * @returns {Object}
 */
async function getBaseParam(data, groupConfig = {}) {
  // 6
  // eslint-disable-next-line no-restricted-properties
  const randomIntVal = Math.pow(10, 5) + Math.floor(Math.random() * (Math.pow(10, 6) - Math.pow(10, 5)));
  const reportTime = Date.now();

  const appId = String(groupConfig.appId || DEFAULT_APP_ID);

  return {
    version: convertVersion(getCurrentPlatform().version), // versionappVersion4.3.35 40335
    report_time_ms: reportTime, // UTC
    rand_num: randomIntVal, // int6
    crc32: crc32(reportTime + '-' + randomIntVal), // 
    biz_side: groupConfig.bizSide || DEFAULT_BIZ_SIDE,
    app: appId,
    common_tags: {
      uid: await getUid() || '0', // userId, 567321
      runningAppId: appId,
      runningPlatform: getRunningPlatform(),
      p: getRunningPlatform(),
      pagePath: groupConfig.reportPath || location.pathname
    },
    datas: data
  };
}

/**
 * pmm 
 * @param {Array} datas
 * @param {string} url
 * @param {Object} groupConfig
 */
async function pmmReport(datas = [], url, groupConfig) {
  const params = await getBaseParam(datas, groupConfig);
  return sendRequest(url, params);
}

/**
 * pmm
 * @param {String} path
 * @returns {String}
 */
const resolvePmmUrl = function (path) {
  const hostname = IS_TEST_ENV ? 'pmmtk.hutaojie.com' : window.__PMM_HOST__ || 'pmmtk-a.pinduoduo.com';
  return `https://${hostname}/${path}`;
};

/**
 * Api
 * @param {Object} opts
 * @param {Object} groupConfig
 * @see https://note.pdd.net/doc/333608990511079424?root=204652268878319616#z2z5k
 */
export function pmmTracking(opts, groupConfig) {
  const {
    method,
    requestTime, // 
    resTimeConsume, // 
    reqData, // 
    resData, // 
    apiUrl, // api
    statusCode,
    pageId // id
  } = opts;

  const reqDataLen = reqData ? JSON.stringify(reqData).length : 0;
  const resDataLen = resData ? JSON.stringify(resData).length : 0;

  const send = networkType => {
    const data = {
      category: 1,
      type: 100,
      id_raw_value: apiUrl.split('?')[0],
      timestamp: requestTime, // UTC
      tags: {
        network: String(networkType), // 0=UNKNOWN1=WIFI2=2G3=3G4=4G
        code: String(statusCode), // 200
        method: method ? method.toUpperCase() : 'POST' //  httpMethodGETPOST
        // conn: 1=2=3=Titan,4=Titan
        // srcPageIdpageId45-1
      },
      lvalues: {
        rspT: { values: [resTimeConsume] },
        reqP: { values: [reqDataLen] },
        rspP: { values: [resDataLen] }
      },
      extras: {
        srcPageId: pageId
      },
      api_ratio: 1
    };

    return pmmReport([data], resolvePmmUrl('api/pmm/api'), groupConfig);
  };

  return getNetWorkType().then(send);
}
