/**
 * @file platformTag.js
 * @author  daben
 */
import { getCurrentPlatform } from '@pdd/platform-util';

export default function platformTag() {
  const platform = getCurrentPlatform();
  if (platform.isWeChatMiniProgram) {
    return platform.WxApp;
  }
  if (platform.isWeChatPlatform) {
    return platform.WeChat;
  }
  if (platform.isQQPlatform) {
    return platform.QQ;
  }
  if (
    platform.isNativePlatform ||
    platform.isTinyNativePlatform
  ) {
    // appweb h5
    return 'h5';
  }
  return '';
}
