import React from 'react';
import ReactDOM from 'react-dom';
import { ChunkProvider } from '../core/chunk/ChunkProvider';
import { createClientContext } from '../core/requestContext/client';
import { waitRawDataReady, catchError } from '../core/util';

export async function clientController(page) {
  await waitRawDataReady();
  const Component = page.Component;
  const isSSR = window.__SSR__;
  const method = isSSR ? 'hydrate' : 'render';
  const [initialProps, chunkProps] = await retrieveClientProps(page, {
    isSSR
  });
  const app = (
    <ChunkProvider initialContext={chunkProps}>
      <Component {...initialProps} />
    </ChunkProvider>
  );

  ReactDOM[method](app, document.getElementById('main'));
}

async function retrieveClientProps(page, config) {
  const getChunkFetcher = (context, initialPropsP) => {
    return typeof page.getChunkDataFetcher === 'function' ?
      page.getChunkDataFetcher(context, initialPropsP) : Promise.resolve({});
  };

  const getChunkProps = async (context, initialPropsP) => {
    const chunkFetcher = await getChunkFetcher(context, initialPropsP);
    return Object.entries(chunkFetcher).reduce((chunkData, [key, retriever]) => {
      chunkData[key] = chunkData[key] || {};
      chunkData[key].retriever = chunkFetcher[key];
      if (!config.isSSR) { // csr chunk  content 
        const chunkPromise = catchError(chunkFetcher[key]);
        chunkData[key].retriever = () => chunkPromise;
      }
      return chunkData;
    }, window.__CHUNK_DATA__ || {});
  };

  const getInitialProps = (context) => {
    // __INITIAL_PROPS__ rawData
    const initialProps = window.__INITIAL_PROPS__ || window.rawData;
    if(initialProps) {
      return Promise.resolve(initialProps);
    }

    return typeof page.getInitialProps === 'function' ? page.getInitialProps(context) : Promise.resolve({});
  };

  const context = createClientContext();
  const initialPropsP = getInitialProps(context);
  const chunkPropsP = getChunkProps(context, initialPropsP);
  return Promise.all([initialPropsP, chunkPropsP]);
}
