import { getNativeVersion, getPlatformByUa, PLATFORM } from '@pdd/platform-util/lib/common';

/**
 * 
 * @private
 * @param {Error} [error=null] error
 * @returns {string} 
 */
export function processStackMsg(error) {
  if(!error || !error.stack) {
    return '';
  }

  const stack = error.stack
    .replace(/\n/gi, '')
    .split(/\bat\b/)
    .slice(0, 5)
    .join('@')
    .replace(/\?[^:]+/gi, '');

  const msg = error.toString();
  return stack.indexOf(msg) < 0 ? msg + '@' + stack : stack;
}

/**
 * Error
 * @private
 * @param {Error} e
 * @returns {{error_msg: string, error_stack: string, Error: Error}}
 */
export function dealError(e) {
  return { error_msg: e && e.message, error_stack: processStackMsg(e), Error: e };
}

/**
 * strPDDAccessToken  PDDAccessToken=59
 * @private
 * @param {string} str 
 * @returns {string} 
 */
export function replaceAccessToken(str) {
  const reg = /(PDDAccessToken=|AccessToken=|"AccessToken":)\s*"?([^;,"]+)"?([;,]?\s*)/gi;
  return str && str.replace(reg, (match, p1, p2, p3) => `${p1}${p2.length}${p3}`);
}

/**
 * JSON.stringify  cyclic bug
 * @private
 * @returns {function(*, *): *}
 */
export function jsonStringifyReplacer() {
  const seen = [];
  return function (key, value) {
    if(value !== null && typeof value === 'object') {
      if(seen.indexOf(value) >= 0) {
        return;
      }
      seen.push(value);
    }
    return value;
  };
}

// 
export function memo(fn) {
  let cache = null;
  return (...args) => {
    if(!cache) {
      cache = fn.apply(null, args);
    }

    return cache;
  };
}

// platform
export const getSimplePlatform = memo(() => {
  const platform = getPlatformByUa(navigator.userAgent);
  const isNativePlatform = platform === PLATFORM.PddAndroid || platform === PLATFORM.PddIOS;
  const isTinyNativePlatform = platform === PLATFORM.PDDTinyAndorid;
  const version = isNativePlatform && getNativeVersion(navigator.userAgent, platform);
  return { isNativePlatform, isTinyNativePlatform, platform, version };
});
