/**
 * @file popLocalCipher.js
 * @author daben
 */
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getCurrentPlatform, compareVersion } from '@pdd/platform-util';
import { getShareModuleName, isLocalCipherSupport } from '../tools/NativeShareEnv';
import { getPageSN } from '../tools/getPageContext';
import trackError from '../ShareTracker/trackError';
import { CipherConsumeMode } from '../CipherConsumeMode';

/**
 * @typedef {object} ReadCipherActionType 
 * @property {number} QUERY_CODE 
 * @property {number} QUERY_TEXT 
 * @property {number} POP_CIPHER 
 */
const ReadCipherActionType = {
  QUERY_CODE: 0,
  QUERY_TEXT: 1,
  POP_CIPHER: 2
};

function formatCipher(template = '', code = '') {
  return template.replace(/%s/g, code);
}

let cipherTemplateCache = {};
function loadCipherText(scene) {
  return invokeBridgeCallNativeFun(
    getShareModuleName(),
    'getLocalCipherInfo',
    {
      action: ReadCipherActionType.QUERY_TEXT,
      page_sn: getPageSN() + '',
      scene
    }
  ).then((data = {}) => {
    const texts = (data.texts || []).map(m => {
      if (!m) {
        return;
      }
      if (m.version && compareVersion(m.version, window.__SHARE_CIPHER_VER__ || '0.0.1') >= 0) {
        return m.text;
      }
      if (!m.version) {
        return m.text;
      }
    }).filter(m => !!m);
    cipherTemplateCache[scene] = cipherTemplateCache[scene] || {};
    cipherTemplateCache[scene].texts = texts;
    return texts;
  }, (e = {}) => {
    trackError({
      errorCode: e.errorCode,
      message: '',
      options: e.data
    });
    return null;
  });
}

function loadCipherCode(scene) {
  return invokeBridgeCallNativeFun(
    getShareModuleName(),
    'getLocalCipherInfo',
    {
      action: ReadCipherActionType.QUERY_CODE,
      page_sn: getPageSN() + '',
      scene
    }
  ).then((data = {}) => {
    return data.ciphers || [];
  }, (e) => {
    trackError({
      errorCode: e.errorCode,
      message: '',
      options: e.data
    });
    return null;
  });
}

function popCipherCode(scene) {
  return invokeBridgeCallNativeFun(
    getShareModuleName(),
    'getLocalCipherInfo',
    {
      action: ReadCipherActionType.POP_CIPHER,
      page_sn: getPageSN() + '',
      scene
    }
  ).then(data => {
    return data.cipher;
  }, e => {
    trackError({
      errorCode: e.errorCode,
      message: '',
      options: e.data
    });
    return null;
  });
}

function getCipherTemplate({ consumeMode, scene }) {
  if (
    typeof consumeMode !== 'number' ||
    [
      CipherConsumeMode.CONSUME_RAW,
      CipherConsumeMode.RECYCLE_RAW
    ].indexOf(consumeMode) >= 0
  ) {
    return Promise.resolve();
  }
  const processText = txts => {
    if (!txts || txts.length <= 0) {
      return;
    }
    if ([
      CipherConsumeMode.CONSUME_TIME_DIVIDE,
      CipherConsumeMode.RECYCLE_TIME_DIVIDE
    ].indexOf(consumeMode) >= 0) {
      const date = new Date();
      const minute = date.getMinutes();
      const second = date.getSeconds();
      const index = Math.floor(txts.length * (minute * 60 + second) / 3600);
      return txts[index];
    }
    // n time_divide 
    if (CipherConsumeMode.RECYCLE_TIME_DIVIDE_MULTI === consumeMode) {
      const length = txts.length;
      const parallelChunksSize = Math.min(Math.max(parseInt(window.__SHARE_CIPHER_PARALLEL_SIZE__, 10) || 0, 2), length);
      // 1
      const targetInterval = Math.max(Math.floor(length / parallelChunksSize), 1);
      const targetChunkIndex = Math.floor(Math.random() * parallelChunksSize);
      const date = new Date();
      const minute = date.getMinutes();
      const second = date.getSeconds();
      const intervalIndex = Math.floor(targetInterval * (minute * 60 + second) / 3600);
      const index = Math.max(targetChunkIndex * targetInterval + intervalIndex, length - 1);
      return txts[index];
    }
    return txts[0];
  };
  const templates = (cipherTemplateCache[scene] || {}).texts;
  if (templates) {
    return Promise.resolve(processText(templates));
  }
  return loadCipherText(scene).then(processText);
}

function getCipherCode({ consumeMode, scene }) {
  if (typeof consumeMode !== 'number') {
    return Promise.resolve();
  }
  if (
    [
      CipherConsumeMode.CONSUME,
      CipherConsumeMode.CONSUME_TIME_DIVIDE,
      CipherConsumeMode.CONSUME_RAW
    ].indexOf(consumeMode) >= 0
  ) {
    return popCipherCode(scene);
  }
  return loadCipherCode(scene).then(ciphers => {
    if (!ciphers || ciphers.length <= 0) {
      return;
    }
    return ciphers[0];
  });
}

export default function popLocalCipher(option = {}) {
  const {
    scene,
    consumeMode
  } = option;
  if (!getCurrentPlatform().isNativePlatform || !isLocalCipherSupport() || !scene || typeof consumeMode !== 'number') {
    return Promise.resolve();
  }
  return Promise.all([
    getCipherCode(option),
    getCipherTemplate(option)
  ]).then((data = []) => {
    const [cipherData, template] = data;
    if (
      cipherData &&
      [
        CipherConsumeMode.CONSUME_RAW,
        CipherConsumeMode.RECYCLE_RAW
      ].indexOf(consumeMode) >= 0
    ) {
      return cipherData;
    }
    if (cipherData && template) {
      return Object.assign({
        text: formatCipher(template, cipherData.cipher)
      }, cipherData);
    }
  });
}
