import { forceHttps } from '../url';
import { encode, decode } from '../base64';
import { replaceImgDomain } from './replaceImgDomain';

const dropCDNParam = url => {
  return url.split('?')[0];
};

const IMAGE_PARAM_REG = /\/image\/(.+?)\//;
function getImageParamFromWatermark(watermark) {
  const matchRes = IMAGE_PARAM_REG.exec(watermark);
  return matchRes && matchRes[1];
}
/**
 * watermarkwidth
 * @returns {string}
 */
export const resizeWatermarkWidth = ({ watermark, width }) => {
  if (!width) {
    return watermark;
  }
  const imageParam = getImageParamFromWatermark(watermark);
  if (!imageParam) {
    return watermark;
  }
  const resizedWatermarkUrl = decode(imageParam) + `?imageMogr2/thumbnail/${width}x`;
  const newImageParam = encode(resizedWatermarkUrl);
  return watermark.replace(imageParam, newImageParam);
};

/**
 * imageView2
 *  [](https://cloud.tencent.com/document/product/460/6929)
 * @returns {string}
 */
function buildImageView2(param) {
  const { mode, width, height, format, quality, rQuality, lQuality } = param;
  const result = [];
  if (mode && (width || height)) {
    result.push(`/${mode}`);
    if (width) {
      result.push(`/w/${width}`);
    }
    if (height) {
      result.push(`/h/${height}`);
    }
  }
  if (quality) {
    result.push(`/q/${quality}`);
  }
  if (format) {
    result.push(`/format/${format}`);
  }
  if (rQuality) {
    result.push(`/rq/${rQuality}`);
  }
  if (lQuality) {
    result.push(`/lq/${lQuality}`);
  }
  return result.length ? `imageView2${result.join('')}` : '';
}

/**
 * imageMogr2
 * @returns {string}
 */
function buildImageMogr2(param) {
  const { sharpen, thumbnail, radius, sigma, custom = '' } = param;
  const commands = custom ? [custom] : [];
  if (sharpen) {
    commands.push(`/sharpen/${sharpen}`);
  }
  if (radius && sigma) {
    commands.push(`/blur/${radius}x${sigma}`);
  }
  // thumbnailwidth
  if (thumbnail && thumbnail.width) {
    commands.push(`/thumbnail/${thumbnail.width}x`);
  }

  return commands.length ? `imageMogr2${commands.join('')}` : '';
}

/**
 * watermark
 *  [](https://cloud.tencent.com/document/product/460/6930)
 * @returns {string}
 */
function buildImageWatermark(param) {
  const { image, gravity, dx, dy, scatype, spcent } = param;
  const result = [];
  if (!image) {
    return '';
  }
  let blogo = param.blogo;
  if (!scatype && !spcent) {
    blogo = blogo || 2;
    result.push(`/blogo/${blogo}`);
  }
  // blogoscatype
  if (!blogo && scatype && spcent) {
    result.push(`/scatype/${scatype}/spcent/${spcent}`);
  }
  if (image) {
    result.push(`/image/${image}`);
  }
  if (gravity) {
    result.push(`/gravity/${gravity}`);
  }
  if (dx) {
    result.push(`/dx/${dx}`);
  }
  if (dy) {
    result.push(`/dy/${dy}`);
  }
  return result.length ? `watermark/1${result.join('')}` : '';
}

const isBoolean = value => typeof value === 'boolean';

function getPrefitWidthParam(prefitWidth) {
  return isBoolean(prefitWidth) && prefitWidth ? 400 : prefitWidth;
}

function getSharpenParam(sharpen) {
  return isBoolean(sharpen) ? 70 : sharpen;
}

function getWatermarkParam(watermark) {
  const param = typeof watermark === 'object' ? watermark : { image: watermark };
  return {
    addToFront: true,
    ...param
  };
}

function getBlurParam(blur = {}) {
  let { radius, sigma } = blur;
  if (radius || sigma) {
    sigma = sigma || 1;
    radius = radius || 1;
  }
  return { radius, sigma };
}

/**
 * @type {string}
 * @description 
 *  [](https://cloud.tencent.com/document/product/460/15293)
 */
const PIPE_CHAR = '%7C';

const getSearchFromParam = param => {
  const { webpEnable, quality, resizeWidth, plainWatermark, sharpen, prefitWidth, watermark, blur, imageMogr2 } = param;
  const { radius, sigma } = blur;
  let commands = [];

  // prefitWith
  commands.push(
    buildImageMogr2({
      thumbnail: { width: prefitWidth }
    })
  );

  let imageWatermarkCommand = plainWatermark || buildImageWatermark(watermark);
  // prefitWidth
  if (prefitWidth) {
    imageWatermarkCommand = resizeWatermarkWidth({
      watermark: imageWatermarkCommand,
      width: prefitWidth
    });
  }
  if (watermark.addToFront) {
    commands.push(imageWatermarkCommand);
  }

  commands.push(
    buildImageMogr2({
      sharpen,
      radius,
      sigma,
      custom: imageMogr2
    })
  );

  commands.push(
    buildImageView2({
      mode: '2',
      width: resizeWidth,
      quality,
      format: webpEnable ? 'webp' : ''
    })
  );

  if (!watermark.addToFront) {
    commands.push(imageWatermarkCommand);
  }

  commands = commands.filter(command => !!command);
  return commands.length ? '?' + commands.join(PIPE_CHAR) : '';
};

export const DEFAULT_PARAM = { resizeWidth: 1300, quality: 80 };
const isHttp = url => url.indexOf('http') === 0 || url.indexOf('//') === 0;

/**
 * url
 *
 * [](https://cloud.tencent.com/document/product/460/6924)
 * @param {string} url - url, 
 * @param {object} [param = { resizeWidth: 1300, quality: 80 }]  { resizeWidth: 1300, quality: 80 }
 * @param {number} param.quality  0-100 
 * @param {object} param.blur  gif 
 * @param {number} param.blur.radius  radius 1-50
 * @param {number} param.blur.sigma   sigma 0
 * @param {string} param.imageMogr2 imageMogr2`/thumbnail/100x100/cut/50x50x0x0`[](https://cloud.tencent.com/document/product/460/6924)
 * @param {string|object} param.watermark stringwatermarkwatermark.image
 * @param {string} param.watermark.image  URL  Base64 
 * @param {string} param.watermark.gravity [](https://cloud.tencent.com/document/product/460/6930#1) southeast
 * @param {number} param.watermark.dx 0
 * @param {number} param.watermark.dy 0
 * @param {number} param.watermark.scatype  scatype 1 scatype 2 scatype 3
 * @param {number} param.watermark.spcent scatype  scatype 1[1,1000]  scatype 2[1,1000]  scatype 3[1,250]
 * @param {boolean} param.watermark.addToFront 
 * @param {string} param.plainWatermark CDNplainWatermarkwatermark
 * @param {boolean|number} param.sharpen value 10-30070true70.
 * @param {number} param.resizeWidth resizeWidth
 * @param {boolean|number} param.prefitWidth plainWatermarkprefitWidthgravitysouthwestsoutheastsouthsouthtrue400
 * @param {boolean} param.webpEnable webp
 * @param {boolean} [param.enableGifToWebp = false] gifwebpissue #44
 * @returns {string} url
 * @example
 *
 * generateCDNImageUrl('//t04img.yangkeduo.com/images/2018-04-14/bf681b1161280ea98532bc4aff6a1af5.png');
 * // => //t04img.yangkeduo.com/images/2018-04-14/bf681b1161280ea98532bc4aff6a1af5.png?imageView2/2/w/1300/q/80
 */
export function generateCDNImageUrl(url, param = DEFAULT_PARAM) {
  if (process.env.NODE_ENV !== 'production') {
    if (!url) {
      throw new Error('generateCDNImageUrl: url');
    }
  } else {
    url = url || '';
  }

  if (!isHttp(url)) {
    return url;
  }

  url = replaceImgDomain(url);
  url = forceHttps(url);

  const hasQueryStr = url.split('?')[1];
  if (hasQueryStr) {
    if (process.env.NODE_ENV !== 'production') {
      console.warn('generateCDNImageUrl: urlquery');
    }
    return url;
  }

  const newParam = {
    ...param,
    sharpen: getSharpenParam(param.sharpen),
    prefitWidth: getPrefitWidthParam(param.prefitWidth),
    watermark: getWatermarkParam(param.watermark),
    blur: getBlurParam(param.blur)
  };

  // gif 
  if (url.indexOf('.gif') >= 0) {
    newParam.sharpen = false;
    // gifwebpissue #44
    newParam.webpEnable = newParam.enableGifToWebp ? newParam.webpEnable : false;
  }

  if (newParam.webpEnable) {
    newParam.quality = newParam.quality || 70;
  }
  return dropCDNParam(url) + getSearchFromParam(newParam);
}
