/**
 * pv,click,impr
 * @param {boolean} useYangkeduoLogingUrl yangkeduo.comiosset-Cookie
 * @returns {string|*} 
 */
function getLoggingUrl(useYangkeduoLogingUrl) {
    if (typeof window !== 'undefined' && window.__LogingUrl__) {
        return window.__LogingUrl__;
    }
    if (useYangkeduoLogingUrl) {
        return 'https://th.yangkeduo.com/t.gif';
    }
    return 'https://th.pinduoduo.com/t.gif';
}

/**
 * 
 * @param useYangkeduoLogingUrl
 * @returns {string|*}
 */
