//  hostname 
//  yangkeduo.com
//  appid  24866 
//   com yangkeduo mobile m (: m.mobile.yangkeduo.com)
function transferHostName(hostName) {
    const res = {
        hostName: hostName || '',
        hasThirdLevelDomain: false
    };
    if (!hostName) {
        res.hostName = '.';
        return res;
    }
    const MIN_DOMAIN_LEVELS = 3;
    // 
    const hostMapArr = ['m.ishangtong.com', 'm.yiqixiegushi.com', 'm.hutaojie.com'];
    const hostArr = hostName.split('.');
    const hostArrLen = hostArr.length;

    //  
    if (hostArrLen <= MIN_DOMAIN_LEVELS) {
        return res;
    }
    // 
    const firstLevelDomain = hostArr[hostArrLen - 2];
    for (let i = 0; i < hostMapArr.length; i++) {
        const item = hostMapArr[i];
        if (item.indexOf(firstLevelDomain) > 0) {
            res.hostName = item;
            res.hasThirdLevelDomain = true;
            return res;
        }
    }

    return res;
}

export default {

    /**
     * GET 
     *
     * @param {Object} opts 
        isModuleVersion ,
        hostName ,
        shareType ,
        defaultApiDomains api 
     * @return {Object} res 
     */
    get: function (opts) {
        opts = opts || {};
        let config;
        // hostname 
        const transferObj = transferHostName(opts.hostName);
        const hostName = transferObj.hostName || '.';

        const hostArr = hostName.split('.');
        if (opts.isModuleVersion) {
            const isHybridTest = typeof window !== 'undefined'
                ? location.href.indexOf('hutaojie') >= 0
                : hostName.indexOf('hutaojie') >= 0;
            config = mpConfigs[isHybridTest ? mps.hybridTest : mps.hybrid];
        } else if (hostArr.length >= 2 && hostMap[hostArr[1]]) {
            config = mpConfigs[mps[hostMap[hostArr[1]][hostArr[0]]]];
        }
        if (!config) {
            config = mpConfigs[mps.mobile];
        }
        config.hasThirdLevelDomain = transferObj.hasThirdLevelDomain || false;

        return init(config);
    }
};
