/**
 * @file showSpinnerNative.js
 * @author tenghu
 */
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getCurrentPlatform } from '@pdd/platform-util';

export function shouldUseNative() {
  return getCurrentPlatform().isNativePlatform;
}

export function showNative(msg, { hideBg } = {}) {
  const param = hideBg
    ? {}
    : {
      loading_style: '2',
      message: msg
    };

  invokeBridgeCallNativeFun('JSUIControl', 'showLoading', param);
}

export function hideNative() {
  invokeBridgeCallNativeFun('JSUIControl', 'hideLoading');
}
