/**
 * @file translateTPApp.js
 * @author daben
 */
import { getPageID, getPageSN } from '../tools/getPageContext';
import { createDefaultAxios } from '@pdd/isomorphic-request';
import { isTPAppShareSupport } from '../tools/NativeShareEnv';
import { pickUpRefer } from './processMiniObjectPath';
import { TPSelectCategory } from '../TPSelectCategory';

// 
const TRANSLATE_TP_PATH = 'api/flow/walrus/inquire/confusion/info';
// 
const TRANSLATE_TP_CARD_PATH = 'api/flow/chron/share/card';

/**
 * @ignore
 */
const DevType = {
  MAIN: 0,
  GENERAL: 1,
  TP: 2
};

function selectInfoByCategory(infos = [], category = TPSelectCategory.TP_FIRST) {
  let generals = [];
  let tps = [];
  let main = [];
  let others = [];
  infos.forEach((info) => {
    if (info) {
      switch (info.dev_type) {
        case DevType.GENERAL:
          generals.push(info);
          break;
        case DevType.TP:
          tps.push(info);
          break;
        case DevType.MAIN:
          main.push(info);
          break;
        default: others.push(info);
      }
    }
  });
  if (!isTPAppShareSupport()) {
    // 
    generals = tps = [];
  }
  return tps[0] || generals[0] || main[0];
}

/**
 * 
 * @param {object} opts 
 * @param {TPSelectCategory} category 
 */
export function translateTPAppCard(opts = {}, category) {
  const httpClient = createDefaultAxios({
    pageInfo: {
      pageId: getPageID()
    }
  }, {
    defaultErrorHandler: _ => { }
  });
  let paramMap = {
    scene_id: opts.activitySceneSn,
    long_url: opts.shareURL || '',
    rom: opts.rom || 0,
    matched_pkg_id: opts.matchedPkgId,
    param: {},
    page_sn: getPageSN(),
    ...pickUpRefer(opts)
  };
  if (opts.payloadMixin && typeof opts.payloadMixin === 'object') {
    paramMap = Object.assign({}, opts.payloadMixin['param_map'], paramMap);
  }
  if (typeof paramMap.param !== 'string') {
    paramMap.param = JSON.stringify(paramMap.param);
  }
  const promise = httpClient.post(TRANSLATE_TP_CARD_PATH, {
    param_map: paramMap
  });
  return promise.then((data = {}) => {
    const infos = (data.infos || []).filter(m => {
      return Object.keys(m).length > 0;
    });
    const info = selectInfoByCategory(infos, category);
    return {
      raw: data,
      info,
      tpInfos: infos,
      tpDowngrade: infos.length <= 0
    };
  });
}

export default function translateTPApp(opts = {}, category) {
  const httpClient = createDefaultAxios({
    pageInfo: {
      pageId: getPageID()
    }
  }, {
    defaultErrorHandler: _ => { }
  });
  let requestParams = {
    long_url: opts.shareURL || '',
    activity_code: (opts.marketActivityID || 0) + '',
    scene_sn: opts.activitySceneSn || '',
    rom: opts.rom || 0,
    matched_pkg_id: opts.matchedPkgId,
    params: {}
  };
  if (opts.payloadMixin && typeof opts.payloadMixin === 'object') {
    // payloadMixin.extra maybe exists. if it is used, here should assign extra
    requestParams = Object.assign(requestParams, opts.payloadMixin);
  }

  if (typeof requestParams.params !== 'string') {
    requestParams.params = JSON.stringify(requestParams.params);
  }

  const promise = httpClient.post(TRANSLATE_TP_PATH, requestParams);

  return promise.then((data = {}) => {
    const infos = (data.infos || []).filter(m => {
      return Object.keys(m).length > 0;
    });
    const info = selectInfoByCategory(infos, category);
    return {
      raw: data,
      info,
      tpInfos: infos,
      tpDowngrade: infos.length <= 0
    };
  });
}
