import { getCurrentPlatform, compareVersion, PLATFORM } from '@pdd/platform-util';
import ShareType from '../ShareType';

const getAppVersion = _ => {
  return getCurrentPlatform().version;
};

export const NATIVE_SAHRE_ERRORS = {
  USER_REFUSE: 60005,
  USER_CANCEL: 60006
};

// check tools
export const checkShareTypeSupport = shareType => {
  let result = false;
  if (typeof shareType !== 'number') {
    return false;
  }
  switch (shareType) {
    case ShareType.WECHAT_SESSION:
    case ShareType.WECHAT_TIMELINE:
    case ShareType.WECHAT_SESSION_APP:
    case ShareType.QQ_CHAT_SESSION:
    case ShareType.QQ_ZONE:
    case ShareType.WECHAT_TIMELINE_IMAGE:
    case ShareType.WECHAT_TIMELINE_IMAGE_WITH_NO_SDK:
    case ShareType.WECHAT_SESSION_IMAGE:
    case ShareType.QQ_CHAT_SESSION_IMAGE:
    case ShareType.QQ_ZONE_IMAGE:
    case ShareType.WECHAT_SESSION_TEXT_WITH_NO_SDK:
    case ShareType.WECHAT_SESSION_TEXT:
      result = true;
      break;
    case ShareType.WEIBO:
      result = validateVersion('4.81.0', '4.81.0');
      break;
    case ShareType.WEIBO_IMAGE:
      result = validateVersion('4.81.0', '4.81.0');
      break;
    case ShareType.WECHAT_SESSION_IMAGE_WITH_NO_SDK:
    case ShareType.WECHAT_SESSION_IMAGES_WITH_NO_SDK:
      result = validateVersion('4.37.0', '4.36.0');
      break;
    case ShareType.WECHAT_TIMELINE_TEXT:
      result = validateVersion('4.39.0', '4.39.0');
      break;
    case ShareType.IOS_SYSTEM_SHARE_CARD:
    case ShareType.WECHAT_SESSION_CARD_WITH_NO_SDK:
    case ShareType.WECHAT_TIMELINE_CARD_WITH_NO_SDK:
      result = checkIOSSystemShare();
      break;
    case ShareType.SHARE_IMAGE_PASSIVELY:
      result = validateVersion('4.49.0', '4.49.0');
      break;
    case ShareType.COPY_LINK:
    case ShareType.COPY_CIPHER:
      result = true;
      break;
    case ShareType.WECHAT_SESSION_MUSIC:
    case ShareType.WECHAT_SESSION_VIDEO:
      result = validateVersion('4.86.0', '4.86.0');
      break;
    // ambiguous usage
    // set default to true
    case ShareType.FRIENDS:
      result = true;
      break;
    // only used in IOS
    // platform irrelevant
    // read enable flag from shareInfo
    case ShareType.WECHAT_BIG_IMAGE:
      result = true;
      break;
    case ShareType.PIN_TIMELINE:
      result = validateVersion('5.6.0', '5.6.0');
      break;
    default:
      break;
  }

  return result;
};

function validateVersion(iosVersion = '', androidVersion = '', tinyAndroidVersion = '') {
  const version = getAppVersion();
  return (
    (getCurrentPlatform().platform === PLATFORM.PddIOS && compareVersion(version, iosVersion) >= 0) ||
    (getCurrentPlatform().platform === PLATFORM.PddAndroid && compareVersion(version, androidVersion) >= 0) ||
    // js
    getCurrentPlatform().platform === PLATFORM.PDDTinyAndorid
    // compareVersion(version, tinyAndroidVersion || androidVersion) >= 0
  );
}

export const checkWeChatSessionShareImageNoSdk = () => {
  return validateVersion('4.37.0', '4.36.0');
};

export const checkIOSSystemShare = () => {
  const version = getAppVersion();
  return getCurrentPlatform().platform === PLATFORM.PddIOS && compareVersion(version, '5.80.0') >= 0;
};

export const checkSupportShareCipher = () => {
  return validateVersion('4.74.0', '4.74.0', '4.80.0');
};

export const isEnableShareCheck = () => {
  return validateVersion('4.51.0', '4.51.0');
};

export const isTPAppShareSupport = () => {
  return validateVersion('4.85.0', '4.85.0');
};

export const checkSupportShareMedia = () => {
  return validateVersion('4.86.0', '4.86.0');
};

export const checkSupportSilentWakeUp = () => {
  if (getCurrentPlatform().platform === PLATFORM.PDDTinyAndorid) {
    return false;
  }
  return validateVersion('4.88.0', '4.88.0');
};

export const isLocalCipherSupport = () => {
  // todo
  return validateVersion('5.41.0', '5.41.0');
};

// history code
// current `JSShare` now is an alias of `AMShare`
export const getShareModuleName = () => {
  return 'JSShare';
};

export const DEFAULT_SHARE_ICON_SIZE = 22;
export const DEFAULT_SHARE_ICON =
  'data:image/png;base64,';
