import { parseQuery, getCookie, setCookie, removeCookie, buildQuery } from '@pdd/core-util';
import { getUserInfo } from '@pdd/native-util';
import { CookieKey, LocalStorageKey } from '../common/constants';
import { UnLoginError } from '../common/errorClass';
import { loginWithCode } from './service';
import { getCurrentPlatform, compareVersion } from '@pdd/platform-util';
import navigation from '@pdd/navigation';

const platform = process.env.BROWSER ? getCurrentPlatform() : null;

function clearCode() {
  navigation.replaceURL(
    location.origin + location.pathname + '?' + buildQuery({ ...parseQuery(location.search), code: null, state: null })
  );
}

function setCookieValue(key, value, attributes = { maxAge: 3600 * 24 * 100 }) {
  if(value && getCookie(key) !== value) {
    setCookie(key, value, attributes);
  }
}

/**
 * @param {string} accessToken
 * @param {number} uid
 * @param {string} uin
 * @ignore
 */
export function setUserCookie(accessToken, uid, uin) {
  // 100
  setCookieValue(CookieKey.AccessToken, accessToken);
  setCookieValue(CookieKey.UID, uid);
  setCookieValue(CookieKey.UIN, uin);
}

/**
 * @ignore
 */
export function clearUserCookie() {
  removeCookie(CookieKey.AccessToken);
  removeCookie(CookieKey.UID);
  removeCookie(CookieKey.UIN);
}

/**
 * @param {string} accessToken
 * @param {number} uid
 * @param {string} uin
 * @ignore
 */
export function setUserLocalstorage(accessToken, uid, uin) {
  accessToken && localStorage.setItem(LocalStorageKey.AccessToken, accessToken);
  uid && localStorage.setItem(LocalStorageKey.UID, (+uid).toString(36));
  uin && localStorage.setItem(LocalStorageKey.UIN, uin);
}

/**
 * @ignore
 */
export function clearUserLocalStorage() {
  localStorage.removeItem(LocalStorageKey.AccessToken);
  localStorage.removeItem(LocalStorageKey.UID);
  localStorage.removeItem(LocalStorageKey.UIN);
}

/**
 * cookielocalStorageaccessToken
 *
 * @return {string}
 * @ignore
 */
export async function getAccessTokenFromLocal() {
  return platform.isNativePlatform
    ? getUserInfo()
        .then(v => v.access_token)
        .catch(() => getCookie(CookieKey.AccessToken))
    : getCookie(CookieKey.AccessToken);
}

/**
 * cookielocalStorageuid
 *
 * @return {string}
 * @ignore
 */
export async function getUidFromLocal() {
  return platform.isNativePlatform
    ? getUserInfo()
        .then(v => v.user_id)
        .catch(() => getCookie(CookieKey.UID))
    : getCookie(CookieKey.UID);
}

/**
 * cookielocalStorageuin
 *
 * @return {string}
 * @ignore
 */
export async function getUinFromLocal() {
  if(platform.isNativePlatform) {
    return getUserInfo()
      .then(v => v.uin || getCookie(CookieKey.UIN))
      .catch(() => getCookie(CookieKey.UIN));
  }

  return getCookie(CookieKey.UIN);
}

/**
 * @ignore
 */
export function callNativeUserInfo() {
  return getUserInfo().then((userInfo) => {
    if(!userInfo.access_token) {
      throw new UnLoginError();
    }
    return {
      accessToken: userInfo.access_token,
      uid: userInfo.user_id,
      uin: userInfo.uin,
      type: userInfo.type
    };
  });
}

/**
 * @param {AxiosInstance} axiosHttp @pdd/isomorphic-request
 *
 * 
 * cookiecodenative api
 * 1. native api
 * 2. cookie ssrservercodecookiecookiecode
 * cookiecsr
 * 3. code
 *
 * @return {Promise<{UserInfo}>}
 * @ignore
 */
export function fetchAccessTokenAndUid(axiosHttp) {
  const cookies = getCookie();
  const accessToken = cookies[CookieKey.AccessToken];
  const uid = cookies[CookieKey.UID];
  const uin = cookies[CookieKey.UIN];

  // todo: uiduin
  const getDefaults = () => {
    return accessToken ? Promise.resolve({ accessToken, uid, uin }) : Promise.reject(new UnLoginError());
  };

  // 1. 
  if(platform.isNativePlatform) {
    return callNativeUserInfo().then(
      data => ({
        ...data,
        // uincookie
        uin: data.uin || (data.accessToken === accessToken ? uin : null)
      }),
      // cookie
      getDefaults
    );
  }

  const { code, state } = parseQuery(location.search);
  code && clearCode(); // ssrcode

  // 2. cookie
  if(accessToken) {
    return getDefaults();
  }

  // 3. code
  return code ? loginWithCode(axiosHttp, { code, state }) : getDefaults();
}

/**
 * nativemodal,Modal
 * @ignore
 * @returns {boolean}
 */
export function isNativeSupportSilentLogin() {
  return platform.isNativePlatform && compareVersion(platform.version, '5.25.0') >= 0;
}

/**
 * 
 */
export function isUseUniformUrl() {
  return platform.isNativePlatform && compareVersion(platform.version, '5.99.0') >= 0;
}
