import React from 'react';
import cx from 'classnames';
import { withStyles } from '@pdd/cui-util';
import styles from './index.iso.scss';

/**
 * @visibleName Toast.BaseToast
 */
function BaseToast({ className, ...otherProps }) {
  return <div {...otherProps} className={cx(styles.root, className)} role='alert' />;
}

export default withStyles(styles)(BaseToast);
