/**
 *  CRC32
 * https://stackoverflow.com/questions/18638900/javascript-crc32
 */

function utf8ToAscii(str) {
  let result = '';
  for (let i = 0; i < str.length; i++) {
    let charcode = str.charCodeAt(i);
    if (charcode < 0x80) {
      result += String.fromCharCode(charcode);
    } else if (charcode < 0x800) {
      result += String.fromCharCode(0xc0 | (charcode >> 6)) + String.fromCharCode(0x80 | (charcode & 0x3f));
    } else if (charcode < 0xd800 || charcode >= 0xe000) {
      result +=
        String.fromCharCode(0xe0 | (charcode >> 12)) +
        String.fromCharCode(0x80 | ((charcode >> 6) & 0x3f)) +
        String.fromCharCode(0x80 | (charcode & 0x3f));
    } else {
      charcode = 0x10000 + (((charcode & 0x3ff) << 10) | (str.charCodeAt(++i) & 0x3ff));
      result +=
        String.fromCharCode(0xf0 | (charcode >> 18)) +
        String.fromCharCode(0x80 | ((charcode >> 12) & 0x3f)) +
        String.fromCharCode(0x80 | ((charcode >> 6) & 0x3f)) +
        String.fromCharCode(0x80 | (charcode & 0x3f));
    }
  }
  return result;
}

function makeCRC32Table() {
  const crcTable = [];
  let c;
  for (let n = 0; n < 256; n++) {
    c = n;
    for (let k = 0; k < 8; k++) {
      c = c & 1 ? 0xedb88320 ^ (c >>> 1) : c >>> 1;
    }
    crcTable[n] = c;
  }
  return crcTable;
}

/**
 * CRCTable
 * @type {Array}
 */
const crcTable = makeCRC32Table();

/**
 * crc32
 *
 * @param {string} str 
 * @param {number} [crc=0] crc32 append 
 * @returns {number} crc32
 * @example
 *
 * crc32('hello') // =>907060870
 */
export function crc32(str, crc = 0) {
  str = utf8ToAscii(str);
  crc = crc ^ -1;
  for (let i = 0; i < str.length; i++) {
    crc = (crc >>> 8) ^ crcTable[(crc ^ str.charCodeAt(i)) & 0xff];
  }
  return (crc ^ -1) >>> 0;
}
