import { throwErrorAsync } from '@pdd/core-util';

/**
 * 
 * @param {object} options
 * @param {string} options.errorMsg - 
 * @param {object} [options.payload] - 
 * @param {Error} [options.error] -  payload 
 */
export default function recordError({ errorMsg, payload, error }) {
  if (!process.env.BROWSER) {
    return;
  }
  const throwError = error || new Error(errorMsg);
  throwErrorAsync(throwError, { payload, error_msg: errorMsg });
}
