import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import { getCurrentPlatform, compareVersion, PLATFORM } from '@pdd/platform-util';
import recordError from './recordError';

let useImageSdk;
export default async function checkImageProtocol() {
  if (typeof useImageSdk !== 'undefined') {
    return useImageSdk;
  }
  const { platform, version } = getCurrentPlatform();
  if (platform !== PLATFORM.PddIOS || compareVersion(version, '5.87.0') < 0) {
    useImageSdk = false;
    return useImageSdk;
  }
  try {
    const imageProtocol = await invokeBridgeCallNativeFun('WebScene', 'checkImageProtocol');
    useImageSdk = imageProtocol.use_image_sdk;
    return useImageSdk;
  } catch (e) {
    recordError({ error: e });
  }
}
