import appConfig from '@pdd/configs/app';
import { getCurrentPlatform, isWebBundle, PLATFORM, SYSTEM } from '@pdd/platform-util';
import { throwErrorAsync } from '@pdd/core-util';
import { invokeBridgeCallNativeFun } from '@pdd/native-util';

const SIGN_URL = 'weixin/jsapi/signature';
//  iOS  url 
const url = process.env.BROWSER && getCurrentPlatform().system === SYSTEM.IOS ? location.href.split('#')[0] : null;

// 
export function fetchWxConfig(http, pageSN) {
  const nonce = Math.random().toString().substring(2);
  const timestamp = Date.now().toString();
  const config = appConfig.get(location.host);
  const wechatAppID = config.ApiKey.WeChat;

  const params = {
    page_sn: pageSN,
    timestamp,
    noncestr: nonce,
    // eslint-disable-next-line
    open_app_id: wechatAppID,
    url: url || location.href.split('#')[0]
  };

  return http.get(SIGN_URL, { params }).then((data) => {
    return {
      timestamp,
      nonceStr: nonce,
      signature: data.signature,
      wechatAppID: data.mirror ? data.open_app_id : wechatAppID, // use tiny domain config instead
      shareTypeEnable: data.share_type_enable,
      shareDomain: data.share_domain,
      mirror: data.mirror,
      magicTrace: data.magic_trace
    };
  }).catch((error) => {
    throwErrorAsync(error);
    return {
      timestamp,
      nonceStr: nonce,
      wechatAppID
    };
  });
}


/**
 * 
 */
export async function getHybridPackVersion(hybridPackName) {
  if(!hybridPackName || !isWebBundle()) {
    return;
  }

  const name = `com.xunmeng.pinduoduo.${hybridPackName}`;
  try {
    const { bundles: [bundle] = [] } = await invokeBridgeCallNativeFun(
      'AMComponent', 'info', { name }, '4.0.0', '4.0.0'
    );

    if(bundle) {
      return getCurrentPlatform().platform === PLATFORM.PddIOS ? bundle.version : bundle;
    }
  } catch (e) {
    return null;
  }
}
