import getConfig from './getConfig';
import InitialUrlCalculator from './InitialUrlCalculator';

let calculator;
export default function getInitialUrl(url, uid) {
  if (!calculator) {
    calculator = new InitialUrlCalculator(getConfig());
  }
  return calculator.getInitialUrl(url, uid);
}
