/**
 * parseURL
 * @file url.js
 * @author  daben
 */

class URLParser {
  constructor(url = '') {
    this.hostname = '';
    this.origin = '';
    this.pagename = '';
    this.pathname = '';
    this.port = 0;
    this.protocol = '';
    this.query = {};
    this.search = '';
    this.hashTag = '';
    this.parseURL(url);
  }

  parseURL(url) {
    const splitHash = url.split('#');
    const noHashTag = splitHash[0] || '';
    this.hashTag = splitHash[1] || '';
    const parts = noHashTag.split('?');
    let path = parts[0] || '';
    const query = parts[1] || '';
    this.search = '?' + query;
    this.query = (query.split('&') || []).reduce((accu, curr = '') => {
      const keyValue = curr.split('=');
      let key;
      let value;
      try {
        key = decodeURIComponent(keyValue[0] || '');
        value = decodeURIComponent(keyValue[1] || '');
      } catch (e) {
        key = '';
      }
      if (key) {
        accu[key] = value;
      }
      return accu;
    }, {});
    const protocolReg = /^([a-zA-Z]+:)\/\//;
    const protocolMatch = path.match(protocolReg) || [];
    this.protocol = protocolMatch[1] || '';
    const hostnameReg = /^((:?[a-zA-Z0-9](:?[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?\.)+[a-zA-Z]{2,6})(:\d+)?\//;
    if (protocolMatch[0]) {
      path = path.replace(protocolMatch[0], '');
    }
    const hostnameResult = path.match(hostnameReg) || [];
    const [hostname, port] = (hostnameResult[0] || '').replace(/\//g, '').split(':');
    this.hostname = hostname;
    this.port = parseInt(port || 0, 10);
    if (hostnameResult[0]) {
      path = path.replace(hostnameResult[0], '');
    }

    if (this.protocol && this.hostname) {
      this.origin = `${this.protocol}//${this.hostname}`;
    }

    this.pathname = '/' + path.replace(/^\//, '');
    const pagenameResult = path.match(/\/?(\w+\.html)$/) || [];
    this.pagename = pagenameResult[1] || '';
  }
}

export default URLParser;
