const REG = {
  MiniProgram: /miniprogram/i,
  WeChat: /MicroMessenger/i,
  QQ: /QQ\/[\d\.]+\s+/i, //eslint-disable-line
  QQAndQzone: /(QQ\/[\d\.]+\s+)|Qzone/i, //eslint-disable-line
  QQApp: /QQ\//i,
  Weibo: /Weibo/i,
  Ios: /iphone|ipad|ipod/i,
  IosVersion: /os (\d+)_(\d+)_?(\d+)?/i,
  Android: /Android/i,
  AndroidVersion: /Android (\d+).?(\d+)?.?(\d+)?/i,
  AndroidNative: /phh_android_version/i,
  IosNative: /phh_ios_version/i,
  IosApiRequest: /BundleID\/com.xunmeng.pinduoduo/i,
  Mobile: /Android|webOS|iPhone|iPad|iPod/i,
  AndroidNativeVersion: /(phh_android_version)\/([^\s]+)\s*/i,
  IosNativeVersion: /(phh_ios_version|AppVersion)\/([^\s]+)\s*/i,
  MecoWebViewCore: /MecoCore\/(\d)/i,
  MecoWebViewSdk: /MecoSDK\/(\d)/i
};

/**
 * @typedef {object} PLATFORM
 * @property {string} Unknown uaplatform
 * @property {string} PddIOS uaplatformios native app
 * @property {string} PddAndroid uaplatformandroid native app
 * @property {string} WeChat uaplatform
 * @property {string} Weibo uaplatform
 * @property {string} QQ uaplatformQQ
 * @property {string} QQApp uaplatformQQ
 * @property {string} WxApp uaplatform
 */
export const PLATFORM = {
  Unknown: 'unknown',
  PddIOS: 'ios',
  PddAndroid: 'android',
  PDDTinyAndorid: 'tinyAndroid',
  WeChat: 'wechat',
  Weibo: 'weibo',
  QQ: 'qq',
  QQApp: 'qqapp',
  WxApp: 'wxapp'
};
/**
 * @typedef {object} SYSTEM
 * @property {string} Android uaandroid
 * @property {string} IOS uaios
 * @property {string} Unknown uaunknown
 */
export const SYSTEM = {
  Android: 'android',
  IOS: 'ios',
  Unknown: 'unknown'
};

/**
 * Native app
 * @ignore
 * @param ua
 * @param platform
 * @returns {string}
 */
export function getNativeVersion(ua, platform) {
  const regNativeVersion = platform === PLATFORM.PddAndroid ? REG.AndroidNativeVersion : REG.IosNativeVersion;
  const matchVersions = ua.match(regNativeVersion);
  if (matchVersions) {
    return matchVersions[2] || '';
  }
  return '';
}

/**
 * uaMecosdk
 * @ignore
 * @private
 * @param ua
 * @returns {*}
 */
export function isMecoWebView(ua) {
  return REG.MecoWebViewCore.test(ua) && REG.MecoWebViewSdk.test(ua);
}

/**
 * x.x.x
 * @param {string} v1 1
 * @param {string} v2 2
 * @returns {number} v1>v20v1=v20v1<v20
 */
export function compareVersion(v1, v2) {
  const validate = function (version) {
    if (typeof version !== 'string') {
      throw new TypeError('Invalid argument expected string');
    }
  };
  [v1, v2].forEach(validate);
  const toIntArray = function (version) {
    return version.split('.').map(token => {
      return parseInt(token, 10) || 0;
    });
  };
  const parts1 = toIntArray(v1);
  const parts2 = toIntArray(v2);
  const length = Math.max(parts1.length, parts2.length);
  for (let i = 0; i < length; ++i) {
    const part1 = parts1[i] || 0;
    const part2 = parts2[i] || 0;
    if (part1 !== part2) {
      return part1 - part2;
    }
  }
  return 0;
}

/**
 *  nodefalseprotocolamcomponent:
 * @return {boolean}
 */
export function isWebBundle() {
  return process.env.BROWSER && window.location.protocol === 'amcomponent:';
}

/**
 * @ignore
 * @private
 * @param {object} cache  object
 * @param key key
 * @param val keyval
 * @returns {*} key
 */
export function getCache(cache, key, val) {
  if (typeof cache[key] === 'undefined') {
    cache[key] = val;
  }
  return cache[key];
}

/**
 * uaminiprogram
 * @param ua
 * @returns {boolean}
 */
function isMiniProgram(ua = '') {
  // uaminiprogram
  return REG.MiniProgram.test(ua) || (typeof window !== 'undefined' && window.__wxjs_environment === 'miniprogram');
}

/**
 * ua
 * @ignore
 * @param {string} ua
 */
export function getPlatformByUa(ua = '') {
  if (REG.AndroidNative.test(ua)) {
    if (ua.includes('app_type/tiny')) {
      return PLATFORM.PDDTinyAndorid;
    }
    return PLATFORM.PddAndroid;
  }
  if (REG.IosNative.test(ua) || REG.IosApiRequest.test(ua)) {
    return PLATFORM.PddIOS;
  }
  if (REG.WeChat.test(ua)) {
    // miniprogram
    return isMiniProgram(ua) ? PLATFORM.WxApp : PLATFORM.WeChat;
  }
  if (REG.Weibo.test(ua)) {
    return PLATFORM.Weibo;
  }
  if (REG.QQApp.test(ua) && isMiniProgram(ua)) {
    return PLATFORM.QQApp;
  }
  if (REG.QQAndQzone.test(ua)) {
    return PLATFORM.QQ;
  }
  return PLATFORM.Unknown;
}

/**
 * Android Ios unknown
 * @ignore
 * @param ua
 * @return {string}
 */
export function getSystem(ua) {
  if (REG.Ios.test(ua)) {
    return SYSTEM.IOS;
  }
  if (REG.Android.test(ua)) {
    return SYSTEM.Android;
  }
  return SYSTEM.Unknown;
}

/**
 * 
 * @ignore
 * @param {string} ua
 * @returns {string}
 */
export function getSystemVersion(ua) {
  const system = getSystem(ua);
  let regSystemVersion = '';
  if (system === SYSTEM.IOS) {
    regSystemVersion = REG.IosVersion;
  }
  if (system === SYSTEM.Android) {
    regSystemVersion = REG.AndroidVersion;
  }
  if (regSystemVersion) {
    const SystemVersions = ua.match(regSystemVersion);
    const formatVersions = SystemVersions
      ? [SystemVersions[1], SystemVersions[2], SystemVersions[3]].map(version => {
        return version ? parseInt(version, 10) : 0;
      })
      : [];
    return formatVersions.join('.');
  }
  return '';
}

/**
 * 
 * @ignore
 * @param ua
 * @returns {boolean}
 */
export function isMobilePlatform(ua = '') {
  return REG.Mobile.test(ua);
}
