import { crc32, getCookie } from '@pdd/core-util';
import {
  getReporterUrl,
  getAppInfo,
  getRunningAppId,
  isObject,
  getDefaultExtraInfo,
  getRealPagePath,
  ERROR_TYPE_CONFIG,
  CATEGORY
} from './utils';
import { getSimplePlatform } from '../common/utils';
import { getSystem, getSystemVersion } from '@pdd/platform-util/lib/common';

export function baseSendPmmError(params, appInfo) {
  const reportUrl = getReporterUrl(params?.datas[0]?.type, appInfo);
  const { isNativePlatform } = getSimplePlatform();
  if(isNativePlatform && window.pinbridge) {
    window.pinbridge.callNative('JSNetwork', 'request', {
      method: 'POST',
      url: reportUrl,
      headers: { 'Content-Type': 'text/plain;charset=UTF-8' },
      data: JSON.stringify(params)
    });
    return;
  }
  const http = new XMLHttpRequest();
  http.open('POST', reportUrl, true);
  http.setRequestHeader('Content-Type', 'text/plain;charset=UTF-8');
  http.withCredentials = true;
  http.send(JSON.stringify(params));
}

export function getOrganizedData(param = {}, appInfo = {}) {
  const { bizSide, appId, testAppId } = appInfo;
  const {
    app_version,
    log_version,
    network,
    page,
    page_id,
    page_name,
    page_sn,
    page_url,
    payload,
    platform,
    refer_page_id,
    refer_page_name,
    refer_page_sn,
    user_agent,
    user_id,
    error_msg, // index_inline 
    error_message, // api error  error_message
    serverIp = '',
    httpCode = '',
    http_code,
    httpMethod = '',
    errorCode = '555555', // 
    error_code, // api error  error_code
    module = '',
    pageId = '', //  mcm  pageId
    referPageId = '', //  mcm  pageId
    type,
    url,
    lvalues,
    fvalues,
    ...elseParam
  } = param;
  const defaultExtraInfo = getDefaultExtraInfo();

  let isResourceError;
  let imgErrorUrl;
  if(error_msg?.indexOf('lazy_image_error') > -1 || error_msg?.indexOf('cui_image_error') > -1) {
    isResourceError = true;
    imgErrorUrl = payload?.src;
  }
  const isNormalLog = type === ERROR_TYPE_CONFIG.NORMAL_LOG;
  const extras = transferToString({ ...defaultExtraInfo, ...payload });
  const { sub_op: subOp } = extras || {};
  const isApiError = subOp === 'api_error';

  //  type ->  -> 
  const errorType =
    type ||
    (isResourceError && ERROR_TYPE_CONFIG.RESOURCE_ERROR) ||
    (isNormalLog && ERROR_TYPE_CONFIG.NORMAL_LOG) ||
    ERROR_TYPE_CONFIG.CUSTOM_ERROR;
  const outerData = getOuterDataStructure({ bizSide, appId, testAppId, app_version });
  const commonTag = getCommonTag(platform, user_id);
  let tags = getTags(
    {
      app_version,
      log_version,
      network,
      page,
      page_id,
      page_name,
      page_sn,
      page_url,
      refer_page_id,
      refer_page_name,
      refer_page_sn,
      user_agent,
      error_msg,
      // eslint-disable-next-line camelcase
      error_message: error_message || extras.error_message,
      serverIp,
      // eslint-disable-next-line camelcase
      httpCode: httpCode || http_code,
      httpMethod,
      errorCode,
      error_code,
      module,
      pageId,
      referPageId,
      url: url || imgErrorUrl
    },
    appInfo,
    isApiError
  );

  if(isNormalLog) {
    const stringElseParam = transferToString(elseParam);
    tags = { ...tags, ...stringElseParam };
  }

  outerData.common_tags = commonTag;
  outerData.datas = [
    {
      category: type === ERROR_TYPE_CONFIG.NORMAL_LOG ? CATEGORY.LOG : CATEGORY.ERROR,
      type: errorType,
      timestamp: Date.now(),
      tags,
      extras,
      ...(type === ERROR_TYPE_CONFIG.NORMAL_LOG
        ? {
            lvalues,
            fvalues
          }
        : {}) //  
    }
  ];

  return outerData;
}

let versionNumber;
// eslint-disable-next-line camelcase
export function getOuterDataStructure(options) {
  const { bizSide, appId, testAppId, app_version } = options || {};
  const appInfo = getAppInfo({ bizSide, appId, testAppId });
  const time = Date.now();
  const randomIntVal = 10 ** 5 + Math.floor(Math.random() * (10 ** 6 - 10 ** 5));
  versionNumber =
    versionNumber !== undefined
      ? versionNumber
      : Number(
          // eslint-disable-next-line camelcase
          (app_version || '')?.split('.').reduce((sum, value, index) => {
            if(index === 0) {
              return sum + value;
            }
            return `${sum}${value.length < 2 ? `0${value}` : value}`;
          }, '')
        );
  return {
    biz_side: appInfo.bizSide,
    app: appInfo.appId,
    /* 0  1 debug 2 test */
    level: 2,
    /* version int 0.x.x, kibana */
    version: versionNumber,
    /* time */
    report_time_ms: time,
    rand_num: randomIntVal,
    crc32: crc32(`${time}-${randomIntVal}`)
  };
}

let pddId;
// eslint-disable-next-line camelcase
export function getCommonTag(platform, user_id) {
  const runningAppId = getRunningAppId(platform);
  pddId = pddId !== undefined ? pddId : getCookie('ETag');
  const systemVersion = getSystemVersion(navigator.userAgent);
  const system = getSystem(navigator.userAgent);
  return {
    p: platform,
    runningPlatform: platform,
    runningAppId: runningAppId,
    b: '',
    did: '',
    mid: '',
    osV: `${system}_${systemVersion}`,
    pid: pddId,
    // eslint-disable-next-line camelcase
    uid: user_id ? user_id + '' : ''
  };
}

// extra  [key: string] 
export function transferToString(payload) {
  if(!isObject(payload)) {
    return {};
  }
  const keys = Object.keys(payload);
  const temp = {};

  keys.forEach((key) => {
    const val = payload[key];
    temp[key] = String(val);
  });

  return temp;
}

export function getTags(params = {}, appInfo = {}, isApiError = false) {
  const { universalPaths, universalQueryKeys, reportPath } = appInfo;
  const {
    app_version,
    // eslint-disable-next-line camelcase
    log_version = '0.0.1',
    network,
    page = '',
    page_id,
    page_name,
    page_sn,
    page_url,
    refer_page_id,
    refer_page_name,
    refer_page_sn,
    user_agent,
    error_msg,
    error_message,
    serverIp = '',
    httpCode = '',
    httpMethod = '',
    errorCode = '555555', // 
    error_code,
    module = '',
    pageId = '', //  mcm  pageId
    referPageId = '', //  mcm  pageId
    url
  } = params;
  const realPathPath = getRealPagePath(universalPaths, universalQueryKeys, reportPath);

  return {
    network: (network || '0') + '', // network 0=UNKNOWN1=WIFI2=2G3=3G4=4G
    serverIp,
    url, // urlAPI
    // eslint-disable-next-line camelcase
    errorCode: String(error_code || errorCode), // 
    // eslint-disable-next-line camelcase
    errorMsg: (error_msg || error_message || '').substr(0, 150), // errorMsg 150
    module, //  n  https://note.pdd.net/doc/348857296509386752
    pageId, // IDMCM  https://volantis3.pdd.net/mcm/page pageId > pagePath > pageSn
    pagePath: realPathPath, //  MCM  https://volantis3.pdd.net/mcm/page,  pageId > pagePath > pageSn
    // eslint-disable-next-line camelcase
    pageSn: page_sn ? page_sn + '' : '', // https://yolo.pdd.net/?app=c,  pageId > pagePath > pageSn
    // eslint-disable-next-line camelcase
    pageUrl: page_url ? page_url : window.location.href,
    referPageId: referPageId,
    // eslint-disable-next-line camelcase
    page_id: page_id ? page_id + '' : '',
    // eslint-disable-next-line camelcase
    refer_page_id: refer_page_id ? refer_page_id + '' : '',
    // eslint-disable-next-line camelcase
    referPageName: refer_page_name ? refer_page_name : '',
    // eslint-disable-next-line camelcase
    referPageSn: refer_page_sn ? refer_page_sn + '' : '',
    // eslint-disable-next-line camelcase
    pageName: page_name ? page_name : '',
    page: page, // page  
    log_version: log_version,
    // eslint-disable-next-line camelcase
    user_agent: user_agent ? user_agent : window.navigator.userAgent,
    // eslint-disable-next-line camelcase
    app_version: app_version ? app_version : '',
    ...(isApiError
      ? {
          httpCode: String(httpCode), //
          httpMethod: httpMethod // http GETPOST
        }
      : {})
  };
}
