/**
 * 
 * @param {import('../Config/constantss').BackupDomainConfig} domainBucketsConfig - 
 * @param {number} bucketId - idid
 * @returns {string} -  or ''
 */
export default function getDomainByBucketId(domainBucketsConfig, bucketId) {
  const domains = Object.keys(domainBucketsConfig);
  const totalBuckets = domains.reduce((acc, domain) => {
    return acc + domainBucketsConfig[domain];
  }, 0);
  bucketId = bucketId % totalBuckets;
  let currentBuckets = 0;
  for (let domain in domainBucketsConfig) {
    currentBuckets += domainBucketsConfig[domain];
    if (bucketId < currentBuckets) {
      return domain;
    }
  }
  return '';
}
