/**
 * @file getCDNImgUrl.js
 * @author tenghu
 */
const DEFAULT_HOST = 'funimg.pddpic.com';
const PROTOCAL = 'https://';

/**
 * getCDNImgUrl
 * @param {string} rootRelativeUrl - url '/feedback/feedback_success.png'
 * @param {string} [host='funimg.pddpic.com'] - host
 * @returns string
 * @example
 *
 * getCDNImgUrl('/feedback/feedback_success.png') // => https://funimg.pddpic.com/feedback/feedback_success.png
 *
 */
export function getCDNImgUrl(rootRelativeUrl = '', host = DEFAULT_HOST) {
  if (process.env.NODE_ENV !== 'production') {
    const isValidRootRelativeUrl = /^(\/[^/]+)/.test(rootRelativeUrl);
    if (!isValidRootRelativeUrl) {
      throw new Error(`rootRelativeUrl${rootRelativeUrl}url`);
    }
    const isValidHost = /^[^/]+$/.test(host);
    if (!isValidHost) {
      throw new Error(`host: ${host}`);
    }
  }

  return `${PROTOCAL}${host}${rootRelativeUrl}`;
}
