import { getUid, getUin } from '@pdd/auth';
import { PLATFORM, getCurrentPlatform } from '@pdd/platform-util';
import { crc32, getCookie, nanoid, parseQuery, getReferInfo, memoize, getExtendInfoFromQuery } from '@pdd/core-util';

//  https://jira2.corp.yiran.com/browse/PDD-28952?filter=-3
let riskCookies = null;
export const getRiskControlParams = (time, op = '') => {
  if(!riskCookies) {
    const { pdd_vds = '', _nano_fp } = getCookie();
    riskCookies = { pdd_vds, _nano_fp };
  }

  const params = {
    cookie_fp: riskCookies._nano_fp,
    storage_fp: localStorage.getItem('_nano_fp') || ''
  };

  const { isNativePlatform, isTinyNativePlatform } = getCurrentPlatform();
  if(!isNativePlatform && !isTinyNativePlatform) {
    params.dcf = getDcf(time, riskCookies.pdd_vds, op);
  }

  return params;
};

// 
export const getBasicEnvParams = async (config) => {
  const time = Date.now();
  const { version, isTinyNativePlatform, platform } = getCurrentPlatform();

  const fields = {
    time,
    log_id: time + nanoid(16),
    user_id: '',
    uin: '',

    app_id: getAppID(config),
    /*page_name: pageManagerInstance.getPageName(),
    page_id: pageManagerInstance.getPageID(),
    page_sn: pageManagerInstance.getPageSN(),*/

    screen_width: window.screen.width, // 
    screen_height: window.screen.height, // 
    dpr: window.devicePixelRatio, // device pixel ratio
    app_version: version, // 
    // : weixin/qq/weibo/android/ios/unknow
    platform: isTinyNativePlatform ? PLATFORM.PddAndroid : platform
  };

  const [uid, uin] = await Promise.all([getUid(), getUin()]);
  fields.user_id = uid;
  fields.uin = uin;

  const wxCheckPdd = localStorage.getItem('WxCheckPdd');
  if(wxCheckPdd !== null) {
    fields.wx_check_pdd = parseInt(wxCheckPdd, 10) || 0;
  }

  return fields;
};

const getReferQuery = memoize(getReferInfo);
const getExtendQuery = memoize(getExtendInfoFromQuery);

// 
export const getPageManagerParams = async () => {
  const referQuery = await getReferQuery();
  // _x_xcx_trace_id 
  const extendQuery = getExtendQuery();

  return { ...referQuery, ...extendQuery };
};

const CHANNEL_KEYS = ['src', 'from', 'campaign', 'cid'];
const REFER_SHARE_KEYS = ['refer_share_id', 'refer_share_uid', 'refer_share_channel', 'refer_share_form'];
const REFER_SHARE_SHORT_MAP = {
  _sid: 'refer_share_id',
  _suid: 'refer_share_uid',
  _sc: 'refer_share_channel',
  _sf: 'refer_share_form'
};

//  pv, impr, click 
export const getTransformKeyParams = () => {
  const query = parseQuery(location.search);
  let params = CHANNEL_KEYS.reduce((params, key) => {
    if(query[key]) {
      params['refer_channel_' + key] = query[key];
    }
    return params;
  }, {});

  params = REFER_SHARE_KEYS.reduce((params, key) => {
    const value = query[key] || query[key.slice(0, 6)];
    if(value) {
      params[key] = value;
    }

    return params;
  }, params);

  // keykey
  for(const key in REFER_SHARE_SHORT_MAP) {
    const value = query[key];
    if(value) {
      const referKey = REFER_SHARE_SHORT_MAP[key];
      params[referKey] = value;
    }
  }
  return params;
};

// query
export const getQueryParams = (isPv) => {
  const query = parseQuery(location.search);
  const result = query.is_back === '1' ? { is_back: '1' } : {};
  const msgId = query.refer_channel_msgid || query.msgid; // add msgid
  if(msgId) {
    result.refer_channel_msgid = msgId;
  }

  if(isPv) {
    Object.entries(query).forEach(([key, value]) => {
      if(key.indexOf('_p_') === 0) {
        result[key] = value;
      }
    });
  }

  return result;
};

