import { pickUpRefer } from './processMiniObjectPath';
export const PaintImageMethod = {
  STATIC: 'static',
  DYNAMIC: 'dynamic'
};

export const processDrawImageOptions = (drawImageOptions) => {
  if (!drawImageOptions) {
    return null;
  }
  // if is an array, use first config
  if (Array.isArray(drawImageOptions) && drawImageOptions.length > 0) {
    return drawImageOptions[0];
  } else if (typeof drawImageOptions === 'object') {
    // if is object
    return drawImageOptions;
  }
  return null;
};

export const processDrawImageOptionsMuti = (drawImageOptions) => {
  if (!drawImageOptions) {
    return [];
  }
  if (Array.isArray(drawImageOptions) && drawImageOptions.length > 0) {
    return drawImageOptions.map((option, index) => {
      if (!option) {
        return null;
      }
      // image url
      if (typeof option === 'string') {
        return {
          type: PaintImageMethod.STATIC,
          data: option
        };
      } else if (typeof option === 'object') {
        if (option.type) {
          return option;
        }
        return {
          type: PaintImageMethod.DYNAMIC,
          options: option
        };
      }
    }).filter((obj) => {
      return !!obj;
    });
  } else if (typeof drawImageOptions === 'object') {
    return [{
      type: PaintImageMethod.DYNAMIC,
      options: drawImageOptions
    }];
  }
  return [];
};

/**
 * processDrawImage
 * @param  {ShareEntity}  shareEntity
 * @param  {Boolean} isMutiImage if is image array
 * @return {Promise} resolve draw image options
 */
export function processDrawImage(shareEntity, isMutiImage, extra = {}) {
  const drawImageOptions = shareEntity.drawImageOptions;
  if (typeof drawImageOptions === 'function') {
    extra = {
      shareRefer: pickUpRefer(shareEntity),
      ...(extra || {})
    };
    return drawImageOptions(extra);
  } else if (typeof drawImageOptions === 'object') {
    if (isMutiImage) {
      return Promise.resolve(processDrawImageOptionsMuti(drawImageOptions));
    }
    return Promise.resolve(processDrawImageOptions(drawImageOptions));
  }
  return Promise.resolve({});
}
