import {
  isTinyDomainType,
  isShortLinkType
} from '../tools/URLWrapType';
import ShareForm from '../ShareForm';
import LimitMethod from '../ShareLimiter/LimitMethod';

export function shareEntityToParams(shareEntity = {}) {
  return {
    title: shareEntity.title,
    message: shareEntity.message,
    thumb_url: shareEntity.thumbnailURL,
    share_url: shareEntity.shareURL
  };
}

// CIPHERsince 19/04/16
// LINKsince 19/04/16
// DOMAINsince 19/04/16
// NORMAL
// IMAGE
// COPY
export function shareEntityToMethod(shareEntity = {}, shareType) {
  if (
    isShortLinkType(shareEntity.urlWrapType) &&
    shareEntity.marketActivityID &&
    shareEntity.getShareForm(shareType) === ShareForm.COMMAND
  ) {
    return LimitMethod.CIPHER;
  } else if (
    isShortLinkType(shareEntity.urlWrapType) &&
    shareEntity.marketActivityID &&
    shareEntity.getShareForm(shareType) === ShareForm.TEXT
  ) {
    return LimitMethod.LINK;
  } else if (
    isTinyDomainType(shareEntity.urlWrapType) &&
    shareEntity.marketActivityID &&
    shareEntity.getShareForm(shareType) === ShareForm.CARD
  ) {
    return LimitMethod.DOMAIN;
  } else if (
    shareEntity.getShareForm(shareType) === ShareForm.IMAGE
  ) {
    return LimitMethod.IMAGE;
  }
  return LimitMethod.NORMAL;
}
