import recordError from '../utils/recordError';
import getURLObject from '../utils/getURLObject';
import { getRetryCount } from './retryCountHelper';

export default function recordLoadError(el, useImageSdk) {
  const src = el.getAttribute('src'); // el.srccss

  let errorMsg;
  let host;
  if (!src) {
    errorMsg = 'cui_image_error_empty_src';
  } else {
    const retryCount = getRetryCount(el);
    errorMsg = retryCount ? `cui_image_error_retry_${retryCount}` : 'cui_image_error';
    host = getURLObject(src).host;
  }

  recordError({
    errorMsg,
    payload: { src, host, useImageSdk }
  });
}
