import React, { PureComponent } from 'react';
import { firstScreen, pageStartStr, firstPaintStr, firstScreenStr, pageEndStr } from './statistics';

/**
 * <head>
 */
export const PageStart = () => (
    <script
        dangerouslySetInnerHTML={{
            __html: pageStartStr,
        }}
    />
);

/**
 * </head><body>
 */
export const FirstPaint = () => (
    <script
        dangerouslySetInnerHTML={{
            __html: firstPaintStr,
        }}
    />
);

/**
 * 
 */
export class FirstScreen extends PureComponent {
    componentDidMount() {
        if (!window._plt.fs) {
            firstScreen(); // csr
        }
    }

    render() {
        return (
            <script
                dangerouslySetInnerHTML={{
                    __html: firstScreenStr,
                }}
            />
        );
    }
}

/**
 * </body>
 */
export const PageEnd = () => (
    <script
        dangerouslySetInnerHTML={{
            __html: pageEndStr,
        }}
    />
);
