/**
 * DynamicTrace
 * @file ShareContentCheck.js
 * @author  daben
 */
import { getPageID, getPageSN, getPageName } from '../tools/getPageContext';
import { createDefaultAxios } from '@pdd/isomorphic-request';

const PATH = 'api/flow/audience/share/trace';

export default function (method = '', opts = {}) {
  const httpClient = createDefaultAxios({
    pageInfo: {
      pageId: getPageID()
    }
  }, {
    defaultErrorHandler: _ => {}
  });
  let promise = null;
  let params = {};
  params.origin = opts.platform || '';
  params.share_params = opts.shareParams || {};
  params.page_sn = opts.pageSn || (getPageSN() + '');
  params.page_info = opts.pageInfo || {
    page_name: getPageName(),
    page_url: location && location.href
  };
  if (opts.payloadMixin && typeof opts.payloadMixin === 'object') {
    params = Object.assign(params, opts.payloadMixin);
  }

  if (method.toLowerCase() === 'post') {
    promise = httpClient.post(PATH, params);
  } else {
    promise = httpClient.get(PATH, { params });
  }
  return promise.then((data = {}) => {
    return data.magic_trace;
  });
}
