import React, { useContext } from 'react';

export const ChunkContext = React.createContext({});

/**
 * @typedef {Object} ClientChunk  ClientChunk 
 * @property {*} data Chunk 
 * @property {boolean} isReady 
 * @property [function(): Promise<*>] retriever 
 */

/**
 *  client  key  chunk 
 * @param {string} key  chunk  key
 * @return {ClientChunk | ServerChunk}  Chunk 
 */
export const useChunk = key => {
  const chunk = useContext(ChunkContext)[key];

  if(process.env.NODE_ENV === 'development' && !chunk) {
    throw new Error(`chunkDataKey: ${key}  Chunk `);
  }

  return chunk;
};
