/**
 * @file MenuWatcher/index.js
 * @author daben
 */
import ShareEventType from '../ShareEventType';
import ShareController from '../ShareController/base';
import ErrorCode from './LimitErrorCode';

class MenuWatcher {
  constructor(opts = {}) {
    this._shareController = null;
    this._onPerformShareErrorHandler = null;
  }

  listen(shareController) {
    if (!shareController || !(shareController instanceof ShareController)) {
      return;
    }
    this.disconnect();
    this._shareController = shareController;
    this._setupListener(shareController);
  }

  disconnect() {
    this.removeListener();
  }

  _setupListener(shareController) {
    this.removeListener();
    this._onPerformShareErrorHandler = this._onPerformShareError.bind(this);
    shareController.on(ShareEventType.PERFORM_LOG_FAILED, this._onPerformShareErrorHandler);
  }

  removeListener() {
    if (!this._shareController) {
      return;
    }
    this._onPerformShareErrorHandler && this._shareController.removeListener(
      ShareEventType.PERFORM_LOG_FAILED,
      this._onPerformShareErrorHandler
    );
  }

  _onPerformShareError(payload = {}) {
    const error = payload.error || {};
    const shareType = payload.shareType;
    const errorCode = parseInt(error.errorCode, 10) || 0;
    if ([
      ErrorCode.REACH_DAY_LIMIT,
      ErrorCode.REACH_HOUR_LIMIT
    ].indexOf(errorCode) >= 0) {
      this._shareController.emit(ShareEventType.SHARE_REACH_LIMIT, {
        shareType,
        errorCode: errorCode
      });
      // hide menu
      this.hideShareMenuOption(shareType);
    }
  }

  hideShareMenuOption(shareType) {
    const shareController = this._shareController;
    if (!shareType || !shareController) {
      return;
    }
    shareController.disableShare(shareType);
  }
}

export default MenuWatcher;
