/**
 *
 * @param {Function} func func
 * @param {number} [cacheSize] cacheSize key
 * @param {Function} [resolver] cacheKey
 * @return {Function} Returns the new memoized function.
 */
export function memoize(func, cacheSize = 100, resolver) {
  if (typeof func !== 'function' || (resolver != null && typeof resolver !== 'function')) {
    throw new TypeError('Expected a function');
  }
  const cache = [];
  const cachesKeys = [];
  return function(...args) {
    const key = resolver ? resolver.apply(this, args) : args[0];
    const index = cachesKeys.indexOf(key);
    if (index > -1) {
      return cache[index];
    }
    const result = func.apply(this, args);
    cache.push(result);
    cachesKeys.push(key);
    if (cache.length > cacheSize) {
      // cache
      cachesKeys.shift();
      cache.shift();
    }
    return result;
  };
}

/**
 *  @pdd/error-logger
 * @param {Error} error 
 * @param {Object} [extraInfo] 
 */
export function throwErrorAsync(error, extraInfo) {
  setTimeout(() => {
    error.extraInfo = extraInfo;
    throw error;
  });
}

/**
 * leading=true, trailing=false
 * @param {Function} func 
 * @param {number} wait ms
 * @returns {Function}
 */
export function throttle(func, wait) {
  let result;
  let previous = 0;

  return function() {
    const now = Date.now();
    if (now - previous >= wait) {
      previous = now;
      result = func.apply(this, arguments);
    }
    return result;
  };
}

/**
 * reject
 * @param {Promise} promise 
 * @param {number} time ms
 * @param {string} [errMsg] 
 * @return {Promise} promise
 */
export function rejectOnTimeout(promise, time, errMsg) {
  return new Promise((res, rej) => {
    setTimeout(() => {
      rej(new Error(errMsg || `Timeout: cost over ${time}ms`));
    }, time);

    promise.then(res).catch(rej);
  });
}
