/**
 * @file LoadErrorHandler.js
 * @description
 * 
 */
import { getRetryCount, setRetryCount } from './retryCountHelper';
import recordLoadError from './recordLoadError';
import isHttp from '../utils/isHttp';
import checkImageProtocol from '../utils/checkImageProtocol';

const RETRY_DURATION_UNIT = 400; // 

/**
 * img
 * img.complete
 * img.naturalHeightsrcimg.naturalHeight0
 */
const isLoadFailed = img => img.complete && img.naturalHeight === 0;
// retryresetretry
const resetWhenRetryFinished = img => setRetryCount(img, 0);
const sleep = async ms => new Promise(resolve => setTimeout(resolve, ms));

export default class LoadErrorHandler {
  constructor({ config, getFallbackUrl, retryDurationUnit }) {
    this._config = config;
    this._getFallbackUrl = getFallbackUrl;
    this._retryDurationUnit = retryDurationUnit || RETRY_DURATION_UNIT;
  }

  /**
     * img
     * @param {HTMLImageElement} img - 
     * @returns {Function} - cleanup
     */
  handleLoadError(img) {
    this._handleError = async () => {
      const useImageSdk = await checkImageProtocol();
      recordLoadError(img, useImageSdk);
      if (!useImageSdk) {
        if (img.getAttribute('src')) {
          if (!isHttp(img.src)) {
            return;
          }
          // img.srccss
          await this._retry(img);
        }
      }
    };
    this._handleLoad = () => {
      resetWhenRetryFinished(img);
    };
    img.addEventListener('error', this._handleError);
    img.addEventListener('load', this._handleLoad);

    // 
    if (isLoadFailed(img)) {
      this._handleError(img);
    }

    return () => {
      img.removeEventListener('error', this._handleError);
      img.removeEventListener('load', this._handleLoad);
    };
  }

  _loadFallbackUrl(img) {
    if (!isLoadFailed(img)) {
      return;
    }
    const retryCount = getRetryCount(img);
    img.src = this._getFallbackUrl(img.src, retryCount);
    setRetryCount(img, retryCount + 1);
  }

  /**
     * 
     * @description
     * 1. 
     * 2. 0ms
     */
  async _retry(img) {
    const retryCount = getRetryCount(img);

    if (retryCount >= this._config.getRetryLimit()) {
      resetWhenRetryFinished(img);
      return;
    }

    await sleep(retryCount * this._retryDurationUnit);
    this._loadFallbackUrl(img);
  }
}
