import WechatPlatform from './wechat';
import NativePlatform from './native';
import QQPlatform from './qq';
import WeiboPlatform from './weibo';
import UnknownPlatform from './unknown';
import { getPlatformByUa, PLATFORM } from './common';
import TinyNativePlatform from './tinyNative';

const cachedUA = [];
const cachedPlatformByUa = new Map();
/**
 * uaPlatform
 * @param {string} ua user-agent
 * @returns {IPlatform} uaIPlatform
 */
export function getPlatform(ua = '') {
  if (cachedPlatformByUa.has(ua)) {
    return cachedPlatformByUa.get(ua);
  }
  const platform = getPlatformByUa(ua);
  let platformInstance;
  switch (platform) {
    case PLATFORM.PddAndroid:
    case PLATFORM.PddIOS:
      platformInstance = new NativePlatform(ua);
      break;
    case PLATFORM.QQ:
    case PLATFORM.QQApp:
      platformInstance = new QQPlatform(ua);
      break;
    case PLATFORM.Weibo:
      platformInstance = new WeiboPlatform(ua);
      break;
    case PLATFORM.WxApp:
    case PLATFORM.WeChat:
      platformInstance = new WechatPlatform(ua);
      break;
    case PLATFORM.PDDTinyAndorid:
      platformInstance = new TinyNativePlatform(ua);
      break;
    default:
      platformInstance = new UnknownPlatform(ua);
  }
  cachePlatformByUa(ua, platformInstance);
  return platformInstance;
}

function cachePlatformByUa(ua, platform) {
  cachedUA.push(ua);
  cachedPlatformByUa.set(ua, platform);
  if (cachedUA.length > 10) {
    const uaForRemove = cachedUA.shift();
    cachedPlatformByUa.delete(uaForRemove);
  }
}

/**
 * uaplatform
 * @returns {IPlatform} uaIPlatform
 */
export function getCurrentPlatform() {
  if (!process.env.BROWSER) {
    console.warn('');
    return;
  }
  const currentUA = window.navigator.userAgent;
  return getPlatform(currentUA);
}

export { compareVersion, SYSTEM, PLATFORM, isWebBundle } from './common';
