import { getCurrentPlatform, compareVersion, PLATFORM } from '@pdd/platform-util';

// [ type = 1toast](https://docs.pdd.net/pinbridge/ui/JSAlert.html#toast)
const ANDROID_TOAST_VERSION = '4.75.0';
const IOS_TOAST_VERSION = '5.52.0';

export default function supportNativeToast() {
  const platform = getCurrentPlatform();
  if (platform.platform === PLATFORM.PddIOS) {
    return compareVersion(platform.version, IOS_TOAST_VERSION) >= 0;
  }
  if (platform.platform === PLATFORM.PddAndroid) {
    return compareVersion(platform.version, ANDROID_TOAST_VERSION) >= 0;
  }
  return false;
}
