import { useState, useEffect } from 'react';

// 
export const useScale = ({
    outerContainerRef,
    contentRef,
    minScale = 0.5,
}, deps = []) => {
    const [visible, setVisible] = useState(false);
    const [scale, setScale] = useState(1);
    const [containerWidth, setContainerWidth] = useState(0);

    useEffect(() => {
        const outerContainerWidth = outerContainerRef.current.offsetWidth;
        const contentWidth = contentRef.current.offsetWidth;
        setContainerWidth(outerContainerWidth);
        if (contentWidth > outerContainerWidth) {
            setScale(Math.max(minScale, outerContainerWidth / contentWidth));
        }
        setVisible(true);
    }, deps);

    return {
        visible,
        scale,
        containerWidth
    };
};
