import { LEO_MOUNTED_ATTR_NAME, LEO_RES_MOUNTED_NAME } from '../../internal/constants';
import { logError } from '../../internal/assert';
import clientInit from '../csr/clientInit';

/**
 * appleoKey
 * @param {Array} keyArray
 */
export function useLeoKeys (keyArray) {
  return function (App) {
    if (App[LEO_MOUNTED_ATTR_NAME]) {
      throw new Error(`${App.name} duplicate declare`);
    } else if (!Array.isArray(keyArray)) {
      throw new Error('needs an array for its 1st param');
    }
    App[LEO_MOUNTED_ATTR_NAME] = keyArray || [];
    if (process.env.BROWSER) {
      clientInit(keyArray);
    }
    return App;
  };
}

/**
 * key
 *
 * @param {String} leoKey key
 * @param {Object} requestContext 
 * @returns {Object} leoKeyformatternull
 */
export function readLeo (leoKey, requestContext) {
  let result;
  requestContext = requestContext || {};
  try {
    if (process.env.BROWSER) {
      result = window.leo.data[leoKey];
    } else {
      result = requestContext.__res[LEO_RES_MOUNTED_NAME].data[leoKey];
    }
  } catch (ex) {
    process.env.NODE_ENV === 'development' && console.log(ex);
    logError(requestContext.__req, `BusinessOrFrameworkError_${leoKey}`, ex);
  }
  if (result === undefined) {
    logError(requestContext.__req, `leoDataNotFoundForKey_${leoKey}`);
    if (process.env.NODE_ENV === 'development') {
      throw new Error(`Unable to find leo data with key[${leoKey}]`);
    }
  }
  return result;
}

export { Formatter } from './formatter';
