import { appendQuery, getPathnameWithSearchAndHash, resolveFullUrl } from '@pdd/core-util';
import URLParser from './URLParser';

export default function buildURL(searchs = {}, url = '', origin = '') {
  // 
  if (window.location.href.indexOf('amcomponent://') === 0) {
    origin = origin || new URLParser(url).origin;
  }
  url = appendQuery(searchs, resolveFullUrl(url || location.href));
  if (origin) {
    origin = origin.replace(/\/$/g, '');
    return `${origin}${getPathnameWithSearchAndHash(url)}`;
  }
  return url;
}
