/**
 * @file client.js axios
 * @author zongheng(zongheng@pinduoduo.com)
 */
import { create } from './create';
import { getCurrentPlatform, isWebBundle } from '@pdd/platform-util';
import nativeRequestAdapter from './adapters/native';
import clientRequestAdapter from './adapters/client';
import { isHutaojie } from './util/env';
import {
  requestLogger,
  appendHeader,
  extendQuery,
  bizErrorCheck,
  responseErrorHandler,
  retrieveData,
  tracker
} from './plugins';

const TEST = '//apiv2.hutaojie.com/';

const PROD = 'https://api.pinduoduo.com/';

/**
 * @type {createClientAxios}
 */
export const createAxios = ({ pageInfo, useGlbDomain }, defaultRequestConfig) => {
  const { isNativePlatform, isTinyNativePlatform } = getCurrentPlatform();
  const isNative = isNativePlatform || isTinyNativePlatform;

  const { http, usePlugins } = create({
    baseURL: getBaseURL(useGlbDomain || isNative),
    withCredentials: true,
    adapter: isNative ? nativeRequestAdapter : clientRequestAdapter,
    headers: {},
    ...defaultRequestConfig
  });

  const plugins = [
    bizErrorCheck(),
    requestLogger(pageInfo),
    appendHeader(),
    extendQuery(),
    responseErrorHandler(),
    retrieveData()
  ];

  if(process.env.NODE_ENV === 'development' && window.DEBUG) {
    plugins.unshift(tracker());
  }

  usePlugins(plugins);

  return http;
};

function getBaseURL(useGlbDomain) {
  useGlbDomain = useGlbDomain || location.hostname === 'csr.pddpic.com';
  const protocol = isWebBundle() ? 'https:' : location.protocol;
  const baseURL = isHutaojie(location.href) ? protocol + TEST : PROD;
  const proxyBase = location.protocol + '//' + location.host + '/proxy/api/';

  return useGlbDomain ? baseURL : proxyBase;
}
