import React from 'react';
import SpinnerH5 from '../SpinnerH5';
import { renderPopup } from '@pdd/cui-util';

let hideSpinner;
export function hideH5() {
  hideSpinner && hideSpinner();
  hideSpinner = null;
}
/**
 * @param {string} msg - 
 * @param {[object]} options - SpinnerH5
 * @param {[object]} options.hideBg - 
 */
export function showH5(msg, options = {}) {
  hideSpinner && hideSpinner();
  hideSpinner = renderPopup(<SpinnerH5 {...options}>{msg}</SpinnerH5>);
}
