import { assert } from '../../internal/assert';
import grayTestBase from '@pdd/gray-test-base';

function assertABJsonFormat (json) {
  assert(!!json, 'nullJson');
  assert(typeof json.salt === 'string' && json.salt.startsWith('cp'), 'abFormatInvalidSalt');
  assert(typeof json.bucketCount === 'number', 'abFormatInvalidBucketCount');
  assert(!!json.models && typeof json.models === 'object', 'abFormatInvalidModels');
}

/**
 * Leo../util/processors
 */
export const Formatter = {
  Default: (val) => val,
  ABTest: {
    // SSR + urlCSR)normalwindow.leo.data
    normal: (val, options) => {
      options = options || {};
      const uid = options.identity ? options.identity.uid : 0;
      let json = null;
      try {
        if (typeof val === 'string') {
          json = JSON.parse(val);
        } else {
          json = val;
        }
      } catch (ex) {
        throw new Error('invalidJsonFormat1');
      }
      assertABJsonFormat(json);
      // falsetrue
      return grayTestBase.getTestStrategy(json, uid, false);
    },
    // CSR + fallbackleo)window.leo.fallbackData
    fallback: (val) => {
      let json = null;
      if (typeof val === 'string') { // devMockobject
        try {
          json = JSON.parse(val);
        } catch (ex) {
          throw new Error('invalidJsonFormat2');
        }
      } else {
        json = val;
      }
      assertABJsonFormat(json);
      Object.keys(json.models).forEach(key => {
        json.models[key] = {
          blackList: [],
          whiteList: [],
          buckets: json.models[key].buckets
        };
      });
      return json;
    }
  }
};

export function getFormatterFunc ({ leoKey, formatter, csrFallback }) {
  let formatterFunc = Formatter.Default;
  if (typeof formatter === 'object' && formatter) {
    // formatterobjectnormalfallback
    const normalFuncExisted = typeof formatter.normal === 'function';
    const fallbackFuncExisted = typeof formatter.fallback === 'function';
    assert(normalFuncExisted && fallbackFuncExisted, `[leoKey=${leoKey}'] both normal and fallback methods needed`);
    formatterFunc = csrFallback ? formatter.fallback : formatter.normal;
  } else if (typeof formatter === 'function') {
    formatterFunc = formatter;
  }
  return formatterFunc;
}
