import logger from '@pdd/logger';

export function assert (val, message) {
  if (!val) {
    throw new Error(message || 'leoAssertError');
  }
}

export function logError (req, info, obj) {
  if (process.env.BROWSER) {
    logger.trackingRecord({
      op: 'real_error',
      error_message: '6011Error_' + info,
      page: JSON.stringify(obj),
      page_url: location.href
    });
  } else if (req && req.app && req.app.logger && req.cat) {
    req.app.logger.error(6011, { reqId: req.reqId, err: JSON.stringify(obj) });
    req.cat.logError('6011Error_' + info, obj);
  }
}

export function logInfo (req, info, obj) {
  if (req && req.app && req.app.logger) {
    req.app.logger.info(info, obj);
  }
}
