import { getPathnameWithSearchAndHash, getExtendInfoFromQuery, parseUrlQuery, appendQuery } from '@pdd/core-util';

/**
 * querystringpathName,url:"index.html?id=1""/index.html"
 * @ignore
 * @private
 * @param url
 * @returns {*}
 */
export function getPathName(url) {
  const pathName = getPathnameWithSearchAndHash(url);
  return pathName.split('?')[0];
}

/**
 * refer_x_
 *
 * @param  {string} url url
 * @return {string}     url
 */
export const addPageTransferParameters = function (url) {
  if (!url) {
    return;
  }
  const urlQuery = parseUrlQuery(url);
  const referParams = {};

  const referPageELSN = urlQuery.page_el_sn;
  if (referPageELSN) {
    referParams.refer_page_el_sn = referPageELSN;
  }

  // _x_
  const extendParams = getExtendInfoFromQuery(true);

  return appendQuery({ ...extendParams, ...urlQuery, ...referParams }, url);
};

/**
 * app forwardrefer
 * @param params
 * @returns {null}
 */
export const getTransientReferPageParams = function (url) {
  if (!url) {
    return null;
  }
  return Object.entries(parseUrlQuery(url)).reduce((pre, [key, value]) => {
    if (key.startsWith('refer_') || key.startsWith('_ex_')) {
      const newKey = key.replace(/^refer_/, '').replace(/^_ex_/, '');
      if (newKey && value) {
        pre = pre || {};
        pre[newKey] = value;
      }
    }
    return pre;
  }, null);
};
