import { resolveFullUrl, getCDNImgUrl, getExtendInfoFromQuery } from '@pdd/core-util';
import ShareType from './ShareType';
import ShareEventType from './ShareEventType';
import ShareManager from './ShareManager';
import ShareInfoFilter from './ShareInfoFilter';
import ShareWaterMark from './ShareWaterMark';
import URLParser from './tools/URLParser';
import { app as applicationConfig } from '@pdd/configs';
import { getCurrentPlatform } from '@pdd/platform-util';
import URLWrapType from './tools/URLWrapType';
import buildURL from './tools/buildURL';
import VenderAppIDCache from './tools/VenderAppIDCache';
import MagicTraceManager from './tools/MagicTraceManager';
// const constant = applicationConfig.constants;

let config = {};
if (typeof window !== 'undefined') {
  config = applicationConfig.get(location.host) || {};
}

const NEED_REMOVED_QUERIES = {
  code: null,
  from: null,
  isappinstalled: null,
  at: null,
  share_id: null,
  is_back: null,
  page_id: null,
  list_id: null
};

const REFER_FILTER_KEYS = [
  'refer_page_name',
  'refer_page_section',
  'refer_page_element',
  'refer_page_id',
  'refer_page_sn',
  'refer_page_el_sn',
  'refer_mid',
  'refer_adg',
  'refer_cam',
  'refer_ced'
];

function addWaterMark(url = '', tail = '', forceChange = false) {
  url = url || '';
  // judge without tag
  if (url.indexOf('!') >= 0 && !forceChange) {
    return url;
  } else {
    url = url.split('!')[0];
  }
  url += tail;
  return url;
}

let instance = null;
class PDDShare {
  constructor(opts = {}) {
    this._hasInited = false;
    this.opts = opts || {};
    this.shopConfig = opts.shopConfig;
    this.defaultShareEntityConfig = {};
    this.shareManager = null;
    this.shareInfoFilter = null;
    this._sdkPrepared = false;
  }

  /**
   * 
   */
  setup() {
    if (this._hasInited) {
      return;
    }
    this._hasInited = true;
    this._setupShareInfoFilter();
    this._setupShareManager();
    !(this.opts.shareManagerConfig || {}).noDefaultShare && this._prepareDefaultShare();
    this.waitAppIDCacheBuild();
  }

  /**
   * @ignore
   */
  _setupShareInfoFilter() {
    if (!this.shareInfoFilter) {
      this.shareInfoFilter = new ShareInfoFilter(this.opts.shareInfoFilterConfig);
    }
  }

  /**
   * @ignore
   */
  _setupShareManager() {
    if (!this.shareManager) {
      const managerConfig = Object.assign({}, {
        noDefaultShare: false,
        disableShare: false,
        useNativeShareButton: false,
        loadErrorRetry: 1,
        enableInfiniteRetry: false
      }, this.opts.shareManagerConfig);
      this.shareManager = new ShareManager(managerConfig);
    }
    // shareManager setup 
    this._managerReadyPromise = this._managerReadyPromise || this.shareManager.setup();
    this._managerReadyPromise.then(_ => {
      this._managerReady = true;
    }).catch(_ => {
      this._managerReadyPromise = null;
    });
    return this._managerReadyPromise;
  }

  /**
   * @ignore
   */
  _prepareDefaultShare() {
    // 
    // 
    if (getCurrentPlatform().isWeChatMiniProgram) {
      return;
    }
    Object.assign(this.defaultShareEntityConfig, {
      title: ``,
      message: ``,
      thumbnailURL: getCDNImgUrl('/base/logo.jpg'),
      shareURL: '/'
    });
    this.prepareShare(this.defaultShareEntityConfig, undefined, {
      skipShareContentCheck: true
    });
  }

  /**
   * shareInfo
   * ( , 
   * @param {object} shareInfoPack 
   * @param {ShareType} shareType 
   * @param {object} opts 
   * @returns {promise} 
   */
  async _prepareShareByShareType(shareInfoPack = {}, shareType, opts) {
    this.setup();
    if (!this._managerReady) {
      await this.waitManagerReady();
    }
    const {
      entityConfig = {}
    } = shareInfoPack;
    // prepare share entity
    this._buildThumbnailURL(
      entityConfig,
      shareType
    );
    // prepare default share url
    this._buildShareURL(entityConfig, shareType);
    return this.shareManager.prepareShare(shareInfoPack, shareType, opts);
  }

  /**
   *  / qq /  
   * prepareShareSafe
   * shareInfoshareTypeshareType
   * @param {object} shareInfo 
   * @param {ShareType} shareType 
   * @param {object} opts 
   * @returns {promise} 
   */
  prepareShare(shareInfo, shareType, opts = {}) {
    this.setup();
    // filter params
    const shareInfoPack = this.shareInfoFilter.filter(shareInfo);
    //
    const result = this._prepareShareByShareType(shareInfoPack, shareType, opts);
    // sdkshareInfoshareInfo
    !this._sdkPrepared && result.then(_ => {
      this._checkSDKPrepared(shareInfoPack);
    }, _ => {
      this._checkSDKPrepared(shareInfoPack);
    });
    return result;
  }

  /**
   *  / qq /  
   * prepareShare
   * @param {object} shareInfo 
   * @param {ShareType} shareType 
   * @returns {promise} 
   * @example
   * await pddShare.prepareShareSafe({
   *   title: 'hello',
   *   message: 'hello',
   *   shareURL: 'index.html'
   * })
   *
   * @example
   * await pddShare.prepareShareSafe({
   *   title: 'hello',
   *   message: 'hello',
   *   shareURL: 'index.html'
   * }, ShareType.WECHAT_SESSION)
   */
  prepareShareSafe(shareInfo = {}, shareType) {
    this.setup();

    // filter params
    const shareInfoPack = this.shareInfoFilter.filter(shareInfo);
    const {
      flowControllConfig = {}
    } = shareInfoPack;

    // if sdk is not ready && this prepareShare is not from page prepare sdk operation
    if (!this._sdkPrepared && !flowControllConfig.isSDKPrepare) {
      return this.waitSDKReady().then(() => {
        return this._prepareShareByShareType(shareInfoPack, shareType);
      });
    }

    // const { compatibleShareConfig } = shareInfoPack;
    // shareType
    // shareType
    const result = this._prepareShareByShareType(shareInfoPack, shareType);

    !this._sdkPrepared && result.then(_ => {
      this._checkSDKPrepared(shareInfoPack);
    }, _ => {
      this._checkSDKPrepared(shareInfoPack);
    });

    return result;
  }

  /**
   * sdk
   * @returns {promise} sdk
   * @example
   * await pddShare.waitSDKReady();
   */
  waitSDKReady() {
    return this.waitWeChatSDKReady().then(() => {
      return this.waitAppIDCacheBuild();
    });
  }

  /**
   * @ignore
   */
  async waitWeChatSDKReady() {
    if (this._sdkPrepared) {
      return Promise.resolve();
    } else if (this.sdkReadyPromise) {
      return this.sdkReadyPromise;
    } else {
      const shareController = await this.shareManager.getShareControllerAsync();
      if (!shareController) {
        return Promise.resolve();
      }
      this.sdkReadyPromise = new Promise((res, rej) => {
        shareController.once(ShareEventType.ON_SDK_READY, () => {
          res();
        });
      });
      return this.sdkReadyPromise;
    }
  }

  /**
   * @ignore
   */
  async waitAppIDCacheBuild() {
    const venderAppIDCache = VenderAppIDCache.getInstance();
    if (venderAppIDCache.isInited()) {
      return Promise.resolve();
    } else if (this._actualWeChatAppID) {
      return venderAppIDCache.build({
        wechatAppID: this._actualWeChatAppID
      });
    } else if (!this.appIDReadyPromise) {
      const shareController = await this.shareManager.getShareControllerAsync();
      if (!shareController) {
        return Promise.resolve();
      }
      this.appIDReadyPromise = new Promise((res, rej) => {
        shareController.once(ShareEventType.ON_SDK_READY, ({
          wechatAppID,
          magicTrace
        }) => {
          Promise.all([
            venderAppIDCache.build({
              wechatAppID: wechatAppID
            }),
            MagicTraceManager.getInstance().build({
              magicTrace
            })
          ]).then(() => {
            res();
          });
        });
      });
    }
    return this.appIDReadyPromise;
  }

  waitManagerReady() {
    if (this._managerReady) {
      return Promise.resolve();
    }
    if (!this._managerReadyPromise) {
      return this._setupShareManager();
    }
    return this._managerReadyPromise;
  }

  /**
   * shareTypeshareInfo
   * @param {ShareType} shareType 
   * @param {object} shareInfo 
   * @return {promise} 
   * @example
   * try {
   *   await pddShare.share(ShareType.WECHAT_SESSION, shareInfo);
   * } catch (e) {
   *   if (e.shareErrorType) {
   *     // handle error
   *   } else if (e.shareResultType === ShareResultType.FAIL) {
   *     // handle share failed case
   *   }
   * }
   */
  async share(shareType, shareInfo) {
    this.setup();
    if (shareInfo) {
      return this.prepareShareSafe(shareInfo, shareType).then(() => {
        return this.shareManager.share(shareType);
      });
    } else {
      if (!this._managerReady) {
        await this.waitManagerReady();
      }
      return this.shareManager.share(shareType);
    }
  }

  /**
   * @ignore
   */
  _buildThumbnailURL(entityConfig = {}, shareType) {
    const thumbnailURL = entityConfig.thumbnailURL;
    if (thumbnailURL === undefined) {
      return;
    }
    let waterMark = entityConfig.waterMark;
    let url = thumbnailURL || '';
    if (waterMark === undefined || waterMark === null) {
      waterMark = ShareWaterMark.DEFAULT;
    }
    url = addWaterMark(url, waterMark);
    // hard code
    if (shareType === ShareType.WECHAT_SESSION &&
            waterMark === ShareWaterMark.DEFAULT) {
      url = addWaterMark(url, ShareWaterMark.WE_CHAT_MSG, true);
    }
    // add protocol
    url = (url || '').replace(/^\/\//, 'http://');
    entityConfig.thumbnailURL = url;
  }

  /**
   * @ignore
   */
  _buildShareURL(entityConfig = {}, shareType, opts = {}) {
    const shareURL = entityConfig.shareURL;
    if (!shareURL) {
      delete entityConfig.shareURL;
      return;
    }

    if (entityConfig.urlWrapType === URLWrapType.RAW) {
      return;
    }

    const urlStatus = new URLParser(shareURL);
    const queries = urlStatus.query || {};
    const referFilter = Object.keys(queries).reduce((accu, next = '') => {
      if (REFER_FILTER_KEYS.indexOf(next) >= 0) {
        accu[next] = null;
      }
      return accu;
    }, {});
    const contextQueries = getExtendInfoFromQuery(true);

    const generalQueries = {
      _wv: 41729,
      _wvx: 10
    };
    // 
    //
    if (getCurrentPlatform().isWeChatPlatform &&
      (
        shareType === ShareType.QQ_CHAT_SESSION ||
        shareType === ShareType.QQ_ZONE
      )
    ) {
      generalQueries['sf_tag'] = 'wechat';
    }

    if (shareType === ShareType.WECHAT_TIMELINE_IMAGE ||
      shareType === ShareType.WECHAT_TIMELINE_IMAGE_WITH_NO_SDK) {
      generalQueries['from'] = 'qrImg';
    }

    const pathname = urlStatus.pathname || '';
    //
    const finalQueries = Object.assign({},
      // general params
      generalQueries,
      // queries in page url
      contextQueries,
      // base queries in shareURL
      queries,
      // need be removed
      NEED_REMOVED_QUERIES,
      // removed refer
      referFilter
    );
    const shareDomain = entityConfig.shareDomain || config.shareDomain;
    entityConfig.shareURL = buildURL(finalQueries, resolveFullUrl(pathname), shareDomain);
  }

  /**
   * @ignore
   */
  async _checkSDKPrepared({
    flowControllConfig = {},
    otherConfig = {}
  }) {
    if (flowControllConfig.isSDKPrepare && !this._sdkPrepared) {
      this._sdkPrepared = true;
      const shareController = await this.shareManager.getShareControllerAsync();
      if (otherConfig.wechatAppID) {
        this._actualWeChatAppID = otherConfig.wechatAppID;
      }
      const wechatEnableMap = otherConfig.shareTypeEnable;
      if (wechatEnableMap && typeof wechatEnableMap === 'object') {
        const disabledTypes = Object.keys(wechatEnableMap)
          .filter(type => !wechatEnableMap[type])
          .map(type => parseInt(type, 10));
        // 
        shareController && shareController.disableShare(disabledTypes);
      }
      shareController && shareController.emit(ShareEventType.ON_SDK_READY, {
        wechatAppID: otherConfig.wechatAppID,
        magicTrace: otherConfig.magicTrace
      });
    }
  }

  /**
   * 
   * @returns {ShareManager|null} 
   */
  getShareManager() {
    this.setup();
    return this.shareManager;
  }

  /**
   * app
   * @param {object} buttonConfig 
   * @param {string} buttonConfig.shareIcon iconbase64
   * @param {number} buttonConfig.shareIconSize icon
   * @param {function} onClickListener 
   * @return {promise} 
   * @example
   * pddShare.setupNativeShareButton({}, () => {
   *   // when share icon clicked
   *   showSharePanel();
   * });
   */
  async setupNativeShareButton(buttonConfig, onClickListener) {
    this.setup();
    if (!this.shareManager) {
      return;
    }
    if (!this._managerReady) {
      await this.waitManagerReady();
    }
    return this.shareManager.setupNativeShareButton(buttonConfig, onClickListener);
  }
}

PDDShare.getInstance = (opts = {}) => {
  if (!instance) {
    instance = new PDDShare(opts);
  }
  return instance;
};

export default PDDShare;
