/**
 * @file index.js 
 * @description
 * Android
 */

import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { getCurrentPlatform, SYSTEM } from '@pdd/platform-util';
import throttle from 'lodash/throttle';

let platform;
function isAndroidSystem() {
    if (!platform) {
        platform = getCurrentPlatform();
    }
    return platform && platform.system === SYSTEM.Android;
}

function preventClick(event) {
    event.stopPropagation();
    event.preventDefault();
}

const DEFAULT_INTERVAL = 200;
function DisableClickWhenScroll(props) {
    const [disableClick, setDisableCLick] = useState(false);
    const timeoutRef = useRef();
    useEffect(() => {
        if (!isAndroidSystem()) {
            return;
        }
        const interval = props.interval || DEFAULT_INTERVAL; // interval()scroll
        const throttleWait = interval / 2; // throttleintervalinterval/2
        const handleScroll = throttle(() => {
            setDisableCLick(true); // 

            clearTimeout(timeoutRef.current);
            timeoutRef.current = setTimeout(() => {
                setDisableCLick(false);
            }, interval);
        }, throttleWait);
        window.addEventListener('scroll', handleScroll);

        return function cleanup() {
            window.removeEventListener('scroll', handleScroll);
        }
    }, [props.interval]);


    const maskStyle = {
        opacity: 0,
        height: '100%',
        width: '100%',
        position: 'fixed',
        top: 0,
        left: 0,
        zIndex: 999,
    };
    return (
        <div style={disableClick ? {} : { display: 'none' }}>
            <div style={maskStyle} onClick={preventClick}/>
        </div>
    );
}

DisableClickWhenScroll.propTypes = {
    /**
     * interval()scroll
     */
    interval: PropTypes.number,
}

let cleanup;
DisableClickWhenScroll.initGlobal = function () {
    if (!process.env.BROWSER) {
        return;
    }

    if (cleanup) {
        return;
    }
    let div = document.createElement('div');
    document.body.appendChild(div);

    cleanup = function() {
        if (!div) {
            return;
        }
        ReactDOM.unmountComponentAtNode(div);
        document.body.removeChild(div);
        div = null;
        cleanup = null;
    };

    return ReactDOM.render(<DisableClickWhenScroll/>, div);
};

/**
 * disable click
 */
DisableClickWhenScroll.cleanup = function() {
    cleanup && cleanup();
}

export default DisableClickWhenScroll;
