import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import Config from '../Config';
import InitialUrlCalculator from '../InitialUrlCalculator';

export const CdnImgConfigContext = React.createContext({});

export default function CdnImgConfigProvider({ uid, config, children }) {
  const initialUrlCalculator = useMemo(() => {
    return new InitialUrlCalculator(new Config(config));
  }, [config]);

  const contextValue = useMemo(() => {
    return {
      getInitialUrl(url) {
        return initialUrlCalculator.getInitialUrl(url, uid);
      }
    };
  }, [uid, initialUrlCalculator]);

  return <CdnImgConfigContext.Provider value={contextValue}>{children}</CdnImgConfigContext.Provider>;
}

CdnImgConfigProvider.propTypes = {
  /**
     *  id
     */
  uid: PropTypes.string,
  /**
     * 
     */
  config: PropTypes.object
};
