/**
 *
 * @ignore
 * @param {string} src base64
 * @returns {string} base64
 */
function tidyB64(src) {
  return src.replace(/[^A-Za-z0-9+/]/g, '');
}

// Decode a string of data which has been encoded using base-64 encoding.
function uniAtob(base64String) {
  return process.env.BROWSER
    ? window.atob(tidyB64(base64String))
    : Buffer.from(base64String, 'base64').toString('ascii');
}

// Create a base-64 encoded ASCII string from a String.
function uniBtoa(string) {
  return process.env.BROWSER ? window.btoa(string) : Buffer.from(string, 'ascii').toString('base64');
}

function utf8Base64Encode(string) {
  // see https://developer.mozilla.org/en-US/docs/Web/API/WindowBase64/Base64_encoding_and_decoding#Solution_4_%E2%80%93_escaping_the_string_before_encoding_it
  if (process.env.BROWSER) {
    return window.btoa(
      encodeURIComponent(string).replace(/%([0-9A-F]{2})/g, (match, p1) => {
        return String.fromCharCode('0x' + p1);
      })
    );
  }
  return Buffer.from(string).toString('base64');
}

function utf8Base64Decode(base64String) {
  if (process.env.BROWSER) {
    return decodeURIComponent(
      window
        .atob(tidyB64(base64String))
        .split('')
        .map(c => '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
        .join('')
    );
  }
  return Buffer.from(base64String, 'base64').toString();
}

/**
 *  string  base64 
 *
 * @param {string} string  base64 
 * @param {boolean} [asciiOnly=false]  ascii  ascii 
 * @returns {string} base64 
 *
 * @example
 * encode('abc') // => "YWJj"
 */
export function encode(string, asciiOnly = false) {
  // eslint-disable-next-line
  if (!asciiOnly && /[^\x00-\x7F]/.test(string)) {
    return utf8Base64Encode(string);
  }
  return uniBtoa(string);
}

/**
 *  base64 
 *
 * @param {string} base64String base64 
 * @param {boolean} [asciiOnly=false]  ascii  ascii 
 * @returns {string} 
 *
 * @example
 * decode('YWJj') // => "abc"
 */
export function decode(base64String, asciiOnly = false) {
  return asciiOnly ? uniAtob(base64String) : utf8Base64Decode(base64String);
}
