import URLParser from './URLParser';
import buildURL from './buildURL';
import dynamicTrace from './dynamicTrace';
import {
  shareEntityToParams
} from '../ShareManager/translation';
import platformTag from '../ShareManager/platformTag';

function transformByMagicTrace(url, magicTrace) {
  if (
    !magicTrace ||
    !magicTrace.key ||
    !magicTrace.value ||
    !magicTrace.trace
  ) {
    return url;
  }
  const urlStatus = new URLParser(url);
  const {
    key,
    value
  } = magicTrace;
  const path = magicTrace.trace;
  return buildURL(Object.assign({
    [key]: value
  }, urlStatus.query), path, urlStatus.origin);
}

class MagicTraceManager {
  constructor(opts = {}) {
    this._isInited = false;
    this._magicTrace = null;
  }

  // 
  // 
  // webSDK_PREPAREDmagicTrace
  // nativeShareLimittypesmagicTrace
  build({
    magicTrace = {}
  }) {
    if (this._isInited) {
      return Promise.resolve();
    }
    // 
    if (
      magicTrace.key &&
      magicTrace.value &&
      magicTrace.trace
    ) {
      this._magicTrace = magicTrace;
      this._isInited = true;
    }
    return Promise.resolve();
  }

  isInited() {
    return this._isInited;
  }

  transform(url) {
    return transformByMagicTrace(url, this._magicTrace);
  }

  transformByShareEntity(shareEntity = {}) {
    return dynamicTrace('post', {
      platform: platformTag(),
      shareParams: shareEntityToParams(shareEntity),
      payloadMixin: shareEntity.payloadMixin
    }).then((magicTrace) => {
      return transformByMagicTrace(shareEntity.shareURL, magicTrace);
    });
  }
}

let instance = null;
MagicTraceManager.getInstance = function (opts = {}) {
  if (!instance) {
    instance = new MagicTraceManager(opts);
  }
  return instance;
};

export default MagicTraceManager;
