const networkTypeMap = {
  unknown: 0,
  wifi: 1,
  '2g': 2,
  '3g': 3,
  '4g': 4
};
const DEFAULT_TYPE_STRING = 'unknown';
const DEFAULT_TYPE = networkTypeMap[DEFAULT_TYPE_STRING];

/**
 * @returns {boolean}
 */
function hasWeiXinAPI() {
  return typeof window === 'object' && window.WeixinJSBridge && window.wx && window.wx.getNetworkType;
}

/**
 * @returns {Promise.<any>}
 */
function wechatGetNetworkType() {
  return new Promise(resolve => {
    window.wx.ready(function() {
      window.wx.getNetworkType({
        success(res) {
          resolve(res && res.networkType);
        },
        fail() {
          resolve(DEFAULT_TYPE_STRING);
        }
      });
    });
  });
}

/**
 * @returns {Promise.<any>}
 */
function getNetWorkTypeBase() {
  return new Promise(resolve => {
    if (!hasWeiXinAPI()) {
      resolve(typeof navigator === 'object' && navigator.connection && navigator.connection.effectiveType);
      return;
    }
    return resolve(wechatGetNetworkType());
  });
}

/**
 * 
 *  issue #21
 * @returns {Promise<number>} -  0: unknow, 1: wifi, 2: 2g, 3: 3g, 4: 4g
 * @example
 *
 * getNetWorkType().then(networkType => console.log(networkType)); // => 4
 */
export function getNetWorkType() {
  return getNetWorkTypeBase().then(networkType => {
    return networkTypeMap[networkType] || DEFAULT_TYPE;
  });
}
