export async function cipherProcess(params = {}, shareEntity) {
  if (typeof shareEntity.cipherProcessor !== 'function') {
    return params;
  }
  try {
    const text = await shareEntity.cipherProcessor({
      cipherText: params.title,
      cipherCode: params.cipher,
      shareEntity
    });
    if (typeof text === 'string') {
      params.title = params.desc = text;
    }
  } catch (e) {
    // todo
  }
  return params;
}
