/**
 * @file FallbackUrlCalculatar.js
 * @author tenghu
 * @description
 *  url
 */
import { parseUrlQuery } from '@pdd/core-util';
import updateSearch from '../utils/updateSearch';
import getRandomDomain from './getRandomDomain';

export default class FallbackUrlCalculator {
  constructor(config) {
    this._config = config;
  }
  /**
     * 1. IPhost
     * 2. hostIPIP
     * 3. hostIPIP
     * @param {string} host -
     */
  _getBackupIp(host) {
    if (!this._config.useIpFallback()) {
      return host;
    }
    const ips = this._config.getBgpIps().filter(ip => ip !== host);
    const randomIndex = Math.floor(Math.random() * ips.length);
    return ips[randomIndex];
  }

  _isBgpIp(host) {
    return this._config.getBgpIps().includes(host);
  }

  /**
     * host''
     * @param {string} host -
     * @param {string} excludeDomain - 
     * @returns {string} -  or ''
     */
  _getBackupDomain(host, excludeDomain) {
    const domainConfig = this._config.getBackupDomainConfig(host);
    return getRandomDomain({ ...domainConfig, [excludeDomain]: 0 });
  }

  /**
     * host:
     * 1. bgpIpqueryhost
     * 2. 
     * 3. host
     */
  _getOriginHost(a) {
    const currentHost = a.host;
    const isBgpIp = this._isBgpIp(currentHost);
    const queries = parseUrlQuery(a.search);
    return (isBgpIp && queries.host) || this._config.getOriginHost(currentHost) || currentHost;
  }

  /**
     * host
     * 1. 
     *   1.1 
     *      - 
     *   1.2 BGP IP
     * 2. 
     *   2.1 BGP IP
     *      - BGP IPBGP IP
     */
  _getNewHost(originHost, currentHost, retriedTimes) {
    let backupDomain;
    if (retriedTimes < 2) {
      backupDomain = this._getBackupDomain(originHost, currentHost);
    }
    return backupDomain || this._getBackupIp(currentHost);
  }

  /**
     * fallback url
     * @param {string} url - url
     * @param {number} [retriedTimes = 0] - 0
     * @returns {string} - fallback url
     */
  getFallbackUrl(url = '', retriedTimes = 0) {
    const a = document.createElement('a');
    a.href = url;

    const originHost = this._getOriginHost(a);
    const newHost = this._getNewHost(originHost, a.host, retriedTimes);

    // cdnhostBGP IPhttpqueryhost
    if (this._isBgpIp(newHost)) {
      a.search = updateSearch(a.search, {
        host: originHost
      });
      a.protocol = 'http:';
    }

    a.host = newHost;
    return a.href;
  }
}
