import React from 'react';
import { Provider, useStaticRendering } from 'mobx-react';
import hoistNonReactStatics from 'hoist-non-react-statics';

export const createPage = (Component) => {
  !process.env.BROWSER && useStaticRendering(true);

  const App = ({ stores, ...otherProps }) => {
    return (
      <Provider {...stores}>
        <Component stores={Object.values(stores)} {...otherProps} />
      </Provider>
    );
  };

  hoistNonReactStatics(App, Component);

  return {
    Component: App,
    getInitialProps: createInitialPropsFun(Component),
    requireLogin: Component.appConfig.requireLogin
  };
};

export { bootClient } from './bootClient';

export { appProvider } from './appProvider';

const createInitialPropsFun = (Component) => {
  return async (context) => {
    //  Chunk  store
    const stores = context.stores = Component.stores || Component.createStores();
    await Promise.all(Object.values(stores).map(store => process.env.BROWSER ?
      store.initClientData(Component.pageInfo) :
      store.initServerData(context, context.userInfo?.accessToken)));

    return { stores };
  };
};
