/**
 * @file createClientContext.js 
 * @author zongheng(zongheng@pinduoduo.com)
 */
import { parseQuery, getCookie } from '@pdd/core-util';
import { getPlatform } from '@pdd/platform-util';
import { getUserInfoSync } from '@pdd/auth';
import { isSupportWebpFromCookie } from './cookie';

/**
 * @external {IPlatform} http://fweb1st.prod.yiran.com:5000/docs/platform-util/class/src/IPlatform.js~IPlatform.html
 */

/**
 * @typedef {Object} ClientContext 
 * @property {string} hostname hostname
 * @property {string} pathname pathname
 * @property {string} userAgent userAgent
 * @property {!Object} cookies cookie
 * @property {!Object} query 
 * @property {!IPlatform} platform 
 * @property {boolean} isSupportWebp webp
 * @property {{accessToken:string, uid:string, uin:string} | null} userInfo null
 */

let isSupportWebpCache;

/**
 * 
 *
 * @ignore
 * @return {ClientContext} context
 */
export function createClientContext() {
  return {
    get pathname() {
      return location.pathname;
    },

    get query() {
      return parseQuery(location.search);
    },

    get userAgent() {
      return navigator.userAgent;
    },

    get hostname() {
      return location.hostname;
    },

    get cookies() {
      return getCookie();
    },

    get platform() {
      return getPlatform(navigator.userAgent);
    },

    get isSupportWebp() {
      if(isSupportWebpCache === undefined) {
        isSupportWebpCache = isSupportWebpFromCookie(this.cookies);
      }
      return isSupportWebpCache;
    },

    get userInfo() {
      const user = getUserInfoSync();
      return user.accessToken ? user : null;
    }
  };
}
