import { sendImprMetrics } from '@pdd/metrics';
import { getCurrentPlatform } from '@pdd/platform-util';
import { registerWhenVisibility, isWebviewInAppTabs } from './utils';
import 'intersection-observer';

const platform = process.env.BROWSER ? getCurrentPlatform() : {};
const MIN_RATIO_THRESHOLD = 0.75;
const OBSERVER_CONFIGS = {
  rootMargin: '0px',
  threshold: [MIN_RATIO_THRESHOLD]
};
const IMPR_DELAY = 300;

class ImprController {
  constructor() {
    this._uniqid = 1;
    this.oberver = null;
    this.entryQuene = {};
    this.elStore = {};
    this.stashQuene = [];
    this.init();
  }

  init = async () => {
    //  imprclient
    if(!process.env.BROWSER) {
      return;
    }
    //  appwhy?
    //  appjs1js 2tab1tab2tab3webviewjs
    //  js
    const isSupportAppDelayImpr = platform.isNativePlatform && platform.statisfy
      && platform.statisfy('4.20.0', '4.26.0', 'greaterThanEqual');
    if(!isSupportAppDelayImpr) {
      this.createOberver();
      return;
    }
    try {
      const res = await isWebviewInAppTabs() || {};
      const showType = parseInt(res.shown_type, 10);
      if(showType) {
        registerWhenVisibility(() => {
          this.createOberver();
        });
      } else {
        this.createOberver();
      }
    } catch (e) {
      this.createOberver();
    }
  }

  //  observer
  //  
  createOberver = () => {
    this.oberver = new IntersectionObserver(this.observerCallBack, OBSERVER_CONFIGS);
    Array.isArray(this.stashQuene) && this.stashQuene.forEach((func) => {
      func && func();
    });
    this.stashQuene = null;
  }

  observerCallBack = (entries) => {
    //  
    entries.forEach((entry) => {
      //  opts.inIntersectionotps.intersectionRatio >= threshold
      //  videooverflow: hidden
      const uniqId = parseInt(entry.target.dataset.uniqid, 10);
      if(!uniqId) {
        return;
      }

      // Guard against show / hide callback, which doesn't respect threshold.
      // https://github.com/w3c/IntersectionObserver/issues/357
      const shouldTrigger = entry.isIntersecting && (entry.intersectionRatio >= MIN_RATIO_THRESHOLD);

      if(shouldTrigger) {
        const { immediate } = this.elStore[uniqId] || {};
        if(immediate) {
          this.log(uniqId);
          return;
        }
        this.entryQuene[uniqId] = entry;
      } else {
        if(this.entryQuene[uniqId]) {
          if(entry.time - this.entryQuene[uniqId].time >= IMPR_DELAY) {
            this.log(uniqId);
          }
          delete this.entryQuene[uniqId];
        }
      }
      //  debounce 
      if(this.lastTimer) {
        clearTimeout(this.lastTimer);
      }
      this.lastTimer = setTimeout(() => {
        Object.keys(this.entryQuene).forEach((id) => {
          this.log(id);
          delete this.entryQuene[id];
        });
      }, IMPR_DELAY);
    });
  }

  //  
  log = (id) => {
    if(!this.elStore[id]) {
      return;
    }
    const { info, once, el, handleCustomTracking } = this.elStore[id] || {};
    once && this.unRegister(el, id);
    if(handleCustomTracking) {
      handleCustomTracking();
    } else {
      sendImprMetrics(info)
    }
  }

  //  
  register = ({ el, info, handleCustomTracking, once = true, immediate = false }) => {
    if(!el || !info) {
      return;
    }
    const id = this._uniqid;
    this._uniqid++;
    el.dataset.uniqid = id;
    this.elStore[id] = handleCustomTracking
      ? { el, info, once, immediate, handleCustomTracking }
      : { el, info, once, immediate };
    if(this.oberver) {
      this.oberver.observe(el);
    } else {
      this.stashQuene.push(() => {
        this.oberver.observe(el);
      });
    }
  }

  //  
  unRegister = (el, id) => {
    if(this.oberver) {
      this.oberver.unobserve(el);
    }
    delete this.elStore[id];
    delete this.entryQuene[id];
  }
}

export default new ImprController();
