/**
 * @file NativeShareEntity.js
 * @author  daben
 */
import ShareMethod from '../ShareMethod';
import ShareForm from '../ShareForm';
import ShareEntity, { format } from './index';
import { getQQShareURL } from '../tools/urlWrapper';
import { getWrappedShareURLInfo } from '../tools/urlWrapperNative';
import ShareType from '../ShareType';
import URLWrapType, { isShortLinkType } from '../tools/URLWrapType';
import makeUpProtocol from '../tools/makeUpProtocol';
import { isShareToQQ } from '../qqShareTypes';
// import { getMiniProgramUrl } from '@pdd/navigation';
import processMiniObjectPath from '../tools/processMiniObjectPath';
import { cipherProcess } from '../tools/cipherProcess';
import { processDrawImage } from '../tools/processImage';
import { SystemShareMode } from '../SystemShareMode';

class NativeShareEntity extends ShareEntity {
  constructor(opts) {
    super(opts);
    // needed for share image
    this.drawImageOptions = opts.drawImageOptions;
    this.redirectURI = opts.redirectURI || '';
    // share miniProgram
    this.miniObjectPath = opts.miniObjectPath || '';
    this.miniObjectID = opts.miniObjectID;
    this.useWxAppShare = opts.useWxAppShare || false;

    // 
    this.forceDowngrade = opts.forceDowngrade || false;
    // for ios system share
    this.excludeShareChoices = opts.excludeShareChoices || [];
    // for generate image  && save to local && launch third party app
    this.thirdPartyOpenType = opts.thirdPartyOpenType;
    // drawImageClass
    this.drawImageClass = opts.drawImageClass;
    // 
    // 
    this.needSharedAmount = opts.needSharedAmount || false;
    // 
    this.cipherScene = opts.cipherScene;
    this.consumeMode = opts.consumeMode;
    // tp app share / activitySceneSn
    this.activitySceneSn = opts.activitySceneSn;
    this.tpSelectCategory = opts.tpSelectCategory;
    // 
    this.pinTimelineParams = opts.pinTimelineParams;
    // 
    this.withShareTicket = opts.withShareTicket;
    // 
    this.systemShareMode = opts.systemShareMode || SystemShareMode.DEFAULT;
    // 
    this.wechatMaskConfig = opts.wechatMaskConfig;
  }

  async normalize(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return this.processCipher();
    }
    // to prevent sb from changing share params
    // while there is still a request waiting
    const shareEntity = Object.assign({}, this);
    // 
    if (shareType === ShareType.PIN_TIMELINE) {
      return shareEntity.pinTimelineParams;
    }

    const params = {
      title: shareEntity.title,
      desc: shareEntity.message,
      thumbnail: shareEntity.thumbnailURL,
      share_url: shareEntity.shareURL,
      share_id: shareEntity.shareID,
      with_share_ticket: !!shareEntity.withShareTicket,
      system_share_mode: shareEntity.systemShareMode
    };
    const wechatMaskConfig = shareEntity.wechatMaskConfig;
    if (wechatMaskConfig && typeof wechatMaskConfig === 'object') {
      const { share_rec_mask: shareRecMask, ...restMaskConfig } = wechatMaskConfig;
      const config = {
        ...restMaskConfig
      };
      if (shareRecMask) {
        config.share_rec_mask = {
          share_type: shareType,
          activity_code: shareEntity.marketActivityID,
          share_id: shareEntity.shareID,
          ...shareRecMask
        };
      }
      params.wechat_mask_config = config;
    }

    // migaoextrashare_prefer
    // appshare_paramsextra
    if (shareEntity.payloadMixin) {
      const extra = shareEntity.payloadMixin.extra;
      if (typeof extra === 'object' && typeof extra.share_prefer !== 'undefined') {
        params.share_prefer = extra.share_prefer;
      }
    }

    /**
     * process share image && share wxapp
     */
    let isImageShare = false;
    switch (shareType) {
      case ShareType.WECHAT_SESSION:
        if (shareEntity.miniObjectPath && typeof shareEntity.miniObjectPath === 'string') {
          params.mini_object_path = processMiniObjectPath(shareEntity);
        } else if (typeof shareEntity.miniObjectPath === 'function') {
          try {
            params.mini_object_path = shareEntity.miniObjectPath(shareEntity.shareURL);
          } catch (e) {
            // todo
          }
        }
        if (shareEntity.miniObjectID) {
          params.mini_object_id = shareEntity.miniObjectID;
        }
        break;
      case ShareType.WECHAT_TIMELINE_IMAGE:
      case ShareType.WECHAT_TIMELINE_IMAGE_WITH_NO_SDK:
      case ShareType.WECHAT_SESSION_IMAGE:
      case ShareType.QQ_CHAT_SESSION_IMAGE:
      case ShareType.QQ_ZONE_IMAGE:
      case ShareType.WEIBO_IMAGE:
      case ShareType.WECHAT_SESSION_IMAGE_WITH_NO_SDK:
        isImageShare = true;
        params.options = await processDrawImage(shareEntity);
        break;
      case ShareType.WECHAT_SESSION_IMAGES_WITH_NO_SDK:
        isImageShare = true;
        params.multi_image_options = await processDrawImage(shareEntity, true);
        break;
      case ShareType.SHARE_IMAGE_PASSIVELY:
        isImageShare = true;
        params.options = await processDrawImage(shareEntity);
        if (shareEntity.thirdPartyOpenType) {
          params.open_type = shareEntity.thirdPartyOpenType;
        }
        break;
      case ShareType.IOS_SYSTEM_SHARE_CARD:
      case ShareType.WECHAT_SESSION_CARD_WITH_NO_SDK:
      case ShareType.WECHAT_TIMELINE_CARD_WITH_NO_SDK:
        params.excluded_types = shareEntity.excludeShareChoices;
        break;
      default:
        break;
    }

    /**
     * process share url && share text
     */
    const shareForm = this.getShareForm.call(shareEntity, shareType);
    const isShareText = [ShareForm.COMMAND, ShareForm.TEXT].indexOf(shareForm) >= 0;

    this.targetShareURL = shareEntity.shareURL;
    this.realShareURL = shareEntity.shareURL;
    this.useShortURL = false;
    // 
    // share_url
    if (shareType === ShareType.WECHAT_SESSION && params.mini_object_path) {
      this.realShareURL = params.mini_object_path;
    }

    if (isShareToQQ(shareType)) {
      const shareToQQURL = getQQShareURL(shareType, shareEntity.shareURL);
      params.share_url = shareToQQURL;
      this.shareURL = shareToQQURL;
      this.realShareURL = shareToQQURL;
      this.targetShareURL = shareToQQURL;
    }

    if (shareType === ShareType.WECHAT_SESSION && params.mini_object_path) {
      this.realShareURL = this.targetShareURL = params.mini_object_path;
    }

    if (
      // 
      // 
      (shareForm === ShareForm.CARD || shareForm === ShareForm.WXAPP_CARD) &&
      // 
      shareEntity.urlWrapType
    ) {
      return getWrappedShareURLInfo(shareEntity).then(
        ({
          realShareURL,
          targetShareURL,
          useShortURL,
          tpDowngrade,
          tpInfo,
          tpInfos,
          appendQuery,
          categoryShareInfo,
          useCipher,
          missed,
          rawResponse = {}
        }) => {
          if (useCipher) {
            return Promise.reject(new Error('operation refused'));
          }
          if (!missed) {
            this.useShortURL = useShortURL;
            this.realShareURL = realShareURL;
            this.targetShareURL = targetShareURL;
          }
          params.share_url = realShareURL;
          if (tpInfo) {
            params.title = tpInfo.title || params.title;
            params.desc = tpInfo.message || params.desc;
          }
          // 
          if (categoryShareInfo) {
            params.title = categoryShareInfo.title || params.title;
            params.thumbnail = categoryShareInfo.thumbnailURL || params.thumbnail;
          }
          params.tpInfo = tpInfo;
          params.tpInfos = tpInfos;
          params.tpDowngrade = tpDowngrade;
          params.rawResponse = rawResponse;
          if (
            shareEntity.urlWrapType === URLWrapType.SERVER_QUERY_APPEND &&
            typeof params.mini_object_path === 'string' &&
            params.mini_object_path
          ) {
            params.mini_object_path = processMiniObjectPath(
              {
                miniObjectPath: params.mini_object_path,
                shareURL: realShareURL
              },
              {
                appendQuery
              }
            );
            this.realShareURL = params.mini_object_path;
          }
          return params;
        }
      );
    } else if (
      isImageShare &&
      (isShortLinkType(shareEntity.urlWrapType) ||
        shareEntity.urlWrapType === URLWrapType.MAGIC_TRACE ||
        shareEntity.urlWrapType === URLWrapType.MAGIC_TRACE_DYNAMIC)
    ) {
      return getWrappedShareURLInfo(shareEntity).then(async (payload = {}) => {
        const { realShareURL, targetShareURL, useShortURL, shareText } = payload;
        this.targetShareURL = targetShareURL;
        if (shareEntity.shortCommandApi) {
          this.pcode = shareText;
          params.share_url = shareText;
        } else {
          this.useShortURL = useShortURL;
          this.realShareURL = realShareURL;
          params.share_url = useShortURL ? makeUpProtocol(realShareURL) : realShareURL;
        }
        if (useShortURL) {
          if (params.multi_image_options) {
            params.multi_image_options = await processDrawImage(shareEntity, true, payload);
          } else if (params.options) {
            params.options = await processDrawImage(shareEntity, false, payload);
            if (params.options && typeof params.options === 'object' && params.options.$params) {
              Object.assign(params, params.options.$params);
              delete params.options.$params;
            }
          }
        }
        return params;
      });
    } else if (isShareText) {
      let shareTextFinal = params.title || '';
      if (isShortLinkType(shareEntity.urlWrapType)) {
        return getWrappedShareURLInfo(shareEntity).then(
          ({
            realShareURL,
            targetShareURL,
            useShortURL,
            shareText,
            shareID,
            cipher,
            tpInfo,
            tpInfos,
            tpDowngrade,
            rawResponse = {}
          }) => {
            if (shareID) {
              params.shareID = shareID;
            }
            if (cipher) {
              params.cipher = cipher;
            }
            if (tpInfo) {
              params.title = tpInfo.link_url || params.title;
              params.desc = tpInfo.link_url || params.desc;
              shareTextFinal = params.title;
            }
            params.tpInfo = tpInfo;
            params.tpInfos = tpInfos;
            params.tpDowngrade = tpDowngrade;
            params.rawResponse = rawResponse;
            this.targetShareURL = targetShareURL;
            if (shareEntity.shortCommandApi) {
              const withoutDefault = useShortURL || cipher ? shareText : '';
              const withDefault = withoutDefault || targetShareURL;
              shareTextFinal = format(shareTextFinal, withDefault, targetShareURL, withoutDefault);
              params.title = params.desc = shareTextFinal;
              this.pcode = shareTextFinal;
            } else {
              const withoutDefault = useShortURL ? realShareURL : '';
              const withDefault = withoutDefault || targetShareURL;
              shareTextFinal = format(shareTextFinal, withDefault, targetShareURL, withoutDefault);
              params.title = params.desc = shareTextFinal;
              this.useShortURL = useShortURL;
              this.realShareURL = withDefault;
            }
            return cipherProcess(params, shareEntity);
          }
        );
      } else {
        shareTextFinal = format(shareTextFinal, shareEntity.shareURL);
        params.title = params.desc = shareTextFinal;
        return cipherProcess(params, shareEntity);
      }
    } else {
      return Promise.resolve(params);
    }
  }

  getShareForm(shareType) {
    if (shareType === ShareType.WECHAT_SESSION) {
      if (this.miniObjectPath || this.useWxAppShare) {
        return ShareForm.WXAPP_CARD;
      } else {
        return ShareForm.CARD;
      }
    }
    if ([4, 2, 5, 6, 24, 25, 18, 9996, 9997].includes(shareType)) {
      return ShareForm.CARD;
    }
    if ([8, 9, 10, 11, 12, 13, 15, 19, 20, 22].includes(shareType)) {
      return ShareForm.IMAGE;
    }
    if ([3, 16, 17, 21, ShareType.COPY_CIPHER].includes(shareType)) {
      if (this.shortCommandApi) {
        return ShareForm.COMMAND;
      } else {
        return ShareForm.TEXT;
      }
    }
    if (shareType === ShareType.COPY_LINK) {
      return ShareForm.COPY_LINK;
    }
    return ShareForm.DEFAULT;
  }

  getShareMethod(shareType) {
    if ([9, 16, 19, 20, 18, 9996, 9997].includes(shareType)) {
      return ShareMethod.SYSTEM;
    }
    if ([1, 2, 3, 4, 5, 8, 10, 11, 12, 13, 15, 17, 21, 24, 25].includes(shareType)) {
      return ShareMethod.SDK;
    }
    if (shareType === ShareType.SHARE_IMAGE_PASSIVELY) {
      return this.thirdPartyOpenType ? ShareMethod.MANUAL : ShareMethod.DEFAULT;
    }
    if (shareType === ShareType.COPY_CIPHER) {
      return ShareMethod.MANUAL;
    }
    return ShareMethod.DEFAULT;
  }
}

export default NativeShareEntity;
