const INVALID_DATE = 'Invalid Date';

/**
 * 
 *
 * @param {Date|number|string} date 
 * - Date: Date 
 * - string:  RFC2822  ISO 8601 
 * - number:  197011 00:00:00 UTC 
 *    [-100000000 , 100000000 ]1  86,400,000 
 * @param {string} [formatStr="YYYY-MM-DD"]  token
 *  `YYYY/YY/M/MM/D/DD/H/HH/m/mm/s/ss/SSS/h/hh/a/A` [day.js format](https://github.com/iamkun/dayjs/blob/dev/docs/en/API-reference.md#format-formatstringwithtokens-string)
 * @returns {string} 
 * @example
 *
 * formateTime(new Date(), 'MM/DD/YYYY HH:mm:ss')
 * // --> 04/21/2019 17:33:04
 */
export function formatTime(date, formatStr = 'YYYY-MM-DD') {
  if (typeof date === 'number' || typeof date === 'string') {
    date = new Date(date);
  }
  if (date.toString() === INVALID_DATE) {
    return INVALID_DATE;
  }

  const year = date.getFullYear();
  const month = date.getMonth(); // month, 0-11
  const date_ = date.getDate(); // date, 1-31
  const hours = date.getHours(); // hour, 0-23
  const minutes = date.getMinutes();
  const seconds = date.getSeconds();

  const matches = {
    YYYY: String(year),
    YY: String(year).slice(-2),
    M: String(month + 1),
    MM: String(month + 1).padStart(2, '0'),
    D: String(date_),
    DD: String(date_).padStart(2, '0'),
    H: String(hours),
    HH: String(hours).padStart(2, '0'),
    m: String(minutes),
    mm: String(minutes).padStart(2, '0'),
    s: String(seconds),
    ss: String(seconds).padStart(2, '0'),
    // milliceconds
    SSS: String(date.getMilliseconds()).padStart(3, '0'),
    // 12-hour clock
    h: String(hours % 12),
    hh: String(hours % 12).padStart(2, '0'),
    a: hours < 12 ? 'am' : 'pm',
    A: hours < 12 ? 'AM' : 'PM'
  };

  return formatStr.replace(
    /\[([^\]]+)]|(YY){1,2}|M{1,2}|D{1,2}|H{1,2}|h{1,2}|a|A|m{1,2}|s{1,2}|SSS/g,
    (match, $1) => $1 || matches[match]
  );
}

/**
 * json
 * @param {string} data 
 * @param {Object} defaultValue 
 * @return {object} 
 */
export function safeParseJson(data, defaultValue = null) {
  try {
    return JSON.parse(data);
  } catch (e) {
    console.error('safeParseJson: ', e);
    return defaultValue;
  }
}

/**
 * 
 * @ignore
 * @param {string|number} price 
 * @param {string} [type] priceyuan,cent
 * @returns {string} 
 */
function formatPrice(price, type) {
  price = Number(price);
  if (isNaN(price)) {
    return '0';
  }
  if (type !== 'cent') {
    price = +`${price}e2`;
  }
  return (Math.floor(price) / 100).toString();
}

/**
 * 
 * @param {string|number} priceCent 
 * @returns {string} 
 */
export function priceCent(priceCent) {
  return formatPrice(priceCent, 'cent');
}

/**
 * @example
 * price('3.035')  3.03
 * price('35.41')  35.41
 * 
 * @param {string|number} priceYuan 
 * @returns {string} 
 */
export function price(priceYuan) {
  return formatPrice(priceYuan);
}

/**
 * @example
 * sales(9998)  '9998'
 * sales(9999)  '0.9'
 * sales(99999)  '9.9'
 * sales(121000)  '12'
 * 
 * @param {string|number} sales 
 * @returns {string} 
 */
export function sales(sales) {
  sales = parseInt(sales, 10) || 0;
  if (sales < 9999) {
    return sales.toString();
  }
  if (sales <= 99999) {
    const thousands = parseInt(sales / 1000, 10);
    return `${parseInt(thousands / 10, 10)}.${thousands % 10}`;
  }
  return `${parseInt(sales / 10000, 10)}`;
}

/**
 * @example
 * round(1.275, 2);   // Returns 1.28
 * round(1.27499, 2); // Returns 1.27
 * round(1234.5678, -2);   // Returns 1200
 * round(1.2345678e+2, 2); // Returns 123.46
 * round(1.2345678e+2, "2"); // Returns 123.46
 * round("123.45");        // Returns 123
 * @param {Number|string} value round
 * @param {Integer|string} precision 
 * @returns {Number} round
 */
export function round(value, precision) {
  if (typeof precision === 'undefined' || +precision === 0) {
    return Math.round(value);
  }
  value = +value;
  precision = +precision;
  // valuenumberexpNaN
  if (isNaN(value) || precision % 1 !== 0) {
    return NaN;
  }
  const exp = Math.pow(10, precision);
  // value*exp
  return Math.round(+`${value}e${precision}`) / exp;
}

/**
 * 
 * @param {number|string} discount 
 * @returns {string} 
 */
export function discount(discount) {
  discount = parseFloat(discount) || 0;
  discount = discount.toFixed(1);
  if (discount === '0.0') {
    discount = '0.1';
  }
  return discount;
}
