/**
 * @file index.js info
 * @author tenghu
 * @description
 * infotoastAPPtoast
 */
import supportNativeToast from './supportNativeToast';
import infoH5 from './infoH5';
import infoNative from './infoNative';

const DEFAULT_DURATION = 2;

function shouldUseNativeToast(duration) {
  // 2sduration
  if (duration !== DEFAULT_DURATION) {
    return false;
  }
  return supportNativeToast();
}

/**
 * infohybrate
 * @param {string} msg - string
 * @param {Object} [options] - infoH5
 * @param {number} [options.duration] - 2**duration2h52s**
 * @param {function} [options.onClose] - 
 * @param {number} [options.type = 1] - toast[toasttype=1](https://docs.pdd.net/pinbridge/ui/JSAlert.html#toast)toast
 */
export default function info(msg, options = {}) {
  const modifiedOptions = {
    ...options,
    duration: options.duration || DEFAULT_DURATION
  };

  shouldUseNativeToast(modifiedOptions.duration) ? infoNative(msg, modifiedOptions) : infoH5(msg, modifiedOptions);
}

export { infoH5 };
