
import { shouldUseNative, showNative, hideNative } from './showSpinnerNative';
import { showH5, hideH5 } from './showSpinnerH5';

function show(...args) {
  if (process.env.BROWSER) {
    shouldUseNative() ? showNative(...args) : showH5(...args);
  }
}

function hide() {
  if (process.env.BROWSER) {
    shouldUseNative() ? hideNative() : hideH5();
  }
}

function showWithoutBg() {
  show('', { hideBg: true });
}
export { show, showH5, showWithoutBg, hide, hideH5 };
