import { getCurrentPlatform } from '@pdd/platform-util';
import navigation from '@pdd/navigation';
import { app as appConfig } from '@pdd/configs';
import { LOGIN_RISK_CONTROL_CODE, ScopeType, WechatAuthType } from '../common/constants';
import { RedirectedError } from '../common/errorClass';
import { resolveWechatLoginUrl, isForbiddenErrorCode, resolveQQLoginUrl } from '../common/loginUrlUtil';

const platform = process.env.BROWSER ? getCurrentPlatform() : null;

function getAppId() {
  const config = appConfig.get(location.host);

  if(platform.isWeChatPlatform) {
    return config.AppID.WeChat;
  }

  if(platform.isWeiboPlatform) {
    return config.AppID.Weibo;
  }

  if(platform.isQQPlatform) {
    return platform.isEmbeddedBrowser ? config.AppID.QQPublic : config.AppID.QQ;
  }

  return 0;
}

/**
 * code
 *
 * @param {AxiosInstance} axiosHttp '@pdd/isomorphic-request'
 * @param {string} code
 * @param {'BASE'|'USERINFO'} [state] 
 * @return {Promise<UserInfo>}
 * @ignore
 */
export async function loginWithCode(axiosHttp, { code, state }) {
  const appId = getAppId();
  const params = state === WechatAuthType.UserInfo ? { code, app_id: appId, is_auth: 1 } : { code, app_id: appId };
  if(platform.isQQPlatform) {
    params.is_qq_connect = true;
    params.redirect_uri = resolveQQLoginUrl(location.host, location.href).redirectUri;
  }

  try {
    // isomorphic-request
    const userInfo = await axiosHttp.post('/login', params, { defaultErrorHandler() {} });
    return {
      ...userInfo,
      accessToken: userInfo.access_token
    };
  } catch (error) {
    const { errorCode, response } = error;
    const { verify_auth_token: verifyAuthToken, verify_ttl: verifyTtl } = response.data || {};

    if(errorCode === LOGIN_RISK_CONTROL_CODE.NeedAuthenticationLogin && platform.isWeChatPlatform) {
      location.href = resolveWechatLoginUrl(location.host, location.href, ScopeType.UserInfo);
      throw new RedirectedError();
    }

    if(
      errorCode === LOGIN_RISK_CONTROL_CODE.LoginVerifyRisk ||
      errorCode === LOGIN_RISK_CONTROL_CODE.DefaultLoginRisk
    ) {
      navigation.reload(
        `psnl_verification.html?VerifyAuthToken=${verifyAuthToken}&from=${encodeURIComponent(location.href)}`
      );
      throw new RedirectedError();
    }
    if(errorCode === LOGIN_RISK_CONTROL_CODE.LoginForbidden) {
      // login_error.html
      navigation.reload(
        `fpctdipk.html?_t_module_name=login_error&&ttl=${verifyTtl}&from=${encodeURIComponent(location.href)}`
      );
      throw new RedirectedError();
    }
    if(isForbiddenErrorCode(errorCode)) {
      navigation.reload(`/commercial_pre_orders.html?_t_module_name=psnl_account_freeze_alert&error_code=${errorCode}`);
      throw new RedirectedError();
    }

    throw error;
  }
}
