/**
 * @file index.js infoNative
 * @author tenghu
 */
import { invokeBridgeCallNativeFun } from '@pdd/native-util';
import infoH5 from './infoH5';

const NATIVE_TOAST_DURATION = 2000;

/**
 * [toast](https://docs.pdd.net/pinbridge/ui/JSAlert.html#toast)
 * type = 0: toast
 * type = 1: toast
 * toast type1
 * 1.  toast #37
 * 2. Android  toast 
 */
const DEFAULT_TOAST_TYPE = 1;

export default function infoNative(msg, options = {}) {
  if (!msg) {
    return;
  }
  const type = typeof options.type === 'number' ? options.type : DEFAULT_TOAST_TYPE;
  invokeBridgeCallNativeFun('JSAlert', 'toast', { message: msg, type })
    .then(() => {
      // ToastToast2
      setTimeout(() => {
        options.onClose && options.onClose();
      }, NATIVE_TOAST_DURATION);
    })
    .catch(() => {
      infoH5(msg, options);
    });
}
