/**
 * @file storeProvider storestore, store
 * @author zongheng@pinduoduo.com
 */
import React from 'react';
import TimeoutSpinner from '@pdd/isomorphic-mobx-framework/lib/components/TimeoutSpinner';
import { errors } from '@pdd/error-status';
import PropTypes from 'prop-types';
import { PropTypes as MobxPropTypes, observer } from 'mobx-react';

const DEFAULTS = {
  redirectUrl: '/',
  requireLogin: true,
  fallbackCSR: false,
  beforeRender: (req, res, stores) => true,
  renderSpinner: () => <TimeoutSpinner />
};

/**
 * @typedef {Object} appConfig
 * @property {string} serverTemplateName 
 * @property {string} [errorTemplateName] serverTemplateName
 * @property {title} [string] 
 * @property {boolean} [requireLogin=true] , true
 * @property {string} [redirectUrl='/'] 
 * @property {boolean} [fallbackCSR=false] csr
 * @property {beforeRender} [beforeRender=() => true] false
 * @property {function(error : *) : *} [dealServerError] 
 * @property {function() : React.Component} [renderSpinner] spinnerspinner
 */

/**
 * @typedef {function(req: Request, res: Response, stores: Object<string, MobxPropTypes.observableObject>) : boolean} beforeRender
 */

/**
 * @typedef {React.Component} App
 * @property {appConfig} appConfig 
 * @property {function() : Object<string, MobxPropTypes.observableObject>} createStores mobx store
 */

/**
 * store
 *
 *  1. store
 *  2. 
 *
 * @param {Object<string, PageStore>} Stores Store
 * @param {appConfig} appConfig 
 * @return {function(Component: React.Component): App} 
 */
export function appProvider(Stores, appConfig) {
  return (Component) => {
    class App extends React.Component {
      static displayName = `appProvider(${Component.displayName || Component.name})`;

      static appConfig = {
        dealServerError: (error) => {
          if(error.errorCode === errors.AUTHENTICATION_FAILED || appConfig.fallbackCSR) {
            return null;
          }

          return error;
        },
        errorTemplateName: appConfig.serverTemplateName,
        ...DEFAULTS,
        ...appConfig
      };

      static propTypes = {
        stores: PropTypes.arrayOf(MobxPropTypes.observableObject),
        pageInfo: PropTypes.object,
      };

      static createStores() {
        return Object.keys(Stores).reduce((result, key) => {
          result[key] = new Stores[key]();
          return result;
        }, {});
      }

      async componentDidMount() {
        // store
        // todostore
        const isClientRender = this.props.stores.some(store => !store.isServerRendered);
        if(!isClientRender) {
          this.props.stores.forEach(store => store.prepareClientData());
        }

        this.props.stores.forEach(store => store.finishInitLoading());
      }

      componentDidCatch(error, info) {
        /* eslint-disable */
        if(process.env.NODE_ENV === 'development') {
          console.log('renderError:', error);
          console.log('renderInfo:', info);
          throw error;
        }
        /* eslint-enable */

        // todo: loggerlogger
        error.message += '_' + JSON.stringify(info);
        // errorreact
        setTimeout(() => {
          throw error;
        });
      }

      render() {
        return this.props.stores.every(store => store.isFinishInitLoading)
          ? <Component />
          : App.appConfig.renderSpinner();
      }
    }

    return observer(App);
  };
}
