import { compareVersion, getCurrentPlatform } from '@pdd/platform-util';
import { nanoid, parseQuery } from '@pdd/core-util';

/**
 * 
 *
 * @class PageInfo
 */
function PageInfo(url, pageProperty) {
  if(typeof pageProperty === 'object') {
    this.pageProperty = pageProperty;
  }
  this.initByUrl(url);
}

PageInfo.prototype.initByUrl = function(url) {
  url = url || window.location.href;
  const search = url.split('#')[0].split('?')[1] || '';
  const urlQuery = parseQuery(search);
  this.pageELSN = urlQuery.page_el_sn || null;
  this.query = urlQuery;
  const shortPathname = getShortPathname(url);
  let pageKey = shortPathname.slice(1).split('.')[0]; // pickup from '/xxx.html'
  this.initWithPageKey(pageKey);
};

PageInfo.prototype.initWithPageKey = function(pageKey) {
  this.pageKey = pageKey || '';
  const info = Object.assign({}, this.pageProperty || {});
  // info
  this.info = info;
  this.pageName = info.pageName || pageKey;
  this.pageSN = info.pageSN || null;
  this.tkShareClick = info.tkShareClick || false;
  this.keyParams = info.keyParams || {};
  this.query = this.query || {};
  this.native = info.native || {};
  if(typeof window !== 'undefined' && window.rawData && window.rawData.pageID) {
    this.pageID = window.rawData.pageID;
  } else if(this.query && this.query.page_id) {
    this.pageID = this.query.page_id;
  } else {
    let name = this.pageName;
    const pageSN = this.pageSN;
    if(name.match(/app_\w+_popup/)) {
      name = 'index';
    }
    this.pageID = info.pageId || createPageID(name, pageSN, pageKey);
  }
  this.pvInfo = info.pv || {};

  if(pageKey.match(/^app_\w+/)) {
    this.pvInfo.isAppModal = true;
  }
};

PageInfo.prototype.getPageName = function() {
  return this.pageName;
};

PageInfo.prototype.getPageSN = function() {
  return this.pageSN;
};

PageInfo.prototype.getPageID = function() {
  return this.pageID;
};

PageInfo.prototype.getQuery = function() {
  return this.query;
};

PageInfo.prototype.getNativePageContext = function() {
  const clientPlatform = getCurrentPlatform();
  if(clientPlatform.isNativePlatform) {
    const pageContext = {};
    for(const key in this.query) {
      if(!key.startsWith('refer')) {
        pageContext[key] = this.query[key];
      }
    }
    return Object.assign(pageContext, {
      /*eslint-disable*/
      page_name: this.pageName,
      page_id: this.pageID,
      page_sn: this.pageSN
      /* eslint-enable */
    });
  }
};

PageInfo.prototype.getAdQuery = function() {
  return {
    cid: this.query.cid,
    src: this.query.src,
    campaign: this.query.campaign
  };
};

PageInfo.prototype.getNativeQuery = function(current) {
  const nativeInfo = this.getNativeInfo(current);
  if(!nativeInfo) {
    return this.query;
  }
  const query = Object.assign({}, this.query);
  if(nativeInfo.queryMap) {
    for(const key in nativeInfo.queryMap) {
      if(nativeInfo.queryMap.hasOwnProperty(key)) {
        const nativeKey = nativeInfo.queryMap[key];
        if(query[key]) {
          query[nativeKey] = query[key];
        }
      }
    }
  }
  return query;
};

PageInfo.prototype.setKeyParams = function(params) {
  for(const key in params) {
    if(params.hasOwnProperty(key) && params[key] == null) {
      delete params[key];
    }
  }
  this.keyParams = Object.assign({}, this.keyParams, params);
};

PageInfo.prototype.getKeyParams = function() {
  return this.keyParams;
};

PageInfo.prototype.getNativeInfo = function(current) {
  return this.native[current.toLowerCase()];
};

PageInfo.prototype.checkNativePageVersion = function(current, version) {
  const nativeInfo = this.getNativeInfo(current);
  if(!nativeInfo || !nativeInfo.version) {
    return false;
  }
  return compareVersion(version, nativeInfo.version) >= 0;
};

PageInfo.prototype.getNativePageName = function(current) {
  const nativeInfo = this.getNativeInfo(current);
  if(!nativeInfo) {
    return;
  }
  return nativeInfo.name;
};

PageInfo.prototype.getKeyParamsFromQuery = function() {
  const keys = this.pvInfo.keys || [];
  const params = {};
  const query = this.getQuery();
  keys.forEach(function(item) {
    let key;
    let value;
    if(typeof item === 'string') {
      key = item;
      value = query[key];
    } else if(typeof item === 'object') {
      key = item.key;
      value = query[key];
      if(item.mapping) {
        item.mapping.forEach(function(mappingKey) {
          if(query[mappingKey] != null) {
            value = query[mappingKey];
          }
        });
      }
      if(value == null && item.default != null) {
        value = item.default;
      }
    }
    if(key != null && value != null) {
      params[key] = value;
    }
  });
  this.setKeyParams(params);
  return this.getKeyParams();
};

PageInfo.prototype.hasIndividualPV = function() {
  return this.pvInfo.indiv;
};

PageInfo.prototype.isAppModal = function() {
  return this.pvInfo.isAppModal;
};

function getShortPathname(url) {
  const arr = url.split('?')[0].split('/');
  return '/' + arr[arr.length - 1];
}

function createPageID(pageName, pageSN, pageKey) {
  return [
    pageSN || pageName || pageKey || '',
    Date.now(),
    nanoid(10)
  ].join('_');
}

export default PageInfo;
