/**
 * @file TrackingEntity.js
 * @author daben
 */
const TRACKING_OP_NAME = 'event';
const TRACKING_SUB_OP = {
  SHARE: 'share',
  SHARE_RESULT: 'share_result'
};

class TrackingEntity {
  constructor(opts = {}) {
    this.userID = opts.userID || '';
    this.uin = opts.uin || '';
    this.shareID = opts.shareID || '';
    this.op = opts.op || TRACKING_OP_NAME;
    this.subOP = opts.subOP || '';
    this.shareChannel = opts.shareChannel || '';
    this.shareResult = opts.shareResult || '';
    this.shareURL = opts.shareURL || '';
    this.shareDomain = opts.shareDomain || '';
    this.shareForm = opts.shareForm || '';
    this.shareMethod = opts.shareMethod || '';
    this.shareAppID = opts.shareAppID;
    this.pcode = opts.pcode;
    this.wxShareCount = opts.wxShareCount;
    this.cipherScene = opts.cipherScene;
  }

  normalize(shareType) {
    if (this.subOP === TRACKING_SUB_OP.SHARE) {
      return {
        op: this.op,
        sub_op: this.subOP,
        share_id: this.shareID,
        share_channel: this.shareChannel,
        share_url: this.shareURL,
        share_domain: this.shareDomain,
        share_form: this.shareForm,
        share_method: this.shareMethod,
        share_app_id: this.shareAppID,
        pcode: this.pcode,
        pcode_scene: this.cipherScene
      };
    } else if (this.subOP === TRACKING_SUB_OP.SHARE_RESULT) {
      const params = {
        op: this.op,
        sub_op: this.subOP,
        event: 'share_result',
        share_id: this.shareID,
        share_channel: this.shareChannel,
        share_url: this.shareURL,
        share_domain: this.shareDomain,
        share_result: this.shareResult,
        share_form: this.shareForm,
        share_method: this.shareMethod,
        share_app_id: this.shareAppID,
        wx_share_count: this.wxShareCount,
        pcode: this.pcode,
        pcode_scene: this.cipherScene
      };
      if (this.walrusID) {
        params.walrus_id = this.walrusID;
        params.walrus_type = this.walrusType;
      }
      return params;
    }

    return {};
  }

  getShareURLTrackingQueries() {
    return TrackingEntity.getTrackingQueries(this);
  }
}

TrackingEntity.TRACKING_SUB_OP = TRACKING_SUB_OP;
TrackingEntity.getTrackingQueries = (trackingEntity = {}) => {
  return {
    refer_share_id: trackingEntity.shareID,
    // refer_share_uid: trackingEntity.userID,
    refer_share_uin: trackingEntity.uin,
    refer_share_channel: trackingEntity.shareChannel
  };
};

export default TrackingEntity;
