/**
 * @file URLWrapType.js
 * @author  daben
 */

const URLWrapType = {
  DEFAULT: 0,
  // url (prepared-config)
  // @deprecated
  REDIRECT: 1,
  // 
  // @deprecated
  SERVER_DOMAIN: 2,
  // (app_id, real-time-config)
  // @deprecated
  SERVER_CONTROL: 3,
  //  (short url, real-time)
  SERVER_SHORT_URL: 4,
  // 
  RAW: 5,
  // ()
  SERVER_TINY_DOMAIN: 6,
  // base64 (new)
  // @deprecated
  SERVER_DOMAIN_BASE64: 7,
  // replace domain
  DOMAIN_REPLACE: 8,
  // magic trace
  MAGIC_TRACE: 9,
  // magic trace dynamic
  MAGIC_TRACE_DYNAMIC: 10,
  // short url with cache
  SERVER_SHORT_URL_WITH_CACHE: 11,
  // tp app_id share
  SERVER_TP_APP: 12,
  // combined category for card
  SERVER_TP_APP_CARD: 13,
  // get extra parmas from server and append to share_url
  SERVER_QUERY_APPEND: 14,
  // get combined category info (limit card)
  CATEGORY_PROCEDURE: 15,
  // get combined category info (info / or custom)
  CATEGORY_PROCEDURE_CUSTOM: 16
};

export function isServerControlType(urlWrapType) {
  return (
    [
      URLWrapType.SERVER_CONTROL,
      URLWrapType.SERVER_SHORT_URL,
      URLWrapType.SERVER_TINY_DOMAIN,
      URLWrapType.SERVER_DOMAIN_BASE64,
      URLWrapType.DOMAIN_REPLACE,
      URLWrapType.SERVER_SHORT_URL_WITH_CACHE
    ].indexOf(urlWrapType) >= 0
  );
}

export function isTinyDomainType(urlWrapType) {
  return (
    [
      URLWrapType.SERVER_CONTROL,
      URLWrapType.SERVER_TINY_DOMAIN,
      URLWrapType.SERVER_DOMAIN_BASE64,
      URLWrapType.DOMAIN_REPLACE
    ].indexOf(urlWrapType) >= 0
  );
}

export function isShortLinkType(urlWrapType) {
  return (
    [
      URLWrapType.SERVER_SHORT_URL,
      URLWrapType.SERVER_SHORT_URL_WITH_CACHE,
      URLWrapType.SERVER_TP_APP_CARD,
      URLWrapType.CATEGORY_PROCEDURE,
      URLWrapType.CATEGORY_PROCEDURE_CUSTOM
    ].indexOf(urlWrapType) >= 0
  );
}

export default URLWrapType;
