import PageInfo from './pageInfo';
import { setPageContext, getReferPageContext, getPageShownType } from '@pdd/native-util';
import { getCurrentPlatform, compareVersion } from '@pdd/platform-util';
import { buildQuery } from '@pdd/core-util';

/**
 * 
 *
 * @class PageManager
 */
function PageManager() {
}

/**
 *  init:
 *  1native jssdk getReferPageContext
 *  2setPageContextpageInfogetNativePageContextnative Context
 *  setPageContext
 * @param {object} opts pageInfo
 * @return {PageInfo}
 */
PageManager.prototype.initPageInfo = function(opts) {
  opts = opts || {};
  if(this.pageInfo) {
    return this.pageInfo;
  }

  this.pageInfo = new PageInfo(window.location.href, opts.pageProperty);
  const query = this.pageInfo.getQuery();
  const referQuery = pickupReferQuery(query);

  if(referQuery) {
    this.updateReferInfo(referQuery);
  }

  return this.pageInfo;
};

/**
 * pageInfo
 * @param {string} url location.href
 * @param {Object} opts pageInfo
 */
PageManager.prototype.init = async function(url, opts) {
  opts = opts || {};

  const pageInfo = this.initPageInfo(opts);

  const callback = typeof opts.callback === 'function' ? opts.callback : () => {
  };

  const { isNativePlatform, version } = getCurrentPlatform();
  if(!isNativePlatform) {
    callback();
    return;
  }

  getReferPageContext().then(
    referInfo => {
      this.updateReferPageContextFromNative(referInfo);
      callback();
    },
    callback
  );

  const checkPageShownTypeFirst = compareVersion(version, '5.73.0') < 0;
  if(checkPageShownTypeFirst) {
    //  iOS  shown_type android , 
    getPageShownType().then(data => +data.shown_type === 1).catch(() => false)
      .then(isEmbedPage => !isEmbedPage && setPageContext(pageInfo.getNativePageContext()));
    return;
  }
  setPageContext(pageInfo.getNativePageContext()).catch((error) => {
    // 60100h5
    if(error && error.errorCode !== 60100) {
      throw error;
    }
  });
};

/**
 * pageInfo
 * @returns {PageInfo|PageInfo}
 */
PageManager.prototype.getPageInfo = function() {
  if(!this.pageInfo) {
    this.init();
  }
  return this.pageInfo;
};

/**
 * query
 * @returns {Object}
 */
PageManager.prototype.getQuery = function() {
  if(!this.pageInfo) {
    this.init();
  }
  return this.pageInfo.getQuery();
};

/**
 * query
 * @returns {Object}
 */
PageManager.prototype.getParameters = function() {
  return this.getQuery();
};

/**
 * querykey
 * @param key 
 * @returns {string} 
 */
PageManager.prototype.getParameter = function(key) {
  const query = this.getQuery() || {};
  return query[key];
};

