/**
 * Refer
 * @param {Object} referQuery
 */
PageManager.prototype.updateReferInfo = function(referQuery) {
  referQuery = referQuery || {};
  if(!this.referInfo) {
    this.referInfo = {};
  }
  for(let key in referQuery) {
    const value = referQuery[key];
    if(value) {
      // refer_,_ex_refer_
      if(!key.startsWith('refer_') && !key.startsWith('_ex_')) {
        key = [ 'refer', key ].join('_');
      } else if(key.startsWith('_ex_')) { // _ex_,_ex_
        key = `refer_${key.slice(4)}`;
      }
      this.referInfo[key] = value;
    }
  }
};

/**
 * referInfo,
 * @returns {Object}
 */
PageManager.prototype.getReferPageQuery = function() {
  if(!this.referInfo) {
    return null;
  }
  return Object.assign({}, this.referInfo);
};

