/**
 * ReferPageQueryString
 * @returns {string}
 */
PageManager.prototype.getReferPageQueryString = function() {
  const query = this.getReferPageQuery();
  return buildQuery(query);
};

PageManager.prototype.addReferPageQueryString = function(url) {
  const pageInfo = new PageInfo(url);
  // 
  const query = Object.assign({}, pageInfo.getQuery(), this.getReferPageQuery());
  return [ url.split('?')[0], buildQuery(query) ].join('?');
};

/**
 * @deprecated addPageAsRefer
 * @param {string} url
 * @returns {string}
 */
PageManager.prototype.addPageNameAsRefer = function(url) {
  const pageInfo = new PageInfo(url);
  // 
  const query = Object.assign({}, pageInfo.getQuery(), {
    /*eslint-disable*/
    refer_page_name: this.getPageName()
    /* eslint-enable */
  });
  const newUrl = [ url.split('?')[0], buildQuery(query) ].join('?');
  return newUrl;
};

/**
 * referurl
 * @param url
 * @returns {string}
 */
PageManager.prototype.addPageAsRefer = function(url) {
  if(!url) {
    return;
  }
  const pageInfo = new PageInfo(url);
  // 
  const referParams = {
    refer_page_name: this.getPageName(),
    refer_page_id: this.getPageID()
  };
  const referPageSN = this.getPageSN();
  if(referPageSN) {
    referParams.refer_page_sn = referPageSN;
  }

  const referPageELSN = pageInfo.pageELSN || '';
  if(referPageELSN) {
    referParams.refer_page_el_sn = referPageELSN;
  }

  const query = Object.assign({}, pageInfo.getQuery(), referParams);
  const newUrl = [ url.split('?')[0], buildQuery(query) ].join('?');
  return newUrl;
};

/**
 * refer_x_
 *
 * @param  {string} url url
 * @return {string}     url
 */
PageManager.prototype.addPageTransferParameters = function(url) {
  if(!url) {
    return;
  }
  const pageInfo = new PageInfo(url);
  //  refer
  const referParams = {
    refer_page_name: this.getPageName(),
    refer_page_id: this.getPageID()
  };
  const referPageSN = this.getPageSN();
  if(referPageSN) {
    referParams.refer_page_sn = referPageSN;
  }

  const referPageELSN = pageInfo.pageELSN || '';
  if(referPageELSN) {
    referParams.refer_page_el_sn = referPageELSN;
  }

  // _x_
  const extendParams = this.getExtendTransferParameters(true);

  const newQuery = Object.assign({}, extendParams, pageInfo.getQuery(), referParams);
  const newUrl = [ url.split('?')[0], buildQuery(newQuery) ].join('?');
  return newUrl;
};

/**
 * refer
 * 1._x_
 * 2.xcx_trace_id,xcx_version
 * 3._ex__x_()
 * 4.msgid_x_msgid()
 * 5.refer_share_id_x_share_id()
 *
 * @param  {boolean} isTransferParameters 
 * @return {object} url
 */
PageManager.prototype.getExtendTransferParameters = function(isTransferParameters) {
  const query = this.getQuery() || {};
  const extendParams = {};
  for(const key in query) {
    if(!query.hasOwnProperty(key) || extendParams[key]) {
      // _x_
      continue;
    }
    let newKey = '';
    // 1._x_
    if(key.startsWith('_x_')) {
      extendParams[key] = query[key];
      // 2. xcx_trace_id 
    } else if(key === 'xcx_trace_id' || key === 'xcx_version') {
      extendParams[key] = query[key];
      // 3._ex__x_()
    } else if(isTransferParameters && key.startsWith('_ex_')) {
      newKey = key.replace('_ex_', '_x_');
      extendParams[newKey] = query[key];
      // 4.msg_id_x_msg_id()
    } else if(isTransferParameters && key === 'msgid') {
      newKey = '_x_msgid';
      extendParams[newKey] = query[key];
      // 5.refer_share_id_x_share_id()
    } else if(isTransferParameters && key === 'refer_share_id') {
      newKey = '_x_share_id';
      extendParams[newKey] = query[key];
    }
  }

  return extendParams;
};

