import buildURL from './buildURL';
import md5 from 'md5';

export default function calculateURLHash(shareEntity = {}) {
  const url = shareEntity.shareURL;
  // refer_share_id / refer_share_uid / refer_share_channel / refer_share_uin 
  const clearURL = buildURL({
    refer_share_id: null,
    refer_share_uid: null,
    refer_share_channel: null,
    refer_share_uin: null
  }, url);
  const payloadMixin = shareEntity.payloadMixin || {};
  const payloadString = JSON.stringify(payloadMixin);
  return md5(clearURL + payloadString);
}
