/**
 * @file ShareController/wechat.js
 * @author  daben
 */
import ShareController from './base';
import ShareType from '../ShareType';
import ShareEventType from '../ShareEventType';
import ShareResultType from '../ShareResultType';
import WeChatShareEntity from '../ShareEntity/WeChatShareEntity';
import ShareError from '../ShareError';
const { ShareErrorCode } = ShareError;

const SHARE_MENUS = {
  [ShareType.WECHAT_SESSION]: {
    name: 'onMenuShareAppMessage',
    tag: 'menuItem:share:appMessage'
  },
  [ShareType.WECHAT_TIMELINE]: {
    name: 'onMenuShareTimeline',
    tag: 'menuItem:share:timeline'
  },
  [ShareType.QQ_CHAT_SESSION]: {
    name: 'onMenuShareQQ',
    tag: 'menuItem:share:qq'
  },
  [ShareType.QQ_ZONE]: {
    name: 'onMenuShareQZone',
    tag: 'menuItem:share:QZone'
  },
  [ShareType.TENCENT_WEIBO]: {
    name: 'onMenuShareWeibo',
    tag: 'menuItem:share:weiboApp'
  }
};

const SUPPORTED_SHARE_TYPE = [
  ShareType.WECHAT_SESSION,
  ShareType.WECHAT_TIMELINE,
  ShareType.QQ_CHAT_SESSION,
  ShareType.QQ_ZONE,
  ShareType.TENCENT_WEIBO,
  ShareType.COPY_CIPHER
];

/**
 * @access private
 * @extends {ShareController}
 */
class WeChatShareController extends ShareController {
  constructor(opts) {
    super(opts);
    this.ShareEntityClass = WeChatShareEntity;
    this._enableShareTypes = SUPPORTED_SHARE_TYPE.concat();
  }

  /**
   * @override
   */
  share(shareType) {
    if (shareType === ShareType.COPY_CIPHER) {
      return this.copyCipher();
    }
    return ShareErrorCode.SHARE_ACTION_DISABLE;
  }

  /**
   * url
   * shareEntity
   * 
   * @ignore
   * @override
  */
  refreshShare(shareEntity, shareType) {
    if (!shareEntity) {
      return Promise.resolve();
    }
    if (shareType) {
      this.shareEntityMap[shareType] = shareEntity;
      if (typeof wx === 'undefined') {
        this.emit(ShareEventType.SDK_INIT_FAILED);
        return Promise.resolve();
      }
      return this.refreshWeChatChannelShare(shareType);
    } else {
      this.shareEntity = shareEntity;
      this._enableShareTypes.forEach((shareType) => {
        this.shareEntityMap[shareType] = this.createShareEntity(shareEntity, shareType);
      });
      return this._setupPlatformShareSDK();
    }
  }

  /**
   * @override
   */
  _setupShareTypes() {
    const currentLength = this._enableShareTypes.length;
    this._enableShareTypes = SUPPORTED_SHARE_TYPE.filter((shareType) => {
      return this.disableShareTypes.indexOf(shareType) < 0;
    });
    return currentLength === this._enableShareTypes.length;
  }

  /**
   * @override
   */
  _setupPlatformShareSDK() {
    if (typeof wx === 'undefined') {
      this.emit(ShareEventType.SDK_INIT_FAILED);
      return Promise.resolve();
    }
    return Promise.all(this._enableShareTypes.map((shareType) => {
      this.refreshWeChatChannelShare(shareType);
    }));
  }

  /**
   * @override
   */
  _adjustMenu() {
    // diff this._enableShareTypes && SUPPORTED_SHARE_TYPE
    // and hide disabled type
    const tags = SUPPORTED_SHARE_TYPE.filter((type) => {
      return this._enableShareTypes.indexOf(type) < 0;
    }).map((type) => {
      return SHARE_MENUS[type].tag;
    });
    window.wx && window.wx.hideMenuItems({
      menuList: tags
    });
  }

  /**
   * 
   * @param {ShareType} shareType 
   */
  refreshWeChatChannelShare(shareType) {
    const config = SHARE_MENUS[shareType];
    if (!config) {
      return Promise.resolve();
    }
    return this._setupWeChatChannelShare(shareType, config.name);
  }

  /**
   * @ignore
   */
  _setupWeChatChannelShare(shareType, interfaceName = '') {
    if (!window.wx || typeof window.wx[interfaceName] !== 'function') {
      return Promise.resolve();
    }
    const shareEntity = this.shareEntityMap[shareType] || this.shareEntity;
    // if no share params, do not setup
    if (!shareEntity) {
      return Promise.resolve();
    }
    this.emit(ShareEventType.ON_SHARE_PREPARE, { shareType });
    return shareEntity.normalize(shareType).then((shareParams) => {
      const config = Object.assign({
        trigger: () => {
          this.emit(ShareEventType.ON_SHARE_TRIGGER, { shareType });
        },
        success: () => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.SUCCESS
          });
          this.refreshWeChatChannelShare(shareType);
        },
        fail: () => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.FAIL
          });
          this.refreshWeChatChannelShare(shareType);
        },
        cancel: () => {
          this.emit(ShareEventType.ON_SHARE_COMPLETED, {
            shareType,
            shareResultType: ShareResultType.CANCEL
          });
          this.refreshWeChatChannelShare(shareType);
        }
      }, shareParams);
      window.wx[interfaceName](config);
    }, this.getNormalizeErrorHandler(shareType));
  }

  /**
   * @override
   * @param {ShareType} shareType 
   */
  checkShareTypeEnable(shareType) {
    if (this._enableShareTypes.indexOf(shareType) < 0) {
      return ShareErrorCode.SHARE_TYPE_DISABLED;
    }
    return 0;
  }
}

WeChatShareController.SUPPORTED_SHARE_TYPE = SUPPORTED_SHARE_TYPE.concat();

export default WeChatShareController;
