import { getUid, getUin } from '@pdd/auth';

let userIDCache = '';
let uinCache = '';
export function refreshUid() {
  return Promise.all([
    getUid().then(uid => {
      userIDCache = uid;
    }).catch(_ => {}),
    getUin().then(uin => {
      uinCache = uin;
    }).catch(_ => {})
  ]);
}

export function getCachedUin() {
  return uinCache;
}

export default function getUserID() {
  return userIDCache;
}
