/**
 * @file getShareChannelByType.js
 * @author  daben
 */

import ShareChannel from '../ShareChannel';
import ShareType from '../ShareType';

/**
 * shareType - sharechannel
 */
const shareTypeMapChannel = {
  1: ShareChannel.APP_MESSAGE,
  2: ShareChannel.TIMELINE,
  3: ShareChannel.SINA_WEIBO,
  4: ShareChannel.QQ,
  5: ShareChannel.QQ_ZONE,
  8: ShareChannel.TIMELINE,
  9: ShareChannel.TIMELINE,
  10: ShareChannel.APP_MESSAGE,
  11: ShareChannel.QQ,
  12: ShareChannel.QQ_ZONE,
  13: ShareChannel.SINA_WEIBO,
  // ONLY IN IOS
  16: ShareChannel.APP_MESSAGE,
  17: ShareChannel.APP_MESSAGE,
  // 18 
  18: ShareChannel.APP_MESSAGE,
  19: ShareChannel.APP_MESSAGE,
  20: ShareChannel.APP_MESSAGE,
  21: ShareChannel.TIMELINE,
  23: ShareChannel.CLIPBOARD,
  24: ShareChannel.APP_MESSAGE,
  25: ShareChannel.APP_MESSAGE,
  [ShareType.COPY_CIPHER]: ShareChannel.CLIPBOARD,
  [ShareType.WECHAT_SESSION_CARD_WITH_NO_SDK]: ShareChannel.APP_MESSAGE,
  [ShareType.WECHAT_TIMELINE_CARD_WITH_NO_SDK]: ShareChannel.TIMELINE
};

export default function getShareChannelByType(shareType = 0) {
  return shareTypeMapChannel[shareType] || ShareChannel.UNKNOWN;
}
