import React, { useState, useEffect, useRef, useMemo } from 'react';
import PropTypes from 'prop-types';
import LazyLoader from './LazyImage/LazyLoader';
import LazyImageContext from './LazyImage/Context';

export default function LazyContainer(props) {
  const { rootMargin, rootRef, children, ...otherProps } = props;
  const [lazyLoader, setLazyLoader] = useState(null);

  // rootRefcontainer
  const useDefaultContainer = rootRef === undefined;
  const containerRef = useDefaultContainer ? useRef(null) : rootRef;

  useEffect(() => {
    const options = { thresholds: rootMargin };
    if (containerRef) {
      options.container = containerRef.current;
    }
    const newLazyLoader = new LazyLoader(options);
    setLazyLoader(newLazyLoader);

    return () => newLazyLoader.destroy();
  }, []);

  const contextValue = useMemo(() => {
    return { lazyLoader };
  }, [lazyLoader]);

  return (
    <LazyImageContext.Provider value={contextValue}>
      {useDefaultContainer ? (
        <div ref={containerRef} {...otherProps}>
          {children}
        </div>
      ) : (
        children
      )}
    </LazyImageContext.Provider>
  );
}

LazyContainer.propTypes = {
  /**
   * [Intersection observerrootMargin](https://developer.mozilla.org/en-US/docs/Web/API/Intersection_Observer_API#Intersection_observer_options)
   */
  rootMargin: PropTypes.string,
  /**
   * refdom[Intersection observerroot](https://developer.mozilla.org/en-US/docs/Web/API/Intersection_Observer_API#Intersection_observer_options)
   * rootRefdivroot
   * nullrootIntersectionObserverroot
   */
  rootRef: PropTypes.object,
  children: PropTypes.node.isRequired,
  /**
   * @deprecated rootMargin
   */
  thresholds: PropTypes.string
};

LazyContainer.defaultProps = {
  rootMargin: '300px',
  thresholds: '300px'
};
