import app from '@pdd/configs/app';
import { getCurrentPlatform } from '@pdd/platform-util';
import {
  getBasicEnvParams,
  getPageManagerParams,
  getTransformKeyParams,
  getQueryParams,
  getRiskControlParams
} from './util';
import { getDefaultReporter } from './reporters';
import { conflictWarning } from './conflictWarning';

/**
 * @typedef {function(url : string, params : Object) : any} MetricReporter
 */

const config = process.env.BROWSER && app.get(location.host);
const isDev = process.env.NODE_ENV === 'development';

const defaultParams = {};
/**
 * 
 *
 * @param {Object} params 
 */
export const setDefaultParams = (params) => {
  Object.assign(defaultParams, params);
};

/**
 * 
 *
 * @param {Object} payload 
 * @param {MetricReporter} [reporter] 
 * @return {Promise<void>}
 */
export const sendMetrics = async (payload, reporter = getDefaultReporter()) => {
  payload = payload || {};
  const op = payload.op;
  // pv, impr, click
  payload = await getCompositeParams(payload, op === 'pv');

  const basicParams = await getBasicEnvParams(config);
  const riskParams = getRiskControlParams(basicParams.time, op || '');

  if(isDev) {
    conflictWarning(payload, ...Object.keys(basicParams), ...Object.keys(riskParams), ...Object.keys(defaultParams));
  }

  payload = { ...defaultParams, ...payload, ...basicParams, ...riskParams };

  return reporter(resolveLogURL(payload, config), payload);
};

/**
 * pv
 *
 * @param {Object} payload 
 * @param {MetricReporter} [reporter] 
 * @return {Promise<void>}
 */
export const sendPageViewMetrics = (payload = {}, reporter) => {
  return sendMetrics({ ...payload, op: 'pv' }, reporter);
};

/**
 * 
 *
 * @param {Object} payload 
 * @param {MetricReporter} [reporter] 
 * @return {Promise<void>}
 */
export const sendImprMetrics = (payload = {}, reporter) => {
  return sendMetrics({ ...payload, op: 'impr' }, reporter);
};

/**
 * 
 *
 * @param {Object} payload 
 * @param {MetricReporter} [reporter] 
 * @return {Promise<void>}
 */
export const sendClickMetrics = (payload = {}, reporter) => {
  return sendMetrics({ ...payload, op: 'click' }, reporter);
};

// 
const getCompositeParams = async (payload, isPv) => {
  const pageParams = await getPageManagerParams();
  const result = {
    ...pageParams,
    ...getTransformKeyParams(),
    ...getQueryParams(isPv)
  };

  if(isPv) {
    // pveventpage_show, pvopevent
    result.event = 'page_show';
    result.page_url = location.href;
  }

  if(isDev) {
    conflictWarning(payload, ...Object.keys(result));
  }

  return Object.assign(result, payload);
};

let isInit = false;
let enterTime = Date.now();

/**
 * epviframe
 *
 * @param {MetricReporter} [reporter] 
 */
export const initGlobalMetrics = (reporter) => {
  if(isInit) {
    console.warn('initGlobalMetrics has been called');
    return;
  }

  if(!getCurrentPlatform().isNativePlatform) {
    // unload
    window.addEventListener('unload', () => {
      sendMetrics(
        {
          op: 'epv',
          sub_op: 'leave',
          enter_time: enterTime,
          close_time: Date.now()
        },
        reporter
      );
    });

    // visibilitychangeonbeforeunload
    if(typeof document.hidden !== 'undefined') {
      document.addEventListener('visibilitychange', () => {
        if(document.hidden) {
          sendMetrics(
            {
              op: 'epv',
              sub_op: 'leave',
              enter_time: enterTime
            },
            reporter
          );
        } else {
          enterTime = Date.now();
        }
      });
    }
  }

  isInit = true;
};

function resolveLogURL(params, config) {
  const logUrl = config.httpsLoggingURL;
  const logRoutes = config.httpsLogRoutes;

  //  https://jira2.corp.yiran.com/browse/PDD-23372  ad 
  // op  impr  click
  // 
  const routeKey = params.ad && ['click', 'impr'].includes(params.op) ? 'ad' : params.op;

  return logRoutes[routeKey] || logUrl;
}
