/**
 * @file retryCountHelper.js
 * @author tenghu
 */
const RETRY_COUNT_KEY = 'cuiRetryCount';

/**
 *
 * @param {HTMLElement} img - dom
 * @param {number} retryCount - retry
 */
export function setRetryCount(img, retryCount) {
  img.dataset[RETRY_COUNT_KEY] = retryCount;
}

/**
 *
 * @param {HTMLElement} img - dom
 * @returns {number} - retry
 */
export function getRetryCount(img) {
  return parseInt(img.dataset[RETRY_COUNT_KEY]) || 0;
}
