/**
 * @file ApiCacheManager.js
 * @author daben
 */

class ApiCacheManager {
  constructor(opts = {}) {
    // key: hash
    // value: { response, count = 1 }
    this._cacheMap = {};
    this._cacheLimit = opts.cacheLimit || 20;
  }

  getResponseByHash(hash = '') {
    const data = this._cacheMap[hash];
    if (!data) {
      return null;
    }
    return data.response;
  }

  cacheResponseByHash(hash = '', response) {
    if (!hash || !response) {
      return;
    }
    const cachedNum = Object.keys(this._cacheMap).length || 0;
    // 
    if (cachedNum >= this._cacheLimit) {
      return;
    }
    const data = this._cacheMap[hash];
    if (data) {
      // , response
      data.response = response;
    } else {
      // 
      this._cacheMap[hash] = {
        response,
        count: 1,
        trigger: false,
        complete: false
      };
    }
  }

  _write(key = '', hash = '', value) {
    const conf = (this._cacheMap[hash] || {});
    conf[key] = value;
    this._cacheMap[hash] = conf;
  }

  _read(key = '', hash = '') {
    return (this._cacheMap[hash] || {})[key];
  }

  getCountByHash(hash = '') {
    return this._read('count', hash) || 0;
  }

  increaseCount(hash = '') {
    return this._write('count', hash, this.getCountByHash() + 1);
  }

  getShareID(hash = '') {
    return this._read('share_id', hash);
  }

  setShareID(hash = '', shareID = null) {
    return this._write('share_id', hash, shareID);
  }
}

let instance = null;
export function getInstance() {
  if (!instance) {
    instance = new ApiCacheManager(...arguments);
  }
  return instance;
}

export default ApiCacheManager;
