import { INITIAL_PROPS_LOADED } from './constant';

const WEBP_KEY = 'webp';
const WEBP_VALUE_TRUE = '1';

/**
 * webp
 * @param {Object} cookies - requestContext.cookies
 * @returns {boolean} webp
 */
export function isSupportWebpFromCookie(cookies) {
  return WEBP_VALUE_TRUE === cookies[WEBP_KEY];
}

export function catchError(func) {
  try {
    return Promise.resolve(func());
  } catch (e) {
    return Promise.reject(e);
  }
}

export function waitRawDataReady() {
  return new Promise(res => {
    // rawDataDOM ready
    if(window.hasOwnProperty('rawData') || document.readyState !== 'loading') {
      res();
    } else {
      document.addEventListener(INITIAL_PROPS_LOADED, res, { once: true });
      document.addEventListener('DOMContentLoaded', res, { once: true });
    }
  });
}

export function onDOMReady(callback) {
  if(document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', callback, { once: true });
  } else {
    callback();
  }
}
